import get from 'lodash/get';

export function getPathParams() {
	let appId = null;
	let roleId = null;

	const re = /\/application\/(.*)\/role\/(.*)$/;
	const path = window.location.pathname.split('?')[0];
	const pathParams = path.match(re);

	if (pathParams.length !== 3) {
		// Missing path params in pathname
		console.error('Missing path params in ' + window.location.pathname);
	} else {
		appId = pathParams[1];
		roleId = pathParams[2];
	}

	return {
		appId,
		roleId,
	};
}

export function getAESAPI() {
	return (
		get(window, 'appEngineStudioBuilderAPI') ||
		get(window, 'frameElement.appEngineStudioBuilderAPI')
	);
}
