import { createCustomElement } from '@servicenow/ui-core';

import styles from './sn-aes-reference-modal.scss';

createCustomElement('sn-aes-reference-modal', {
	view({ properties: { id, headerText, description, headerIconSrc } }) {
		return (
			<div id={`reference-modal-${id}`} key={id} className="reference-modal">
				<div className="header now-align">
					{headerIconSrc ? (
						<img alt="" className="header-icon" src={headerIconSrc} />
					) : null}
					<div className="header-text">{headerText}</div>
				</div>
				{description ? <div className="description">{description}</div> : null}
			</div>
		);
	},
	properties: {
		/**
		 * Guidance modal id
		 * @type {string}
		 */
		id: { default: '' },
		/**
		 * Guidance modal header text
		 * @type {string}
		 */
		headerText: { default: '' },
		/**
		 * Guidance modal description
		 * @type {string}
		 */
		description: { default: '' },
		/**
		 * icon displayed on header title
		 * @type {string}
		 */
		headerIconSrc: { default: '' },
	},
	actionHandlers: {},
	dispatches: {},
	styles,
});
