import {
	FILE_SET,
	FILE_REMOVE,
	FILE_DROP,
	DRAGOVER_SET,
	PARSE_PREVIEW_IMG,
	SET_PREVIEW_IMG,
	UPLOAD_STARTED,
	UPLOAD_SUCCESS,
	UPLOAD_FAILURE,
	UPLOAD_FILE,
} from './constants';
import { parsePreviewImageEffect, handleFileDropEffect, uploadImageEffect } from './effects';

export default {
	[FILE_SET]: ({ action: { payload }, updateState }) =>
		updateState({
			selectedFile: payload,
			isComplete: false,
			isUploading: false,
		}),
	[FILE_REMOVE]: ({ updateState }) =>
		updateState({
			selectedFile: null,
			selectedImage: null,
			isComplete: false,
			isUploading: false,
		}),
	[SET_PREVIEW_IMG]: ({ action: { payload }, updateState }) =>
		updateState({
			selectedImage: payload.img,
		}),
	[DRAGOVER_SET]: ({
		action: {
			payload: { isDragging },
		},
		updateState,
	}) =>
		updateState({
			isDragging,
		}),
	[UPLOAD_STARTED]: ({ updateState }) =>
		updateState({
			isUploading: true,
		}),
	/**
	 * To upload a file, set a ref on <gac-file-uploader ref={createRef()} /> implementation
	 * then dispatch UPLOAD_FILE with request body: ref.current.dispatch(UPLOADFILE, options)
	 * Required fields for upload request body: {
	 * 	  table_name, (string, table name to attach file to)
	 *    table_sys_id, ( string, record id to attach file to)
	 *    file_name: (string, name of file)
	 * }
	 */
	[UPLOAD_FILE]: uploadImageEffect,
	[UPLOAD_SUCCESS]: ({
		action: {
			payload: { uploadResult },
		},
		updateState,
	}) =>
		updateState({
			isUploading: false,
			isComplete: true,
			uploadResult,
		}),
	[UPLOAD_FAILURE]: () => {},
	[FILE_DROP]: handleFileDropEffect,
	[PARSE_PREVIEW_IMG]: parsePreviewImageEffect,
};
