import get from 'lodash/get';
import pick from 'lodash/pick';
import { t } from 'sn-translate';

export const renderSingleTab = (tabs, template) => {
	const tabId = tabs[0].id;

	switch (tabId) {
		case 'instructions':
			return (
				<now-rich-text
					className="instructions"
					html={get(template, 'details.how_was_built', '')}
				/>
			);
		case 'data':
			return (
				<img className="tab-image" src={get(template, 'details.data_image', '')} alt="" />
			);
		default:
			return (
				<img className="tab-image" src={get(template, 'details.schema_image', '')} alt="" />
			);
	}
};

export const renderTabContent = state => {
	const {
		selectedTabId,
		properties: {
			template: {
				details: { how_was_built, data_image, schema_image },
			},
		},
	} = state;

	let render;

	if (selectedTabId === 'instructions') {
		return (
			<div className="tab-content instructions">
				<now-rich-text html={how_was_built} tabindex="0" />
			</div>
		);
	} else if (selectedTabId === 'data') {
		render = data_image;
	} else {
		render = schema_image;
	}

	return (
		<div className="tab-content">
			<img className="tab-image" src={render} alt="" />
		</div>
	);
};

export const getCarouselElements = template => {
	const imagePath = ['details.hero1_image', 'details.hero2_image', 'details.hero3_image'];
	const elements = [];

	imagePath.forEach(path => {
		const image = get(template, path);
		// we can't provide a property for the carousel to have rounded images. so manually it is.
		const boxShadow = 'box-shadow: 0 2px 4px RGBA(131, 131, 131, 0.25);';
		const style = `max-width: 630px; max-height: 420px; border-radius: 12px;${boxShadow};`;

		if (image) {
			elements.push({
				useCustomHtml: true,
				backgroundTransparent: true,
				richText: `<img style="${style}" src="${image}" alt=""/>`,
			});
		}
	});

	return elements;
};

export const getTabElements = template => {
	const allItems = [
		{ id: 'instructions', label: t('Instructions'), path: 'details.how_was_built' },
		{ id: 'data', label: t('Data'), path: 'details.data_image' },
		{ id: 'schema', label: t('Schema'), path: 'details.schema_image' },
	];
	const requiredProps = ['id', 'label'];
	const tabs = [];

	allItems.forEach(item => {
		const data = get(template, item.path);

		if (data) {
			tabs.push(pick(item, requiredProps));
		}
	});

	return tabs;
};
