// Amount of objects to show when
// all categories are selected
const PREVIEW_SIZE = 3;
const APP_STATUS_COLOR = {
	[IN_DEVELOPMENT]: 'low',
	[PENDING_APPROVAL]: 'high',
	[IN_VALIDATION]: 'blue',
	[PUBLISHED]: 'positive',
	[REJECTED]: 'critical',
};

function renderFilterTabs(state, hasSearchResults) {
	const { filesByCategoryId, searchResultsByCategoryId, categoriesById, category } = state;
	return (
		<now-tabs
			selectedItem={category.sys_id}
			items={getTabs(
				hasSearchResults,
				categoriesById,
				filesByCategoryId,
				searchResultsByCategoryId
			)}
		/>
	);
}

function renderInput(state, dispatch) {
	const {
		category,
		properties: {
			app: { files },
		},
	} = state;

	const showSort = window.location.search.match('debug');
	return (
		<div>
			{showSort ? (
				// TODO: Temporary sorting only visible if "?debug" is present in url
				// FIXME: DELETE CODE WHEN SORT IS IMPLEMENTED
				<div>
					<h2>Sort</h2>
					<now-button
						variant={state.sortType === 'dateDesc' ? 'primary' : 'secondary'}
						label="Date Descending"
						on-click={() => dispatch(APP_TAB__SORT_TYPE_SET, 'dateDesc')}
					/>
					<now-button
						variant={state.sortType === 'dateAsc' ? 'primary' : 'secondary'}
						label="Date Ascending"
						on-click={() => dispatch(APP_TAB__SORT_TYPE_SET, 'dateAsc')}
					/>
					<now-button
						variant={state.sortType === 'nameAsc' ? 'primary' : 'secondary'}
						label="Name Ascending"
						on-click={() => dispatch(APP_TAB__SORT_TYPE_SET, 'nameAsc')}
					/>
					<now-button
						variant={state.sortType === 'nameDesc' ? 'primary' : 'secondary'}
						label="Name Descending"
						on-click={() => dispatch(APP_TAB__SORT_TYPE_SET, 'nameDesc')}
					/>
				</div>
			) : null}
			<div className="search-input-container">
				<sn-aes-search-input
					name="sn-aes-app-tab-search-input"
					className="search-input"
					placeholder={t('Search {0}', category.name.toLocaleLowerCase())}
					disabled={files.length === 0}
				/>
			</div>
		</div>
	);
}

function renderEmptyFileCard({
	category,
	isAllTab,
	isGuidanceModalOpen,
	sidebarCurrentStep,
	sidebarDisplayed,
	sortedFiles,
}) {
	return (
		<div className="empty-aoc-wrapper">
			{isAllTab &&
			isGuidanceModalOpen &&
			sidebarDisplayed &&
			sidebarCurrentStep.id === category.sys_id ? (
				<sn-aes-guidance-modal
					id={`${category.sys_id}-guidance-modal`}
					className="guidance-modal -middle"
					headerText={t('Add {0}', sidebarCurrentStep.displayText.toLowerCase())}
					illustration={`/${sidebarCurrentStep.category.icon}.iix`}
					description={getGuidanceDescription(category, sortedFiles)}
					modalPosition="middle"
				/>
			) : null}
			<sn-aes-application-object-card
				id={category.sys_id}
				empty={true}
				emptyText={getEmptyCardText(category)}
				categoryName={category.name}
			/>
		</div>
	);
}

function getCategorySubtitle(category) {
	const categorySubtitleMapping = {
		[DATA_CATEGORY_SYS_ID]: t('Store information in your app'),
		[EXPERIENCE_CATEGORY_SYS_ID]: t('Create interfaces for users to interact with the app'),
		[AUTOMATION_CATEGORY_SYS_ID]: t('Add automated workflows to improve productivity'),
		[SECURITY_CATEGORY_SYS_ID]: t('Control access to your app'),
	};
	if (category) {
		return categorySubtitleMapping[category.sys_id] || '';
	}
}

function getEmptyCardText(category) {
	const emptyCardTextMapping = {
		[DATA_CATEGORY_SYS_ID]: t('Add a table or upload a spreadsheet'),
		[EXPERIENCE_CATEGORY_SYS_ID]: t('Add an interface'),
		[AUTOMATION_CATEGORY_SYS_ID]: t('Add a process or third-party integration'),
		[SECURITY_CATEGORY_SYS_ID]: t('Add a role or access control'),
	};
	if (category) {
		return emptyCardTextMapping[category.sys_id] || '';
	}
}

function getGuidanceDescription(category, files) {
	const guidanceDescriptionEmptyMapping = {
		[DATA_CATEGORY_SYS_ID]: t(
			'Data is the starting point for any app. Start with a table or upload a spreadsheet.'
		),
		[EXPERIENCE_CATEGORY_SYS_ID]: t(
			'Start on the front-end by adding an interface, like a workspace or portal.'
		),
		[AUTOMATION_CATEGORY_SYS_ID]: t(
			'Automation streamlines processes and workflows within your organization.'
		),
		[SECURITY_CATEGORY_SYS_ID]: t(
			'Add roles, like admins and collaborators, to understand who has access to what.'
		),
	};
	const guidanceDescriptionMapping = {
		[DATA_CATEGORY_SYS_ID]: t('Need more data in your app? Add another table to your app.'),
		[EXPERIENCE_CATEGORY_SYS_ID]: t(
			'Need more on the front-end? Add another interface to your app.'
		),
		[AUTOMATION_CATEGORY_SYS_ID]: t(
			'Need more automation? Add another automated workflow to your app.'
		),
		[SECURITY_CATEGORY_SYS_ID]: t(
			'Need more security? Add another role or access control to your app.'
		),
	};

	if (category) {
		return files && files.length
			? guidanceDescriptionMapping[category.sys_id] || ''
			: guidanceDescriptionEmptyMapping[category.sys_id] || '';
	}
}

function getCategoryTitle({
	dispatch,
	isAllTab,
	isGuidanceModalOpen,
	sidebarCurrentStep,
	sidebarDisplayed,
	category,
	allLength = 0,
	sortedFiles = [],
}) {
	return (
		<Fragment>
			<div className="category-title-wrapper">
				<div className="now-align -inline-start -block-center">
					<h2 className="category-title">{category.name}</h2>
					<div className="button-wrapper">
						<now-button-iconic
							bare
							icon="circle-plus-outline"
							size="md"
							variant="tertiary"
							configAria={{
								'aria-label': t(
									'Click to create object for category {0}',
									category.name
								),
							}}
							on-click={() => {
								dispatch(APP_TAB__CATEGORY_CREATE_PRESSED, {
									category,
								});
							}}
						/>
					</div>
				</div>
				{sortedFiles.length > 0 &&
				isAllTab &&
				isGuidanceModalOpen &&
				sidebarDisplayed &&
				sidebarCurrentStep.id === category.sys_id ? (
					<sn-aes-guidance-modal
						id={category.sys_id}
						className="guidance-modal -top"
						headerText={t('Add {0}', sidebarCurrentStep.displayText.toLowerCase())}
						illustration={`/${sidebarCurrentStep.category.icon}.iix`}
						description={getGuidanceDescription(category, sortedFiles)}
						modalPosition="top"
					/>
				) : null}
			</div>
			<div className="see-all now-align -block-center">
				{isAllTab && sortedFiles.length ? (
					<now-button-bare
						size="sm"
						id={`see-all-button-${category.sys_id}`}
						className="see-all-button"
						label={t('See all ({0})', allLength)}
						variant="secondary"
						on-click={() =>
							dispatch('NOW_TABS#SELECTED_ITEM_SET', {
								value: category.sys_id,
							})
						}
					/>
				) : null}
			</div>
			<div className="category-subtitle-wrapper">
				<h3 className="category-subtitle">{getCategorySubtitle(category)}</h3>
			</div>
		</Fragment>
	);
}

function renderFileCard(file, searchTerm, icon, type, tableName) {
	const {
		launch_url: launchUrl,
		description: { display_value: descriptionValue } = {},
		updated: { display_value: timestampValue } = {},
	} = file;
	let dropDownItems = [
		{
			id: 'edit',
			label: t('Edit'),
		},
	];

	// deleting is not allowed on referenced items
	// Delete cascading isn't currently supported for Mobile objects
	if (!file.is_referenced && !isMobileObject(file)) {
		dropDownItems.push({
			id: 'delete',
			label: t('Delete'),
		});
	}

	if (typeof launchUrl === 'string' && launchUrl.length > 0) {
		dropDownItems.push({
			id: 'launch',
			label: t('Preview'),
		});
	}

	return (
		<div className="card-container">
			<sn-aes-application-object-card
				id={file.sys_id}
				displayName={file.name}
				description={descriptionValue}
				timestamp={timestampValue}
				status="ready"
				highlightText={searchTerm.length >= 3 ? searchTerm : ''}
				items={dropDownItems}
				type={type}
				isReference={file.is_referenced}
				tableName={tableName}>
				<img
					alt=""
					width={24}
					height={24}
					aria-hidden="true"
					slot={APPLICATION_OBJECT_CARD_IMG_SLOT}
					src={icon}
				/>
			</sn-aes-application-object-card>
		</div>
	);
}

function renderAllCategories(state, dispatch, hasSearchResults) {
	const {
		category,
		filesByCategoryId,
		searchResultsByCategoryId,
		filesById,
		categoriesById,
		sortType,
		searchTerm,
		objectTypesById,
		isGuidanceModalOpen,
		properties: { sidebarCurrentStep, sidebarDisplayed },
	} = state;
	const allCategories = Object.values(categoriesById);

	const filesToParse = hasSearchResults ? searchResultsByCategoryId : filesByCategoryId;

	if (category.sys_id === ALL_CATEGORY) {
		return sortBy(allCategories, 'order').map(category => {
			const files = filesToParse[category.sys_id] || [];

			// Store the length before slicing for the "see all" link
			const allLength = files && files.length;

			const sortedFiles = files
				.map(fileId => filesById[fileId])
				.sort(sortMap[sortType])
				.slice(0, PREVIEW_SIZE);

			//TODO: This className will change when translations are turned on or the language
			// is changed on the instance. This should be redone.
			return (
				<div className={`${category.name.toLowerCase()} list category-preview-container`}>
					<div className="category-heading">
						{getCategoryTitle({
							dispatch,
							isAllTab: true,
							isGuidanceModalOpen,
							sidebarCurrentStep,
							sidebarDisplayed,
							category,
							allLength,
							sortedFiles,
						})}
					</div>
					{sortedFiles.length === 0
						? renderEmptyFileCard({
								category,
								isAllTab: true,
								isGuidanceModalOpen,
								sidebarCurrentStep,
								sidebarDisplayed,
								sortedFiles,
						  })
						: null}
					{sortedFiles.map(file => {
						const objectType =
							objectTypesById[file.taxonomy_definition] ||
							objectTypesById[file.taxonomy_definition.sys_id] ||
							{};
						const objectTypeIcon = get(objectType, 'icon');
						const type = get(objectType, 'type', '');
						const tableName = get(objectType, 'table_name');
						// NOTE: It would seem a good practice to run this on each object once and store it on state
						addTaxonomyDefinitionToFile(file, objectTypesById);
						return renderFileCard(file, searchTerm, objectTypeIcon, type, tableName);
					})}
				</div>
			);
		});
	}
}

function renderFilesForCategory(state, dispatch, hasSearchResults) {
	const {
		category,
		sortType,
		filesById,
		searchResultsByCategoryId,
		filesByCategoryId,
		pagination,
		searchTerm,
		objectTypesById,
		isGuidanceModalOpen,
		properties: { sidebarCurrentStep, sidebarDisplayed },
	} = state;
	const filesToParse = hasSearchResults ? searchResultsByCategoryId : filesByCategoryId;
	const files = filesToParse[category.sys_id] || [];
	const currentPage =
		(pagination[category.sys_id] && pagination[category.sys_id].currentPage) || 1;

	const sortedFiles = files.map(fileId => filesById[fileId]).sort(sortMap[sortType]);

	const ITEMS_PER_PAGE = 10;
	const basePage = (currentPage - 1) * ITEMS_PER_PAGE;
	const paginatedFiles = sortedFiles.slice(basePage, basePage + ITEMS_PER_PAGE);

	if (category.sys_id !== ALL_CATEGORY) {
		//TODO: This className will change when translations are turned on or the language
		// is changed on the instance. This should be redone.
		return (
			<div className={`${category.name.toLowerCase()} category-list-container`}>
				{getCategoryTitle({
					dispatch,
					isAllTab: false,
					isGuidanceModalOpen,
					sidebarCurrentStep,
					sidebarDisplayed,
					category,
				})}
				<div className="cards-container">
					{sortedFiles.length === 0
						? renderEmptyFileCard({
								dispatch,
								isAllTab: true,
								isGuidanceModalOpen,
								sidebarCurrentStep,
								sidebarDisplayed,
								category,
								allLength: sortedFiles.length,
								sortedFiles,
						  })
						: paginatedFiles.map(file => {
								const objectType =
									objectTypesById[file.taxonomy_definition] ||
									objectTypesById[file.taxonomy_definition.sys_id] ||
									{};
								const objectTypeIcon = get(objectType, 'icon');
								const type = get(objectType, 'type', '');
								const tableName = get(objectType, 'table_name');
								addTaxonomyDefinitionToFile(file, objectTypesById);
								// NOTE: It would seem a good practice to run this on each object once and store it on state
								return renderFileCard(
									file,
									searchTerm,
									objectTypeIcon,
									type,
									tableName
								);
						  })}
				</div>
				{sortedFiles.length > ITEMS_PER_PAGE ? (
					<div className="now-align -inline">
						<sn-aes-simple-paginate
							name={category.sys_id}
							item-length={sortedFiles && sortedFiles.length}
							current-page={currentPage}
							managed
						/>
					</div>
				) : null}
			</div>
		);
	}
}

createCustomElement('sn-aes-app-tab', {
	view: (state, { updateState, dispatch }) => {
		const {
			category,
			searchTerm,
			isSubmittingApp,
			isDeploymentRequestOpen,
			submitRef,
			appStatus,
			publishDate,
			submitTooltipContent,
			isGuidanceModalOpen,
			OverlayComponent,
			properties: { sidebarCurrentStep, sidebarDisplayed },
		} = state;

		const { app } = state.properties;
		const hasSearchResults = searchTerm.length >= SEARCH_INPUT_MINIMUM_LENGTH;

		if (!app) {
			return null;
		}

		return (
			<section>
				{OverlayComponent ? <OverlayComponent /> : null}

				<div className="sn-aes-app-tab">
					<div className="app-home-header">
						<div className="brand">
							{app.illustration ? (
								<div
									className="brand__logo"
									role="img"
									aria-label={t('Application logo')}
									style={{
										'background-image': `url("${app.illustration}")`,
									}}
								/>
							) : (
								<div
									className="brand__logo"
									role="img"
									aria-label={t('Application logo')}
									style={{
										'background-image': `url("${getPlaceholderImage(
											app.sys_id
										)}")`,
									}}
								/>
							)}
							<h1 className="app-title">{app.name.display_value}</h1>
							{appStatus ? (
								<now-highlighted-value
									id="app-status"
									label={appStatus.display_value || ''}
									icon=""
									color={APP_STATUS_COLOR[appStatus.value]}
								/>
							) : null}
							{publishDate ? (
								<span className="publish-date">
									{t('Updated: {0}', publishDate)}
								</span>
							) : null}
						</div>
						<div className="header-action">
							<now-button-iconic
								icon="gear-fill"
								tooltipContent={t('Edit application properties')}
								size="md"
								variant="secondary"
								className="header-action-btn btn-settings"
								configAria={{
									'aria-label': t('Edit application properties'),
								}}
								append-to-payload={{ source: 'update-properties' }}
							/>
							<now-button
								id="submit"
								disabled={isSubmittingApp || isDeploymentRequestOpen}
								size="md"
								variant="secondary"
								className="header-action-btn btn-submit"
								label={t('Submit')}
								append-to-payload={{ source: 'submit' }}
								on-mouseenter={({ target }) => {
									isDeploymentRequestOpen && updateState({ submitRef: target });
								}}
								on-mouseleave={() => {
									updateState({ submitRef: undefined });
								}}
							/>
							<now-tooltip
								content={submitTooltipContent}
								delay={500}
								id="submit-tooltip"
								opened={submitRef && submitRef.matches(':hover')}
								target-ref={submitRef}
							/>
							{category.sys_id === ALL_CATEGORY &&
							isGuidanceModalOpen &&
							sidebarDisplayed &&
							sidebarCurrentStep.type === SIDEBAR_STEP_TYPE_COMPLETION ? (
								<sn-aes-guidance-modal
									id="launch-step"
									className="guidance-modal -bottom"
									headerText={t('Submit')}
									illustration={'aes_submit_tourpoint.svg'}
									description={t(
										'Ready to launch? Once your app is approved, it will be live and ready to use.'
									)}
									modalPosition="bottom"
									variant="secondary"
								/>
							) : null}
						</div>
						<div className="filter-container">
							<div className="tabs-container">
								{renderFilterTabs(state, hasSearchResults)}
							</div>
							<div className="search-container">{renderInput(state, dispatch)}</div>
						</div>
					</div>
					<div className="app-home-content">
						<div className="main-content">
							{renderAllCategories(state, dispatch, hasSearchResults)}
							{renderFilesForCategory(state, dispatch, hasSearchResults)}
						</div>
					</div>
				</div>
			</section>
		);
	},
	initialState: {
		// Data stores
		filesById: {},
		searchResultsById: {},
		categoriesById: {},
		objectTypesById: {},

		// Data reference groups
		filesByCategoryId: {},
		searchResultsByCategoryId: {},

		category: { name: ALL_CATEGORY, sys_id: ALL_CATEGORY },
		sortType: 'dateDesc',
		objectSearchIndex: null,
		searchTerm: '',
		pagination: {},
		isSubmittingApp: false,
		appStatus: null,
		publishDate: null,
		isDeploymentRequestOpen: true,
		submitTooltipContent: getSubmitTooltipContent(),

		// Guidance modal
		isGuidanceModalOpen: false,
		OverlayComponent: null,
	},
	properties: {
		app: { default: null },
		filesById: {
			computed({ properties: { app } }) {
				return normalizeById(app.files);
			},
		},
		taxonomyCategoriesById: {
			computed({ properties: { app } }) {
				return normalizeById(app.taxonomy_categories);
			},
		},
		taxonomyDefinitionsById: {
			computed({ properties: { app } }) {
				return normalizeById(app.taxonomy_definitions);
			},
		},
		importSetTableNames: {
			default: PROVIDER_PROPERTIES[IMPORTING_DATA_PROPERTY],
		},
		appTabOverlayTargetRef: { default: null },
		appCreatedFromScratch: {
			computed({ properties: { app } }) {
				const details = app.details;
				let operations;

				// if details property doesn't exist on app, perhaps because
				// an error occurred while creating app details record, show guidance
				if (!details) {
					return true;
				}

				operations = get(details, 'operations', '[]');

				try {
					operations = JSON.parse(operations);
				} catch (e) {
					return false;
				}

				return isCreateFromScratchApp(operations);
			},
		},
		templates: {
			default: PROVIDER_PROPERTIES[TEMPLATE_DATA_PROPERTY],
		},
		sidebarAllSteps: {
			default: PROVIDER_PROPERTIES[SIDEBAR_ALL_STEPS_PROPERTY],
		},
		sidebarCurrentStep: {
			default: PROVIDER_PROPERTIES[SIDEBAR_CURRENT_STEP_PROPERTY],
		},
		sidebarDisplayed: {
			default: PROVIDER_PROPERTIES[SIDEBAR_DISPLAYED_PROPERTY],
		},
		sidebarOpen: {
			default: PROVIDER_PROPERTIES[SIDEBAR_OPEN_PROPERTY],
		},
		appTabFocused: { default: false },
		prodInstanceAesConfig: {
			default: PROVIDER_PROPERTIES.prodInstanceAesConfig,
		},
		prodAdminContact: {
			computed({ properties: { prodInstanceAesConfig } }) {
				return getAdminContact(prodInstanceAesConfig);
			},
		},
	},
	actionHandlers,
	behaviors: [
		{
			behavior: sessionScopeBehavior,
			options: {
				onVisibleOnly: true,
			},
		},
	],
	styles,
});
