import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-loader';

import '../../../components/sn-aes-image-card';
import '../../../components/sn-aes-section-header';
import '../../../components/sn-aes-empty-state';

import { topicContainsContent, topicsIncludesTopic } from '../helpers';

import { RESOURCES__CONTENT_FETCH_REQUESTED } from '../constants';

import styles from './sn-aes-resources-content.scss';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

createCustomElement('sn-aes-resources-content', {
	view: ({ properties }) => {
		const { content, topics, showLoader } = properties;

		if (showLoader) {
			return <now-loader />;
		}

		const renderContent = topic => {
			return content
				.filter(({ topics }) => topicsIncludesTopic(topics, topic))
				.sort((a, b) => parseInt(a.order, 10) - parseInt(b.order, 10))
				.map(contentItem => {
					return (
						<a
							href={contentItem.content_url}
							className="card-container-link"
							target="_blank"
							tabindex="-1">
							<sn-aes-image-card
								coverSize
								longTitle
								id={contentItem.sys_id}
								key={contentItem.sys_id}
								cardImage={contentItem.image}
								footerTitle={contentItem.title}
								footerDescription={contentItem.description}
							/>
						</a>
					);
				});
		};

		const renderResources = () => {
			if (topics.length) {
				return topics.map(topic => {
					if (topicContainsContent(topic, content)) {
						return (
							<section className="fade-in">
								<header className="section-header">
									<sn-aes-section-header
										id={topic.sys_id}
										key={topic.sys_id}
										headerText={topic.label}
										headerTextSize="md"
									/>
								</header>
								<div className="four-up content">{renderContent(topic)}</div>
							</section>
						);
					}
					return null;
				});
			}
			return null;
		};

		return (
			<div className="resources-content-container">
				<div className="resources">{renderResources()}</div>
			</div>
		);
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch(RESOURCES__CONTENT_FETCH_REQUESTED);
		},
	},
	properties: {
		content: {
			default: [],
		},
		topics: {
			default: [],
		},
		showLoader: {
			default: true,
		},
	},
	styles,
});
