import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_PROPERTY_CHANGED } = actionTypes;
import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	BEHAVIOR_NAME,
	TAXONOMIES_DATA_PROPERTY,
	TAXONOMY_CATEGORIES_DATA_PROPERTY,
	LINKED_TAXONOMIES_DATA_PROPERTY,
	TAXONOMY_INITIAL_FETCH_PROPERTY,
	TAXONOMY_CATEGORY_INITIAL_FETCH_PROPERTY,
	FETCH_TAXONOMIES_URL,
	FETCH_CATEGORIES_URL,
	TAXONOMY_DATA_BEHAVIOR__INITIAL_FETCH,
	TAXONOMY_DATA_BEHAVIOR__FETCH_TAXONOMIES,
	TAXONOMY_DATA_BEHAVIOR__TAXONOMIES_FETCH_SUCCEEDED,
	TAXONOMY_DATA_BEHAVIOR__TAXONOMIES_FETCH_FAILED,
	TAXONOMY_DATA_BEHAVIOR__FETCH_TAXONOMY_CATEGORIES,
	TAXONOMY_DATA_BEHAVIOR__TAXONOMY_CATEGORIES_FETCH_SUCCEEDED,
	TAXONOMY_DATA_BEHAVIOR__TAXONOMY_CATEGORIES_FETCH_FAILED,
} from './constants';

import { linkTaxonomyCategory } from './helpers';

const taxonomyDataBehavior = {
	name: BEHAVIOR_NAME,
	properties: {
		[TAXONOMIES_DATA_PROPERTY]: {
			default: [],
			selectable: true,
		},
		[TAXONOMY_CATEGORIES_DATA_PROPERTY]: {
			default: [],
			selectable: true,
		},
		[LINKED_TAXONOMIES_DATA_PROPERTY]: {
			default: [],
			selectable: true,
		},
		[TAXONOMY_INITIAL_FETCH_PROPERTY]: {
			default: false,
			selectable: true,
		},
		[TAXONOMY_CATEGORY_INITIAL_FETCH_PROPERTY]: {
			default: false,
			selectable: true,
		},
	},
	actionHandlers: {
		[TAXONOMY_DATA_BEHAVIOR__INITIAL_FETCH]: ({ properties, dispatch }) => {
			if (
				properties[TAXONOMY_INITIAL_FETCH_PROPERTY] &&
				properties[TAXONOMY_CATEGORY_INITIAL_FETCH_PROPERTY]
			) {
				return;
			}
			dispatch(TAXONOMY_DATA_BEHAVIOR__FETCH_TAXONOMIES);
			dispatch(TAXONOMY_DATA_BEHAVIOR__FETCH_TAXONOMY_CATEGORIES);
		},
		[TAXONOMY_DATA_BEHAVIOR__FETCH_TAXONOMIES]: createHttpEffect(FETCH_TAXONOMIES_URL, {
			method: 'GET',
			successActionType: TAXONOMY_DATA_BEHAVIOR__TAXONOMIES_FETCH_SUCCEEDED,
			errorActionType: TAXONOMY_DATA_BEHAVIOR__TAXONOMIES_FETCH_FAILED,
			batch: false,
		}),
		[TAXONOMY_DATA_BEHAVIOR__TAXONOMIES_FETCH_SUCCEEDED]: ({ action, updateProperties }) => {
			const { result: taxonomies } = action.payload;
			updateProperties({
				[TAXONOMIES_DATA_PROPERTY]: taxonomies,
				[TAXONOMY_INITIAL_FETCH_PROPERTY]: true,
			});
		},
		[TAXONOMY_DATA_BEHAVIOR__TAXONOMIES_FETCH_FAILED]: ({ action }) => {
			console.error(action.payload);
		},
		[TAXONOMY_DATA_BEHAVIOR__FETCH_TAXONOMY_CATEGORIES]: createHttpEffect(
			FETCH_CATEGORIES_URL,
			{
				method: 'GET',
				successActionType: TAXONOMY_DATA_BEHAVIOR__TAXONOMY_CATEGORIES_FETCH_SUCCEEDED,
				errorActionType: TAXONOMY_DATA_BEHAVIOR__TAXONOMY_CATEGORIES_FETCH_FAILED,
				batch: false,
			}
		),
		[TAXONOMY_DATA_BEHAVIOR__TAXONOMY_CATEGORIES_FETCH_SUCCEEDED]: ({
			action,
			updateProperties,
		}) => {
			const { result: taxonomyCategories } = action.payload;
			updateProperties({
				[TAXONOMY_CATEGORIES_DATA_PROPERTY]: taxonomyCategories,
				[TAXONOMY_CATEGORY_INITIAL_FETCH_PROPERTY]: true,
			});
		},
		[TAXONOMY_DATA_BEHAVIOR__TAXONOMY_CATEGORIES_FETCH_FAILED]: ({ action }) => {
			console.error(action.payload);
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			action: {
				payload: { name },
			},
			properties,
			updateProperties,
		}) => {
			if (name !== TAXONOMIES_DATA_PROPERTY && name !== TAXONOMY_CATEGORIES_DATA_PROPERTY) {
				// Early exit if it's neither the taxonomies or taxonomyCategories property
				return;
			}

			const taxonomies = properties[TAXONOMIES_DATA_PROPERTY];
			const taxonomyCategories = properties[TAXONOMY_CATEGORIES_DATA_PROPERTY];

			if (
				properties[TAXONOMY_INITIAL_FETCH_PROPERTY] &&
				properties[TAXONOMY_CATEGORY_INITIAL_FETCH_PROPERTY]
			) {
				const linkedTaxonomies = taxonomies.map(taxonomy =>
					linkTaxonomyCategory(taxonomyCategories, taxonomy)
				);
				updateProperties({
					[LINKED_TAXONOMIES_DATA_PROPERTY]: linkedTaxonomies,
				});
			}
		},
	},
};

export default taxonomyDataBehavior;
