import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import debounce from 'lodash/debounce';
import get from 'lodash/get';

import '@servicenow/now-icon';
import '@servicenow/now-toggle';
import '@servicenow/now-tooltip';

import {
	SIDEBAR_ACCESSIBLE_STEPS_PROPERTY,
	SIDEBAR_ALL_STEPS_PROPERTY,
	SIDEBAR_CURRENT_STEP_PROPERTY,
	SIDEBAR_DISPLAYED_PROPERTY,
	SIDEBAR_OPEN_PROPERTY,
	SIDEBAR_STEPS_LOCKED_PROPERTY,
	SIDEBAR_TOGGLE_INTERACTED_PROPERTY,
	sidebarToggle,
	sidebarSetStep,
	sidebarSetToggleInteracted,
} from '../../behaviors/sidebar-data';
import { PROVIDER_PROPERTIES } from '../sn-aes-provider';
import { t } from 'sn-translate';

import {
	USER_DATA_PROPERTY,
	USER_PREF_GUIDANCE_ENABLED,
	USER_DATA_BEHAVIOR__UPDATE_PREF,
} from '../../behaviors/user-data';

import styles from './sn-aes-guidance-sidebar.scss';

function getStep(step, currentStep, sidebarAccessibleSteps, sidebarStepsLocked, dispatch) {
	const isDisabled = !sidebarAccessibleSteps
		.map(accessibleStep => accessibleStep.id)
		.includes(step.id);

	return (
		<button
			id={step.id}
			aria-label={step.displayText}
			class={{
				'step-button': true,
				disabled: isDisabled || sidebarStepsLocked,
				'now-align': true,
				'-block-center': true,
			}}
			on-click={() => {
				if (!isDisabled && !sidebarStepsLocked) {
					sidebarSetStep(dispatch, { step });
				}
			}}>
			<span
				class={{
					'step-text': true,
					active: step.id === currentStep.id,
				}}>
				{step.displayText}
			</span>
		</button>
	);
}

function updateGuidanceEnabledPref(dispatch, isOpen) {
	dispatch(USER_DATA_BEHAVIOR__UPDATE_PREF, {
		name: USER_PREF_GUIDANCE_ENABLED,
		value: isOpen,
	});
}

function setUserPrefGuidanceEnabled(dispatch, properties) {
	const { user, isOpen, sidebarToggleInteracted } = properties;
	let userPrefGuidanceEnabled = true;

	if (get(user, 'preferences')) {
		const userPreferences = user.preferences.find(
			pref => pref.name === USER_PREF_GUIDANCE_ENABLED
		);

		if (userPreferences && userPreferences.value) {
			userPrefGuidanceEnabled = userPreferences && userPreferences.value !== 'false';
		}
	}

	if (!sidebarToggleInteracted && isOpen !== userPrefGuidanceEnabled) {
		sidebarToggle(dispatch, { isOpen: userPrefGuidanceEnabled });
	}
}

const debouncedUpdateGuidanceEnabledPref = debounce(updateGuidanceEnabledPref, 250);

createCustomElement('sn-aes-guidance-sidebar', {
	view(
		{
			guidanceIconRef,
			guidanceToggleRef,
			properties: {
				currentStep,
				isDisplayed,
				isOpen,
				sidebarAllSteps,
				sidebarAccessibleSteps,
				sidebarStepsLocked,
			},
		},
		{ updateState, dispatch }
	) {
		if (!isDisplayed) {
			return null;
		}

		return (
			<div
				class={{
					wrapper: true,
					'-open': isOpen,
				}}>
				<div
					class={{
						'guidance-stepper': true,
						'-closed': !isOpen,
					}}>
					{sidebarAllSteps.map(step =>
						getStep(
							step,
							currentStep,
							sidebarAccessibleSteps,
							sidebarStepsLocked,
							dispatch
						)
					)}
				</div>
				<div
					class={{
						toggle: true,
						'-closed': !isOpen,
						'now-align': true,
						'-inline': true,
						'-block-center': true,
					}}>
					<now-toggle
						hook-insert={({ elm }) => {
							updateState({
								guidanceToggleRef: elm,
							});
						}}
						hook-destroy={() =>
							updateState({
								guidanceToggleRef: null,
							})
						}
						size="md"
						manage-checked
						checked={isOpen}
						config-aria={{
							'aria-label': isOpen
								? t('Close guidance sidebar')
								: t('Open guidance sidebar'),
						}}
					/>
					{isOpen ? (
						<div className="now-align -inline-center">
							<div className="text">Guidance</div>
							<now-icon
								icon="circle-info-outline"
								size="md"
								bare
								variant="primary"
								hook-insert={({ elm }) => {
									updateState({
										guidanceIconRef: elm,
									});
								}}
								hook-destroy={() =>
									updateState({
										guidanceIconRef: null,
									})
								}
							/>
							<now-tooltip
								content={t(
									'This panel displays which step you are on in the app creation process. Use this toggle to show or hide this panel at any time'
								)}
								delay={500}
								id="guidance-icon-tooltip"
								target-ref={guidanceIconRef}
							/>
						</div>
					) : (
						<now-tooltip
							content={t(
								'Guidance is disabled. Use the toggle switch to turn it back on.'
							)}
							delay={500}
							id="guidance-toggle-tooltip"
							target-ref={guidanceToggleRef}
						/>
					)}
				</div>
			</div>
		);
	},
	initialState: {
		guidanceIconRef: null,
		guidanceToggleRef: null,
	},
	properties: {
		currentStep: {
			default: PROVIDER_PROPERTIES[SIDEBAR_CURRENT_STEP_PROPERTY],
		},
		isDisplayed: {
			default: PROVIDER_PROPERTIES[SIDEBAR_DISPLAYED_PROPERTY],
		},
		isOpen: {
			default: PROVIDER_PROPERTIES[SIDEBAR_OPEN_PROPERTY],
		},
		sidebarAllSteps: {
			default: PROVIDER_PROPERTIES[SIDEBAR_ALL_STEPS_PROPERTY],
		},
		sidebarAccessibleSteps: {
			default: PROVIDER_PROPERTIES[SIDEBAR_ACCESSIBLE_STEPS_PROPERTY],
		},
		sidebarStepsLocked: {
			default: PROVIDER_PROPERTIES[SIDEBAR_STEPS_LOCKED_PROPERTY],
		},
		sidebarToggleInteracted: {
			default: PROVIDER_PROPERTIES[SIDEBAR_TOGGLE_INTERACTED_PROPERTY],
		},
		user: {
			default: PROVIDER_PROPERTIES[USER_DATA_PROPERTY],
		},
	},
	actionHandlers: {
		['NOW_TOGGLE#CHECKED_SET']: ({ action, dispatch }) => {
			const isOpen = action.payload.value;

			sidebarSetToggleInteracted(dispatch);
			sidebarToggle(dispatch, { isOpen });
			debouncedUpdateGuidanceEnabledPref(dispatch, isOpen);
		},
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch }) => {
			setUserPrefGuidanceEnabled(dispatch, properties);
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action: {
				payload: { name },
			},
			properties,
			dispatch,
		}) => {
			if (name === 'user') {
				setUserPrefGuidanceEnabled(dispatch, properties);
			}
		},
	},
	styles,
});
