import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	BATCH_ROUTE,
	ROLE_BUILDER_ALL_OPERATION_LIST,
	ROLE_BUILDER_BATCH_HEADERS,
	ROLE_BUILDER_EXPERIENCE_UX_ROUTE_TYPE,
} from './constants';
import { t } from 'sn-translate';
import { getAESAPI } from './iframe-helpers';

export function getBatchEffect(successActionType, errorActionType) {
	return createHttpEffect(BATCH_ROUTE, {
		method: 'POST',
		successActionType,
		errorActionType,
		queryParams: ['sysparm_transaction_scope'],
		dataParam: 'batchData',
		batch: false,
	});
}

export function batchRequestFailed() {
	const aesAPI = getAESAPI();
	if (aesAPI) {
		aesAPI.showAlert({
			status: 'critical',
			content: t('We encountered an error while saving the role.'),
		});
	}
}

export function batchRequestInnerAPIFailed(batchResponse) {
	// Using set to avoid duplicate error messages (experience acl's)
	const errorSet = new Set();

	batchResponse.forEach(response => {
		const data = JSON.parse(response.id);
		if (response.status_code >= 400) {
			errorSet.add(
				t(
					'We encountered an error while {0} {1} ACL for {2}.',
					data.apiOperation === 'delete' ? 'deleting' : 'creating',
					data.aclOperation,
					data.fileName
				)
			);
		}
	});

	const aesAPI = getAESAPI();
	if (aesAPI) {
		for (let errMsg of errorSet) {
			aesAPI.showAlert({
				status: 'critical',
				content: errMsg,
			});
		}
	}

	return !!errorSet.size;
}

export function getDeleteACLRequest(appId, sysId, fileName, operation) {
	return {
		id: JSON.stringify({
			apiOperation: 'delete',
			tableName: 'sys_security_acl',
			fileName,
			aclOperation: operation,
		}),
		url: `/api/now/table/sys_security_acl/${sysId}?sysparm_transaction_scope=${appId}`,
		method: 'DELETE',
		headers: ROLE_BUILDER_BATCH_HEADERS,
	};
}

export function getCreateACLRequest(appId, file, operation, type) {
	let name = file.acl_file_name;
	if (type === ROLE_BUILDER_EXPERIENCE_UX_ROUTE_TYPE) {
		name = `${name}.*`;
	}

	return {
		id: JSON.stringify({
			apiOperation: 'create',
			tableName: 'sys_security_acl',
			fileName: file.name.display_value,
			aclOperation: operation,
		}),
		url: `/api/now/table/sys_security_acl?sysparm_transaction_scope=${appId}`,
		method: 'POST',
		body: JSON.stringify({
			name,
			operation,
			type,
		}),
		headers: ROLE_BUILDER_BATCH_HEADERS,
	};
}

export function getCreateAssociationRequest(appId, roleId, aclId, fileName, operation) {
	return {
		id: JSON.stringify({
			apiOperation: 'create',
			tableName: 'sys_security_acl_role',
			fileName,
			aclOperation: operation,
		}),
		url: `/api/now/table/sys_security_acl_role?sysparm_transaction_scope=${appId}`,
		method: 'POST',
		headers: ROLE_BUILDER_BATCH_HEADERS,
		body: JSON.stringify({
			sys_security_acl: aclId,
			sys_user_role: roleId,
		}),
	};
}

export function getCreateACLBatch(appId, originalRole, updatedRole) {
	const aclBatchData = { batch_request_id: 'create-batch-acl', rest_requests: [] };
	for (let idx = 0; idx < originalRole.files.length; idx++) {
		const file = originalRole.files[idx];
		const originalACLs = originalRole.files[idx].acls;
		const updatedACLs = updatedRole.files[idx].acls;

		if (originalRole.files[idx].sys_id !== updatedRole.files[idx].sys_id) {
			// This will never trigger, if it does, then something is mutating the
			// file order during updates
			throw new Error(
				'The order of files has changed, the update operations should only modify ACLs'
			);
		}

		ROLE_BUILDER_ALL_OPERATION_LIST.forEach(operation => {
			if (!originalACLs[operation] && updatedACLs[operation]) {
				// This acl was added to the file
				const [aclOperation, type] = operation.split('#');

				aclBatchData.rest_requests.push(
					getCreateACLRequest(appId, file, aclOperation, type)
				);
			}
		});
	}

	return aclBatchData;
}

export function getCreateAssociationBatch(batchACLResponse, appId, roleId) {
	// Loop over batchACLResponse and create association batch request
	const associationBatchData = {
		batch_request_id: 'create-batch-association',
		rest_requests: [],
	};

	batchACLResponse.forEach(response => {
		const responseBody = JSON.parse(atob(response.body));

		// Only create association if acl was created correctly
		if (
			response.status_code >= 200 &&
			response.status_code < 400 &&
			responseBody.result.sys_id
		) {
			const data = JSON.parse(response.id);

			associationBatchData.rest_requests.push(
				getCreateAssociationRequest(
					appId,
					roleId,
					responseBody.result.sys_id,
					data.fileName,
					data.aclOperation
				)
			);
		}
	});

	return associationBatchData;
}

export function getDeleteACLBatch(appId, originalRole, updatedRole) {
	const deleteBatchData = { batch_request_id: 'delete-batch-acl', rest_requests: [] };
	for (let idx = 0; idx < originalRole.files.length; idx++) {
		const originalACLs = originalRole.files[idx].acls;
		const updatedACLs = updatedRole.files[idx].acls;

		if (originalRole.files[idx].sys_id !== updatedRole.files[idx].sys_id) {
			// This will never trigger, if it does, then something is mutating the
			// file order during updates
			throw new Error(
				'The order of files has changed, the update operations should only modify ACLs'
			);
		}

		ROLE_BUILDER_ALL_OPERATION_LIST.forEach(operation => {
			if (originalACLs[operation] && !updatedACLs[operation]) {
				const [aclOperation] = operation.split('#');

				// Remove ACL from the file
				deleteBatchData.rest_requests.push(
					getDeleteACLRequest(
						appId,
						originalACLs[operation].sys_id,
						originalRole.files[idx].name.display_value,
						aclOperation
					)
				);
			}
		});
	}
	return deleteBatchData;
}
