import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import { TABS__SELECT_TAB, TABS__CLOSE_TAB } from '../sn-aes-tabs/constants';
import styles from './sn-aes-overflow-tab.scss';

createCustomElement('sn-aes-overflow-tab', {
	view: ({ properties: { item, isSelected } }, { dispatch }) => {
		return (
			<div
				class={{
					'overflow-tab': true,
					'is-selected': isSelected,
				}}>
				<button
					className="tab-button"
					on-click={() => dispatch(TABS__SELECT_TAB, { tabId: item.id })}>
					<div className="tab-button--status">
						<now-icon
							class={{ 'is-unsaved': item.isUnsaved }}
							icon="status-fill"
							size="sm"
						/>
					</div>
					<div className="tab-button--text">
						<div className="title">{item.title}</div>
						<div className="subtitle">{item.subtitle}</div>
					</div>
				</button>
				{item.canClose !== false ? (
					<now-button-iconic
						tooltipContent={t('Close tab')}
						bare
						icon="close-fill"
						on-click={() => {
							dispatch(TABS__CLOSE_TAB, { tabId: item.id });
						}}
						config-aria={{
							'aria-label': t('Close {0} tab', item.title),
						}}
					/>
				) : null}
			</div>
		);
	},
	properties: {
		item: { default: {} },
		isSelected: { default: false },
	},
	styles,
});
