import { BUILDER_EXIT, BUILDER_UPDATE, BUILDER_UPDATE_SESSION, BUILDER_NEW_TAB } from './constants';
import { actionTypes as headerActionTypes } from '../sn-aes-header-chrome/constants';
import { ALERTS_BEHAVIOR__ADD_ITEM } from '../../behaviors/alerts';

/** (eguo)
 * This might be a bit confusing but for legacy forms/lists, I didn't want to break existing
 * functionality so I opted to use the same `devStudioAPI` namespace.
 *
 * For non-legacy form/list UI, we can just use `creatorStudioAPI` namespace instead
 */

// Updates the sn-aes-tab state
function updateTabState(dispatch, state = {}) {
	const { title, subtitle, hasUnsavedChanges } = state;

	// Conditionally add keys based on their presence
	const newState = {
		...(title && { title }),
		...(subtitle && { subtitle }),
		...(hasUnsavedChanges && { hasUnsavedChanges }),
	};

	return dispatch(BUILDER_UPDATE, newState);
}

function updateTabSession(dispatch, { title, subtitle, url }) {
	const sessionUpdates = {
		...(title && { title }),
		...(subtitle && { subtitle }),
		...(url && { url }),
	};

	return dispatch(BUILDER_UPDATE_SESSION, sessionUpdates);
}

// Closes the currently active sn-aes-tab. nextSelectedTabId (optional) controls which tab is selected
// after the current one is close.
function closeCurrentTab(dispatch, payload) {
	const { appId, nextSelectedTabId = '' } = payload;

	// Little helper so users don't need pass the app id to navigate back to the home tab
	const goToTab = nextSelectedTabId.toLowerCase() === 'home' ? appId : nextSelectedTabId;

	return dispatch(BUILDER_EXIT, { nextSelectedTabId: goToTab });
}

// Opens a new sn-aes-tab, assuming relative path on the same instance
function openAppEngineStudioTab(dispatch, newTab) {
	return dispatch(BUILDER_NEW_TAB, newTab);
}

// opens help panel with payload
function showHelpPanel(dispatch, payload) {
	return dispatch(headerActionTypes.HEADER_CHROME__SHOW_HELP_SET, payload);
}

// shows alert
function showAlert(dispatch, payload) {
	return dispatch(ALERTS_BEHAVIOR__ADD_ITEM, payload);
}

export const AES_BUILDER_ACTION_MAP = {
	'SN_AES_BUILDER#UPDATE_TAB_STATE': (dispatch, state) => updateTabState(dispatch, state),
	'SN_AES_BUILDER#UPDATE_TAB_SESSION': (dispatch, payload) => updateTabSession(dispatch, payload),
	'SN_AES_BUILDER#CLOSE_CURRENT_TAB': (dispatch, payload) => closeCurrentTab(dispatch, payload),
	'SN_AES_BUILDER#OPEN_APP_ENGINE_STUDIO_TAB': (dispatch, payload) =>
		openAppEngineStudioTab(dispatch, payload),
	'SN_AES_BUILDER#SHOW_HELP_PANEL': (dispatch, payload) =>
		showHelpPanel(dispatch, { value: true, ...payload }),
	'SN_AES_BUILDER#SHOW_ALERT': (dispatch, payload) => showAlert(dispatch, payload),
};

export default function(dispatch, appScope, appId) {
	return {
		isAppEngineStudio: true,
		applicationScope: appScope,
		updateTabState: state => updateTabState(dispatch, state),
		updateTabSession: payload => updateTabSession(dispatch, payload),
		closeCurrentTab: payload => closeCurrentTab(dispatch, { ...payload, appId }),
		openAppEngineStudioTab: newTab => openAppEngineStudioTab(dispatch, newTab),
		showHelpPanel: helpPanelPayload =>
			showHelpPanel(dispatch, { value: true, helpPanelPayload }),
		showAlert: alertPayload => showAlert(dispatch, alertPayload),
	};
}
