import { createCustomElement } from '@servicenow/ui-core';
import get from 'lodash/get';

import { IMAGE_CARD__CARD_CLICKED } from '../sn-aes-image-card/constants';

import '../../components/sn-aes-image-card';

createCustomElement('sn-aes-app-template-card', {
	view: ({ properties }) => {
		const { template } = properties;

		return (
			<sn-aes-image-card
				coverSize
				id={template.sys_id}
				cardImage={get(template, 'details.icon', '')}
				footerTitle={get(template, 'name.display_value', '')}
				footerDescription={get(template, 'details.short_description.display_value', '')}
			/>
		);
	},
	properties: {
		template: {
			required: true,
			schema: {
				type: 'object',
				properties: {
					sys_id: {
						type: 'string',
					},
				},
			},
		},
		onClickAction: {
			required: true,
			schema: {
				type: 'string',
			},
		},
	},
	actionHandlers: {
		[IMAGE_CARD__CARD_CLICKED]: ({ dispatch, action, properties }) => {
			action.stopPropagation();
			dispatch(properties.onClickAction, { template: properties.template });
		},
	},
});
