import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import isTruncated from '../../helpers/isTruncated';
import getPlaceholderImage from '../../helpers/get-placeholder-image';
import '@servicenow/now-icon';
import '@servicenow/now-tooltip';

import styles from './sn-aes-image-card.scss';
import {
	IMAGE_CARD__CARD_CLICKED,
	IMAGE_CARD_IMG_SLOT,
	IMAGE_CARD_FOOTER_SLOT,
	IMAGE_CARD_TOOLTIP_DELAY,
} from './constants';

createCustomElement('sn-aes-fake-now-card', {
	properties: {
		configAria: { default: { cast: 'object' } },
	},
	view: (state, { dispatch }) => {
		const { configAria } = state.properties;

		return (
			<article
				className="fake-now-card"
				on-click={action => {
					action.stopPropagation();
					dispatch('FAKE_NOW_CARD#CLICKED');
				}}>
				<button
					className="card-button"
					on-click={action => {
						action.stopPropagation();
						dispatch('FAKE_NOW_CARD#CLICKED');
					}}
					{...configAria}
				/>
				<slot name={IMAGE_CARD_IMG_SLOT} />
				<slot name={IMAGE_CARD_FOOTER_SLOT} />
			</article>
		);
	},
	styles,
});

export const actionHandlers = {
	/**
	 * @param {object} coeffects
	 * @param {object} coeffects.action
	 * @param {function} coeffects.action.stopPropagation
	 * @param {{}} coeffects.action.payload
	 */
	['FAKE_NOW_CARD#CLICKED']: ({
		state: {
			properties: { id },
		},
		action,
		dispatch,
	}) => {
		action.stopPropagation();
		dispatch(IMAGE_CARD__CARD_CLICKED, { id });
	},
};

function view(state, { updateState }) {
	let {
		footerTitleRef,
		footerDescriptionRef,
		properties: {
			id,
			cardImage,
			footerTitle,
			longTitle,
			footerImage,
			footerDescription,
			isSelected,
			bare,
			maxSize,
			coverSize,
			imageFooterBleed,
		},
	} = state;

	if (!cardImage) {
		cardImage = getPlaceholderImage(id);
	}

	return (
		<Fragment>
			<div
				key={id}
				class={{
					card: true,
					'type-default': !(bare || maxSize || coverSize),
					'type-bare': bare,
					'type-full-size': maxSize || coverSize,
					'-is-selected': isSelected,
				}}>
				<sn-aes-fake-now-card
					config-aria={{
						'aria-label': t(`Click to open {0}`, footerTitle),
					}}>
					<div
						class={{
							'image-container': true,
							'full-size': maxSize || coverSize,
							'add-border': !(maxSize || coverSize) && !imageFooterBleed,
							'image-footer-bleed': imageFooterBleed,
						}}
						slot={IMAGE_CARD_IMG_SLOT}>
						{cardImage ? (
							<div
								class={{
									'card-image': true,
									'is-fixed': bare,
									'is-resized': !(coverSize || bare),
									'is-covered': coverSize,
									'now-align': true,
									'-inline-center': true,
									'-block-center': true,
								}}
								style={
									coverSize
										? {
												'background-image': `url('${cardImage}')`,
										  }
										: null
								}>
								{coverSize ? null : <img src={cardImage} alt="" />}
							</div>
						) : (
							<div className="sn-image" />
						)}
					</div>
					<div className="footer" slot={IMAGE_CARD_FOOTER_SLOT}>
						{footerTitle ? (
							<Fragment>
								<div
									class={{
										'footer-title-wrapper': true,
										'now-align': !bare,
										'image-footer-bleed': imageFooterBleed,
										'-inline-start': !bare,
										'-block-center': !bare,
									}}>
									{footerImage ? (
										<div className="footer-image">
											<img src={footerImage} alt="" />
										</div>
									) : null}
									<div
										hook-insert={({ elm }) => {
											updateState({
												footerTitleRef: elm,
											});
										}}
										hook-destroy={() =>
											updateState({
												footerTitleRef: null,
											})
										}
										class={{
											'footer-title': true,
											truncate: !bare && !longTitle,
											'long-title': longTitle,
											'multiline-truncate': bare && !longTitle,
											'remove-padding': !footerImage,
										}}>
										{footerTitle}
									</div>
								</div>
								{isTruncated(footerTitleRef, bare) ? (
									<now-tooltip
										content={footerTitle}
										delay={IMAGE_CARD_TOOLTIP_DELAY}
										id={`footerTitleTooltip`}
										target-ref={footerTitleRef}
									/>
								) : null}
							</Fragment>
						) : null}
						{footerDescription ? (
							<Fragment>
								<div
									hook-insert={({ elm }) =>
										updateState({
											footerDescriptionRef: elm,
										})
									}
									hook-destroy={() =>
										updateState({
											footerDescriptionRef: null,
										})
									}
									class={{
										'footer-description': true,
										'multiline-truncate': true,
										'remove-padding': !footerImage,
									}}>
									{footerDescription}
								</div>

								{isTruncated(footerDescriptionRef, true) ? (
									<now-tooltip
										content={footerDescription}
										delay={IMAGE_CARD_TOOLTIP_DELAY}
										id={`footerDescriptionTooltip`}
										target-ref={footerDescriptionRef}
									/>
								) : null}
							</Fragment>
						) : null}
					</div>
				</sn-aes-fake-now-card>
			</div>
		</Fragment>
	);
}

/**
 * Card component for application, templates and objects. Re-sizes based on height and width of
 * the parent and might look weird if the parent doesn't have dimensions set.
 *
 *
 * @seismicElement sn-aes-image-card
 * @summary  Card component for application, templates and objects.
 */
createCustomElement('sn-aes-image-card', {
	properties: {
		/**
		 * The sys_id for the card. Currently it's used as a key and also for selecting
		 * the gradient for template card.
		 * @type {any}
		 */
		id: { default: null },
		/**
		 * Image to be displayed in the card
		 * @type {string}
		 */
		cardImage: { default: null },
		/**
		 * Card footer title/name text.
		 * @type {string}
		 */
		footerTitle: { default: '' },
		/**
		 * Image to be displayed in the footer
		 * @type {string}
		 */
		footerImage: { default: '' },
		/**
		 * Card footer description text
		 * @type {string}
		 */
		footerDescription: { default: '' },
		/**
		 * Boolean to configure if the card is currently selected
		 * This will display the same styles as the :hover and :focus-within styles
		 * @type {boolean}
		 */
		isSelected: { default: false },
		/**
		 * Boolean to configure if the card is bare
		 * @type {boolean}
		 */
		bare: { default: false },
		/**
		 * Boolean to configure if the card main image is full size
		 * This preserves the aspect ratio and allows the card image to
		 * take as much space as possible
		 * @type {boolean}
		 */
		maxSize: { default: false },
		/**
		 * Boolean to configure if the card main image is full size
		 * This forces the image to scale and take the whole space and
		 * might cause stretching if the image has non-confirming dimensions.
		 * @type {boolean}
		 */
		coverSize: { default: false },
		/**
		 * Boolean to configure the footer title to accomodate longer strings.
		 * _Should_ work independently of the `bare`, `maxSize`, and `coverSize` variants
		 * @type {boolean}
		 */
		longTitle: { default: false },
		/**
		 * Boolean to configure if the card image background bleeds into
		 * the footer background with no border separating the two.
		 * @type {boolean}
		 */
		imageFooterBleed: { default: false },
	},
	initialState: {
		footerTitleRef: null,
		footerDescriptionRef: null,
	},
	view,
	dispatches: {
		/**
		 * Dispatched when the card is clicked or there is a keydown 'Enter'
		 * event on the card.
		 * @type {{ item: { id: string, label: string }}}
		 */
		[IMAGE_CARD__CARD_CLICKED]: {},
	},
	actionHandlers,
	styles,
});
