import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-modal';
import { t } from 'sn-translate';
import styles from './sn-aes-login-modal.scss';

createCustomElement('sn-aes-login-modal', {
	view: state => {
		const { onLogin } = state.properties;
		return (
			<now-modal header-label={t('Login')} size="md" manageOpened opened>
				<div className="frame-container">
					<iframe
						title={t('Login')}
						className="login-form"
						frameBorder="0"
						scrolling="no"
						src="/amb_login.do"
						on-load={({ target: iframe }) => {
							const {
								g_ck: token,
								location: { pathname },
							} = iframe.contentWindow;

							// If the iframe loads `/amb_welcome.do`, login is successful
							if (pathname === '/amb_welcome.do') {
								onLogin(token);
							}
						}}
					/>
				</div>
			</now-modal>
		);
	},
	properties: {
		// Callback passed in to update the new g_ck token on the window
		onLogin: {
			default: null,
		},
	},
	actionHandlers: {
		'NOW_MODAL#OPENED_SET': ({ action: { stopPropagation } }) => {
			// Catch this and prevent bubbling up to the <sn-app-eng-studio />
			stopPropagation();
		},
	},
	styles,
});
