import {
	BEHAVIOR_NAME,
	CATEGORY_INITIAL_FETCH_PROPERTY,
	SORTED_CATEGORIES_PROPERTY,
	CATEGORY_BEHAVIOR__INITIAL_FETCH,
	CATEGORIES_URL,
	CATEGORY_BEHAVIOR__FETCH_CATEGORIES,
	CATEGORY_BEHAVIOR__CATEGORIES_FETCH_SUCCEEDED,
	CATEGORY_BEHAVIOR__CATEGORIES_FETCH_FAILED,
} from './constants';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { ALERTS_BEHAVIOR__ADD_ITEM } from '../alerts';
import { t } from 'sn-translate';

const categoryBehavior = {
	name: BEHAVIOR_NAME,
	properties: {
		[CATEGORY_INITIAL_FETCH_PROPERTY]: {
			default: false,
			selectable: true,
		},
		[SORTED_CATEGORIES_PROPERTY]: {
			default: [],
			selectable: true,
		},
	},
	actionHandlers: {
		[CATEGORY_BEHAVIOR__INITIAL_FETCH]: ({ properties, dispatch }) => {
			if (properties[CATEGORY_INITIAL_FETCH_PROPERTY]) {
				return;
			}
			dispatch(CATEGORY_BEHAVIOR__FETCH_CATEGORIES);
		},
		[CATEGORY_BEHAVIOR__FETCH_CATEGORIES]: createHttpEffect(CATEGORIES_URL, {
			method: 'GET',
			successActionType: CATEGORY_BEHAVIOR__CATEGORIES_FETCH_SUCCEEDED,
			errorActionType: CATEGORY_BEHAVIOR__CATEGORIES_FETCH_FAILED,
			batch: false,
		}),
		[CATEGORY_BEHAVIOR__CATEGORIES_FETCH_SUCCEEDED]: ({
			action: {
				payload: { result: categories },
			},
			updateProperties,
		}) => {
			updateProperties({
				[CATEGORY_INITIAL_FETCH_PROPERTY]: true,
				[SORTED_CATEGORIES_PROPERTY]: categories,
			});
		},
		[CATEGORY_BEHAVIOR__CATEGORIES_FETCH_FAILED]: ({ dispatch }) => {
			dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
				status: 'critical',
				content: t('We encountered an error while fetching categories.'),
			});
		},
	},
};

export default categoryBehavior;
