import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import 'sn-polyfills/dist/polyfills/ce-sd.min';
import { snHelpPanelBehavior, actionNames } from '@devsnc/sn-help-panel';
import '@servicenow/now-alert';
import '@servicenow/now-modal';
import '../sn-aes-user-data-provider';
import '../sn-aes-help-manager';
import '../sn-aes-login-modal';
import '../sn-aes-app-wrapper';
import '../../pages/sn-aes-page-aes-access-check';
import { AES_PROVIDER_ID, PROVIDER_PROPERTIES } from '../sn-aes-provider';

import { actionTypes as headerActionTypes } from '../sn-aes-header-chrome';
import { ROUTER_BEHAVIOR__ROUTE_ENTERED } from '../../behaviors/router';

import alertsBehavior, { ALERTS_BEHAVIOR__CLEANUP } from '../../behaviors/alerts';

import aesAccessCheckBehavior from '../../behaviors/aes-access-check';

import {
	HEADER_NAV_PAGES,
	ROOT__SHOW_MODAL,
	ROOT__HIDE_MODAL,
	ROOT__SHOW_LOGIN_MODAL,
	AES_APP_CONTEXT,
	INTERNAL_OVERLAY_ACTION_DO_NOT_USE,
} from './constants';

import styles from './sn-app-engine-studio.scss';
import '../sn-aes-modal-overlay';
import {
	getPageForRoute,
	getActivePageName,
	hasUserViewedWelcomeModal,
	shouldRenderHeader,
	getHeaderChromeHeight,
} from './helpers';
import { WELCOME_GUIDANCE_ID } from '../sn-aes-help-manager/constants';
import { resetAuthToken } from '../../helpers/get-auth-token';
import { USER_DATA_PROPERTY } from '../../behaviors/user-data';

createCustomElement('sn-app-engine-studio', {
	view: (state, { updateState }) => {
		const {
			modalOpen,
			ModalComponent,
			overlayOpen,
			OverlayComponent,
			activeNavTab,
			showHeader,
			PageComponent,
			behaviors: {
				ALERTS_BEHAVIOR: { alerts },
				AES_ACCESS: { areChecksComplete, accessPageView },
			},
			enableGuidanceModal,
		} = state;

		if (!areChecksComplete) {
			return null;
		}

		if (accessPageView) {
			return <sn-aes-page-aes-access-check pageView={accessPageView} />;
		}

		return (
			<sn-aes-provider component-id={AES_PROVIDER_ID}>
				{alerts.length ? (
					<now-alert-list configAriaLive="polite" manageItems={true} items={alerts} />
				) : null}

				{!ModalComponent ? (
					<sn-aes-help-manager
						guidanceId={WELCOME_GUIDANCE_ID}
						enableGuidanceModal={enableGuidanceModal}
					/>
				) : null}

				{ModalComponent ? <ModalComponent /> : null}

				{/* page content (only!). If changed, update the css grid */}
				<sn-aes-app-wrapper
					aria-hidden={modalOpen ? 'true' : false}
					component-id="sn-aes-app-wrapper">
					{showHeader ? (
						<div slot="sn-aes-header-chrome">
							<sn-aes-header-chrome
								hook-insert={({ elm }) => updateState({ headerRef: elm })}
								hook-destroy={() => updateState({ headerRef: null })}
								pages={HEADER_NAV_PAGES}
								user={PROVIDER_PROPERTIES.userDataProperty}
								active-page={activeNavTab}
								show-help={state.showHelpPanel}
								manage-active-page
								manage-show-help
							/>
						</div>
					) : null}

					<sn-help-panel
						opened={state.showHelpPanel}
						top={getHeaderChromeHeight(state.headerRef)}
						app-context={state.helpPanelAppContext}
					/>

					{!overlayOpen ? (
						<div
							aria-hidden={overlayOpen ? 'true' : false}
							className="sn-aes-page-renderer">
							{PageComponent ? (
								<PageComponent
									className="sn-aes-route-outlet"
									routeState={PROVIDER_PROPERTIES.routeState}
									role="main"
								/>
							) : null}
						</div>
					) : null}

					<div className="overlay-container">
						<sn-aes-modal-overlay
							opened={overlayOpen}
							decorated={false}
							aria-hidden={overlayOpen ? null : 'true'}>
							{overlayOpen ? <OverlayComponent /> : null}
						</sn-aes-modal-overlay>
					</div>
				</sn-aes-app-wrapper>
			</sn-aes-provider>
		);
	},

	behaviors: [alertsBehavior, snHelpPanelBehavior, aesAccessCheckBehavior],

	initialState: {
		showHelpPanel: false,
		modalOpen: false,
		ModalComponent: null,
		overlayOpen: false,
		OverlayComponent: null,
		activeNavTab: null,
		PageComponent: null,
		showHeader: false,
		headerRef: null,
		helpPanelAppContext: null,
		routeName: null,
		enableGuidanceModal: false,
	},

	properties: {
		user: {
			default: PROVIDER_PROPERTIES[USER_DATA_PROPERTY],
		},
	},

	actionHandlers: {
		[ROUTER_BEHAVIOR__ROUTE_ENTERED]: ({ action, dispatch, updateState, state }) => {
			const { routeName, routeState } = action.payload;
			const { restrictUserNavigation } = routeState;

			const showHeader = restrictUserNavigation ? false : shouldRenderHeader(routeName);

			updateState({
				activeNavTab: getActivePageName(routeName),
				PageComponent: getPageForRoute(routeName, routeState),
				showHeader,
				routeName,
				helpPanelAppContext: state.showHelpPanel
					? {
							...AES_APP_CONTEXT,
							page: routeName,
					  }
					: null,
			});
			dispatch(ALERTS_BEHAVIOR__CLEANUP);
			dispatch(INTERNAL_OVERLAY_ACTION_DO_NOT_USE, {
				OverlayComponent: null,
				overlayOpen: false,
			});
		},

		[headerActionTypes.HEADER_CHROME__SHOW_HELP_SET]: ({
			action,
			updateState,
			dispatch,
			state,
		}) => {
			const { value: showHelpPanel } = action.payload;
			if (showHelpPanel) {
				const helpPanelAppContext = action.payload.appContext || {
					...AES_APP_CONTEXT,
					page: state.routeName,
				};

				updateState({
					showHelpPanel: true,
					helpPanelAppContext,
				});
			} else {
				dispatch(actionNames.HIDE_PANEL);
			}
		},

		[ROOT__SHOW_MODAL]: ({ action: { payload }, updateState }) => {
			updateState({
				ModalComponent: payload.renderer,
				modalOpen: true,
			});
		},

		[ROOT__HIDE_MODAL]: ({ updateState }) => {
			updateState({
				ModalComponent: null,
				modalOpen: false,
			});
		},

		[ROOT__SHOW_LOGIN_MODAL]: ({ updateState, dispatch }) => {
			updateState({
				ModalComponent: () => {
					return (
						<sn-aes-login-modal
							onLogin={token => {
								resetAuthToken(token);
								dispatch(ROOT__HIDE_MODAL);
								/* Using the platform login form relies on a redirect which adds to window.history.
								 * Calling history.back() quietly here as the modal is closing pops it off
								 */
								history.back();
							}}
						/>
					);
				},
				modalOpen: true,
			});
		},

		// Handle modal close
		'NOW_MODAL#OPENED_SET': ({ action: { payload }, dispatch }) => {
			if (!payload.value) {
				dispatch(ROOT__HIDE_MODAL);
			}
		},

		// This is necessary because the router exists one level down and so
		// the controller can't be attached to the root component *sad eric*
		[INTERNAL_OVERLAY_ACTION_DO_NOT_USE]: ({ action: { payload }, updateState }) => {
			updateState({
				OverlayComponent: payload.OverlayComponent,
				overlayOpen: payload.overlayOpen,
			});
		},

		[actionNames.PANEL_HIDDEN]: ({ updateState }) => {
			updateState({ showHelpPanel: false, helpPanelAppContext: null });
		},

		// update whether the user has seen the welcome modal when user field has been updated
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({ action, updateState }) => {
			const {
				payload: { name, value },
			} = action;
			if (name === 'user') {
				const enableGuidanceModal = !hasUserViewedWelcomeModal(value);
				updateState({ enableGuidanceModal });
			}
		},
	},

	styles,
});
