import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import { transitionTo } from '../../behaviors/router';

import get from 'lodash/get';

import dispatchActionViaPayload from '../../helpers/dispatch-action-via-payload';

import '@servicenow/now-button';
import '@servicenow/now-loader';

import '../../components/sn-aes-apps-overlay-content';
import '../../components/sn-aes-image-card';
import '../../components/sn-aes-container';
import '../../components/sn-aes-skeleton-loader';
import '../../components/sn-aes-app-template-card';
import '../../components/sn-aes-section-header';
import '../../components/sn-aes-template-overlay';
import '../../components/sn-aes-app-add-object';
import '../../components/sn-aes-app-add-automation';

import { USER_DATA_PROPERTY } from '../../behaviors/user-data';

import {
	APPLICATIONS_DATA_PROPERTY,
	APPLICATIONS_REQUEST_COMPLETED_PROPERTY,
	APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS,
} from '../../behaviors/apps-data';
import {
	TEMPLATE_DATA_BEHAVIOR__INITIAL_FETCH,
	TEMPLATE_DATA_PROPERTY,
	OBJECT_TEMPLATES,
	APPLICATION_TEMPLATES,
	TEMPLATE_CATEGORY_REQUEST_COMPLETED_PROPERTY,
	TEMPLATE_REQUEST_COMPLETED_PROPERTY,
	OBJECT_TEMPLATE_TABLE_DEMO_SYS_ID,
	OBJECT_TEMPLATE_EMPTY_FLOW_SYS_ID,
} from '../../behaviors/template-data';
import { PROVIDER_PROPERTIES } from '../../components/sn-aes-provider';
import { EXPERIENCE_CATEGORY_SYS_ID } from '../../components/sn-aes-app-tab/constants';
import { IMAGE_CARD__CARD_CLICKED } from '../../components/sn-aes-image-card/constants';
import { ROOT__SHOW_OVERLAY } from '../../components/sn-app-engine-studio/constants';
import {
	HOME__CREATE_APP_CLICKED,
	HOME__SEE_ALL_APPS_CLICKED,
	HOME__SEE_ALL_TEMPLATES_CLICKED,
	HOME__QUICK_START_CARD_CLICKED,
	HOME__APP_CARD_CLICKED,
	HOME__TEMPLATE_CARD_CLICKED,
} from './constants';
import { CREATE_APPLICATION_OPERATION } from '../../components/sn-aes-wizard-manager/constants';

import styles from './sn-aes-page-home.scss';

import buildExperienceIcon from './../../assets/build-experience.svg';
import createFlowIcon from './../../assets/create-flow.svg';
import useTemplateIcon from './../../assets/use-template.svg';
import resources from '../../assets/resources.svg';
import { CATEGORY_BEHAVIOR__INITIAL_FETCH } from '../../behaviors/category';
import { sidebarDisplay } from '../../behaviors/sidebar-data';

const experienceQuickActionCallback = dispatch => {
	const expCategory = { sys_id: EXPERIENCE_CATEGORY_SYS_ID, name: t('experience') };
	dispatch(ROOT__SHOW_OVERLAY, {
		renderer: [
			() => (
				<sn-aes-apps-overlay-content
					object={{
						type: t('experience'),
						typePluralCapitalized: t('Experiences'),
						modalTitle: expCategory.name,
					}}
				/>
			),
			() => <sn-aes-app-add-object category={expCategory} />,
		],
	});
};

const flowQuickActionCallback = dispatch => {
	dispatch(ROOT__SHOW_OVERLAY, {
		renderer: [
			() => (
				<sn-aes-apps-overlay-content
					object={{
						type: t('automated flow'),
						typePluralCapitalized: t('Automated flows'),
						modalTitle: t('automated flow'),
					}}
				/>
			),
			() => <sn-aes-app-add-automation />,
		],
	});
};

function view(state) {
	const {
		properties: {
			user: { firstName },
			quickActions,
			featuredAppTemplates,
			recentApps,
			applicationsRequestCompleted,
			templatesRequestCompleted,
			templateCategoryRequestCompleted,
		},
	} = state;

	const loading =
		!applicationsRequestCompleted ||
		!templatesRequestCompleted ||
		!templateCategoryRequestCompleted;

	return (
		<sn-aes-container>
			<section id="intro">
				<header className="section-header">
					<h1 className="welcome section-header__heading">
						<span className="welcome__greeting" aria-live="polite">
							{firstName ? t('Hi, {0}', firstName) : t('Hi')}
						</span>
						<span className="welcome__prompt">
							{t('How do you want to get started?')}
						</span>
					</h1>
					<now-button
						size="md"
						variant="primary"
						className="sn-aes-page-home__create-app-button"
						label={t('Create app')}
						append-to-payload={{
							actionName: HOME__CREATE_APP_CLICKED,
						}}
					/>
				</header>
			</section>
			<div aria-busy={loading} aria-live="polite">
				{loading ? (
					<Fragment>
						<section>
							<sn-aes-skeleton-loader className="five-up" type="minimal" count={5} />
						</section>
						<section>
							<sn-aes-skeleton-loader className="four-up" count={4} />
						</section>
						<section>
							<sn-aes-skeleton-loader className="four-up" count={8} />
						</section>
					</Fragment>
				) : (
					<Fragment>
						{quickActions.length ? (
							<section id="quick-start" className="fade-in">
								<sn-aes-section-header
									headerText={t('Quick start')}
									headerTextSize="md"
								/>
								<div className="five-up">
									{quickActions.map(({ id, icon, title, callback }) => (
										<sn-aes-image-card
											bare
											id={id}
											key={id}
											cardImage={icon}
											footerTitle={title}
											append-to-payload={{
												actionName: HOME__QUICK_START_CARD_CLICKED,
												callback,
											}}
										/>
									))}
								</div>
							</section>
						) : null}

						{recentApps.length ? (
							<section id="recent-apps" className="fade-in">
								<header className="section-header">
									<sn-aes-section-header
										headerText={t('My recent apps')}
										headerTextSize="md"
									/>
									<now-button-bare
										label={t('See all of my apps')}
										size="md"
										variant="secondary"
										append-to-payload={{
											actionName: HOME__SEE_ALL_APPS_CLICKED,
										}}
									/>
								</header>
								<div className="four-up">
									{recentApps.map(app => (
										<sn-aes-image-card
											id={app.sys_id}
											key={app.sys_id}
											cardImage={app.illustration}
											footerTitle={get(app, 'name.display_value')}
											footerDescription={get(
												app,
												'description.display_value'
											)}
											append-to-payload={{
												actionName: HOME__APP_CARD_CLICKED,
												sys_id: app.sys_id,
											}}
											{...!app.illustration && { coverSize: true }}
										/>
									))}
								</div>
							</section>
						) : null}

						{featuredAppTemplates.length ? (
							<section id="templates" className="fade-in">
								<header className="section-header">
									<sn-aes-section-header
										headerText={t('Templates')}
										headerTextSize="md"
									/>
									<now-button-bare
										label={t('See all templates')}
										size="md"
										variant="secondary"
										append-to-payload={{
											actionName: HOME__SEE_ALL_TEMPLATES_CLICKED,
										}}
									/>
								</header>
								<div className="four-up">
									{featuredAppTemplates.map(template => (
										<sn-aes-app-template-card
											template={template}
											id={template.sys_id}
											key={template.sys_id}
											onClickAction={HOME__TEMPLATE_CARD_CLICKED}
										/>
									))}
								</div>
							</section>
						) : null}
					</Fragment>
				)}
			</div>
		</sn-aes-container>
	);
}

export const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
		// This is required to ensure that sidebar doesn't flicker
		// switching between apps with different sidebar open status
		sidebarDisplay(dispatch, { isDisplayed: false });

		dispatch(CATEGORY_BEHAVIOR__INITIAL_FETCH);
		dispatch(TEMPLATE_DATA_BEHAVIOR__INITIAL_FETCH);
		dispatch(APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS, {
			sort_order: 'desc',
			sort_field: 'updated',
		}); // default "most recent" apps
	},

	['NOW_BUTTON#CLICKED']: dispatchActionViaPayload,

	['NOW_BUTTON_BARE#CLICKED']: dispatchActionViaPayload,

	[IMAGE_CARD__CARD_CLICKED]: dispatchActionViaPayload,

	[HOME__QUICK_START_CARD_CLICKED]: ({ action, dispatch }) => {
		action.stopPropagation();
		action.payload.callback(dispatch, action.payload);
	},

	[HOME__APP_CARD_CLICKED]: ({ action, dispatch }) => {
		action.stopPropagation();
		transitionTo(dispatch, 'my-apps.app', { sysId: action.payload.sys_id });
	},

	[HOME__TEMPLATE_CARD_CLICKED]: ({ action, dispatch }) => {
		action.stopPropagation();
		dispatch(ROOT__SHOW_OVERLAY, {
			renderer: () => <sn-aes-template-overlay template={action.payload.template} />,
		});
	},

	[HOME__CREATE_APP_CLICKED]: ({ action, dispatch }) => {
		action.stopPropagation();
		transitionTo(dispatch, 'template', {
			operations: [{ operation: CREATE_APPLICATION_OPERATION }],
		});
	},

	[HOME__SEE_ALL_APPS_CLICKED]: ({ action, dispatch }) => {
		action.stopPropagation();
		transitionTo(dispatch, 'my-apps');
	},

	[HOME__SEE_ALL_TEMPLATES_CLICKED]: ({ action, dispatch }) => {
		action.stopPropagation();
		transitionTo(dispatch, 'templates');
	},
};

/**
 * App Engine Studio home page component
 *
 * ```jsx
 * <sn-aes-page-home />
 * ```
 *
 * @seismicElement sn-aes-page-home
 * @summary summary text.
 */
createCustomElement('sn-aes-page-home', {
	properties: {
		user: {
			default: PROVIDER_PROPERTIES[USER_DATA_PROPERTY],
		},
		applications: {
			default: PROVIDER_PROPERTIES[APPLICATIONS_DATA_PROPERTY],
		},
		applicationsRequestCompleted: {
			default: PROVIDER_PROPERTIES[APPLICATIONS_REQUEST_COMPLETED_PROPERTY],
		},
		templatesRequestCompleted: {
			default: PROVIDER_PROPERTIES[TEMPLATE_REQUEST_COMPLETED_PROPERTY],
		},
		templateCategoryRequestCompleted: {
			default: PROVIDER_PROPERTIES[TEMPLATE_CATEGORY_REQUEST_COMPLETED_PROPERTY],
		},
		templates: {
			default: PROVIDER_PROPERTIES[TEMPLATE_DATA_PROPERTY],
		},
		quickActions: {
			computed({ properties: { templates } }) {
				let staticQuickActions;

				const tableTemplate = templates[OBJECT_TEMPLATES].find(
					template => template.sys_id === OBJECT_TEMPLATE_TABLE_DEMO_SYS_ID
				);

				const flowTemplate = templates[OBJECT_TEMPLATES].find(
					template => template.sys_id === OBJECT_TEMPLATE_EMPTY_FLOW_SYS_ID
				);

				// Don't show anything if we don't have templates data yet
				if (!tableTemplate) {
					return [];
				}

				/**
				 * Quick Actions are independent actions the user can take
				 *
				 * Some examples include launching an application template,
				 * an object template, or visiting a page.
				 *
				 * Each quick action object includes a callback method which will
				 * be invoked when the corresponding sn-aes-image-card is clicked.
				 *
				 * We have quick actions both statically defined and also coming from
				 * an API call (ex. table template), so this computed property
				 * merges the static ones with the ones from the API
				 */
				staticQuickActions = [
					{
						id: 'build-an-experience',
						title: t('Add an experience'),
						icon: buildExperienceIcon,
						callback: experienceQuickActionCallback,
					},
					{
						id: 'use-a-template',
						title: t('Browse templates'),
						icon: useTemplateIcon,
						callback(dispatch) {
							transitionTo(dispatch, 'templates');
						},
					},
					{
						id: 'resources',
						title: t('Learn the tools'),
						icon: resources,
						callback(dispatch) {
							transitionTo(dispatch, 'resources');
						},
					},
				];

				if (tableTemplate) {
					const {
						details: { icon },
					} = tableTemplate;

					// Add table template at second position in quick actions
					staticQuickActions.splice(0, 0, {
						id: 'add-or-import-data',
						title: t('Add a table'),
						icon: icon,
						callback(dispatch) {
							dispatch(ROOT__SHOW_OVERLAY, {
								renderer: () => (
									<sn-aes-apps-overlay-content
										type="object-template"
										object={{
											sys_id: tableTemplate.sys_id,
											type: t('table'),
											typePluralCapitalized: t('Tables'),
											modalTitle: t('table'),
											imageSrc: icon,
											description: get(
												tableTemplate,
												'details.short_description.display_value',
												''
											),
										}}
									/>
								),
								initialState: {
									templateSysId: tableTemplate.sys_id,
								},
							});
						},
					});
				}

				if (flowTemplate) {
					// Add table template at fourth position in quick actions
					staticQuickActions.splice(2, 0, {
						id: 'create-automated-flow',
						title: t('Add an automated flow'),
						icon: createFlowIcon,
						callback: flowQuickActionCallback,
					});
				}

				return staticQuickActions;
			},
		},
		featuredAppTemplates: {
			computed({ properties: { templates } }) {
				return templates[APPLICATION_TEMPLATES].slice(0, 8);
			},
		},
		recentApps: {
			computed({ properties: { applications } }) {
				return applications.slice(0, 4);
			},
		},
	},
	view,
	actionHandlers,
	styles,
});
