import {
	BEHAVIOR_NAME,
	STORAGE_KEYS,
	LOCAL_STORAGE_PROPERTY,
	SESSION_STORAGE_PROPERTY,
	STORAGE_BEHAVIOR__SET_STORAGE_ITEM,
	STORAGE_BEHAVIOR__UPDATE_STORAGE_ITEM,
	STORAGE_BEHAVIOR__CLEAR_STORAGE_ITEM,
} from './constants';

export function setSessionItem(dispatch, key, value) {
	const payload = { key, value };
	const meta = { propName: SESSION_STORAGE_PROPERTY };
	dispatch(STORAGE_BEHAVIOR__SET_STORAGE_ITEM, payload, meta);
}

export function updateSessionItem(dispatch, key, value) {
	const payload = { key, value };
	const meta = { propName: SESSION_STORAGE_PROPERTY };
	dispatch(STORAGE_BEHAVIOR__UPDATE_STORAGE_ITEM, payload, meta);
}

export function clearSessionItem(dispatch, key) {
	const payload = { key };
	const meta = { propName: SESSION_STORAGE_PROPERTY };
	dispatch(STORAGE_BEHAVIOR__CLEAR_STORAGE_ITEM, payload, meta);
}

export function setLocalItem(dispatch, key, value) {
	const payload = { key, value };
	const meta = { propName: LOCAL_STORAGE_PROPERTY };
	dispatch(STORAGE_BEHAVIOR__SET_STORAGE_ITEM, payload, meta);
}

export function updateLocalItem(dispatch, key, value) {
	const payload = { key, value };
	const meta = { propName: LOCAL_STORAGE_PROPERTY };
	dispatch(STORAGE_BEHAVIOR__UPDATE_STORAGE_ITEM, payload, meta);
}

export function clearLocalItem(dispatch, key) {
	const payload = { key };
	const meta = { propName: LOCAL_STORAGE_PROPERTY };
	dispatch(STORAGE_BEHAVIOR__CLEAR_STORAGE_ITEM, payload, meta);
}

export function typeOf(data) {
	const type = typeof data;

	if (type === 'object') {
		if (!data) {
			return 'null';
		}

		if (Array.isArray(data)) {
			return 'array';
		}
	}

	return type;
}

export function getStorage(propName) {
	switch (propName) {
		case LOCAL_STORAGE_PROPERTY:
			return window.localStorage;

		case SESSION_STORAGE_PROPERTY:
			return window.sessionStorage;

		default:
			throw new Error(`[${BEHAVIOR_NAME}]: Could not find a store for '${propName}'`);
	}
}

export function getStorageKeys(storage) {
	let keys = getItem(storage, STORAGE_KEYS);

	if (typeOf(keys) !== 'array') {
		keys = [];
		setItem(storage, STORAGE_KEYS, keys);
	}

	return keys;
}

export function addStorageKey(storage, key) {
	const keys = getItem(storage, STORAGE_KEYS) || [];

	if (!keys.includes(key)) {
		keys.push(key);
		setItem(storage, STORAGE_KEYS, keys);
	}
}

export function removeStorageKey(storage, key) {
	const keys = getItem(storage, STORAGE_KEYS) || [];

	if (keys.includes(key)) {
		const newKeys = keys.filter(storageKey => storageKey !== key);
		setItem(storage, STORAGE_KEYS, newKeys);
	}
}

export function loadData(propName) {
	const data = {};
	const storage = getStorage(propName);
	const keys = getStorageKeys(storage);

	for (const key of keys) {
		data[key] = getItem(storage, key);
	}

	return data;
}

export function storeData(propName, key, value) {
	const storage = getStorage(propName);
	addStorageKey(storage, key);
	setItem(storage, key, value);
}

export function clearStorageItem(propName, key) {
	const storage = getStorage(propName);

	if (storage.hasOwnProperty(key)) {
		removeStorageKey(storage, key);
		delete storage[key];
	}
}

export function getItem(storage, key) {
	return JSON.parse(storage.getItem(key));
}

export function setItem(storage, key, value) {
	storage.setItem(key, JSON.stringify(value));
}
