import { createCustomElement, actionTypes } from '@servicenow/ui-core';

import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import '../sn-aes-image-card';
import '../sn-aes-search-input';
import '@servicenow/now-loader';

import { t } from 'sn-translate';
import styles from './sn-aes-apps-overlay-content.scss';

import {
	APPS_OVERLAY__SET_SEARCH_INDEX,
	APPS_OVERLAY__CONTINUE_CLICKED,
	APPS_OVERLAY_CREATE_APP_ID,
} from './constants';

import { IMAGE_CARD__CARD_CLICKED } from '../sn-aes-image-card/constants';
import { SEARCH_INPUT__CHANGED } from '../sn-aes-search-input/constants';
import { ROOT__HIDE_OVERLAY } from '../sn-app-engine-studio/constants';

import newAppIcon from './../../assets/new-app.svg';
import { createObjectSearchIndex, getFilterFnForInputLength } from '../../helpers/object-search';
import dispatchActionViaPayload from '../../helpers/dispatch-action-via-payload';
import { APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS } from '../../behaviors/apps-data';
import { PROVIDER_PROPERTIES } from '../sn-aes-provider';
import { SEARCH_INPUT_MINIMUM_LENGTH } from '../../helpers/constants';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const SORT_BY_OPTIONS = [
	{ id: 'dateDesc', label: t('Sort (newest)') },
	{ id: 'dateAsc', label: t('Sort (oldest)') },
	{ id: 'nameAsc', label: t('Sort (a to z)') },
	{ id: 'nameDesc', label: t('Sort (z to a)') },
];

const filterApplications = getFilterFnForInputLength(SEARCH_INPUT_MINIMUM_LENGTH);

const sortQueryParams = sortStrategy => ({
	sort_order: /Asc$/.test(sortStrategy) ? 'asc' : 'desc',
	sort_field: /^name/.test(sortStrategy) ? 'name' : 'updated',
});

function renderFilteredCards(filteredApplications, completedRequest, selectedApp) {
	if (!completedRequest) {
		return <now-loader />;
	} else {
		return filteredApplications.map(app => {
			const { name, sys_id, illustration, updated } = app;
			return (
				<sn-aes-image-card
					append-to-payload={{
						app,
					}}
					{...(illustration ? { maxSize: true } : { coverSize: true })}
					id={sys_id}
					key={sys_id}
					cardImage={illustration}
					footerTitle={name.display_value}
					footerDescription={t('Last updated on {0}', updated.display_value)}
					is-selected={selectedApp.sys_id === sys_id}
				/>
			);
		});
	}
}

const view = state => {
	const {
		properties: { object },
		selectedApp,
		appSearchInput,
		sortStrategy,
		filteredApplications,
		properties,
	} = state;

	return (
		<div className="all-apps-overlay">
			<div />
			<div className="main">
				<div className="overlay-title">{t('Quick Start')}</div>
				<div className="overlay-action">
					<div>
						<h1 className="main-text">
							{t('Where do you want to add this {0}?', object.type)}
						</h1>
					</div>
					<div className="secondary-text">
						{t(
							'{0} need to be added to an app. You can add this to an existing app or we can help you create a new one.',
							object.typePluralCapitalized
						)}
					</div>
				</div>
				<div className="overlay-search now-align -inline">
					<sn-aes-search-input
						inputValue={appSearchInput}
						name="sn-aes-apps-overlay-search-input"
						className="search-input"
						placeholder={t('Search all apps...')}
					/>

					<now-dropdown
						items={SORT_BY_OPTIONS}
						select="single"
						selected-items={[sortStrategy]}
						size="sm"
						variant="secondary"
						bare
						showPadding
					/>
				</div>
				<div className="app-list-container">
					<div className="four-up">
						<sn-aes-image-card
							id={APPS_OVERLAY_CREATE_APP_ID}
							key={APPS_OVERLAY_CREATE_APP_ID}
							cardImage={newAppIcon}
							footerTitle={t('Add to a new app')}
							append-to-payload={{
								app: { sys_id: APPS_OVERLAY_CREATE_APP_ID },
							}}
							is-selected={selectedApp.sys_id === APPS_OVERLAY_CREATE_APP_ID}
							image-footer-bleed={true}
							class={{
								'single-card': !filteredApplications.length,
							}}
						/>
						{renderFilteredCards(
							filteredApplications,
							properties.completedRequest,
							selectedApp
						)}
					</div>
				</div>
			</div>
			<div className="footer now-align -inline-end">
				<now-button
					className="now-p-inline-end--md"
					id="cancel-btn"
					variant="tertiary"
					label={t('Cancel')}
					append-to-payload={{
						actionName: ROOT__HIDE_OVERLAY,
					}}
				/>
				<now-button
					className="now-p-inline-end--3xl"
					id="continue-btn"
					variant="primary"
					label={t('Continue')}
					disabled={selectedApp.sys_id ? false : true}
					append-to-payload={{
						actionName: APPS_OVERLAY__CONTINUE_CLICKED,
						appSysId:
							selectedApp.sys_id === APPS_OVERLAY_CREATE_APP_ID
								? ''
								: selectedApp.sys_id,
					}}
				/>
			</div>
		</div>
	);
};

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ state, properties: { applications }, dispatch }) => {
		const requestPayload = sortQueryParams(state.sortStrategy);
		const { sort_order, sort_field } = requestPayload;
		dispatch(APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS, {
			sort_order,
			sort_field,
		});
		dispatch(APPS_OVERLAY__SET_SEARCH_INDEX, { applications });
	},
	[COMPONENT_PROPERTY_CHANGED]: ({
		action: {
			payload: { name, value: applications },
		},
		dispatch,
	}) => {
		// This will catch whenever applications have been re-requested, to rebuild the index
		if (name !== 'applications') {
			return;
		}
		dispatch(APPS_OVERLAY__SET_SEARCH_INDEX, { applications });
	},
	[APPS_OVERLAY__SET_SEARCH_INDEX]: ({
		action: {
			payload: { applications },
		},
		state,
		updateState,
	}) => {
		const { appSearchInput } = state;
		const applicationsIndex = createObjectSearchIndex(
			'sys_id',
			['name.display_value', 'description.display_value'],
			applications
		);

		updateState({
			applicationsIndex,
			filteredApplications: filterApplications(
				{
					list: applications,
					index: applicationsIndex,
				},
				appSearchInput
			),
		});
	},
	[IMAGE_CARD__CARD_CLICKED]: ({
		action,
		action: {
			payload: { app },
		},
		updateState,
	}) => {
		action.stopPropagation();
		updateState({ selectedApp: app });
	},
	[SEARCH_INPUT__CHANGED]: ({
		action,
		state: { applicationsIndex },
		properties: { applications },
		updateState,
	}) => {
		action.stopPropagation();
		const { value: appSearchInput } = action.payload;

		updateState({
			appSearchInput,
			filteredApplications: filterApplications(
				{
					list: applications,
					index: applicationsIndex,
				},
				appSearchInput
			),
		});
	},
	['NOW_BUTTON#CLICKED']: dispatchActionViaPayload,
	['NOW_DROPDOWN#SELECTED_ITEMS_SET']: ({ action, updateState, dispatch }) => {
		action.stopPropagation();
		const [sortStrategy] = action.payload.value;
		updateState({
			sortStrategy,
		});
		const requestQuery = sortQueryParams(sortStrategy);
		const { sort_order, sort_field } = requestQuery;
		dispatch(APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS, {
			sort_order: sort_order,
			sort_field: sort_field,
		});
	},
};

createCustomElement('sn-aes-apps-overlay-content', {
	initialState: {
		selectedApp: { sys_id: null },
		appSearchInput: '',
		sortStrategy: 'dateDesc',
		applicationsIndex: {},
		filteredApplications: [],
	},
	properties: {
		object: { default: {} },
		applications: {
			default: PROVIDER_PROPERTIES.applicationsDataProperty,
		},
		completedRequest: {
			default: PROVIDER_PROPERTIES.appsRequestCompleted,
		},
	},
	view,
	actionHandlers,
	styles,
});
