import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import get from 'lodash/get';
import remove from 'lodash/remove';
import cloneDeep from 'lodash/cloneDeep';
import uniq from 'lodash/uniq';

import '../../components/sn-aes-app-tab';
import '../../components/sn-aes-builder';
import '../../components/sn-aes-tabs';
import '../../components/sn-aes-guidance-sidebar';
import '../../../../../../../sn-aes-role-builder';
import { SHOW_HEADER_CHROME_PROPERTY } from '../../components/sn-aes-app-wrapper/constants';

import {
	requestFetchApp,
	appMetadataSubscriptionEffect,
	appRecordSubscriptionEffect,
	appSubscriptionErrorOccurred,
	appSubscriptionDataChanged,
	handleAppSubscription,
	appMetadataDeleteChanged,
	appMetadataDeleteSubscriptionEffect,
} from './actions';

import { transitionTo } from '../../behaviors/router';
import { PROVIDER_PROPERTIES } from '../../components/sn-aes-provider';
import { getSidebarStartStep } from './helpers';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED } = actionTypes;

import {
	FETCH_APP_STARTED,
	FETCH_APP_SUCCEEDED,
	FETCH_APP_FAILED,
	UPDATE_APP_VERSION,
	APP_HOME__TABS_LEFT_ICON_CLICKED,
	APP_SUBSCRIPTIONS_INITIATED,
	APP_SUBSCRIPTIONS_TERMINATED,
	SYS_METADATA_SUBSCRIBE,
	SYS_METADATA_DELETE_SUBSCRIBE,
	SYS_APP_RECORD_SUBSCRIBE,
	SYS_METADATA_DATASET_CHANGED,
	SYS_METADATA_DELETE_DATASET_CHANGED,
	SYS_APP_RECORD_CHANGED,
	SYS_METADATA_SUBSCRIBE_FAILED,
	SYS_METADATA_DELETE_SUBSCRIBE_FAILED,
	SYS_APP_SUBSCRIBE_FAILED,
	APP_HOME__DELETE_FILE,
	FETCH_APP_THROTTLED,
} from './constants';

import {
	APP_TAB__HIDE_OVERLAY_CONFIRM,
	APP_TAB__SHOW_BLANK_OVERLAY,
	APP_TAB__SHOW_OVERLAY,
} from '../../components/sn-aes-app-tab/constants';
import {
	BUILDER_EXIT,
	BUILDER_EXIT_TYPE_SUCCESS,
	BUILDER_EXIT_TYPE_DELETE,
} from '../../components/sn-aes-builder/constants';

import {
	WIZARD_MANAGER__EDIT_RECORD,
	WIZARD_MANAGER__TEMPLATE_COMPLETE,
} from '../../components/sn-aes-wizard-manager/constants';

import {
	TABS__LEFT_ICONS_SLOT,
	TABS__MAIN_TAB_FOCUSED,
	TABS__UPDATED,
} from '../../components/sn-aes-tabs/constants';

import styles from './sn-aes-page-app.scss';
import { CATEGORY_BEHAVIOR__INITIAL_FETCH } from '../../behaviors/category';
import { SIDEBAR_ALL_STEPS_PROPERTY, sidebarSetStep } from '../../behaviors/sidebar-data';
import { SESSION_STORAGE_PROPERTY } from '../../behaviors/storage';
import dispatchActionViaPayload from '../../helpers/dispatch-action-via-payload';

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch }) => {
		const { sysId } = properties.routeState;
		dispatch(FETCH_APP_THROTTLED, { sysId });
		dispatch(CATEGORY_BEHAVIOR__INITIAL_FETCH);
		dispatch(APP_SUBSCRIPTIONS_INITIATED, { sysId });
	},
	['NOW_BUTTON_ICONIC#CLICKED']: dispatchActionViaPayload,
	[FETCH_APP_THROTTLED]: {
		modifier: { name: 'throttle', limit: 2000 },
		effect: ({
			dispatch,
			action: {
				payload: { sysId },
			},
		}) => {
			dispatch(FETCH_APP_STARTED, { sysId });
		},
	},
	[FETCH_APP_STARTED]: requestFetchApp,
	[FETCH_APP_FAILED]: ({
		dispatch,
		action: {
			payload: { message },
		},
	}) => {
		// FIXME: Add error alerts
		alert(message); // eslint-disable-line
		transitionTo(dispatch, 'home');
	},
	[FETCH_APP_SUCCEEDED]: ({ dispatch, state, properties, action, updateState }) => {
		const app = action.payload.result;
		const { sidebarAllSteps } = properties;
		const { hideTabs } = state;
		const { tabs = [] } = properties.routeState;

		if (!hideTabs) {
			// Do not set step if overlay is open
			const step = getSidebarStartStep(app, sidebarAllSteps);
			sidebarSetStep(dispatch, { step });
		}

		updateState({
			app,
			taxonomyTableNames: app.taxonomy_definitions.map(({ table_name }) => table_name),
			initialTabState: {
				items: [
					{
						id: app.sys_id,
						title: app.name.display_value,
						subtitle: t('App Home'),
						canClose: false,
					},
					...tabs,
				],
			},
		});
	},
	[UPDATE_APP_VERSION]: ({ action, updateState }) => {
		const { version } = action.payload;

		updateState({
			path: 'app.version',
			value: version,
		});
	},
	[TABS__UPDATED]: ({ action, dispatch, state: { app } }) => {
		const { previousTabState, currentTabState } = action.payload;

		// The app previously had unsaved changes, and now it doesn't.
		// So either the save button was clicked or the app reverted to
		// its original state. We cannot differentiate between the two,
		// so we will have to refresh the app for both cases.
		if (previousTabState.isUnsaved && !currentTabState.isUnsaved) {
			dispatch(FETCH_APP_STARTED, { sysId: app.sys_id });
		}
	},

	// GraphQL Subscription actions
	[APP_SUBSCRIPTIONS_INITIATED]: handleAppSubscription,
	[APP_SUBSCRIPTIONS_TERMINATED]: handleAppSubscription,
	[SYS_APP_RECORD_SUBSCRIBE]: appRecordSubscriptionEffect,
	[SYS_APP_RECORD_CHANGED]: appSubscriptionDataChanged,
	[SYS_APP_SUBSCRIBE_FAILED]: appSubscriptionErrorOccurred,
	[SYS_METADATA_SUBSCRIBE]: appMetadataSubscriptionEffect,
	[SYS_METADATA_DATASET_CHANGED]: appSubscriptionDataChanged,
	[SYS_METADATA_SUBSCRIBE_FAILED]: appSubscriptionErrorOccurred,
	[SYS_METADATA_DELETE_SUBSCRIBE]: appMetadataDeleteSubscriptionEffect,
	[SYS_METADATA_DELETE_DATASET_CHANGED]: appMetadataDeleteChanged,
	[SYS_METADATA_DELETE_SUBSCRIBE_FAILED]: appSubscriptionErrorOccurred,

	[BUILDER_EXIT]: ({ action, properties, dispatch }) => {
		const type = get(action, 'payload.type', null);
		const { sysId } = properties.routeState;

		// TODO: Capture a payload of the created record so only that record
		// needs to be loaded.
		// FIXME: Only request the newly modified record, loading all for now
		if (type === BUILDER_EXIT_TYPE_SUCCESS || type === BUILDER_EXIT_TYPE_DELETE || !type) {
			dispatch(FETCH_APP_THROTTLED, { sysId });
		}
	},

	[APP_HOME__TABS_LEFT_ICON_CLICKED]: ({ dispatch }) => {
		transitionTo(dispatch, 'my-apps');
	},

	[TABS__MAIN_TAB_FOCUSED]: ({
		action: {
			payload: { appTabFocused },
		},
		updateState,
	}) => {
		updateState({ appTabFocused });
	},

	[WIZARD_MANAGER__TEMPLATE_COMPLETE]: ({ properties, dispatch }) => {
		const { sysId } = properties.routeState;
		dispatch(FETCH_APP_THROTTLED, { sysId });
	},
	[APP_TAB__HIDE_OVERLAY_CONFIRM]: ({ updateState }) => {
		updateState({
			hideTabs: false,
		});
	},
	[APP_TAB__SHOW_OVERLAY]: ({ updateState }) => {
		updateState({
			hideTabs: true,
		});
	},
	[APP_TAB__SHOW_BLANK_OVERLAY]: ({ updateState }) => {
		updateState({
			hideTabs: true,
		});
	},

	[WIZARD_MANAGER__EDIT_RECORD]: ({ properties, action, updateState, dispatch }) => {
		const { sysId } = properties.routeState;

		updateState({ editTabIds: action.payload.editTabIds });
		dispatch(FETCH_APP_THROTTLED, { sysId });
	},
	[COMPONENT_DISCONNECTED]: ({ dispatch, state }) => {
		const sysId = get(state, 'app.sys_id');
		dispatch(APP_SUBSCRIPTIONS_TERMINATED, { sysId });
	},
	[APP_HOME__DELETE_FILE]: ({ action, updateState, properties, dispatch }) => {
		// The updateState helper handler ensures that parallel/rapid
		// triggers of updateState will not have stale data.
		updateState([
			({ state }) => {
				// Update the app for deleted files
				const { app } = state;
				// We are cloning app here, so that we dont run into update issues
				let modifiedApp = cloneDeep(app);
				remove(modifiedApp.files, file => file.sys_id === action.payload.deletedSysId);

				const { sidebarAllSteps } = properties;
				const step = getSidebarStartStep(modifiedApp, sidebarAllSteps);
				sidebarSetStep(dispatch, { step });

				return {
					path: 'app',
					operation: 'set',
					value: modifiedApp,
				};
			},
			({ state }) => {
				// Update the deletedItems to close open tabs
				return {
					path: 'deletedItems',
					operation: 'set',
					value: uniq([...state.deletedItems, action.payload.deletedSysId]),
				};
			},
		]);
	},
};

function getRenderedElement(app, item, appTabOverlayTargetRef, appTabFocused) {
	if (item.url) {
		return (
			<sn-aes-builder-proxy tabId={item.id}>
				<sn-aes-builder
					style={{ height: '100%' }}
					url={item.url}
					appScope={app && app.scope_name}
					appId={app && app.sys_id}
				/>
			</sn-aes-builder-proxy>
		);
	} else {
		return (
			<sn-aes-app-tab
				app={app}
				appTabOverlayTargetRef={appTabOverlayTargetRef}
				appTabFocused={appTabFocused}
			/>
		);
	}
}

/**
 * App page
 *
 * ```jsx
 * <sn-aes-page-app />
 * ```
 *
 * @seismicElement sn-aes-page-app
 * @summary This page displays the application that the user is developing
 */
createCustomElement('sn-aes-page-app', {
	view: (state, { updateState }) => {
		const {
			initialTabState,
			app,
			hideTabs,
			appTabFocused,
			appTabOverlayTargetRef,
			deletedItems,
		} = state;
		const { routeState, sessionStorageData, showHeaderChrome } = state.properties;

		return (
			<div
				class={{
					'sn-aes-page-app-fullscreen': !showHeaderChrome,
					'sn-aes-page-app': true,
				}}>
				<sn-aes-guidance-sidebar />
				<div className="main">
					{/* Do not delete me. I look empty inside but in reality I'm full of life. */}
					<div
						id="app-tab-overlay-target"
						hook-insert={({ elm }) => updateState({ appTabOverlayTargetRef: elm })}
						hook-destroy={() => updateState({ appTabOverlayTargetRef: null })}
					/>

					{initialTabState.items.length ? (
						<sn-aes-tabs
							aria-hidden={hideTabs ? 'true' : false}
							class={{
								hidden: hideTabs,
							}}
							routeState={routeState}
							sessionStorageData={sessionStorageData}
							items={initialTabState.items}
							deletedItems={deletedItems}
							showHeaderChrome={showHeaderChrome}
							panelRenderer={item =>
								getRenderedElement(app, item, appTabOverlayTargetRef, appTabFocused)
							}>
							<div slot={TABS__LEFT_ICONS_SLOT} className="now-align -inline-center">
								<now-button-iconic
									key="tabs-left-button"
									id="tabs-left-button"
									bare
									icon="grid-four-fill"
									variant="tertiary"
									size="lg"
									append-to-payload={{
										actionName: APP_HOME__TABS_LEFT_ICON_CLICKED,
									}}
									configAria={{ 'aria-label': t('Click to go to my apps') }}
								/>
							</div>
						</sn-aes-tabs>
					) : null}
				</div>
			</div>
		);
	},
	initialState: {
		appTabFocused: false,
		initialTabState: {
			items: [],
		},
		searchResults: [],
		app: null,
		taxonomyTableNames: [],
		deletedItems: [],
		hideTabs: false,
		appTabOverlayTargetRef: null,
	},
	properties: {
		// Default routeState is defined in the `initialState` property on this route's definition
		routeState: { default: {} },
		showHeaderChrome: { default: SHOW_HEADER_CHROME_PROPERTY },
		sessionStorageData: {
			default: PROVIDER_PROPERTIES[SESSION_STORAGE_PROPERTY],
		},
		sidebarAllSteps: {
			default: PROVIDER_PROPERTIES[SIDEBAR_ALL_STEPS_PROPERTY],
		},
	},
	actionHandlers,
	styles,
});
