import BrowserRouter from './BrowserRouter';
import {
	BEHAVIOR_NAME,
	ROUTE_NAME_PROPERTY,
	ROUTE_STATE_PROPERTY,
	ROUTER_BEHAVIOR__TRANSITION_TO,
	ROUTER_BEHAVIOR__REPLACE_WITH,
	ROUTER_BEHAVIOR__UPDATE_ROUTE_STATE,
	ROUTER_BEHAVIOR__BACK,
	ROUTER_BEHAVIOR__BEFORE_RUN,
	ROUTER_BEHAVIOR__RUN,
	ROUTER_BEHAVIOR__SET_DELEGATE,
	ROUTER_BEHAVIOR__UNSET_DELEGATE,
	ROUTER_BEHAVIOR__DELEGATE_RUN,
} from './constants';
import { RouteTransaction, RouteTransition, RouteReplacement } from './RouteTransaction';
import { replaceWith, emitNotFound, attachHandlers } from './helpers';

import { actionTypes } from '@servicenow/ui-core';
import { IMPORTING_DATA__REMOVE_PENDING_RECORDS } from '../importing-data/constants';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED } = actionTypes;

/** @type {BrowserRouter} */
export let ROUTER;

/**
 *  Dispatches
 *  ['ROUTER_BEHAVIOR#ROUTE_ENTERED']: {},
 *	['ROUTER_BEHAVIOR#ROUTE_NOT_FOUND']: {},
 *	['ROUTER_BEHAVIOR__BEFORE_RUN']: {},
 *	['ROUTER_BEHAVIOR__RUN']: {}
 */
const routerBehavior = {
	name: BEHAVIOR_NAME,

	properties: {
		[ROUTE_NAME_PROPERTY]: {
			default: null,
			selectable: true,
		},
		[ROUTE_STATE_PROPERTY]: {
			default: {},
			selectable: true,
		},
		delegates: { default: {} },
	},

	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ state, properties, action, dispatch, updateProperties }) => {
			if (!ROUTER) {
				const { options: behaviorOptions } = action.payload;
				const options = {
					...behaviorOptions,
					namespace:
						typeof behaviorOptions.namespace === 'function'
							? behaviorOptions.namespace({ state, properties })
							: behaviorOptions.namespace,
				};

				ROUTER = new BrowserRouter(options);
				ROUTER.onPopState((event, router) => {
					let { state: routeState = {} } = event;
					const { pathname } = window.location;
					const routeName = router.getRouteNameByUrl(pathname);

					if (routeName) {
						const routeParams = router.extractRouteParams(routeName, pathname);
						routeState = {
							...routeState,
							...routeParams,
						};
					}

					const transaction = new RouteTransaction(router);
					attachHandlers(transaction, dispatch, updateProperties);

					dispatch(ROUTER_BEHAVIOR__BEFORE_RUN, {
						routeTransition: transaction,
						routeName,
						routeState,
					});
				});

				const {
					currentRouteName: routeName,
					currentHistoryState: routeState = {},
				} = ROUTER;

				if (!routeName) {
					emitNotFound(dispatch, updateProperties, routeState);
					return;
				}

				const { currentRouteParams } = ROUTER;
				replaceWith(dispatch, routeName, {
					...routeState,
					...currentRouteParams,
				});
			}
		},

		[ROUTER_BEHAVIOR__BEFORE_RUN]: ({
			properties: { routeName: currentRouteName, delegates },
			dispatch,
			action: {
				payload: { routeTransition, routeName, routeState },
			},
		}) => {
			const payload = {
				routeProps: {
					originalDelegates: { ...delegates },
					routeTransition,
					routeName,
					routeState,
				},
			};

			// Check if we have any delegates and whether
			// the route is changing or not
			const numDelegates = Object.keys(delegates).length;
			if (numDelegates && currentRouteName !== routeName) {
				dispatch(ROUTER_BEHAVIOR__DELEGATE_RUN, payload);
				return;
			}

			dispatch(ROUTER_BEHAVIOR__RUN, payload);
		},
		[ROUTER_BEHAVIOR__DELEGATE_RUN]: ({
			updateProperties,
			properties: { delegates, routeName },
			dispatch,
			action: { payload },
		}) => {
			const delegateIds = Object.keys(delegates);
			if (!delegateIds.length) {
				// All delegates have completed successfully.
				dispatch(ROUTER_BEHAVIOR__RUN, payload);
				return;
			}

			const currentDelegateId = delegateIds.shift();
			const nextFunction = () => {
				dispatch(IMPORTING_DATA__REMOVE_PENDING_RECORDS);

				// Run the next delegate after delegate has been removed
				const onDelegateUpdate = () => dispatch(ROUTER_BEHAVIOR__DELEGATE_RUN, payload);

				// remove the delegate which was just processed
				dispatch(ROUTER_BEHAVIOR__UNSET_DELEGATE, {
					id: currentDelegateId,
					onDelegateUpdate,
				});
			};

			const cancelFunction = () => {
				// reset all the delegates since one of the delegates was cancelled
				updateProperties({ delegates: payload.routeProps.originalDelegates });

				const { currentRouteName } = ROUTER;

				// When using the browser back / forward button. The routeName changes prior to
				// the confirmation modal. Here we only handle the case where the back browser button
				// has been clicked, and redirect back in order to stay on the same page.
				if (routeName !== currentRouteName) {
					/**
					 * NOTE: there is an edge case in which the user follows the following:
					 * 1. goes to a page with a delegate
					 * 2. navigates to a separate page
					 * 3. presses the browser back button to the delegate, and starts editing
					 * 4. presses the browser forward button and chooses to cancel
					 * at this point, we would update the URL route incorrectly, but the user would still be able to
					 * make modifications, and the route would change appropiately upon completion.
					 */
					window.history.forward();
				}
			};

			// Run the delegate
			delegates[currentDelegateId](nextFunction, cancelFunction);
		},
		[ROUTER_BEHAVIOR__RUN]: ({
			action: {
				payload: { routeProps },
			},
		}) => {
			const { routeTransition, routeName, routeState } = routeProps;
			routeTransition.run(routeName, routeState);
		},
		[ROUTER_BEHAVIOR__SET_DELEGATE]: ({
			updateProperties,
			properties: { delegates },
			action: {
				payload: { id, delegate },
			},
		}) => {
			// Throw a warning if the user tries to leave without delegates cleared
			window.onbeforeunload = () =>
				'You have unsaved changes. Are you sure you want to leave?';
			const updatedDelegates = { ...delegates, ...{ [id]: delegate } };
			updateProperties({ delegates: updatedDelegates });
		},
		[ROUTER_BEHAVIOR__UNSET_DELEGATE]: ({
			updateProperties,
			properties: { delegates },
			action: {
				payload: { id, onDelegateUpdate },
			},
		}) => {
			const updatedDelegates = { ...delegates };
			if (id in updatedDelegates) {
				delete updatedDelegates[id];
				updateProperties({ delegates: updatedDelegates });
			}

			if (!Object.keys(updatedDelegates).length) {
				// Remove the warning since all delegates are removed
				window.onbeforeunload = () => null;
			}

			if (onDelegateUpdate) {
				onDelegateUpdate();
			}
		},

		[ROUTER_BEHAVIOR__TRANSITION_TO]: async ({ action, dispatch, updateProperties }) => {
			action.stopPropagation();

			if (!ROUTER) {
				return;
			}

			const { routeName } = action.payload;
			const routeState = (await action.payload.routeState) || {};
			const transition = new RouteTransition(ROUTER);
			attachHandlers(transition, dispatch, updateProperties);

			dispatch(ROUTER_BEHAVIOR__BEFORE_RUN, {
				routeTransition: transition,
				routeName,
				routeState,
			});
		},

		[ROUTER_BEHAVIOR__REPLACE_WITH]: async ({ action, dispatch, updateProperties }) => {
			action.stopPropagation();

			if (!ROUTER) {
				return;
			}

			const { routeName } = action.payload;
			const routeState = (await action.payload.routeState) || {};
			const replacement = new RouteReplacement(ROUTER);
			attachHandlers(replacement, dispatch, updateProperties);
			dispatch(ROUTER_BEHAVIOR__BEFORE_RUN, {
				routeTransition: replacement,
				routeName,
				routeState,
			});
		},

		[ROUTER_BEHAVIOR__UPDATE_ROUTE_STATE]: async ({
			properties,
			action,
			dispatch,
			updateProperties,
		}) => {
			action.stopPropagation();

			if (!ROUTER) {
				return;
			}

			const { routeName, routeState: currentRouteState } = properties;
			const routeState = (await action.payload.routeState) || {};
			const replacement = new RouteReplacement(ROUTER);
			attachHandlers(replacement, dispatch, updateProperties);

			dispatch(ROUTER_BEHAVIOR__BEFORE_RUN, {
				routeTransition: replacement,
				routeName,
				routeState: {
					...currentRouteState,
					...routeState,
				},
			});
		},
		[ROUTER_BEHAVIOR__BACK]: () => {
			history.back();
		},
		[COMPONENT_DISCONNECTED]: () => {
			if (ROUTER) {
				ROUTER.destroy();
				ROUTER = undefined;
			}
		},
	},
};

export default routerBehavior;
