import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

import times from 'lodash/times';

import './../sn-aes-skeleton-card';

import styles from './sn-aes-skeleton-loader.scss';

createCustomElement('sn-aes-skeleton-loader', {
	view({ properties: { loaders } }) {
		return <Fragment>{loaders}</Fragment>;
	},
	properties: {
		type: {
			default: '',
			schema: { type: 'string' },
		},
		count: {
			default: 1,
			schema: { type: 'integer' },
		},
		loaders: {
			computed({ properties: { type, count } }) {
				return times(count, index => <sn-aes-skeleton-card key={index} type={type} />);
			},
		},
	},
	styles,
});
