import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;
import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	BEHAVIOR_NAME,
	CURRENT_INSTANCE_AES_CONFIGURATION_ENDPOINT,
	CURRENT_INSTANCE_AES_CONFIGURATION_PROPERTY,
	CURRENT_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH,
	CURRENT_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_SUCCEEDED,
	CURRENT_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_FAILED,
	PROD_INSTANCE_AES_CONFIGURATION_ENDPOINT,
	PROD_INSTANCE_AES_CONFIGURATION_PROPERTY,
	PROD_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH,
	PROD_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_SUCCEEDED,
	PROD_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_FAILED,
} from './constants';

const aesConfigurationBehavior = {
	name: BEHAVIOR_NAME,
	properties: {
		[CURRENT_INSTANCE_AES_CONFIGURATION_PROPERTY]: {
			default: [],
			selectable: true,
		},
		[PROD_INSTANCE_AES_CONFIGURATION_PROPERTY]: {
			default: [],
			selectable: true,
		},
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch(CURRENT_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH);
			dispatch(PROD_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH);
		},
		[CURRENT_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH]: createHttpEffect(
			CURRENT_INSTANCE_AES_CONFIGURATION_ENDPOINT,
			{
				successActionType: CURRENT_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_SUCCEEDED,
				errorActionType: CURRENT_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_FAILED,
			}
		),
		[CURRENT_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_SUCCEEDED]: ({
			updateProperties,
			action: {
				payload: { result },
			},
		}) => {
			updateProperties({
				[CURRENT_INSTANCE_AES_CONFIGURATION_PROPERTY]: result,
			});
		},
		[CURRENT_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_FAILED]: () => {},
		[PROD_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH]: createHttpEffect(
			PROD_INSTANCE_AES_CONFIGURATION_ENDPOINT,
			{
				successActionType: PROD_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_SUCCEEDED,
				errorActionType: PROD_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_FAILED,
			}
		),
		[PROD_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_SUCCEEDED]: ({
			action: {
				payload: { result },
			},
			updateProperties,
		}) => {
			updateProperties({
				[PROD_INSTANCE_AES_CONFIGURATION_PROPERTY]: result,
			});
		},
		[PROD_INSTANCE_AES_CONFIGURATION_BEHAVIOR__FETCH_FAILED]: () => {},
	},
};

export default aesConfigurationBehavior;
