import { IMAGE_CARD_PLACEHOLDER, PLACEHOLDER_IMAGE_COUNT } from './constants';

/**
 * Returns the URL of the placeholder image for cards with none
 *
 * @param {string} id
 * @returns {string}
 */
export default function getPlaceholderImage(id) {
	if (!id) {
		return '';
	}

	let index = 0;

	if (isNaN(parseInt(id[0]))) {
		index = (id.charCodeAt(0) % PLACEHOLDER_IMAGE_COUNT) + 1;
	} else {
		index = (parseInt(id[0]) % PLACEHOLDER_IMAGE_COUNT) + 1;
	}

	return `/${IMAGE_CARD_PLACEHOLDER}${index}.svg`;
}
