import {
	ROUTER_BEHAVIOR_UNKNOWN_ROUTE,
	ROUTE_NAME_PROPERTY,
	ROUTE_STATE_PROPERTY,
	ROUTER_BEHAVIOR__ROUTE_ENTERED,
	ROUTER_BEHAVIOR__ROUTE_NOT_FOUND,
	ROUTER_BEHAVIOR__TRANSITION_TO,
	ROUTER_BEHAVIOR__REPLACE_WITH,
	ROUTER_BEHAVIOR__UPDATE_ROUTE_STATE,
} from './constants';

/**
 * A helper function to facilitate transitioning between routes
 * @param {function(string, object)} dispatch Dispatch bound to host component
 * @param {string} routeName The name of the desired route
 * @param {Promise<object>|object} routeState Arbitrary data to store in browser history
 */
export function transitionTo(dispatch, routeName, routeState) {
	dispatch(ROUTER_BEHAVIOR__TRANSITION_TO, {
		routeName,
		...(routeState && { routeState }),
	});
}

/**
 * A helper function to facilitate replacing the state of the specified route
 * @param {function(string, object)} dispatch Dispatch bound to host component
 * @param {string} routeName The name of the desired route
 * @param {Promise<object>|object} routeState Arbitrary data to store in browser history
 */
export function replaceWith(dispatch, routeName, routeState) {
	dispatch(ROUTER_BEHAVIOR__REPLACE_WITH, {
		routeName,
		...(routeState && { routeState }),
	});
}

/**
 * A helper function to facilitate replacing the state of the current route
 * @param {function(string, object)} dispatch Dispatch bound to host component
 * @param {Promise<object>|object} routeState Arbitrary data to store in browser history
 */
export function updateRouteState(dispatch, routeState) {
	dispatch(ROUTER_BEHAVIOR__UPDATE_ROUTE_STATE, { routeState });
}

export function emitNotFound(dispatch, updateProperties, routeState) {
	const payload = {
		[ROUTE_NAME_PROPERTY]: ROUTER_BEHAVIOR_UNKNOWN_ROUTE,
		[ROUTE_STATE_PROPERTY]: routeState,
	};
	updateProperties(payload);
	dispatch(ROUTER_BEHAVIOR__ROUTE_NOT_FOUND, payload);
}

/**
 *
 * @param {RouteTransaction} transaction
 * @param {function} dispatch
 * @param {function} updateProperties
 */
export function attachHandlers(transaction, dispatch, updateProperties) {
	transaction.on('notfound', ({ routeState }) => {
		emitNotFound(dispatch, updateProperties, routeState);
	});

	transaction.on('reroute', ({ type, routeName, routeState }) => {
		switch (type) {
			case 'transition':
				transitionTo(dispatch, routeName, routeState);
				break;

			case 'replacement':
				replaceWith(dispatch, routeName, routeState);
				break;
		}
	});

	transaction.on('complete', ({ routeName, routeState }) => {
		const payload = {
			[ROUTE_NAME_PROPERTY]: routeName,
			[ROUTE_STATE_PROPERTY]: routeState,
		};

		updateProperties(payload);
		dispatch(ROUTER_BEHAVIOR__ROUTE_ENTERED, payload);
	});
}
