import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

import { t } from 'sn-translate';

import '@servicenow/now-icon';
import '@servicenow/now-dropdown';
import '@servicenow/now-button';
import '@servicenow/now-tooltip';
import '@servicenow/now-popover';
import '../sn-aes-reference-modal';

import isTruncated from '../../helpers/isTruncated';
import dispatchActionViaPayload from '../../helpers/dispatch-action-via-payload';

import {
	APPLICATION_OBJECT_CARD_IMG_SLOT,
	APPLICATION_OBJECT_CARD_TOOLTIP_DELAY,
	APPLICATION_OBJECT_CARD__CARD_CLICKED,
	APPLICATION_OBJECT_CARD__ACTION_ITEM_CLICKED,
	APPLICATION_OBJECT_CARD__ACTION_CLICKED,
} from './constants';
import styles from './sn-aes-application-object-card.scss';
import refIconSVG from '../../assets/ref_role.svg';
import refIconFillSVG from '../../assets/ref-img-fill.svg';

/**
 * Converts data string to an array of elements
 * where the highlighted text are wrapped in mark tags.
 * If there are no instances of highlighted text, it returns
 * the original string.
 * @param {string} data
 * @param {string} highlightText
 * @returns {string|Array<string|Object>}
 */
function getHighlightedText(data = '', highlightText) {
	if (!highlightText) {
		return data;
	}

	const elms = [];
	const lowercaseData = data.toLowerCase();
	const lowercaseHighlightText = highlightText.toLowerCase();
	const len = highlightText.length;
	let key = 0;
	let lastIdx = 0;

	// eslint-disable-next-line no-constant-condition
	while (true) {
		let idx = lowercaseData.indexOf(lowercaseHighlightText, lastIdx);

		if (idx === -1) {
			break;
		}

		elms.push(data.substring(lastIdx, idx));
		elms.push(<mark key={key++}>{data.substring(idx, idx + len)}</mark>);
		lastIdx = idx + len;
	}
	elms.push(data.substring(lastIdx, data.length));

	return elms;
}

function CardItem(props) {
	const highlightedText = getHighlightedText(props.data, props.highlightText);
	const highlightLabel = `${props.data}${
		highlightedText.length > 1 ? t(' highlighted text: {0}', props.highlightText) : ''
	}`;
	const { targetRef } = props;
	const ariaDescribeProp = isTruncated(targetRef)
		? { 'aria-describedby': `${props.id}Tooltip` }
		: {};

	return (
		<div
			className={`${props.id} truncate`}
			hook-insert={({ elm }) => props.setRef(props.id, elm)}
			hook-destroy={() => props.setRef(props.id, null)}
			{...ariaDescribeProp}
			{...props.configAria}>
			<span aria-label={highlightLabel}>
				{props.id === 'timestamp' ? t('Last edited ') : null}
				<span aria-hidden="true">{highlightedText}</span>
			</span>
			{isTruncated(targetRef) ? (
				<now-tooltip
					content={
						props.id === 'timestamp' ? t('Last edited {0}', props.data) : props.data
					}
					delay={APPLICATION_OBJECT_CARD_TOOLTIP_DELAY}
					id={`${props.id}Tooltip`}
					target-ref={targetRef}
				/>
			) : null}
		</div>
	);
}

function getReferenceObjectDescription(type, tableName) {
	const description = t(
		'This {0} belongs to another application scope and cannot be edited in this app. ',
		type
	);

	if (tableName === 'sys_user_role') {
		return (
			description +
			t(
				'However, you can remove all of its access controls to remove it from this app. Open the role to edit access controls.'
			)
		);
	}

	return description;
}

function view(state, { dispatch, updateState }) {
	const {
		properties: {
			id,
			items,
			displayName,
			description,
			timestamp,
			alertMessage,
			alertActions,
			highlightText,
			empty,
			categoryName,
			emptyText,
			type,
			isReference,
			hasLaunchUrl,
			tableName,
		},
		imgRefOpened,
	} = state;

	const setRef = (refId, ref) => {
		updateState({
			path: `cardItemRef.${refId}`,
			value: ref,
			operation: 'set',
		});
	};

	return (
		<div
			class={{
				wrapper: true,
				'is-empty': empty,
				'is-not-empty': !empty,
			}}
			on-click={action => {
				action.stopPropagation();
				dispatch(APPLICATION_OBJECT_CARD__CARD_CLICKED, {
					id,
					empty,
					categoryName,
				});
			}}>
			<button
				aria-label={
					empty
						? t('Click to create object of type {0}', categoryName)
						: t('Click to open object')
				}
				className="card-button"
				on-click={action => {
					action.stopPropagation();
					dispatch(APPLICATION_OBJECT_CARD__CARD_CLICKED, {
						id,
						empty,
						categoryName,
					});
				}}
			/>
			{empty ? (
				<div className="application-object-card-empty now-align -block-center -inline-center">
					<now-icon icon="circle-plus-outline" />
					<span className="text">{emptyText}</span>
				</div>
			) : (
				<Fragment>
					<div
						class={{
							'application-object-card': true,
							'-round-all': !alertMessage,
							'-round-top': alertMessage,
						}}>
						<div className="icon">
							{isReference ? (
								<div>
									<now-popover
										managedOpened={true}
										opened={imgRefOpened}
										positions={[{ target: 'top-center' }]}>
										<img
											alt=""
											slot="trigger"
											width={28}
											height={28}
											className="reference-icon"
											src={refIconSVG}
											onmouseenter={() => {
												updateState({ imgRefOpened: true });
											}}
											onmouseleave={() => {
												updateState({ imgRefOpened: false });
											}}
										/>
										<sn-aes-reference-modal
											slot="content"
											id="reference"
											headerText={t('Referenced Object')}
											description={getReferenceObjectDescription(
												type,
												tableName
											)}
											modalPosition="top"
											headerIconSrc={refIconFillSVG}
										/>
									</now-popover>
								</div>
							) : null}
							<slot name={APPLICATION_OBJECT_CARD_IMG_SLOT} />
						</div>
						<CardItem
							configAria={{ ['aria-label']: t('Object Name') }}
							data={displayName}
							targetRef={state.cardItemRef['name']}
							id="name"
							setRef={setRef}
							highlightText={highlightText}
						/>
						<CardItem
							configAria={{ ['aria-label']: t('Object Type') }}
							data={type}
							targetRef={state.cardItemRef['type']}
							id="type"
							setRef={setRef}
							highlightText={highlightText}
						/>
						<CardItem
							configAria={{ ['aria-label']: t('Object Description') }}
							data={description}
							targetRef={state.cardItemRef['description']}
							id="description"
							setRef={setRef}
							highlightText={highlightText}
						/>
						<CardItem
							configAria={{ ['aria-label']: t('Object Timestamp') }}
							data={timestamp}
							targetRef={state.cardItemRef['timestamp']}
							id="timestamp"
							setRef={setRef}
							highlightText={highlightText}
						/>
						{hasLaunchUrl ? (
							<div className="preview">
								<now-button-bare
									size="sm"
									id="launch-preview"
									className="launch-preview"
									label={t('PREVIEW')}
									variant="secondary"
									iconStart="eye-fill"
									append-to-payload={{
										actionName: APPLICATION_OBJECT_CARD__ACTION_ITEM_CLICKED,
										id,
										item: items.find(item => item.id === 'launch'),
									}}
									// We need these so that the click/keydown doesn't bubble up to the card
									on-click={event => event.stopPropagation()}
									on-keydown={event => event.stopPropagation()}
								/>
							</div>
						) : (
							// `<div />` needed over null to properly align items in application object card
							<div />
						)}
						<div className="menu">
							<now-dropdown
								bare
								className="now-align -inline-center -block-center"
								config-aria={{ 'aria-label': t('Object Menu') }}
								items={items}
								select="none"
								append-to-payload={{
									id,
									actionName: APPLICATION_OBJECT_CARD__ACTION_ITEM_CLICKED,
								}}
								// We need these so that the click/keydown doesn't bubble up to the card
								on-click={event => event.stopPropagation()}
								on-keydown={event => event.stopPropagation()}>
								<now-icon slot="trigger-content" icon="ellipsis-h-fill" size="lg" />
							</now-dropdown>
						</div>
					</div>
					{alertMessage ? (
						<div
							className="application-object-card-alert now-align -inline-end -block-center -round-bottom"
							aria-label={t('Object Alert')}>
							{alertMessage}
							<div
								id="applicationObjectCardActions"
								aria-label={t('Object Alert Actions')}>
								{alertActions.map(({ actionId, label }, index) => (
									<Fragment>
										<now-button-bare
											label={`[${label}]`}
											size="sm"
											variant="secondary"
											append-to-payload={{
												actionName: APPLICATION_OBJECT_CARD__ACTION_CLICKED,
												id,
												action: { actionId, label },
											}}
											aria-label={t('Action {0} : {1}', index + 1, label)}
											// We need these so that the click/keydown doesn't bubble up to the card
											on-click={event => event.stopPropagation()}
											on-keydown={event => event.stopPropagation()}
										/>
										&nbsp;
									</Fragment>
								))}
							</div>
						</div>
					) : null}
				</Fragment>
			)}
		</div>
	);
}

export const actionHandlers = {
	['NOW_DROPDOWN#ITEM_CLICKED']: dispatchActionViaPayload,
	['NOW_BUTTON_BARE#CLICKED']: dispatchActionViaPayload,
};

/**
 * Application object card to display files inside app page.
 *
 * <sn-aes-application-object-card
 *     id="sys_id"
 *     displayName="title/name text"
 *     description="description text"
 *     timestamp="timestamp text"
 *     status="ready"
 *     items={dropdownItems}
 *     alertMessage="alert message"
 *     highlightText="highlight text"
 *     alertActions={alertActions}>
 *         <img
 *             slot={APPLICATION_OBJECT_CARD_IMG_SLOT}
 *             src="img src"
 *         />
 *     empty={true/false}
 *     categoryName="category name for empty state"
 * </sn-aes-application-object-card>
 * ```
 *
 * @seismicElement sn-aes-application-object-card
 * @summary Application object card to display files inside app page.
 */
createCustomElement('sn-aes-application-object-card', {
	view,
	styles,
	properties: {
		/**
		 * The sys_id for the application object card.
		 * @type {string}
		 */
		id: { default: '' },
		/**
		 * Application object card title/name text.
		 * @type {string}
		 */
		displayName: { default: '' },
		/**
		 * Application object card description text.
		 * @type {string}
		 */
		description: { default: '' },
		/**
		 * Application object card timestamp text.
		 * @type {string}
		 */
		timestamp: { default: '' },
		/**
		 * Status of the object card ("ready" | "setup" | "review")
		 * @type {string}
		 */
		status: { default: 'ready' },
		/**
		 * Text to highlight in displayName, description and timestamp.
		 * @type {string}
		 */
		highlightText: { default: '' },
		/**
		 * Array of actions to be rendered in dropdown menu.
		 * @type {[{id: string, label: string}]}
		 */
		items: { default: [] },
		/**
		 * Alert message to display alert box.
		 * @type {string}
		 */
		alertMessage: { default: '' },
		/**
		 * Actions to be rendered in alert box.
		 * @type {[{actionId: string, label: string}]}
		 */
		alertActions: { default: [] },
		/**
		 * Boolean for controlling display of empty application object card.
		 * @type {boolean}
		 */
		empty: { default: false },
		/**
		 * Category Name to display in empty application object card.
		 * @type {string}
		 */
		categoryName: { default: '' },
		/**
		 * Text to display in empty application object card.
		 * @type {string}
		 */
		emptyText: { default: '' },
		/**
		 * Type to display in empty application object card.
		 * @type {string}
		 */
		type: { default: '' },
		/**
		 * Boolean for controlling display of referenced items
		 * @type {boolean}
		 */
		isReference: { default: false },
		/**
		 * Table name of the object
		 * @type {string}
		 */
		tableName: { default: '' },
		/*
		 * Boolean to test if items has 'launch'
		 * @type {boolean}
		 */
		hasLaunchUrl: {
			computed({ properties: { items } }) {
				return items.map(item => item.id).includes('launch');
			},
		},
	},
	initialState: {
		cardItemRef: {},
		imgRefOpened: false,
	},
	actionHandlers,
	dispatches: {
		/**
		 * Dispatched when the card is clicked or there is a keydown 'Enter'
		 * event on the card.
		 * @type {{ id: string}}
		 */
		[APPLICATION_OBJECT_CARD__CARD_CLICKED]: {},
		/**
		 * Dispatched when an option in the dropdown menu is selected.
		 * @type {{ id: string, item: { id: string, label: string}}}
		 */
		[APPLICATION_OBJECT_CARD__ACTION_ITEM_CLICKED]: {},
		/**
		 * Dispatched when an action is clicked in the alert box.
		 * @type {{ id: string, item: { actionId: string, label: string}}}
		 */
		[APPLICATION_OBJECT_CARD__ACTION_CLICKED]: {},
	},
});
