import { createCustomElement } from '@servicenow/ui-core';
import { TOGGLE_FULL_SCREEN } from './constants';
import { toggleFullScreen } from './actionHandlers';
import styles from './sn-aes-app-wrapper.scss';

createCustomElement('sn-aes-app-wrapper', {
	view: ({ properties: { showHeaderChrome } }) => {
		return (
			<div className="app-wrapper">
				{showHeaderChrome ? (
					<slot id="sn-aes-header-chrome-id" name="sn-aes-header-chrome" />
				) : null}
				<slot />
			</div>
		);
	},
	actionHandlers: {
		[TOGGLE_FULL_SCREEN]: toggleFullScreen,
	},
	properties: {
		showHeaderChrome: {
			default: true,
			selectable: true,
		},
	},
	styles,
});
