import {
	OVERLAY_CONTROLLER,
	OVERLAY_CONTROLLER__PRIMARY_CLICKED,
	OVERLAY_CONTROLLER__NEXT_RENDERER,
	OVERLAY_CONTROLLER__RENDERERS_COMPLETED,
	defaultOverlayState,
} from './constants';
import {
	ROOT__SHOW_OVERLAY,
	ROOT__HIDE_OVERLAY,
	INTERNAL_OVERLAY_ACTION_DO_NOT_USE,
} from '../../components/sn-app-engine-studio/constants';
import {
	CREATE_OBJECT_TEMPLATE_OPERATION,
	CREATE_APPLICATION_OPERATION,
} from '../../components/sn-aes-wizard-manager/constants';
import { APPS_OVERLAY__CONTINUE_CLICKED } from '../../components/sn-aes-apps-overlay-content/constants';
import { ADD_OBJECT__CONTINUE } from '../../components/sn-aes-app-add-object/constants';
import { ADD_AUTOMATION__CONTINUE } from '../../components/sn-aes-app-add-automation/constants';
import { MODAL_OVERLAY__OPEN_SET } from '../../components/sn-aes-modal-overlay/constants';
import { transitionTo } from '../router';

export const getTemplatePageOperations = ({ appSysId, templateSysId }) => {
	let operations = [];

	if (!appSysId && templateSysId) {
		operations = [
			{
				operation: CREATE_APPLICATION_OPERATION,
				params: {
					// The wizard can use this to know that we are chaining templates together on the frontend.
					// For example we chain the app template and table template together.
					collectedResults: { frontendComposed: true },
				},
			},
			{
				operation: CREATE_OBJECT_TEMPLATE_OPERATION,
				params: { templateId: templateSysId },
			},
		];
	}

	if (appSysId && templateSysId) {
		operations = [
			{
				operation: CREATE_OBJECT_TEMPLATE_OPERATION,
				params: {
					appId: appSysId,
					templateId: templateSysId,
				},
			},
		];
	}

	if (!operations.length) {
		console.warn('Wizard manager requires at least one operation');
	}

	return operations;
};

const genericContinueClickedEffect = ({ action: { stopPropagation, payload }, dispatch }) => {
	// FIXME: Temporary to get around now-hoist not calling meta.event.stopPropagation()
	if (payload.inAppHome) {
		return;
	}
	stopPropagation();
	dispatch(OVERLAY_CONTROLLER__PRIMARY_CLICKED, payload);
};

const overlayControllerBehavior = {
	name: OVERLAY_CONTROLLER,

	initialState: {
		// Supporting an array of renderers
		renderers: [],
		// Index helps control navigation within overlay
		activeRendererIdx: null,
		// The "continue" action of each renderer will add to a shared state object that will
		// be passed in as props to whatever should happen after all renderers have completed
		sharedState: {},
	},

	actionHandlers: {
		[ROOT__SHOW_OVERLAY]: ({
			action: {
				stopPropagation,
				payload: { renderer, initialState },
			},
			updateState,
			dispatch,
		}) => {
			stopPropagation();
			const isArr = Array.isArray(renderer);

			const newOverlayState = {
				renderers: isArr ? renderer : [renderer],
				activeRendererIdx: 0,
				sharedState: {
					...initialState,
				},
			};

			updateState({
				path: 'behaviors.OVERLAY_CONTROLLER',
				value: newOverlayState,
				operation: 'set',
			});

			dispatch(INTERNAL_OVERLAY_ACTION_DO_NOT_USE, {
				OverlayComponent: isArr ? renderer[0] : renderer,
				overlayOpen: true,
			});
		},

		[ROOT__HIDE_OVERLAY]: ({ updateState, dispatch }) => {
			updateState({
				path: 'behaviors.OVERLAY_CONTROLLER',
				value: defaultOverlayState,
				operation: 'set',
			});

			dispatch(INTERNAL_OVERLAY_ACTION_DO_NOT_USE, {
				OverlayComponent: null,
				overlayOpen: false,
			});
		},
		[MODAL_OVERLAY__OPEN_SET]: ({ dispatch }) => {
			dispatch(ROOT__HIDE_OVERLAY);
		},

		// Mapping of overlay content continue actions
		[APPS_OVERLAY__CONTINUE_CLICKED]: genericContinueClickedEffect,
		[ADD_OBJECT__CONTINUE]: genericContinueClickedEffect,
		[ADD_AUTOMATION__CONTINUE]: genericContinueClickedEffect,

		[OVERLAY_CONTROLLER__PRIMARY_CLICKED]: ({
			state: {
				behaviors: {
					OVERLAY_CONTROLLER: { sharedState },
				},
			},
			action: {
				payload: { appSysId, templateSysId },
			},
			updateState,
			dispatch,
		}) => {
			updateState({
				path: 'behaviors.OVERLAY_CONTROLLER.sharedState',
				value: {
					...sharedState,
					// Some ES6 nonsense for conditionally adding keys to this object
					...(appSysId && { appSysId }),
					...(templateSysId && { templateSysId }),
				},
				operation: 'set',
			});

			dispatch(OVERLAY_CONTROLLER__NEXT_RENDERER);
		},

		[OVERLAY_CONTROLLER__NEXT_RENDERER]: ({
			state: {
				behaviors: { OVERLAY_CONTROLLER: overlayState },
			},
			dispatch,
			updateState,
		}) => {
			const { renderers, activeRendererIdx } = overlayState;

			if (activeRendererIdx >= renderers.length - 1) {
				dispatch(OVERLAY_CONTROLLER__RENDERERS_COMPLETED);
				return;
			}

			const newIndex = activeRendererIdx + 1;

			updateState({
				path: 'behaviors.OVERLAY_CONTROLLER',
				value: {
					...overlayState,
					activeRendererIdx: newIndex,
				},
				operation: 'set',
			});

			dispatch(INTERNAL_OVERLAY_ACTION_DO_NOT_USE, {
				OverlayComponent: renderers[newIndex],
				overlayOpen: true,
			});
		},

		[OVERLAY_CONTROLLER__RENDERERS_COMPLETED]: ({
			state: {
				behaviors: {
					OVERLAY_CONTROLLER: {
						sharedState: { appSysId, templateSysId },
					},
				},
			},
			dispatch,
		}) => {
			if (appSysId && !templateSysId) {
				console.warn('Overlay controller received an appSysId but no templateSysId');
				return;
			}
			const operations = getTemplatePageOperations({ appSysId, templateSysId });
			transitionTo(dispatch, 'template', { operations });
			dispatch(ROOT__HIDE_OVERLAY);
		},
	},
};

export default overlayControllerBehavior;
