import {
	SIDEBAR__SET_STEP,
	SIDEBAR__NEXT_STEP,
	SIDEBAR__PREVIOUS_STEP,
	SIDEBAR__TOGGLE_SIDEBAR,
	SIDEBAR__DISPLAY_SIDEBAR,
	SIDEBAR__SET_ACCESSIBLE_STEPS,
	SIDEBAR__SET_MODAL_OPEN,
	SIDEBAR__SET_CATEGORIES,
	SIDEBAR__LOCK_STEPS,
	SIDEBAR__SET_TOGGLE_INTERACTED,
} from './constants';

export function sidebarNextStep(dispatch) {
	dispatch(SIDEBAR__NEXT_STEP);
}

export function sidebarPreviousStep(dispatch) {
	dispatch(SIDEBAR__PREVIOUS_STEP);
}

export function sidebarSetStep(dispatch, value) {
	dispatch(SIDEBAR__SET_STEP, value);
}

export function sidebarToggle(dispatch, value) {
	dispatch(SIDEBAR__TOGGLE_SIDEBAR, value);
}

export function sidebarDisplay(dispatch, value) {
	dispatch(SIDEBAR__DISPLAY_SIDEBAR, value);
}

export function sidebarSetAccessibleSteps(dispatch, value) {
	dispatch(SIDEBAR__SET_ACCESSIBLE_STEPS, value);
}

export function sidebarSetCategories(dispatch, value) {
	dispatch(SIDEBAR__SET_CATEGORIES, value);
}

export function sidebarSetModalOpen(dispatch, value) {
	dispatch(SIDEBAR__SET_MODAL_OPEN, value);
}

export function sidebarLockSteps(dispatch, value) {
	dispatch(SIDEBAR__LOCK_STEPS, value);
}

export function sidebarSetToggleInteracted(dispatch) {
	dispatch(SIDEBAR__SET_TOGGLE_INTERACTED);
}
