import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	BEHAVIOR_NAME,
	DEPENDENCIES_DATA_BEHAVIOR__GET_TEMPLATE_DEPENDENCIES,
	DEPENDENCIES_DATA_BEHAVIOR__SET_DEPENDENCIES,
	DEPENDENCIES_DATA_BEHAVIOR__FETCH_DEPENDENCIES,
	DEPENDENCIES_DATA_BEHAVIOR__FETCH_DEPENDENCIES_SUCCEEDED,
	DEPENDENCIES_DATA_PROPERTY,
	GET_SPOKE_URL,
	DEPENDENCY_RESULT_TYPE_DETAILS_MAP,
} from './constants';

import { TEMPLATE_DATA_BEHAVIOR__UPDATE_OBJECT_TEMPLATES } from '../template-data';

const dependenciesDataBehavior = {
	name: BEHAVIOR_NAME,
	properties: {
		[DEPENDENCIES_DATA_PROPERTY]: {
			default: {},
			selectable: true,
		},
	},
	actionHandlers: {
		[DEPENDENCIES_DATA_BEHAVIOR__GET_TEMPLATE_DEPENDENCIES]: ({
			action: {
				payload: { templates },
			},
			dispatch,
			properties,
		}) => {
			const newDependencies = {};
			const filteredTemplates = templates.filter(template => !template.dependencies);

			if (filteredTemplates.length === 0) {
				return;
			}

			filteredTemplates.forEach(template => {
				const dependenciesResult = get(template, 'dependency_evaluation_results', []);
				template.dependencies = {};

				dependenciesResult.forEach(result => {
					const resultTypeDetails = DEPENDENCY_RESULT_TYPE_DETAILS_MAP[result.type];
					if (!resultTypeDetails) {
						// We are not equipped for this result yet
						return;
					}
					const dependencyId = resultTypeDetails.getId(result);

					if (dependencyId) {
						template.dependencies[dependencyId] = [
							...(template.dependencies[dependencyId] || []),
							result.type,
						];

						const existingDependency =
							properties[DEPENDENCIES_DATA_PROPERTY][dependencyId] || {};

						if (
							isEmpty(existingDependency) ||
							!existingDependency.hasOwnProperty(result.type)
						) {
							// It is completely new dependency or a new check on an existing one
							newDependencies[dependencyId] = {
								...existingDependency,
								...(newDependencies[dependencyId] || {}),
								name: resultTypeDetails.getName(result),
								scopeName: get(result, 'dependency.inputs.scope_name'), // might not be available on all dependencies
								id: dependencyId,
								[result.type]: !result.checkFailed,
							};
						}
					}
				});
			});

			dispatch(TEMPLATE_DATA_BEHAVIOR__UPDATE_OBJECT_TEMPLATES, {
				templates: filteredTemplates,
			});

			if (!isEmpty(newDependencies)) {
				dispatch(DEPENDENCIES_DATA_BEHAVIOR__SET_DEPENDENCIES, {
					dependencies: newDependencies,
				});
			}
		},
		[DEPENDENCIES_DATA_BEHAVIOR__SET_DEPENDENCIES]: ({
			properties,
			action: {
				payload: { dependencies },
			},
			dispatch,
			updateProperties,
		}) => {
			updateProperties({
				[DEPENDENCIES_DATA_PROPERTY]: {
					...properties[DEPENDENCIES_DATA_PROPERTY],
					...dependencies,
				},
			});

			const dependencyScopes = Object.values(dependencies).filter(dep => dep.scopeName);

			if (dependencyScopes.length) {
				dispatch(DEPENDENCIES_DATA_BEHAVIOR__FETCH_DEPENDENCIES, {
					scope_name: dependencyScopes.map(dep => dep.scopeName).join(),
				});
			}
		},
		[DEPENDENCIES_DATA_BEHAVIOR__FETCH_DEPENDENCIES]: createHttpEffect(GET_SPOKE_URL, {
			method: 'GET',
			queryParams: ['scope_name'],
			successActionType: DEPENDENCIES_DATA_BEHAVIOR__FETCH_DEPENDENCIES_SUCCEEDED,
		}),
		[DEPENDENCIES_DATA_BEHAVIOR__FETCH_DEPENDENCIES_SUCCEEDED]: ({
			properties,
			action: {
				payload: { result },
			},
			updateProperties,
		}) => {
			const dependencies = result.reduce((res, dep) => {
				const existingDependency = get(
					properties,
					[DEPENDENCIES_DATA_PROPERTY, dep.scope],
					{}
				);
				res[dep.scope] = {
					...existingDependency,
					name: dep.name,
					id: dep.sys_id,
					logo: dep.logo,
				};
				return res;
			}, {});

			updateProperties({
				[DEPENDENCIES_DATA_PROPERTY]: {
					...properties[DEPENDENCIES_DATA_PROPERTY],
					...dependencies,
				},
			});
		},
	},
};

export default dependenciesDataBehavior;
