import { createCustomElement } from '@servicenow/ui-core';
import { transitionTo } from '../../behaviors/router';
import storageBehavior from '../../behaviors/storage';
import categoryBehavior from '../../behaviors/category';
import sidebarBehavior, { sidebarSetCategories } from '../../behaviors/sidebar-data';
import userDataBehavior from '../../behaviors/user-data';
import taxonomyDataBehavior from '../../behaviors/taxonomy-data';
import templateDataBehavior from '../../behaviors/template-data';
import applicationsDataBehavior from '../../behaviors/apps-data';
import aesConfigurationBehavior from '../../behaviors/aes-configuration';
import dependenciesDataBehavior from '../../behaviors/dependencies-data';
import overlayControllerBehavior from '../../behaviors/overlay-controller';
import resourceDataBehavior from '../../behaviors/resource-data';

import { actionTypes as headerActionTypes } from '../sn-aes-header-chrome';
import { ROUTE_DEFINITIONS } from './route_definitions';
import { CATEGORY_BEHAVIOR__CATEGORIES_FETCH_SUCCEEDED } from '../../behaviors/category/constants';
import importingDataBehavior from '../../behaviors/importing-data';

createCustomElement('sn-aes-provider', {
	view: () => <slot />,

	actionHandlers: {
		[headerActionTypes.HEADER_CHROME__ACTIVE_PAGE_SET]: ({ action, dispatch }) => {
			const { value: routeName } = action.payload;
			transitionTo(dispatch, routeName);
		},
		[CATEGORY_BEHAVIOR__CATEGORIES_FETCH_SUCCEEDED]: ({ action, dispatch }) => {
			sidebarSetCategories(dispatch, action.payload);
		},
	},
	behaviors: [
		ROUTE_DEFINITIONS,
		storageBehavior,
		categoryBehavior,
		sidebarBehavior,
		userDataBehavior,
		taxonomyDataBehavior,
		templateDataBehavior,
		applicationsDataBehavior,
		aesConfigurationBehavior,
		dependenciesDataBehavior,
		importingDataBehavior,
		overlayControllerBehavior,
		resourceDataBehavior,
	],
});
