import get from 'lodash/get';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import isCreateFromScratchApp from '../../helpers/is-create-from-scratch-app';
import { linkProjectRoute, CREATE_APP_DETAILS_ROUTE } from '../../helpers/constants';
import {
	TEMPLATE__LINK_FAILURE,
	TEMPLATE__LINK_PROJECT,
	TEMPLATE__LINK_SUCCESS,
	TEMPLATE__CREATE_APP_DETAILS,
	TEMPLATE__CREATE_APP_DETAILS_SUCCESS,
	TEMPLATE__CREATE_APP_DETAILS_FAILURE,
} from './constants';

import {
	WIZARD_MANAGER__TEMPLATE_COMPLETE,
	WIZARD_MANAGER__CANCEL,
	WIZARD_MANAGER__EDIT_RECORD,
	WIZARD_MANAGER__APP_CREATED,
	WIZARD_MANAGER__CRIT_FAIL,
} from '../../components/sn-aes-wizard-manager/constants';

import '../../components/sn-aes-builder';
import { transitionTo } from '../../behaviors/router';
import { APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS } from '../../behaviors/apps-data';
import { ROUTER_BEHAVIOR__BACK } from '../../behaviors/router/constants';
import '../../components/sn-aes-wizard-manager';
import {
	SIDEBAR_STEP_TYPE_CREATION,
	SIDEBAR_ALL_STEPS_PROPERTY,
	sidebarDisplay,
	sidebarSetAccessibleSteps,
	sidebarSetStep,
} from '../../behaviors/sidebar-data';
import { PROVIDER_PROPERTIES } from '../../components/sn-aes-provider';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;
import styles from './sn-aes-page-template.scss';
import { CATEGORY_BEHAVIOR__INITIAL_FETCH } from '../../behaviors/category';
import { setDelegate, showModal, unsetDelegate } from '../sn-aes-page-app/helpers';

import { getTabDetails } from '../../components/sn-aes-app-tab/sn-aes-app-tab-utils';
import { IMPORTING_DATA__REMOVE_PENDING_RECORDS } from '../../behaviors/importing-data/constants';

/**
 * template page
 *
 * ```jsx
 * <sn-aes-page-template />
 * ```
 *
 * @seismicElement sn-aes-page-template
 * @summary A page for a template's guided wizard to be displayed.
 */

function setSidebar(sidebarAllSteps, dispatch) {
	const step = sidebarAllSteps[0];
	sidebarSetStep(dispatch, { step });
	sidebarSetAccessibleSteps(dispatch, {
		types: [SIDEBAR_STEP_TYPE_CREATION],
	});
	sidebarDisplay(dispatch, { isDisplayed: true });
}

export const actionHandlers = {
	[WIZARD_MANAGER__TEMPLATE_COMPLETE]: ({ action, dispatch }) => {
		const {
			sys_app: { sys_id },
		} = action.payload;

		unsetDelegate({ dispatch, delegateId: 'sn-aes-page-template' });

		// Perform clean up for Import
		dispatch(IMPORTING_DATA__REMOVE_PENDING_RECORDS);

		transitionTo(dispatch, 'my-apps.app', { sysId: sys_id });
	},

	[WIZARD_MANAGER__EDIT_RECORD]: ({ action, dispatch }) => {
		const { filesToEdit, appSysId } = action.payload;

		// Perform clean up for Import
		dispatch(IMPORTING_DATA__REMOVE_PENDING_RECORDS);

		const tabs = filesToEdit.map(file => getTabDetails(appSysId, file));

		unsetDelegate({ dispatch, delegateId: 'sn-aes-page-template' });
		transitionTo(dispatch, 'my-apps.app', {
			sysId: appSysId,
			tabs: tabs,
			tabId: get(filesToEdit, '[0].sys_id'), // Make the first edit tab selected
		});
	},

	[WIZARD_MANAGER__CANCEL]: ({ action, dispatch }) => {
		const { showConfirmModal } = action.payload;
		if (!showConfirmModal) {
			unsetDelegate({ dispatch, delegateId: 'sn-aes-page-template' });
			dispatch(ROUTER_BEHAVIOR__BACK);
			return;
		}
		showModal({
			dispatch,
			id: 'sn-aes-page-template-confirm',
			nextFunction: () => {
				// Perform clean up for Import
				dispatch(IMPORTING_DATA__REMOVE_PENDING_RECORDS);

				// Remove the delegate if it exists
				unsetDelegate({ dispatch, delegateId: 'sn-aes-page-template' });

				// Close the tab since it's confirmed now.
				dispatch(ROUTER_BEHAVIOR__BACK);
			},
		});
	},

	[WIZARD_MANAGER__CRIT_FAIL]: ({ dispatch }) => {
		unsetDelegate({ dispatch, delegateId: 'sn-aes-page-template' });
		dispatch(ROUTER_BEHAVIOR__BACK);
	},

	[WIZARD_MANAGER__APP_CREATED]: ({ action, updateState, dispatch }) => {
		updateState({
			workingAppSysId: action.payload.sysId,
			workingOperations: action.payload.operations,
		});

		dispatch(TEMPLATE__LINK_PROJECT, {
			sysId: action.payload.sysId,
		});
	},

	[TEMPLATE__LINK_PROJECT]: createHttpEffect(linkProjectRoute, {
		method: 'POST',
		successActionType: TEMPLATE__LINK_SUCCESS,
		errorActionType: TEMPLATE__LINK_FAILURE,
		pathParams: ['sysId'],
		batch: false,
	}),

	[TEMPLATE__LINK_SUCCESS]: ({ state: { workingAppSysId, workingOperations }, dispatch }) => {
		dispatch(TEMPLATE__CREATE_APP_DETAILS, {
			sysId: workingAppSysId,
			data: { operations: workingOperations },
		});
	},

	// TODO: (Devin) We need to think how we can expose this to the user.
	//       If this happens the app will be lost forever from the citizen dev perspective
	[TEMPLATE__LINK_FAILURE]: ({ action: { payload } }) => {
		console.warn('App linking failed', payload);
	},

	[TEMPLATE__CREATE_APP_DETAILS]: createHttpEffect(CREATE_APP_DETAILS_ROUTE, {
		method: 'POST',
		successActionType: TEMPLATE__CREATE_APP_DETAILS_SUCCESS,
		errorActionType: TEMPLATE__CREATE_APP_DETAILS_FAILURE,
		pathParams: ['sysId'],
		dataParam: 'data',
		batch: false,
	}),

	[TEMPLATE__CREATE_APP_DETAILS_SUCCESS]: ({ dispatch }) => {
		dispatch(APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS);
	},

	[TEMPLATE__CREATE_APP_DETAILS_FAILURE]: ({ dispatch }) => {
		dispatch(APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS);
	},

	[COMPONENT_BOOTSTRAPPED]: ({
		properties: {
			routeState: { operations },
			sidebarAllSteps,
		},
		dispatch,
	}) => {
		dispatch(CATEGORY_BEHAVIOR__INITIAL_FETCH);

		const showSidebar = isCreateFromScratchApp(operations);

		if (showSidebar) {
			setSidebar(sidebarAllSteps, dispatch);
		} else {
			sidebarDisplay(dispatch, { isDisplayed: false });
		}
		setDelegate({
			dispatch,
			delegateId: 'sn-aes-page-template',
		});
	},
};
createCustomElement('sn-aes-page-template', {
	view: state => {
		const {
			properties: {
				routeState: { operations },
			},
		} = state;

		return (
			<div className="sn-aes-page-template">
				<sn-aes-guidance-sidebar />
				<sn-aes-wizard-manager operations={operations} />
			</div>
		);
	},

	initialState: {
		workingAppSysId: null,
		workingOperations: null,
	},

	properties: {
		// Default routeState is defined in the `initialState` property on this route's definition
		routeState: { default: {} },
		sidebarAllSteps: {
			default: PROVIDER_PROPERTIES[SIDEBAR_ALL_STEPS_PROPERTY],
		},
	},

	actionHandlers,

	styles,
});
