import { actionTypes, createCustomElement } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;
import { t } from 'sn-translate';
import get from 'lodash/get';
import map from 'lodash/map';
import intersection from 'lodash/intersection';

import '@servicenow/now-icon';
import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import '@servicenow/now-loader';

import '../../components/sn-aes-text-card';
import '../sn-aes-wizard-manager';
import dispatchActionViaPayload from '../../helpers/dispatch-action-via-payload';

import { TEXT_CARD__CLICKED } from '../sn-aes-text-card/constants';
import { PROVIDER_PROPERTIES } from '../sn-aes-provider';

import {
	CATEGORIZED_TEMPLATES_REQUEST_COMPLETED_PROPERTY,
	OBJECT_TEMPLATES,
	TEMPLATE_DATA_BEHAVIOR__INITIAL_FETCH,
	CATEGORIZED_TEMPLATES_DATA_PROPERTY,
} from '../../behaviors/template-data';
import {
	DEPENDENCIES_DATA_BEHAVIOR__GET_TEMPLATE_DEPENDENCIES,
	DEPENDENCIES_DATA_PROPERTY,
	DEPENDENCY_STATUS_RESULT_TYPES_MAP,
} from '../../behaviors/dependencies-data';
import { getDependencyCheckDetails } from '../../behaviors/dependencies-data/helper';
import { ROOT__SHOW_MODAL } from '../sn-app-engine-studio/constants';
import { AUTOMATION_CATEGORY_SYS_ID } from '../sn-aes-app-tab/constants';
import { OBJECT_DETAILS_MODAL__CONFIRM_CLICKED } from '../sn-aes-object-template-details-modal';
import {
	ADD_AUTOMATION__SHOW_DETAILS,
	ADD_AUTOMATION__CONTINUE,
	AES_APP_ADD_BUILD_FROM_SCRATCH_SYS_ID,
	AES_AUTOMATION_HELP,
} from './constants';
import { RESOURCE_DATA_PROPERTY } from '../../behaviors/resource-data';

import styles from './sn-aes-app-add-automation.scss';

export const getSpokesIcons = (template, dependencies = {}) =>
	map(template.dependencies || {}, (checkTypes, scopeName) => {
		const dependency = dependencies[scopeName];
		const details = getDependencyCheckDetails(
			dependency,
			intersection(DEPENDENCY_STATUS_RESULT_TYPES_MAP.SPOKE_INSTALLED, checkTypes)
		);

		if (details) {
			return {
				...details,
				illustration: dependency.logo,
				greyOut: !details.hasPassedChecks,
				name: details.hasPassedChecks ? details.name : details.message,
			};
		}
		return null;
	});

function view(state) {
	const {
		properties: {
			filteredCards,
			dependencies,
			categorizedTemplatesRequestCompleted,
			inAppHome,
			automationHelpResourceUrl,
		},
	} = state;
	if (!categorizedTemplatesRequestCompleted) {
		// Categorized templates is not ready, so wait for it
		return <now-loader />;
	}

	return (
		<div className="sn-aes-app-add-automation">
			<div className="text-wrapper">
				<div>
					<h1 className="title">{t('ADD AUTOMATION')}</h1>
				</div>
				<div className="action">
					{t('How do you want to add an automated workflow to your app?')}
				</div>
				<div className="action-info">
					<span>
						{t(
							'Select and customize one of these automation templates to automate your workflows. Or, build a new automated workflow from scratch.'
						)}
					</span>
					{automationHelpResourceUrl ? (
						<span>
							{' '}
							<now-text-link
								href={automationHelpResourceUrl}
								label={t('Learn more about automated workflows.')}
								underlined
								variant="primary"
								opensWindow={true}
							/>
						</span>
					) : null}
				</div>
			</div>
			<sn-aes-text-card
				id="empty-card"
				type="flow"
				empty
				emptyText={t('Build from scratch')}
				append-to-payload={{
					actionName: ADD_AUTOMATION__CONTINUE,
					templateSysId: AES_APP_ADD_BUILD_FROM_SCRATCH_SYS_ID,
					inAppHome,
				}}
			/>
			{filteredCards.map(ot => (
				<sn-aes-text-card
					id={ot.sys_id}
					cardTitle={get(ot, 'name.display_value', '')}
					iconList={getSpokesIcons(ot, dependencies)}
					append-to-payload={{
						actionName: ADD_AUTOMATION__SHOW_DETAILS,
						template: ot,
					}}
				/>
			))}
		</div>
	);
}

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch, properties }) => {
		dispatch(DEPENDENCIES_DATA_BEHAVIOR__GET_TEMPLATE_DEPENDENCIES, {
			templates: properties.filteredCards,
		});
		dispatch(TEMPLATE_DATA_BEHAVIOR__INITIAL_FETCH);
	},
	[COMPONENT_PROPERTY_CHANGED]: ({
		properties: { filteredCards },
		action: {
			payload: { name },
		},
		dispatch,
	}) => {
		if (name === 'categorizedTemplates') {
			dispatch(DEPENDENCIES_DATA_BEHAVIOR__GET_TEMPLATE_DEPENDENCIES, {
				templates: filteredCards,
			});
		}
	},

	[TEXT_CARD__CLICKED]: dispatchActionViaPayload,

	[ADD_AUTOMATION__SHOW_DETAILS]: ({
		action: {
			payload: { template },
		},
		dispatch,
	}) => {
		dispatch(ROOT__SHOW_MODAL, {
			renderer: () => (
				<sn-aes-object-template-details-modal
					template={template}
					onConfirm={() => dispatch(OBJECT_DETAILS_MODAL__CONFIRM_CLICKED, { template })}
				/>
			),
		});
	},

	[OBJECT_DETAILS_MODAL__CONFIRM_CLICKED]: ({
		// FIXME: Remove once hoisting bug is fixed
		properties: { inAppHome = false },
		action: {
			stopPropagation,
			payload: { template },
		},
		dispatch,
	}) => {
		stopPropagation();
		dispatch(ADD_AUTOMATION__CONTINUE, { templateSysId: template.sys_id, inAppHome });
	},
};

/**
 * App Experience Objects page
 *
 * ```jsx
 * <sn-aes-app-add-automation />
 * ```
 *
 * @seismicElement sn-aes-app-add-automation
 * @summary This page displays all automation templates which could be added to an app
 */
createCustomElement('sn-aes-app-add-automation', {
	view,
	initialState: {},
	properties: {
		appSysId: { default: null },
		categorizedTemplatesRequestCompleted: {
			default: PROVIDER_PROPERTIES[CATEGORIZED_TEMPLATES_REQUEST_COMPLETED_PROPERTY],
		},
		categorizedTemplates: {
			default: PROVIDER_PROPERTIES[CATEGORIZED_TEMPLATES_DATA_PROPERTY],
		},
		dependencies: {
			default: PROVIDER_PROPERTIES[DEPENDENCIES_DATA_PROPERTY],
		},
		filteredCards: {
			computed({ properties: { categorizedTemplates } }) {
				return categorizedTemplates[OBJECT_TEMPLATES].filter(ot => {
					return ot.categories.some(cat => {
						return cat.taxonomyCategorySysId === AUTOMATION_CATEGORY_SYS_ID;
					});
				});
			},
		},
		// FIXME: Delete once hoisting bug is fixed
		inAppHome: {
			default: false,
		},
		resourceData: {
			default: PROVIDER_PROPERTIES[RESOURCE_DATA_PROPERTY],
		},
		automationHelpResourceUrl: {
			computed({ properties: { resourceData } }) {
				const automationHelpResource = get(resourceData, 'content', []).find(entry => {
					return entry.sys_id === AES_AUTOMATION_HELP;
				});

				return get(automationHelpResource, 'content_url');
			},
		},
	},
	actionHandlers,
	styles,
});
