import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	BEHAVIOR_NAME,
	APPLICATIONS_DATA_PROPERTY,
	APPLICATIONS_INITIAL_FETCH_PROPERTY,
	APPLICATIONS_REQUEST_COMPLETED_PROPERTY,
	FETCH_APPLICATIONS_URL,
	APPLICATIONS_DATA_BEHAVIOR__INITIAL_FETCH,
	APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS,
	APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_STARTED,
	APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_SUCCEEDED,
	APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_FAILED,
} from './constants';

const applicationsDataBehavior = {
	name: BEHAVIOR_NAME,
	initialState: {
		initialFetched: false,
	},
	properties: {
		[APPLICATIONS_DATA_PROPERTY]: {
			default: [],
			selectable: true,
		},
		[APPLICATIONS_INITIAL_FETCH_PROPERTY]: {
			default: false,
			selectable: true,
		},
		[APPLICATIONS_REQUEST_COMPLETED_PROPERTY]: {
			default: false,
			selectable: true,
		},
	},
	actionHandlers: {
		[APPLICATIONS_DATA_BEHAVIOR__INITIAL_FETCH]: ({ properties, dispatch, action }) => {
			if (properties[APPLICATIONS_INITIAL_FETCH_PROPERTY]) {
				return;
			}
			const { sort_field, sort_order } = action.payload;
			dispatch(APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS, {
				sort_order: sort_order,
				sort_field: sort_field,
			});
		},
		[APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS]: createHttpEffect(FETCH_APPLICATIONS_URL, {
			method: 'GET',
			startActionType: APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_STARTED,
			successActionType: APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_SUCCEEDED,
			errorActionType: APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_FAILED,
			queryParams: ['sort_order', 'sort_field'],
			batch: false,
		}),
		[APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_SUCCEEDED]: ({
			action,
			updateProperties,
		}) => {
			const { result: taxonomies } = action.payload;
			updateProperties({
				[APPLICATIONS_DATA_PROPERTY]: taxonomies,
				[APPLICATIONS_INITIAL_FETCH_PROPERTY]: true,
				[APPLICATIONS_REQUEST_COMPLETED_PROPERTY]: true,
			});
		},
		[APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_STARTED]: ({ updateProperties }) => {
			updateProperties({
				[APPLICATIONS_REQUEST_COMPLETED_PROPERTY]: false,
			});
		},
		[APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_FAILED]: ({ action, updateProperties }) => {
			updateProperties({
				[APPLICATIONS_REQUEST_COMPLETED_PROPERTY]: true,
			});
			console.error(action.payload);
		},
	},
};

export default applicationsDataBehavior;
