import { createObjectSearchIndex, getFilterFnForInputLength } from '../../helpers/object-search';
import { SEARCH_INPUT_MINIMUM_LENGTH } from '../../helpers/constants';
import { SEARCH_ID_FIELD, SEARCH_INDEX_FIELDS } from './constants';
import isEmpty from 'lodash/isEmpty';

export const filterContent = getFilterFnForInputLength(SEARCH_INPUT_MINIMUM_LENGTH);

export const getContentIndex = content => {
	return createObjectSearchIndex(SEARCH_ID_FIELD, SEARCH_INDEX_FIELDS, content);
};

export function topicsIncludesTopic(topics, topic) {
	if (isEmpty(topics)) {
		return false;
	}
	return topics.includes(topic.sys_id);
}

export function topicContainsContent(topic, content) {
	return content.some(({ topics }) => topicsIncludesTopic(topics, topic));
}
