export const addVisibilityListener = callback => {
	// Set the name of the hidden property and the change event for visibility
	var hidden, visibilityChange;
	if (typeof document.hidden !== 'undefined') {
		// Opera 12.10 and Firefox 18 and later support
		hidden = 'hidden';
		visibilityChange = 'visibilitychange';
	} else if (typeof document.msHidden !== 'undefined') {
		hidden = 'msHidden';
		visibilityChange = 'msvisibilitychange';
	} else if (typeof document.webkitHidden !== 'undefined') {
		hidden = 'webkitHidden';
		visibilityChange = 'webkitvisibilitychange';
	}

	if (typeof document.addEventListener === 'undefined' || hidden === undefined) {
		console.warn(
			'Page Visibility API is not supported on this browser. Some features of the application may not work as expected'
		);
	} else {
		// Handle page visibility change
		document.addEventListener(
			visibilityChange,
			handleVisibilityChange(hidden, callback),
			false
		);
	}
};

export const handleVisibilityChange = (hidden, callback) => () => callback(document[hidden]);
