import { BUILDER_NEW_TAB } from './constants';
import { generateLegacyFormUrl } from '../../helpers/generateBuilderUrl';

// Part of the globalDmf: https://code.devsnc.com/dev/dev-studio/blob/master/dev-studio/src/main/plugins/com.glide.dev-studio/ui.html/scripts/dev-studio/editing/dmf/factory.globalDmf.js#L14
export default ({ contentWindow }, dispatch, applicationId) => {
	// Gotta keep function declarations up top cause hoisting doesn't seem to work here
	const openStudioTab = (url, name) => {
		const newTab = {
			title: name,
			url: generateLegacyFormUrl(url, { applicationId }),
		};
		dispatch(BUILDER_NEW_TAB, newTab);
	};

	if (contentWindow) {
		contentWindow.isDevStudio = true;
		contentWindow.openStudioTab = openStudioTab;
		// contentWindow.openRL = openRelatedLink; // Currently not needed for the `sys_db_object` case but any related lists links will be broken here

		// devStudioAPI
		contentWindow.devStudioAPI = {
			openStudioTab,
		};
	}
};
