const token = {};
export default token;

// Updates the auth token header and global.
// Currenlty should only be set on initialization and on re-login (see <gac-login-popup>).
export const resetAuthToken = new_g_ck => {
	window.g_ck = token['X-UserToken'] = new_g_ck;
};

// Initially set the token to window.g_ck (see $sn_guided_application_creator.xml).
resetAuthToken(window.g_ck);
