/**
 * Normalizes an array by its keyIdentifier which defaults to sys_id
 *
 * @param {Object[]} array
 * @param {string} keyIdentifier - key that uniquely identifies items in the Object[] array
 * @returns {Object} - Object containing key/object pairs:
 * 	- key will be the unique value of the keyIdentifier field from each object in the Object[] array;
 * 	- value will be the associated object from the Object[] array for the key
 *
 * @example
 *	const array = [
 * 		{
 * 			"sys_id": "abcde",
 * 			"key2": "some value here",
 * 		},
 * 		{
 * 			"sys_id": "fghij",
 * 			"key2": "some value here",
 * 		}
 *	];
 *
 *	const keyIdentifier = "sys_id";
 *
 *	normalizeById(array, keyIdentifier);
 *
 *  // expected output:
 *  //	{
 *  //    "abcde": {
 *  //        "sys_id": "abcde",
 *  //        "key2": "some value here"
 *  //    },
 *  //    "fghij": {
 *  //        "sys_id": "fghij",
 *  //        "key2": "some value here"
 *  //    }
 *  // }
 */
export function normalizeById(array, keyIdentifier = 'sys_id') {
	return array.reduce((acc, item) => {
		acc[item[keyIdentifier]] = item;
		return acc;
	}, {});
}

/**
 *
 * @param {Object[]} array
 * @param {string} groupIdentifier - key in which to group the items in array by (for example,
 * 		some files need to go into "bucket_type" = "Bucket 1", others into "bucket_type" = "Bucket 2")
 * @param {string} valueIdentifier - key in which the items in array should be uniquely identified by
 * @return {Object} - Object containing key/array pairs:
 * - key will the value of the field in which you group by
 * - value will be an array of values that uniquely identify items in the array
 *
 * @example
 *	const array = [
 * 		{
 * 			"key1": "aUniqueId1",
 * 			"key2": "value2",
 * 			"key3": "groupItem1"
 * 		},
 * 		{
 * 			"key1": "aUniqueId2",
 * 			"key2": "value2",
 * 			"key3": "groupItem1"
 * 		},
 * 		{
 * 			"key1": "aUniqueId3",
 * 			"key2": "value2",
 * 			"key3": "groupItem2"
 * 		},
 *	];
 *
 * 	const groupIdentifier = "key3";
 *
 * 	const valueIdentifier = "key1";
 *
 * 	groupBy(array, groupIdentifier, valueIdentifier);
 *  // expected output:
 *  //	{
 * 	//		"groupItem1":["aUniqueId1","aUniqueId2"],
 * 	// 		"groupItem2":["aUniqueId3"]
 *  // 	}
 */
export function groupBy(array, groupIdentifier, valueIdentifier = 'sys_id') {
	// TODO: Use reduce or something fancy

	if (!groupIdentifier || groupIdentifier === '') {
		throw new Error('groupIdentifier is required');
	}

	const grouped = {};
	array.forEach(item => {
		const key = item[groupIdentifier];
		const value = item[valueIdentifier];
		if (!grouped[key]) {
			grouped[key] = [value];
		} else {
			grouped[key] = grouped[key].concat(value);
		}
	});

	return grouped;
}
