/**
 * Add an actionName property the append-to-payload prop
 * of your component.
 *
 * An action with actionName will be dispatched and passed the payload
 *
 * @param {object} coeffects
 * @param {boolean} stopPropagation
 */
const dispatchActionViaPayload = ({ action, dispatch }, stopPropagation = true) => {
	if (stopPropagation) {
		action.stopPropagation();
	}

	dispatch(action.payload.actionName, action.payload);
};

export default dispatchActionViaPayload;
