import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;
import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	BEHAVIOR_NAME,
	FETCH_RESOURCES_ENDPOINT,
	RESOURCE_DATA_PROPERTY,
	RESOURCE_DATA_BEHAVIOR__FETCH,
	RESOURCE_DATA_BEHAVIOR__FETCH_SUCCEEDED,
	RESOURCE_DATA_BEHAVIOR__FETCH_FAILED,
} from './constants';

const resourceDataBehavior = {
	name: BEHAVIOR_NAME,
	properties: {
		[RESOURCE_DATA_PROPERTY]: {
			default: {
				topics: [],
				content: [],
			},
			selectable: true,
		},
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch(RESOURCE_DATA_BEHAVIOR__FETCH);
		},
		[RESOURCE_DATA_BEHAVIOR__FETCH]: createHttpEffect(FETCH_RESOURCES_ENDPOINT, {
			successActionType: RESOURCE_DATA_BEHAVIOR__FETCH_SUCCEEDED,
			errorActionType: RESOURCE_DATA_BEHAVIOR__FETCH_FAILED,
		}),
		[RESOURCE_DATA_BEHAVIOR__FETCH_SUCCEEDED]: ({
			updateProperties,
			action: {
				payload: { result },
			},
		}) => {
			updateProperties({
				[RESOURCE_DATA_PROPERTY]: result,
			});
		},
		[RESOURCE_DATA_BEHAVIOR__FETCH_FAILED]: () => {},
	},
};

export default resourceDataBehavior;
