import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-icon';
import { TABS__SELECT_TAB, TABS__CLOSE_TAB } from '../sn-aes-tabs/constants';

import styles from './sn-aes-tab.scss';

const CsTab = (state, { dispatch, updateState }) => {
	const {
		tabId,
		title,
		isSelected,
		isUnsaved,
		isLoading,
		isUpdated,
		canClose,
		subtitle,
	} = state.properties;

	const { isHovered } = state;

	const tabClasses = {
		tab: true,
		'is-selected': isSelected,
		'is-updated': isUpdated,
		'is-non-closable-tab': !canClose,
	};

	return (
		<button
			title={title + ' ' + subtitle}
			tabindex="-1"
			class={tabClasses}
			on-click={e => {
				if (!e.defaultPrevented) {
					dispatch(TABS__SELECT_TAB, { tabId });
				}
			}}
			onmouseenter={() => updateState({ isHovered: true })}
			onmouseleave={() => updateState({ isHovered: false })}>
			<div className="status-column">
				<div className="status-wrapper">
					{isUnsaved ? (
						<now-icon className="is-unsaved" icon="status-fill" size="sm" />
					) : null}
				</div>
			</div>
			<div className="tab-text">
				{subtitle ? <div className="subtitle">{subtitle}</div> : null}
				{title ? <div className="title">{title}</div> : null}
			</div>
			{(!isLoading || isHovered) && canClose ? (
				<now-icon
					icon="close-fill"
					className="close-icon"
					size="sm"
					on-click={e => {
						e.preventDefault();
						dispatch(TABS__CLOSE_TAB, { tabId });
					}}
				/>
			) : null}

			{isLoading && !isHovered ? (
				<now-icon className="loading-icon" icon="loader-fill" spin={true} size="sm" />
			) : null}
		</button>
	);
};

createCustomElement('sn-aes-tab', {
	view: CsTab,
	properties: {
		tabId: { default: null },
		title: { default: null },
		subtitle: { default: null },
		isSelected: { default: false, reflect: true },
		isUnsaved: { default: false },
		isUpdated: { default: false },
		isLoading: { default: false },
		canClose: { default: 'true', reflect: true },
	},
	styles,
});
