import get from 'lodash/get';
import { t } from 'sn-translate';
import {
	EXPERIENCE_UX_APP_SYS_ID,
	ROLE_BUILDER__CLOSE,
	ROLE_BUILDER__SAVE,
	ROLE_BUILDER__TOGGLE_ACL,
	ROLE_BUILDER__TOGGLE_ALL_ACLS,
	ROLE_BUILDER__TOGGLE_FILE_ACLS,
	ROLE_BUILDER_ADD_ACTION,
	ROLE_BUILDER_DATA_OPERATION_LIST,
	ROLE_BUILDER_REMOVE_ACTION,
	DATA_CATEGORY_SYS_ID,
	EXPERIENCE_CATEGORY_SYS_ID,
} from './constants';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { checkboxState, getRoleName } from './ui-helpers';
import { generateStyleToolTipContent } from '../main/plugins/com.snc.app-engine-studio/ui.html/scripts/helpers/sn-naming-helpers';

function ExperiencePanel({ panelTitle, experienceFiles }) {
	return (
		<div className="experience-panel">
			<div className="details-title">{panelTitle}</div>
			{experienceFiles.length ? (
				<ul className={`object-list object-list--access`}>
					<li className="object-list-item -header">
						<div className="item-cell-title -name">
							<now-checkbox
								id="all-experience"
								key="all-experience"
								checked={checkboxState(experienceFiles, 2)}
								manage-checked
								config-aria={{
									'aria-label': t('Grant all ACLs to Experiences'),
								}}
								append-to-payload={{
									actionName: ROLE_BUILDER__TOGGLE_ALL_ACLS,
									roleBuilderAction: checkboxState(experienceFiles, 2)
										? ROLE_BUILDER_REMOVE_ACTION
										: ROLE_BUILDER_ADD_ACTION,
									files: experienceFiles,
									taxonomy_category: EXPERIENCE_CATEGORY_SYS_ID,
								}}
							/>
							{t('Select All')}
						</div>
					</li>

					{experienceFiles.map(experienceFile => {
						return (
							<li key={experienceFile.id} className="object-list-item">
								<div className="item-cell -name">
									<now-checkbox
										id={`file-${experienceFile.sys_id}`}
										key={`file-${experienceFile.sys_id}`}
										checked={checkboxState([experienceFile], 2)}
										manage-checked
										config-aria={{
											'aria-label': t(
												'Grant access to {0}',
												experienceFile.name.display_value
											),
										}}
										append-to-payload={{
											actionName: ROLE_BUILDER__TOGGLE_FILE_ACLS,
											roleBuilderAction: checkboxState(
												[experienceFile],
												2,
												true
											)
												? ROLE_BUILDER_REMOVE_ACTION
												: ROLE_BUILDER_ADD_ACTION,
											fileId: experienceFile.sys_id,
											taxonomy_category: EXPERIENCE_CATEGORY_SYS_ID,
										}}
									/>
									{experienceFile.name.display_value}
								</div>
							</li>
						);
					})}
				</ul>
			) : (
				<div className="empty-panel">
					{t(
						'You cannot configure these permissions until you add an experience to this app.'
					)}
				</div>
			)}
		</div>
	);
}

function DataPanel({ panelTitle, dataFiles }) {
	return (
		<div className="data-panel">
			<div className="details-title">{panelTitle}</div>
			{dataFiles.length ? (
				<ul className={`object-list object-list--crud`}>
					<li className="object-list-item -header">
						<div className="item-cell-title -name">
							<now-checkbox
								id="all-data"
								key="all-data"
								checked={checkboxState(dataFiles, 4)}
								manage-checked
								config-aria={{
									'aria-label': t('Grant all ACLs to Data'),
								}}
								append-to-payload={{
									actionName: ROLE_BUILDER__TOGGLE_ALL_ACLS,
									roleBuilderAction: checkboxState(dataFiles, 4)
										? ROLE_BUILDER_REMOVE_ACTION
										: ROLE_BUILDER_ADD_ACTION,
									files: dataFiles,
									taxonomy_category: DATA_CATEGORY_SYS_ID,
								}}
							/>
							{t('Select All')}
						</div>

						<div className="item-cell-title -create">{t('Create')}</div>
						<div className="item-cell-title -read">{t('Read')}</div>
						<div className="item-cell-title -write">{t('Write')}</div>
						<div className="item-cell-title -delete">{t('Delete')}</div>
					</li>

					{dataFiles.map(dataFile => {
						function renderCheckbox(operation) {
							const acl = dataFile.acls[operation];
							const [aclOperation] = operation.split('#');
							return (
								<now-checkbox
									id={`acl-${dataFile.sys_id}-${aclOperation}`}
									key={`acl-${dataFile.sys_id}-${aclOperation}`}
									checked={!!acl}
									manage-checked
									config-aria={{
										'aria-label': acl
											? t(
													`Allow {0} for ${dataFile.name.display_value}`,
													operation
											  )
											: t(
													`Remove {0} for ${dataFile.name.display_value}`,
													operation
											  ),
									}}
									append-to-payload={{
										actionName: ROLE_BUILDER__TOGGLE_ACL,
										roleBuilderAction: acl
											? ROLE_BUILDER_REMOVE_ACTION
											: ROLE_BUILDER_ADD_ACTION,
										fileId: dataFile.sys_id,
										operation,
									}}
								/>
							);
						}

						return (
							<li key={dataFile.sys_id} className="object-list-item">
								<div className="item-cell -name">
									<now-checkbox
										id={`file-${dataFile.sys_id}`}
										key={`file-${dataFile.sys_id}`}
										checked={checkboxState([dataFile], 4)}
										manage-checked
										config-aria={{
											'aria-label': t(
												'Select all ACLs for {0}',
												dataFile.name.display_value
											),
										}}
										append-to-payload={{
											actionName: ROLE_BUILDER__TOGGLE_FILE_ACLS,
											roleBuilderAction: checkboxState([dataFile], 4, true)
												? ROLE_BUILDER_REMOVE_ACTION
												: ROLE_BUILDER_ADD_ACTION,
											fileId: dataFile.sys_id,
											taxonomy_category: DATA_CATEGORY_SYS_ID,
										}}
									/>
									{dataFile.name.display_value}
								</div>
								{ROLE_BUILDER_DATA_OPERATION_LIST.map(operation => (
									<div
										className={{
											'item-cell': true,
											[operation]: true,
										}}>
										{renderCheckbox(operation)}
									</div>
								))}
							</li>
						);
					})}
				</ul>
			) : (
				<div className="empty-panel">
					{t('You cannot configure these permissions until you add a data to this app.')}
				</div>
			)}
		</div>
	);
}

export const view = ({ updatedRole, isDirty }) => {
	if (!updatedRole) {
		// Still loading
		return null;
	}

	return (
		<div className="role-builder">
			<sn-aes-builder-header
				icon={updatedRole.icon}
				builderTitle={getRoleName(get(updatedRole, 'name.display_value', ''))}
				buttonList={[
					{
						id: 'close',
						buttonLabel: t('Close'),
						variant: 'secondary',
						actionName: ROLE_BUILDER__CLOSE,
					},
					{
						id: 'save',
						buttonLabel: t('Save'),
						variant: 'primary',
						actionName: ROLE_BUILDER__SAVE,
						disabled: !isDirty,
					},
				]}
			/>
			<div className="details-wrapper">
				<div className="role-details">
					<div className="general-title">{t('General')}</div>
					<now-input
						name="name"
						value={get(updatedRole, 'name.display_value', '')}
						placeholder={t('Name this role')}
						label={t('Name')}
						helperContent={generateStyleToolTipContent(t('Role name'))}
						size="lg"
						autocomplete="off"
						disabled="true"
					/>
					<now-input
						name="description"
						value={get(updatedRole, 'description.display_value')}
						placeholder={t('Describe this role')}
						label={t('Description')}
						helperContent={generateStyleToolTipContent(t('Role description'))}
						size="lg"
						autocomplete="off"
						disabled="true"
					/>
					<now-input
						name="scope"
						value={get(updatedRole, 'scope.display_value')}
						placeholder={t('Scope for the role')}
						label={t('Scope')}
						helperContent={generateStyleToolTipContent(t('Role scope'))}
						size="lg"
						autocomplete="off"
						disabled="true"
					/>
				</div>
				<div className="acl-panels">
					<Fragment>
						<ExperiencePanel
							panelTitle="Experience"
							experienceFiles={updatedRole.files.filter(
								// Get only ux app files for now which have experience category
								file =>
									file.taxonomy_category === EXPERIENCE_CATEGORY_SYS_ID &&
									file.taxonomy_definition === EXPERIENCE_UX_APP_SYS_ID
							)}
						/>
						<DataPanel
							panelTitle="Data"
							dataFiles={updatedRole.files.filter(
								// Get all file which have data category
								file => file.taxonomy_category === DATA_CATEGORY_SYS_ID
							)}
						/>
					</Fragment>
				</div>
			</div>
		</div>
	);
};
