import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import focusBehavior from '@servicenow/behavior-focus';
import '@servicenow/now-button';
import { MODAL_OVERLAY__OPEN_SET } from './constants';
import quarterCircleImg from './assets/quarter-circle.svg';
import styles from './sn-aes-modal-overlay.scss';
import { t } from 'sn-translate';

// A `role="main"` needs to be conditionally added to this overlay since it will cover the contents of the
// page beneath. However, add-object and add-automation always use the main overlay even on the app page.
const isAppPage = () => window.location.pathname.includes('my-apps/');

createCustomElement('sn-aes-modal-overlay', {
	view: ({ properties: { opened, manageClose, align, decorated, blank } }) => {
		if (!opened) {
			return null;
		}

		if (blank) {
			return (
				<div className="sn-aes-modal-overlay blank-overlay fade-in">
					<section>
						<slot className="content-slot" />
					</section>
				</div>
			);
		}

		return (
			<div className="sn-aes-modal-overlay fade-in" {...!isAppPage() && { role: 'main' }}>
				{!manageClose ? (
					<now-button-iconic
						bare
						className="close-button"
						icon="close-fill"
						size="lg"
						append-to-payload={{ source: 'close' }}
						configAria={{ 'aria-label': t('Click to close') }}
					/>
				) : null}
				{decorated ? (
					<img
						className="quarter-circle"
						src={quarterCircleImg}
						aria-hidden="true"
						alt=""
					/>
				) : null}
				<section
					class={{
						'body-content-wrapper': true,
						'now-align': true,
						'-inline-center': true,
						'-block-center': align === 'center',
						'-block-start': align === 'top',
						'-block-end': align === 'bottom',
					}}>
					<slot className="content-slot" />
				</section>
			</div>
		);
	},
	properties: {
		opened: {
			default: false,
		},
		align: {
			default: 'top',
		},
		manageClose: {
			default: false,
		},
		decorated: {
			default: true,
		},
		blank: {
			default: false,
		},
	},
	behaviors: [focusBehavior],
	actionHandlers: {
		[actionTypes.COMPONENT_TREE_RENDERED]: ({
			properties: { opened },
			action: {
				payload: { host },
			},
		}) => {
			if (opened) {
				host.focus();
			}
		},
		['NOW_BUTTON_ICONIC#CLICKED']: ({
			action,
			action: {
				payload: { source },
			},
			dispatch,
		}) => {
			action.stopPropagation();
			if (source === 'close') {
				// FIXME (devin): This needs to be renamed
				dispatch(MODAL_OVERLAY__OPEN_SET, { opened: false });
			}
		},
	},
	styles,
});
