import uuid from 'uuid';

import getRelativeUrl from './get-relative-url';
import { LEGACY_BUILDER, GENERIC_BUILDER, FORM_DESIGNER_BUILDER } from './constants';

// TODO (eguo): We need to figure out a long term way to determine what
// builder handler should be used here. Right now, assuming anything with
// `.do` in the URL is rendered by a `ui.jforms` file and hence legacy
// forms/lists.
// Used in sn-aes-builder/index.js and in sn-aes-app-tab.js
// TODO (snuti): Hardcoding flow-designer as temporary fix before
// a better approach to checking legacy url is implemented
// (nhariprasad): The exception to the `.do` pattern mentioned by (eguo) is flow designer and form designer
// FIXME (nhariprasad): To handle more builder types add a new column to taxonomy with a choice field
// in order to allow select a bulder type.

export const getBuilderType = url => {
	if ((url || '').includes('/$ng_fd.do')) {
		return FORM_DESIGNER_BUILDER;
	} else if (!(url || '').includes('flow-designer') && /\.do\b/.test(url)) {
		return LEGACY_BUILDER;
	} else {
		return GENERIC_BUILDER;
	}
};

/**
 * Function for replacing client tokens, i.e. `{client:StackName}` or `{client:ScopeId}`, that can
 * be present in the `creator_url` or `editor_url` of a taxonomy object.
 */
const replaceClientTokens = (url, stackName, { applicationId, scopeName, name }) =>
	url
		.replace(/{client:ScopeId}/g, applicationId)
		.replace(/{client:ScopeName}/g, scopeName)
		.replace(/{client:StackName}/g, stackName)
		.replace(/{client:Name}/g, name);

const replaceReferringUrlToken = (url, referringUrl) =>
	url.replace(/{client:ReferringUrl}/g, referringUrl);
/**
 * Function for setting the `searchParams` that are always necessary for all builder URLs.
 *
 * The one exception being `sysparm_referring_url` as it will already be present and valid in
 * the `creator_url` case. When creating new records, the `sysparm_referring_url` will contain
 * `sys_id%3D%24sys_id_ui11`. Whereas for the editing case, will will be an actual sysId.
 */
const setSearchParams = (url, stackName, applicationId, referringUrl) => {
	const parserUrl = new URL(url, window.location.origin);
	parserUrl.searchParams.set('sysparm_nameofstack', stackName);
	parserUrl.searchParams.set('sysparm_transaction_scope', applicationId);
	if (!parserUrl.searchParams.get('sysparm_referring_url')) {
		parserUrl.searchParams.set('sysparm_referring_url', referringUrl);
	}
	return getRelativeUrl(parserUrl);
};

/**
 * Function for generating a valid `sysparm_referring_url`.A `sysparm_referring_url` works
 * with a `sysparm_nameofstack` to direct where a user goes after the completion of a form
 * action.
 *
 * Currently, for all CS use cases, it's always going to look very similar to the base
 * url:
 * 1. The `sysparm_referring_url` cannot contain a `sysparm_referring_url` itself
 * 2. The `sysparm_nameofstack` needs to be be shared between the base url and `sysparm_referring_url`
 * 3. The `sysparm_referring_url` needs to be URI encoded without the leading `/`
 */
const generateReferringUrl = (url, stackName) => {
	const parserUrl = new URL(url, window.location.origin);
	parserUrl.searchParams.set('sysparm_nameofstack', stackName);
	parserUrl.searchParams.delete('sysparm_referring_url');
	return encodeURIComponent(getRelativeUrl(parserUrl).slice(1));
};

/**
 * Will generate a valid URL for both create and edit cases, should also handle other arbitrary
 * formats used by other builders.
 */
const generateBuilderUrl = (url, config) => {
	if (!config) {
		throw new Error('a config needs to be passed in as a second argument');
	}
	const { applicationId, scopeName = '', name = '' } = config;
	if (!applicationId || typeof applicationId !== 'string') {
		throw new TypeError('applicationId is required and has to be a string');
	}
	const stackName = `${uuid.v4()}_bottomless`;
	let generatedUrl = replaceClientTokens(url, stackName, {
		applicationId,
		scopeName,
		name,
	});
	const referringUrl = generateReferringUrl(generatedUrl, stackName);
	generatedUrl = replaceReferringUrlToken(generatedUrl, referringUrl);
	generatedUrl = setSearchParams(generatedUrl, stackName, applicationId, referringUrl);
	return generatedUrl;
};

export default generateBuilderUrl;

/**
 * There's seemingly a special case for legacy forms, since `dev-studio` adds `sysparm_nofocus=true`
 * to all URLs that open a classic form. I didn't do much digging but it's related to element/form
 * focus, assuming it's for a11y. Not sure what the impact is if we leave it on for all URLs.
 *
 * For original dev studio code, please see:
 * https://code.devsnc.com/dev/dev-studio/blob/5a07a79ed18f56a6e26bccbf924d01ad45fcc539/dev-studio/src/main/plugins/com.glide.dev-studio/ui.html/scripts/dev-studio/editing/tabs/factory.tabUrlBuilder.js#L35
 */

export const generateLegacyFormUrl = (url, config) => {
	const baseUrl = generateBuilderUrl(url, config);
	return baseUrl.concat('&sysparm_nofocus=true');
};
