import { groupBy } from '../../helpers/normalize';
import {
	SIDEBAR_STEP_TYPE_CATEGORY,
	SIDEBAR_STEP_TYPE_COMPLETION,
} from '../../behaviors/sidebar-data';
import get from 'lodash/get';
import {
	ROUTER_BEHAVIOR__SET_DELEGATE,
	ROUTER_BEHAVIOR__UNSET_DELEGATE,
} from '../../behaviors/router/constants';
import { ROOT__SHOW_MODAL } from '../../components/sn-app-engine-studio/constants';

export const getSidebarStartStep = (app, sidebarAllSteps) => {
	const filesByCategoryId = groupBy(app.files, 'taxonomy_category', 'sys_id');

	for (let step of sidebarAllSteps) {
		if (step.type !== SIDEBAR_STEP_TYPE_CATEGORY) {
			continue;
		}

		const category = (app.taxonomy_categories || []).find(c => c.sys_id === step.id);

		if (!get(filesByCategoryId, `[${category.sys_id}].length`)) {
			return step;
		}
	}
	return sidebarAllSteps.find(step => step.type === SIDEBAR_STEP_TYPE_COMPLETION);
};

export const getSidebarStepFromCategory = (category, sidebarAllSteps) => {
	return sidebarAllSteps.find(step => step.id === category.sys_id);
};

export const setDelegate = ({ dispatch, delegateId, headerLabel, content }) => {
	dispatch(ROUTER_BEHAVIOR__SET_DELEGATE, {
		id: delegateId,
		delegate: (nextFunction, cancelFunction) => {
			showModal({
				dispatch,
				id: `${delegateId}-confirm`,
				headerLabel,
				content,
				nextFunction,
				cancelFunction,
			});
		},
	});
};

export const unsetDelegate = ({ dispatch, delegateId }) => {
	dispatch(ROUTER_BEHAVIOR__UNSET_DELEGATE, {
		id: delegateId,
	});
};

export const showModal = ({ dispatch, id, headerLabel, content, nextFunction, cancelFunction }) => {
	dispatch(ROOT__SHOW_MODAL, {
		renderer: () => (
			<sn-aes-confirm
				{...{
					id,
					...(headerLabel && { headerLabel }),
					...(content && { content }),
				}}
				nextFunction={nextFunction}
				cancelFunction={cancelFunction}
			/>
		),
	});
};
