export const appMetadataSubscription = `
	subscription {
		GlideRecord_Subscription {
			sys_metadata(queryConditions: "sys_scope={{sysId}}") {
				_subscription {
					subscription_channel
					subscription_operation
					subscription_state
					serialized_subscription
				}
				_results {
					sys_id { value }
					sys_update_name { value }
					sys_class_name { value }
					sys_updated_on { value: displayValue }
				}
			}
		}
	}
`;

