import { ROUTER_BEHAVIOR_UNKNOWN_ROUTE } from '../../behaviors/router';
import { USER_PREF_HAS_VIEWED_WELCOME } from '../../behaviors/user-data/constants';

export function shouldRenderHeader(routeName) {
	if (routeName === ROUTER_BEHAVIOR_UNKNOWN_ROUTE) {
		return false;
	}
	return true;
}

export function getHeaderChromeHeight(headerRef) {
	return headerRef ? headerRef.getBoundingClientRect().height : 0;
}

/**
 * Uses the entered route's name to determine which header option to highlight
 *
 * @example
 * getActivePageName('my-apps') // => 'my-apps'
 * getActivePageName('my-apps.app') // => 'my-apps'
 * getActivePageName(ROUTER_BEHAVIOR_UNKNOWN_ROUTE) // => ''
 * getActivePageName(null) // => ''
 *
 * @param {string} routeName The name of the entered route
 */
export function getActivePageName(routeName) {
	if (typeof routeName !== 'string' || routeName === ROUTER_BEHAVIOR_UNKNOWN_ROUTE) {
		return '';
	}

	return routeName.split('.').shift();
}

/**
 * Pulls the name of the component to render from the route's state.
 * It also takes into consideration special cases:
 *
 * 1. If the route is undefined in the below routes
 * 2. If the route is defined, but has no corresponding component (which is a valid case)
 * 3. If a component is defined, making sure that it's been properly imported and registered
 *
 * @param {string} routeName The name of the entered route
 * @param {object} routeState The state of the entered route
 */
export function getPageForRoute(routeName, routeState) {
	// Show 404 when at a url that doesn't match a designated route
	if (routeName === ROUTER_BEHAVIOR_UNKNOWN_ROUTE) {
		return 'sn-aes-page-404';
	}

	const { PageComponent } = routeState;

	// In case we're in a route with no 'sn-aes-page-*'
	// ex. root
	if (typeof PageComponent !== 'string') {
		return null;
	}

	// Make sure the requested `sn-aes-page-*` component has been imported above
	if (!customElements.get(PageComponent)) {
		throw new Error(`Cannot find a page component for: '${PageComponent}'`);
	}

	return PageComponent;
}

/**
 * Determines if the user has viewed the welcome modal by checking the user preference attribute
 *
 * @param user
 * @returns {boolean}
 */
export function hasUserViewedWelcomeModal(user) {
	if (!user || Object.keys(user).length === 0) {
		return true;
	}

	if (user && user.preferences) {
		const hasViewedWelcomeModalObj = user.preferences.find(
			preference => preference.name === USER_PREF_HAS_VIEWED_WELCOME
		);
		// user preferences are stored as strings
		if (hasViewedWelcomeModalObj && hasViewedWelcomeModalObj.value === 'true') {
			return true;
		}
	}

	return false;
}
