import { actionTypes, createCustomElement } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;
import { createHttpEffect } from '@servicenow/ui-effect-http';
import '@servicenow/now-checkbox';
import '@servicenow/now-input';
import cloneDeep from 'lodash/cloneDeep';
import get from 'lodash/get';
import isEqual from 'lodash/isEqual';
import omit from 'lodash/omit';
import { t } from 'sn-translate';

import userDataBehavior, {
	USER_INITIAL_FETCH_PROPERTY,
	USER_DATA_PROPERTY,
} from './behaviors/user-data';

import './sn-aes-builder-header';
import { getAESAPI, getPathParams } from './iframe-helpers';
import { getRoleName, cannotEditRoles } from './ui-helpers';
import {
	getBatchEffect,
	batchRequestFailed,
	getCreateACLBatch,
	getDeleteACLBatch,
	getCreateAssociationBatch,
	batchRequestInnerAPIFailed,
} from './batch-api-helpers';
import { view } from './view';

import {
	ROLE_BUILDER__TOGGLE_ACL,
	ROLE_BUILDER__TOGGLE_ALL_ACLS,
	ROLE_BUILDER__TOGGLE_FILE_ACLS,
	GET_ROLE_DETAILS_ROUTE,
	ROLE_BUILDER__CLOSE,
	ROLE_BUILDER__GET_ROLE_DETAILS,
	ROLE_BUILDER__GET_ROLE_DETAILS_FAILED,
	ROLE_BUILDER__GET_ROLE_DETAILS_SUCCEEDED,
	ROLE_BUILDER__SAVE,
	ROLE_BUILDER__UPDATE,
	ROLE_BUILDER_ADD_ACTION,
	ROLE_BUILDER_REMOVE_ACTION,
	ROLE_BUILDER__BATCH_CREATE_ACL,
	ROLE_BUILDER__BATCH_CREATE_ACL_FAILED,
	ROLE_BUILDER__BATCH_CREATE_ACL_SUCCEEDED,
	ROLE_BUILDER__BATCH_CREATE_ASSOCIATION,
	ROLE_BUILDER__BATCH_CREATE_ASSOCIATION_FAILED,
	ROLE_BUILDER__BATCH_CREATE_ASSOCIATION_SUCCEEDED,
	ROLE_BUILDER__BATCH_DELETE_ACL,
	ROLE_BUILDER__BATCH_DELETE_ACL_FAILED,
	ROLE_BUILDER__BATCH_DELETE_ACL_SUCCEEDED,
	ROLE_BUILDER__BATCH_OPERATIONS_COMPLETE,
	ROLE_BUILDER_CREATE_BATCH_OPERATION,
	ROLE_BUILDER_DELETE_BATCH_OPERATION,
	ROLE_BUILDER_DATA_OPERATION_LIST,
	ROLE_BUILDER_EXPERIENCE_OPERATION_LIST,
	EXPERIENCE_CATEGORY_SYS_ID,
} from './constants';

import styles from './sn-aes-role-builder.scss';

function addACL(originalRole, role, { fileId, operation }) {
	// Check if ACL exists on originalRole
	const originalFileIdx = originalRole.files.findIndex(file => file.sys_id === fileId);
	const fileIdx = role.files.findIndex(file => file.sys_id === fileId);

	if (role.files[fileIdx].acls[operation]) {
		// the ACL already exists, so don't do anything
		return role;
	}

	if (originalRole.files[originalFileIdx].acls[operation]) {
		// originalRole had this ACL, so copy it back to the role
		role.files[fileIdx].acls[operation] = originalRole.files[originalFileIdx].acls[operation];
	} else {
		// this is a new ACL added to role
		role.files[fileIdx].acls[operation] = {
			sys_id: null,
			security_acl_role_sys_id: null,
			operation,
		};
	}

	return role;
}

function removeACL(role, { fileId, operation }) {
	// Remove acl from file from role
	const fileIdx = role.files.findIndex(file => file.sys_id === fileId);

	if (!role.files[fileIdx].acls[operation]) {
		// the ACL doesn't exists, so don't do anything
		return role;
	}

	// remove the role
	delete role.files[fileIdx].acls[operation];

	return role;
}

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch, updateState }) => {
		const pathParams = getPathParams();

		updateState({ appId: pathParams.appId, roleId: pathParams.roleId });
		dispatch(ROLE_BUILDER__GET_ROLE_DETAILS, {
			appSysId: pathParams.appId,
			roleSysId: pathParams.roleId,
		});
	},
	[COMPONENT_PROPERTY_CHANGED]: ({
		action: {
			payload: { name },
		},
		properties,
	}) => {
		const user = properties[USER_DATA_PROPERTY];

		// User has been fetched and cannot edit roles
		if (
			name === USER_INITIAL_FETCH_PROPERTY &&
			properties[USER_INITIAL_FETCH_PROPERTY] &&
			cannotEditRoles(user)
		) {
			const aesAPI = getAESAPI();
			if (aesAPI) {
				aesAPI.showAlert({
					status: 'warning',
					content: t(
						'You need to elevate your role to security_admin to make modifications to these items'
					),
				});
			}
		}
	},

	[ROLE_BUILDER__GET_ROLE_DETAILS]: createHttpEffect(GET_ROLE_DETAILS_ROUTE, {
		method: 'GET',
		batch: false,
		successActionType: ROLE_BUILDER__GET_ROLE_DETAILS_SUCCEEDED,
		errorActionType: ROLE_BUILDER__GET_ROLE_DETAILS_FAILED,
		pathParams: ['appSysId', 'roleSysId'],
	}),

	[ROLE_BUILDER__GET_ROLE_DETAILS_SUCCEEDED]: ({
		action: {
			payload: { result },
		},
		updateState,
	}) => {
		const aesAPI = getAESAPI();
		if (aesAPI) {
			aesAPI.updateTabState({
				title: getRoleName(get(result, 'name.display_value', '')),
				hasUnsavedChanges: false,
			});

			// Not necessary, but we can update the title here for consistency
			aesAPI.updateTabSession({
				title: getRoleName(get(result, 'name.display_value', '')),
			});
		}

		updateState({
			originalRole: result,
			updatedRole: result,
		});
	},

	[ROLE_BUILDER__GET_ROLE_DETAILS_FAILED]: () => {
		const aesAPI = getAESAPI();
		if (aesAPI) {
			aesAPI.showAlert({
				status: 'critical',
				content: t('We encountered an error while getting role details.'),
			});
		}
	},

	[ROLE_BUILDER__BATCH_CREATE_ACL]: getBatchEffect(
		ROLE_BUILDER__BATCH_CREATE_ACL_SUCCEEDED,
		ROLE_BUILDER__BATCH_CREATE_ACL_FAILED
	),

	[ROLE_BUILDER__BATCH_CREATE_ACL_SUCCEEDED]: ({
		action: {
			payload: { serviced_requests, innerApiFailed },
		},
		state: { appId, roleId },
		updateState,
		dispatch,
	}) => {
		// Dispatch the create association batch call
		const createAssociationBatchData = getCreateAssociationBatch(
			serviced_requests,
			appId,
			roleId
		);

		dispatch(ROLE_BUILDER__BATCH_CREATE_ASSOCIATION, {
			batchData: createAssociationBatchData,
			sysparm_transaction_scope: appId,
		});

		// Show all failed create acl's
		const hasErrors = batchRequestInnerAPIFailed(serviced_requests);

		// Set inner api has errors on state ( We have to do this since createHTTPEffect does not allow params)
		updateState({ innerApiFailed: innerApiFailed || hasErrors });
	},

	[ROLE_BUILDER__BATCH_CREATE_ACL_FAILED]: batchRequestFailed,

	[ROLE_BUILDER__BATCH_CREATE_ASSOCIATION]: getBatchEffect(
		ROLE_BUILDER__BATCH_CREATE_ASSOCIATION_SUCCEEDED,
		ROLE_BUILDER__BATCH_CREATE_ASSOCIATION_FAILED
	),

	[ROLE_BUILDER__BATCH_CREATE_ASSOCIATION_SUCCEEDED]: ({
		action: {
			payload: { serviced_requests },
		},
		dispatch,
		state: { batchOperations, innerApiFailed },
		updateState,
	}) => {
		// Check if delete has completed
		if (!batchOperations[ROLE_BUILDER_DELETE_BATCH_OPERATION]) {
			// Delete has already completed, so all batch operations are done
			// So update the view with latest role details
			dispatch(ROLE_BUILDER__BATCH_OPERATIONS_COMPLETE);
		}

		// Update the state to signify that the create batch operations are completed
		const newBatchOperations = {
			...omit(batchOperations, ROLE_BUILDER_CREATE_BATCH_OPERATION),
		};

		// Show all failed create associations
		const hasErrors = batchRequestInnerAPIFailed(serviced_requests);

		// Set inner api has errors on state ( We have to do this since createHTTPEffect does not allow params)
		updateState({
			batchOperations: newBatchOperations,
			innerApiFailed: innerApiFailed || hasErrors,
		});
	},

	[ROLE_BUILDER__BATCH_CREATE_ASSOCIATION_FAILED]: batchRequestFailed,

	[ROLE_BUILDER__BATCH_DELETE_ACL]: getBatchEffect(
		ROLE_BUILDER__BATCH_DELETE_ACL_SUCCEEDED,
		ROLE_BUILDER__BATCH_DELETE_ACL_FAILED
	),
	[ROLE_BUILDER__BATCH_DELETE_ACL_SUCCEEDED]: ({
		action: {
			payload: { serviced_requests },
		},
		dispatch,
		state: { batchOperations, innerApiFailed },
		updateState,
	}) => {
		// Check if create has completed
		if (!batchOperations[ROLE_BUILDER_CREATE_BATCH_OPERATION]) {
			// Create has already completed, so all batch operations are done
			// So update the view with latest role details
			dispatch(ROLE_BUILDER__BATCH_OPERATIONS_COMPLETE);
		}

		// Update the state to signify that the delete batch operations are completed
		const newBatchOperations = {
			...omit(batchOperations, ROLE_BUILDER_DELETE_BATCH_OPERATION),
		};

		// Show all failed deletes
		const hasErrors = batchRequestInnerAPIFailed(serviced_requests);

		// Set inner api has errors on state ( We have to do this since createHTTPEffect does not allow params)
		updateState({
			batchOperations: newBatchOperations,
			innerApiFailed: innerApiFailed || hasErrors,
		});
	},

	[ROLE_BUILDER__BATCH_DELETE_ACL_FAILED]: batchRequestFailed,

	[ROLE_BUILDER__BATCH_OPERATIONS_COMPLETE]: ({
		dispatch,
		updateState,
		state: { appId, roleId, updatedRole, innerApiFailed },
	}) => {
		// All operations complete, so get the role details again
		dispatch(ROLE_BUILDER__GET_ROLE_DETAILS, {
			appSysId: appId,
			roleSysId: roleId,
		});

		// Show a message to the user everything succeeded
		const aesAPI = getAESAPI();
		if (aesAPI && !innerApiFailed) {
			aesAPI.showAlert({
				status: 'positive',
				content: t(
					'You have successfully saved {0}.',
					getRoleName(get(updatedRole, 'name.display_value', ''))
				),
			});
		} else {
			// reset the innerApiFailed flag, since the errors are already displayed in alerts
			updateState({ innerApiFailed: false });
		}
	},

	[ROLE_BUILDER__UPDATE]: ({
		state: { originalRole, updatedRole },
		action: {
			payload: { updatedFiles },
		},
		updateState,
	}) => {
		let role = cloneDeep(updatedRole);
		// Update the files here
		updatedFiles.forEach(({ fileId, operation, roleBuilderAction }) => {
			if (roleBuilderAction === ROLE_BUILDER_ADD_ACTION) {
				role = addACL(originalRole, role, { fileId, operation });
			}

			if (roleBuilderAction === ROLE_BUILDER_REMOVE_ACTION) {
				role = removeACL(role, { fileId, operation });
			}
		});

		const isDirty = !isEqual(role, originalRole);
		const aesAPI = getAESAPI();
		if (aesAPI) {
			aesAPI.updateTabState({
				hasUnsavedChanges: isDirty,
			});
		}

		updateState({ updatedRole: role, isDirty });
	},

	[ROLE_BUILDER__SAVE]: ({
		state: { appId, originalRole, updatedRole },
		dispatch,
		updateState,
	}) => {
		const createACLBatchData = getCreateACLBatch(appId, originalRole, updatedRole);
		const deleteBatchData = getDeleteACLBatch(appId, originalRole, updatedRole);
		const batchOperations = {};

		if (createACLBatchData.rest_requests.length) {
			// There are create requests so add it to batchOperations
			batchOperations[ROLE_BUILDER_CREATE_BATCH_OPERATION] = true;

			dispatch(ROLE_BUILDER__BATCH_CREATE_ACL, {
				batchData: createACLBatchData,
				sysparm_transaction_scope: appId,
			});
		}

		if (deleteBatchData.rest_requests.length) {
			// There are delete requests so add it to batchOperations
			batchOperations[ROLE_BUILDER_DELETE_BATCH_OPERATION] = true;

			dispatch(ROLE_BUILDER__BATCH_DELETE_ACL, {
				batchData: deleteBatchData,
				sysparm_transaction_scope: appId,
			});
		}

		updateState({ batchOperations, isDirty: false });
	},

	[ROLE_BUILDER__CLOSE]: () => {
		const aesAPI = getAESAPI();
		if (aesAPI) {
			aesAPI.closeCurrentTab();
		}
	},

	['NOW_CHECKBOX#CHECKED_SET']: ({ action, dispatch }, stopPropagation = true) => {
		if (stopPropagation) {
			action.stopPropagation();
		}

		dispatch(action.payload.actionName, action.payload);
	},

	[ROLE_BUILDER__TOGGLE_ALL_ACLS]: ({
		action: {
			payload: { roleBuilderAction, files, taxonomy_category },
		},
		dispatch,
	}) => {
		const updatedFiles = [];
		const operations =
			taxonomy_category === EXPERIENCE_CATEGORY_SYS_ID
				? // Create only read acl for experience category
				  ROLE_BUILDER_EXPERIENCE_OPERATION_LIST
				: // Create all 4 CRUD acl's for data category
				  ROLE_BUILDER_DATA_OPERATION_LIST;

		operations.forEach(operation => {
			files.forEach(file => {
				updatedFiles.push({
					fileId: file.sys_id,
					operation,
					roleBuilderAction,
				});
			});
		});
		dispatch(ROLE_BUILDER__UPDATE, {
			updatedFiles,
		});
	},
	[ROLE_BUILDER__TOGGLE_FILE_ACLS]: ({
		dispatch,
		action: {
			payload: { fileId, roleBuilderAction, taxonomy_category },
		},
	}) => {
		const operations =
			taxonomy_category === EXPERIENCE_CATEGORY_SYS_ID
				? // Create only read acl for experience category
				  ROLE_BUILDER_EXPERIENCE_OPERATION_LIST
				: // Create all 4 CRUD acl's for data category
				  ROLE_BUILDER_DATA_OPERATION_LIST;

		dispatch(ROLE_BUILDER__UPDATE, {
			updatedFiles: operations.map(operation => ({
				fileId,
				operation,
				roleBuilderAction,
			})),
		});
	},

	[ROLE_BUILDER__TOGGLE_ACL]: ({
		dispatch,
		action: {
			payload: { fileId, operation, roleBuilderAction },
		},
	}) => {
		dispatch(ROLE_BUILDER__UPDATE, {
			updatedFiles: [
				{
					fileId,
					operation,
					roleBuilderAction,
				},
			],
		});
	},
};

createCustomElement('sn-aes-role-builder', {
	view: view,
	initialState: {
		isDirty: false,
		originalRole: null,
		updatedRole: null,
		batchOperations: {},
		appId: null,
		roleId: null,
		innerApiFailed: false,
	},
	actionHandlers,
	styles,
	behaviors: [userDataBehavior],
});
