import {
	BEHAVIOR_NAME,
	SIDEBAR_DISPLAYED_PROPERTY,
	SIDEBAR_OPEN_PROPERTY,
	SIDEBAR_MODAL_OPEN_PROPERTY,
	SIDEBAR_CURRENT_STEP_PROPERTY,
	SIDEBAR_STEPS_LOCKED_PROPERTY,
	SIDEBAR__SET_STEP,
	SIDEBAR__NEXT_STEP,
	SIDEBAR__PREVIOUS_STEP,
	SIDEBAR__DISPLAY_SIDEBAR,
	SIDEBAR__TOGGLE_SIDEBAR,
	SIDEBAR__SET_ACCESSIBLE_STEPS,
	SIDEBAR_ACCESSIBLE_STEPS_PROPERTY,
	SIDEBAR_ALL_STEPS_PROPERTY,
	SIDEBAR__SET_CATEGORIES,
	SIDEBAR_CREATION_OPTIONS,
	SIDEBAR_COMPLETION_OPTIONS,
	SIDEBAR_STEP_TYPE_CATEGORY,
	SIDEBAR__SET_MODAL_OPEN,
	SIDEBAR__LOCK_STEPS,
	SIDEBAR_TOGGLE_INTERACTED_PROPERTY,
	SIDEBAR__SET_TOGGLE_INTERACTED,
} from './constants';

const sidebarDataBehavior = {
	name: BEHAVIOR_NAME,
	properties: {
		[SIDEBAR_CURRENT_STEP_PROPERTY]: {
			default: {},
			selectable: true,
		},
		[SIDEBAR_DISPLAYED_PROPERTY]: {
			default: false,
			selectable: true,
		},
		[SIDEBAR_OPEN_PROPERTY]: {
			default: true,
			selectable: true,
		},
		// Tri-state variable
		// true -> guidance helper modal visible
		// false -> guidance helper modal hidden
		// null -> guidance helper modal permanently hidden
		[SIDEBAR_MODAL_OPEN_PROPERTY]: {
			default: false,
			selectable: true,
		},
		[SIDEBAR_STEPS_LOCKED_PROPERTY]: {
			default: false,
			selectable: true,
		},
		[SIDEBAR_ALL_STEPS_PROPERTY]: {
			default: [],
			selectable: true,
		},
		[SIDEBAR_ACCESSIBLE_STEPS_PROPERTY]: {
			default: [],
			selectable: true,
		},
		[SIDEBAR_TOGGLE_INTERACTED_PROPERTY]: {
			default: false,
			selectable: true,
		},
	},
	actionHandlers: {
		[SIDEBAR__NEXT_STEP]: ({ properties, dispatch }) => {
			const currentIndex = properties[SIDEBAR_ACCESSIBLE_STEPS_PROPERTY].findIndex(
				property => property.index === properties[SIDEBAR_CURRENT_STEP_PROPERTY].index
			);

			const nextStepIdx = Math.min(
				currentIndex + 1,
				properties[SIDEBAR_ACCESSIBLE_STEPS_PROPERTY].length - 1
			);

			dispatch(SIDEBAR__SET_STEP, {
				step: properties[SIDEBAR_ACCESSIBLE_STEPS_PROPERTY][nextStepIdx],
			});
		},
		[SIDEBAR__PREVIOUS_STEP]: ({ properties, dispatch }) => {
			const currentIndex = properties[SIDEBAR_ACCESSIBLE_STEPS_PROPERTY].findIndex(
				property => property.index === properties[SIDEBAR_CURRENT_STEP_PROPERTY].index
			);

			const prevStepIdx = Math.max(currentIndex - 1, 0);

			dispatch(SIDEBAR__SET_STEP, {
				step: properties[SIDEBAR_ACCESSIBLE_STEPS_PROPERTY][prevStepIdx],
			});
		},
		[SIDEBAR__SET_STEP]: ({ action, updateProperties }) => {
			updateProperties({
				[SIDEBAR_CURRENT_STEP_PROPERTY]: action.payload.step,
			});
		},
		[SIDEBAR__DISPLAY_SIDEBAR]: ({ action, updateProperties }) => {
			updateProperties({
				[SIDEBAR_DISPLAYED_PROPERTY]: action.payload.isDisplayed,
			});
		},
		[SIDEBAR__TOGGLE_SIDEBAR]: ({ action, properties, updateProperties }) => {
			updateProperties({
				[SIDEBAR_OPEN_PROPERTY]: action.payload.isOpen,
				[SIDEBAR_MODAL_OPEN_PROPERTY]:
					properties[SIDEBAR_MODAL_OPEN_PROPERTY] === null
						? null
						: !action.payload.isOpen,
			});
		},
		[SIDEBAR__SET_MODAL_OPEN]: ({ properties, action, updateProperties }) => {
			updateProperties({
				[SIDEBAR_MODAL_OPEN_PROPERTY]:
					properties[SIDEBAR_MODAL_OPEN_PROPERTY] === null
						? null
						: action.payload.isModalOpen,
			});
		},
		[SIDEBAR__LOCK_STEPS]: ({ action, updateProperties }) => {
			updateProperties({
				[SIDEBAR_STEPS_LOCKED_PROPERTY]: action.payload.isLocked,
			});
		},
		[SIDEBAR__SET_ACCESSIBLE_STEPS]: ({
			action: {
				payload: { types },
			},
			properties,
			updateProperties,
		}) => {
			const accessibleSteps = properties[SIDEBAR_ALL_STEPS_PROPERTY].filter(step =>
				types.includes(step.type)
			);
			updateProperties({
				[SIDEBAR_ACCESSIBLE_STEPS_PROPERTY]: accessibleSteps,
			});
		},
		[SIDEBAR__SET_CATEGORIES]: ({ action, updateProperties }) => {
			const sortedCategories = action.payload.result;

			const allSteps = [];
			let index = 0;
			SIDEBAR_CREATION_OPTIONS.forEach(option => {
				allSteps.push({ id: index, index: index++, ...option });
			});
			sortedCategories.forEach(category =>
				allSteps.push({
					id: category.sys_id,
					index: index++,
					displayText: category.category,
					type: SIDEBAR_STEP_TYPE_CATEGORY,
					category,
				})
			);
			SIDEBAR_COMPLETION_OPTIONS.forEach(option =>
				allSteps.push({ id: index, index: index++, ...option })
			);

			updateProperties({
				[SIDEBAR_ALL_STEPS_PROPERTY]: allSteps,
			});
		},
		[SIDEBAR__SET_TOGGLE_INTERACTED]: ({ updateProperties }) => {
			updateProperties(({ properties }) => {
				// Only update SIDEBAR_TOGGLE_INTERACTED_PROPERTY from false to true
				const shouldUpdate = !properties[SIDEBAR_TOGGLE_INTERACTED_PROPERTY];

				return {
					path: SIDEBAR_TOGGLE_INTERACTED_PROPERTY,
					value: true,
					shouldUpdate,
				};
			});
		},
	},
};

export default sidebarDataBehavior;
