import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import '@servicenow/now-modal';
import '@servicenow/now-text-link';

import { ROOT__HIDE_MODAL } from '../sn-app-engine-studio/constants';
import { PROVIDER_PROPERTIES } from '../sn-aes-provider';
import getAdminContact from '../../helpers/get-admin-contact';

import { SYS_PROP_MOBILE_ACCESS } from './constants';
import styles from './sn-aes-edit-mobile-dialog.scss';

const renderNoAccessModal = prodAdminContact => (
	<now-modal
		opened
		manage-opened
		size="md"
		headerLabel={t('Contact your system administrator')}
		footerActions={[
			{
				id: 'ok-btn',
				size: 'md',
				variant: 'primary',
				label: t('OK'),
			},
		]}>
		<p>
			{t(
				"App Engine Studio generates a default mobile experience for you. To further customize this experience, use Mobile Studio to improve your app's mobile experience."
			)}
		</p>
		<p>{t('To use Mobile Studio, please contact your system administrator.')}</p>
		{prodAdminContact ? (
			<now-text-link
				href={`mailto:${prodAdminContact}`}
				label={t('Contact your admin')}
				variant="primary"
			/>
		) : null}
	</now-modal>
);

const renderAccessModal = () => (
	<now-modal
		opened
		manage-opened
		size="md"
		headerLabel={t('Opening Mobile Studio')}
		footerActions={[
			{
				id: 'go-to-mobile-studio',
				size: 'md',
				variant: 'primary',
				label: t('Continue to Mobile Studio'),
			},
		]}>
		<p>
			{t(
				'Your mobile experience can be edited within Mobile Studio. There, you can further customize this experience and apply advanced configurations.'
			)}
		</p>

		<now-text-link
			label={t('Learn more about Mobile Studio')}
			href="/api/now/v1/context_doc_url/CSHelp:Agent-Config"
			opensWindow
		/>
	</now-modal>
);

createCustomElement('sn-aes-edit-mobile-dialog', {
	view: state => {
		const { hasMobileStudioAccess, prodAdminContact } = state.properties;
		return hasMobileStudioAccess ? renderAccessModal() : renderNoAccessModal(prodAdminContact);
	},

	properties: {
		editorUrl: {
			schema: {
				type: 'string',
			},
		},
		currentInstanceAesConfig: { default: [] },
		hasMobileStudioAccess: {
			default: true,
			computed(state) {
				const { currentInstanceAesConfig: aesConfig = [] } = state.properties;

				// Something went very wrong and just assume true
				if (!Array.isArray(aesConfig)) {
					return true;
				}

				const sysProp = aesConfig.find(({ name }) => name === SYS_PROP_MOBILE_ACCESS);
				return sysProp ? sysProp.value === 'true' : false;
			},
		},
		prodInstanceAesConfig: {
			default: PROVIDER_PROPERTIES.prodInstanceAesConfig,
		},
		prodAdminContact: {
			computed({ properties: { prodInstanceAesConfig } }) {
				return getAdminContact(prodInstanceAesConfig);
			},
		},
	},

	actionHandlers: {
		['NOW_MODAL#FOOTER_ACTION_CLICKED']: ({ properties, action, dispatch }) => {
			const { editorUrl, hasMobileStudioAccess } = properties;
			action.stopPropagation();

			if (hasMobileStudioAccess) {
				window.open(editorUrl, '_blank');
			}

			dispatch(ROOT__HIDE_MODAL);
		},
	},
	styles,
});
