import { createCustomElement, actionTypes } from '@servicenow/ui-core';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED, COMPONENT_DISCONNECTED } = actionTypes;
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import '../sn-aes-tab';
import '../sn-aes-tabs-overflow-panel';
import '../sn-aes-confirm';
import styles from './sn-aes-tabs.scss';
import unionBy from '../../helpers/unionBy';
import getRelativeUrl from '../../helpers/get-relative-url';
import includes from 'lodash/includes';
import dispatchActionViaPayload from '../../helpers/dispatch-action-via-payload';

import {
	TABS__OPEN_TABS,
	TABS__CLOSE_TAB,
	TABS__SELECT_TAB,
	TABS__UPDATE_TAB,
	TABS__UPDATE_SESSION,
	TABS__REF_SET,
	TABS__SCROLL_INTO_VIEW,
	TABS__OVERFLOW_OPENED_SET,
	TABS__SELECT_FOCUSED_TAB,
	TABS__FOCUS_SELECTED_TAB,
	TABS__LEFT_ICONS_SLOT,
	TABS__ARROW_KEY_HANDLER,
	TABS__DELETE_KEY_HANDLER,
	TABS__CLOSE_TAB_CONFIRM,
	TABS__MAIN_TAB_FOCUSED,
	TABS__UPDATED,
} from './constants';
import { APP_PROPERTIES__UPDATE_COMPLETED } from '../sn-aes-app-properties/constants';
import { TOGGLE_FULL_SCREEN } from '../sn-aes-app-wrapper/constants';
import { t } from 'sn-translate';
import { updateRouteState } from '../../behaviors/router';
import { setSessionItem } from '../../behaviors/storage';
import { setDelegate, showModal, unsetDelegate } from '../../pages/sn-aes-page-app/helpers';

export function getSessionKey(routeState) {
	const { sysId } = routeState;
	return `TABS_${sysId}`;
}

/**
 * Mouse wheel should scroll tabs horizontally
 * without having to hold shift
 */
function onWheelTabs(event, element) {
	event.preventDefault();

	// Bail out if user is actually scrolling
	// horizontally (shift/mac trackpad)
	if (event.deltaY === 1 || !event.deltaY) {
		return;
	}

	if (event.deltaY > 0) {
		element.scroll(element.scrollLeft + 20, 0);
	} else {
		element.scroll(element.scrollLeft - 20, 0);
	}
}

/**
 * Used to determine if a tab is in the viewpoint
 * so the it can be scrolled to if it is not.
 */
function isElementInViewport(el) {
	const rect = el.getBoundingClientRect();
	return (
		rect.top >= 0 &&
		rect.left >= 0 &&
		rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
		rect.right <= (window.innerWidth || document.documentElement.clientWidth)
	);
}

const CsTabs = (state, { dispatch }) => {
	const { items, selectedItemId, tabsRef, overflowOpened } = state;
	const { panelRenderer, showHeaderChrome } = state.properties;

	if (!panelRenderer) {
		return null;
	}

	return (
		<div className="sn-aes-tabs">
			<div className="tab-bar">
				<slot name={TABS__LEFT_ICONS_SLOT} />
				<div
					className="tab-wrapper"
					role="tablist"
					aria-label={t('Use left and right arrow keys to navigate tabs')}
					hook-insert={({ elm }) => dispatch(TABS__REF_SET, { elm })}
					on-wheel={event => onWheelTabs(event, tabsRef)}
					on-keydown={event => {
						switch (event.key) {
							case 'ArrowLeft':
								event.preventDefault();
								dispatch(TABS__ARROW_KEY_HANDLER, {
									value: -1,
								});
								break;
							case 'ArrowRight':
								event.preventDefault();
								dispatch(TABS__ARROW_KEY_HANDLER, { value: 1 });
								break;
							case 'Backspace':
							case 'Delete':
								event.preventDefault();
								dispatch(TABS__DELETE_KEY_HANDLER);
								break;
							case 'Enter':
							case ' ':
								event.preventDefault();
								dispatch(TABS__SELECT_FOCUSED_TAB);
								break;
							default:
								return;
						}
					}}>
					{items.map(item => {
						return (
							<sn-aes-tab
								id={`${item.id}-tab`}
								role="tab"
								tabindex={item.id === selectedItemId ? '0' : '-1'}
								data-tabId={item.id}
								tabId={item.id}
								title={item.title}
								subtitle={item.subtitle}
								isLoading={item.isLoading}
								isSelected={item.id === selectedItemId}
								isUnsaved={item.isUnsaved}
								isUpdated={item.isUpdated}
								iconName={item.iconName}
								canClose={item.canClose}
								aria-controls={`${item.id}-panel`}
								aria-selected={item.id === selectedItemId ? 'true' : false}
								aria-label={
									item.canClose !== false
										? t('Press delete or backspace to close tab')
										: null
								}
							/>
						);
					})}
				</div>
				<now-button-iconic
					tooltipContent={t('Tab menu')}
					className="tab-menu"
					bare
					icon={`chevron-${overflowOpened ? 'up' : 'down'}-fill`}
					variant="tertiary"
					size="lg"
					config-aria={{
						'aria-label': overflowOpened
							? t('Close list of tabs')
							: t('Open to see list of tabs'),
					}}
					append-to-payload={{
						actionName: TABS__OVERFLOW_OPENED_SET,
						opened: !overflowOpened,
					}}
				/>
				<now-button-iconic
					tooltipContent={
						showHeaderChrome ? t('Enter full screen') : t('Exit full screen')
					}
					bare
					icon={showHeaderChrome ? 'expand-fill' : 'collapse-fill'}
					variant="tertiary"
					size="lg"
					config-aria={{
						'aria-label': showHeaderChrome
							? t('Enter full screen')
							: t('Exit full screen'),
					}}
					append-to-payload={{ actionName: TOGGLE_FULL_SCREEN }}
				/>
			</div>

			{overflowOpened ? (
				<Fragment>
					<sn-aes-tabs-overflow-panel items={items} selectedItemId={selectedItemId} />
					<div
						className="overlay-backdrop"
						on-click={() =>
							dispatch(TABS__OVERFLOW_OPENED_SET, {
								opened: false,
							})
						}
					/>
				</Fragment>
			) : null}

			{items.map(item => {
				return (
					<div
						id={`${item.id}-panel`}
						tabindex="0"
						hidden={item.id !== selectedItemId}
						className="tab-panel"
						role="tabpanel"
						aria-labelledby={`${selectedItemId}-tab`}
						key={item.id}>
						{panelRenderer(item)}
					</div>
				);
			})}
		</div>
	);
};

export const actionHandlers = {
	['NOW_BUTTON_ICONIC#CLICKED']: dispatchActionViaPayload,
	[TABS__REF_SET]: ({ action: { payload }, updateState }) => {
		updateState({ tabsRef: payload.elm });
	},
	[COMPONENT_BOOTSTRAPPED]({ properties, updateState, dispatch }) {
		const { items, routeState, sessionStorageData } = properties;
		const { tabId } = routeState;
		const sessionKey = getSessionKey(routeState);

		/**
			 * The Union below supports the following cases:
			 *
			 * 1. Entering the tabs for the first time and no session data exists
			 * 2. Entering the tabs when session tab data exists

			 * Union:
			 *  - tab items that are initially passed into the component on bootstrap
			 *  - tab items from the session
			 */
		let newSessionTabState = unionBy(items, sessionStorageData[sessionKey] || [], 'id');

		let selectedItemId = tabId;

		// This covers the case when sending a browser url and the editor_url isn't
		// known. The selected tab should be set back to the default app tab.
		if (!newSessionTabState.some(t => t.id === tabId)) {
			const [id] = items;
			selectedItemId = id;
			updateRouteState(dispatch, { tabId: id });
		}

		setSessionItem(dispatch, sessionKey, newSessionTabState);

		dispatch(TABS__MAIN_TAB_FOCUSED, {
			appTabFocused: selectedItemId === newSessionTabState[0].id,
		});

		updateState({
			items: newSessionTabState,
			selectedItemId,
		});

		dispatch(TABS__SCROLL_INTO_VIEW, { tabId });
	},
	[COMPONENT_DISCONNECTED]: () => {
		// Remove the warning since we are force leaving the page
		window.onbeforeunload = () => null;
	},
	[APP_PROPERTIES__UPDATE_COMPLETED]: ({ action: { payload }, dispatch }) => {
		// TODO: Refactor this so tab is updated when items being passed down as prop
		// update (after FETCH_APP_SUCCEEDED runs in sn-aes-page-app)

		const { sysId, appName } = payload;
		dispatch(TABS__UPDATE_TAB, { tabId: sysId, subtitle: appName });
	},
	[TABS__OVERFLOW_OPENED_SET]: ({
		updateState,
		action: {
			payload: { opened },
		},
	}) => updateState({ overflowOpened: opened }),
	[TABS__SELECT_TAB]: ({
		state,
		updateState,
		action: {
			payload: { tabId },
		},
		dispatch,
	}) => {
		const tabToSelect = state.items.find(i => i.id === tabId);

		dispatch(TABS__SCROLL_INTO_VIEW, { tabId });
		updateRouteState(dispatch, { tabId: tabToSelect.id });

		updateState({
			path: 'selectedItemId',
			value: tabToSelect.id,
			operation: 'set',
		});

		dispatch(TABS__OVERFLOW_OPENED_SET, { opened: false });
	},
	[TABS__UPDATE_TAB]({ dispatch, state, updateState, action: { payload } }) {
		const { items } = state;
		const { tabId, isUnsaved, isLoading, title, subtitle } = payload;
		let previousTabState = null;
		let currentTabState = null;

		const updatedItems = items.map(item => {
			if (item.id === tabId) {
				// Saving updated item for delegate title
				previousTabState = item;
				currentTabState = {
					...item,
					title: title || item.title,
					subtitle: subtitle || item.subtitle,
					isLoading: isLoading !== undefined ? isLoading : item.isLoading,
					isUnsaved: !!isUnsaved,
				};

				return currentTabState;
			}

			return item;
		});

		if (isUnsaved) {
			setDelegate({
				dispatch,
				delegateId: `sn-aes-tab-${tabId}`,
				headerLabel: t('Leave {0} tab without saving?', currentTabState.title),
				content: t('Changes in this tab will be lost if you leave now.'),
			});
		} else {
			// Remove the delegate if it exists
			unsetDelegate({ dispatch, delegateId: `sn-aes-tab-${tabId}` });
		}

		dispatch(TABS__UPDATED, {
			tabId: currentTabState.id,
			previousTabState,
			currentTabState,
		});

		updateState({
			path: 'items',
			value: updatedItems,
			operation: 'set',
		});
	},
	[TABS__UPDATE_SESSION]: ({ dispatch, properties, action: { payload } }) => {
		const { routeState, sessionStorageData } = properties;
		const { tabId, title, subtitle, url } = payload;

		const sessionKey = getSessionKey(routeState);
		const storedTabs = [...sessionStorageData[sessionKey]];
		let urlObj;

		if (url) {
			try {
				urlObj = new URL(url);
			} catch (e) {
				urlObj = false;
			}
		}

		const newTabs = storedTabs.map(item => {
			if (item.id === tabId) {
				return {
					...item,
					title: title || item.title,
					subtitle: subtitle || item.subtitle,
					url: (urlObj && getRelativeUrl(urlObj)) || item.url,
				};
			}

			return item;
		});

		setSessionItem(dispatch, sessionKey, newTabs);
	},
	[TABS__OPEN_TABS]: ({ state, properties, action, updateState, dispatch }) => {
		const { tabs } = action.payload;
		const { routeState, sessionStorageData } = properties;
		const sessionKey = getSessionKey(routeState);
		let storedTabs = [...sessionStorageData[sessionKey]];
		const newTabState = {
			...state,
			selectedItemId: tabs[0].id,
		};

		// find tabs which already exist and remove them
		const openIds = state.items.map(tab => tab.id);
		const newTabs = tabs.filter(tab => !openIds.includes(tab.id));

		if (newTabs.length) {
			// There are new tabs, so open them
			newTabState.items = [...state.items, ...newTabs];
			storedTabs = [...storedTabs, ...newTabs];
		}

		// Set tab to the first tab
		updateRouteState(dispatch, { tabId: tabs[0].id });

		setSessionItem(dispatch, sessionKey, storedTabs);

		updateState({
			...newTabState,
		});

		// Scroll to first tab
		dispatch(TABS__SCROLL_INTO_VIEW, { tabId: tabs[0].id });
	},
	[TABS__CLOSE_TAB]: ({
		state,
		action: {
			payload: { tabId, overrideConfirmationModal = false, nextSelectedTabId },
		},
		dispatch,
	}) => {
		const { items } = state;
		const closedTab = items.find(i => i.id === tabId);

		if (!closedTab || closedTab.canClose === false) {
			// tab was not found in items list or it doesn't allow closing
			// so do nothing
			return;
		}

		if (closedTab.isUnsaved && !overrideConfirmationModal) {
			showModal({
				dispatch,
				id: `sn-aes-tab-${tabId}-confirm`,
				headerLabel: t('Leave {0} tab without saving?', closedTab.title),
				content: t('Changes in this tab will be lost if you leave now.'),
				nextFunction: () => {
					// Remove the delegate if it exists
					unsetDelegate({ dispatch, delegateId: `sn-aes-tab-${tabId}` });

					// Close the tab since it's confirmed now.
					dispatch(TABS__CLOSE_TAB_CONFIRM, { tabId, nextSelectedTabId });
				},
			});
		} else {
			// Remove the delegate if it exists
			unsetDelegate({ dispatch, delegateId: `sn-aes-tab-${tabId}` });

			// Close the tab
			dispatch(TABS__CLOSE_TAB_CONFIRM, { tabId, nextSelectedTabId });
		}
	},
	[TABS__CLOSE_TAB_CONFIRM]: {
		schema: {
			type: 'object',
			properties: {
				tabId: { type: 'string' },
				nextSelectedTabId: { type: 'string' },
			},
			required: ['tabId'],
		},
		effect: ({
			action: {
				payload: { tabId, nextSelectedTabId },
			},
			updateState,
			dispatch,
		}) => {
			// We have to use updateState handlers to ensure that closing of tab
			// is correct in multiple parallel triggers
			updateState([
				({ state, properties }) => {
					const { routeState } = properties;
					const { items, selectedItemId } = state;
					const closedTabId = tabId;

					let newSelectedItemId = nextSelectedTabId || selectedItemId;

					// When closing a tab that is selected. Set the selected
					// tab to it's neighbor on the right. If it's the last tab
					// set the selected tab to the neighbor on the left.
					if (selectedItemId === closedTabId && !nextSelectedTabId) {
						const selectedItemIndex = items.findIndex(item => item.id === closedTabId);

						if (items[selectedItemIndex + 1]) {
							newSelectedItemId = items[selectedItemIndex + 1].id;
						} else if (items[selectedItemIndex - 1]) {
							newSelectedItemId = items[selectedItemIndex - 1].id;
						} else {
							// When closing the last tab
							newSelectedItemId = null;
						}
					}

					const newRouteStateTabs = (routeState.tabs || []).filter(
						t => t.id !== closedTabId
					);
					updateRouteState(dispatch, {
						tabId: newSelectedItemId,
						tabs: newRouteStateTabs,
					});
					dispatch(TABS__FOCUS_SELECTED_TAB);

					return {
						path: 'selectedItemId',
						operation: 'set',
						value: newSelectedItemId,
					};
				},
				({ state, properties }) => {
					const { routeState } = properties;
					const sessionKey = getSessionKey(routeState);
					const { items } = state;

					const newTabItems = items.filter(i => i.id !== tabId);
					setSessionItem(dispatch, sessionKey, newTabItems);

					return {
						path: 'items',
						operation: 'set',
						value: newTabItems,
					};
				},
			]);
		},
	},

	[TABS__SELECT_FOCUSED_TAB]: ({ state: { tabsRef }, dispatch }) => {
		const tabElm = tabsRef.querySelector('sn-aes-tab:focus-within');
		const tabId = tabElm.getAttribute('data-tab-id');
		if (tabId) {
			dispatch(TABS__SELECT_TAB, { tabId });
		}
	},
	[TABS__FOCUS_SELECTED_TAB]: ({ state: { tabsRef } }) => {
		const tabElm = tabsRef.querySelector('sn-aes-tab[is-selected]');
		if (tabElm) {
			requestAnimationFrame(() => {
				tabElm.focus();
			});
		}
	},
	[TABS__SCROLL_INTO_VIEW]: ({
		state: { items, tabsRef },
		action: {
			payload: { tabId },
		},
	}) => {
		if (!tabsRef) {
			return;
		}

		const tabToSelect = items.find(i => i.id === tabId);
		const tabToSelectElm = tabsRef.querySelector(`[data-tab-id='${tabToSelect.id}']`);

		// Scrolls the newly selected tab into view
		// when selecting from the overflow panel
		if (tabToSelectElm) {
			if (!isElementInViewport(tabToSelectElm)) {
				requestAnimationFrame(() => {
					tabToSelectElm.scrollIntoView({
						behavior: 'smooth',
						inline: 'end',
					});
				});
			}
		}
	},
	// Keyboard Control Action Handlers
	[TABS__ARROW_KEY_HANDLER]: ({
		state: { tabsRef },
		action: {
			payload: { value },
		},
	}) => {
		const tabElm = tabsRef.querySelector('sn-aes-tab:focus-within');
		const lastTabElm = tabsRef.lastElementChild;
		const firstTabElm = tabsRef.firstElementChild;

		let nextTab;

		if (value === 1) {
			nextTab = tabElm.nextElementSibling || firstTabElm;
		} else if (value === -1) {
			nextTab = tabElm.previousElementSibling || lastTabElm;
		}

		if (nextTab) {
			nextTab.focus();
		}
	},
	[TABS__DELETE_KEY_HANDLER]: ({ state: { tabsRef }, dispatch }) => {
		const tabElm = tabsRef.querySelector('sn-aes-tab:focus-within');
		const tabId = tabElm.getAttribute('data-tab-id');
		if (tabElm.getAttribute('can-close')) {
			dispatch(TABS__CLOSE_TAB, { tabId });
		}
	},
	[COMPONENT_PROPERTY_CHANGED]: ({
		dispatch,
		state: { items: openTabs },
		action: {
			payload: { name, value },
		},
		properties: { items, deletedItems },
	}) => {
		if (name === 'routeState') {
			// Disable sidebar if selected tab is not main tab
			dispatch(TABS__MAIN_TAB_FOCUSED, {
				appTabFocused: value.tabId === items[0].id,
			});
		}

		if (name === 'deletedItems') {
			openTabs.forEach(tab => {
				if (includes(deletedItems, tab.id)) {
					// The sys_id doesn't exist on the app, so the file was deleted
					// So we have to force close this tab
					dispatch(TABS__CLOSE_TAB, { tabId: tab.id, overrideConfirmationModal: true });
				}
			});
		}
	},
};

createCustomElement('sn-aes-tabs', {
	view: CsTabs,
	initialState: {
		items: [],
		selectedItemId: null,
		overflowOpened: false,
	},
	properties: {
		items: { default: [] },
		deletedItems: { default: [] },
		panelRenderer: { default: null },
		showHeaderChrome: { default: true },

		// TODO: move session management to sn-aes-page-app
		routeState: {},

		// TODO: move session management to sn-aes-page-app
		sessionStorageData: {},
	},
	actionHandlers,
	styles,
});
