import { createCustomElement } from '@servicenow/ui-core';

import '@servicenow/now-icon';
import '@servicenow/now-text-link';

import { t } from 'sn-translate';

import {
	FILE_EXTENSIONS,
	filetypes,
	FILE_SET,
	FILE_REMOVE,
	UPLOAD_FAILURE,
	PARSE_PREVIEW_IMG,
	FILE_DROP,
	DRAGOVER_SET,
	EXISTING_IMG_REMOVED,
} from './constants';
import actionHandlers from './actions';
import styles from './sn-aes-gac-image-uploader.scss';

const FILE_TYPE_ERROR_MSG = t('Wrong file type');
const fileTypesString = FILE_EXTENSIONS.join(', ');

function validateFileType(file) {
	const { type } = file;
	return filetypes.includes(type);
}

const GACImageUploader = (state, { dispatch }) => {
	const {
		properties: { label, disabled, existingImageUrl },
		isDragging,
		selectedFile,
		selectedImage,
	} = state;
	let inputRef;

	const classes = {
		'sn-aes-gac-image-uploader-drop-target': true,
		'-image-selected': existingImageUrl || selectedImage,
		'-dragging': isDragging,
	};

	const onInputChange = event => {
		const file = event.target.files[0];
		const isValid = validateFileType(file);
		if (isValid) {
			dispatch(FILE_SET, file);
			dispatch(PARSE_PREVIEW_IMG, file);
		} else {
			dispatch(UPLOAD_FAILURE, FILE_TYPE_ERROR_MSG);
		}
	};

	const onFileDrop = event => {
		event.preventDefault();
		if (event.dataTransfer.items && !selectedFile) {
			if (event.dataTransfer.items[0].kind === 'file') {
				const file = event.dataTransfer.items[0].getAsFile();
				const isValid = validateFileType(file);
				if (isValid) {
					dispatch(PARSE_PREVIEW_IMG, file);
					dispatch(FILE_DROP, { file });
				} else {
					dispatch(UPLOAD_FAILURE, FILE_TYPE_ERROR_MSG);
				}
			}
		}
		dispatch(DRAGOVER_SET, { isDragging: false });
	};

	const onDragover = event => {
		event.preventDefault();
		!isDragging && dispatch(DRAGOVER_SET, { isDragging: true });
	};

	const onDragleave = event => {
		event.preventDefault();
		isDragging && dispatch(DRAGOVER_SET, { isDragging: false });
	};

	const dropTarget = () => {
		if (existingImageUrl) {
			return (
				<img
					className="sn-aes-gac-image-uploader-preview-image"
					src={existingImageUrl}
					alt={t('Application logo')}
				/>
			);
		} else if (selectedFile && selectedImage) {
			return (
				<img
					className="sn-aes-gac-image-uploader-preview-image"
					src={selectedImage}
					alt={selectedFile.name}
				/>
			);
		} else {
			return (
				<div>
					<now-icon icon="upload-fill" size="lg" />
					<div>
						<now-text-link
							accessibleLabel={`${t(
								'Browse files. Accepted file types: {0}',
								fileTypesString
							)}`}
							on-click={() => !disabled && inputRef.click()}
							label={label}
							variant="primary"
							href="javascript:void(0)"
						/>
					</div>
				</div>
			);
		}
	};

	const clearSelection = () => {
		if (existingImageUrl) {
			return (
				<div className="sn-aes-gac-image-uploader-image-link">
					<now-icon icon="circle-close-outline" variant="primary" />
					<now-text-link
						label={t('Remove image')}
						on-click={() => dispatch(EXISTING_IMG_REMOVED)}
						variant="primary"
						href="javascript:void(0)"
					/>
				</div>
			);
		} else if (selectedFile) {
			return (
				<div className="sn-aes-gac-image-uploader-image-link">
					<now-icon icon="circle-close-outline" variant="primary" />
					<now-text-link
						label={t('Remove image')}
						on-click={() => {
							dispatch(FILE_REMOVE);
							inputRef.value = '';
						}}
						variant="primary"
						href="javascript:void(0)"
					/>
				</div>
			);
		} else {
			return (
				<p className="sn-aes-gac-image-uploader-file-types">
					{fileTypesString.replace(/\./g, '').toUpperCase()}
				</p>
			);
		}
	};

	return (
		<section className="sn-aes-gac-image-uploader">
			<input
				ref={element => (inputRef = element)}
				accept={filetypes.join()}
				hidden
				on-change={onInputChange}
				type="file"
			/>
			<div
				class={classes}
				on-dragover={onDragover}
				on-dragleave={onDragleave}
				on-drop={onFileDrop}>
				{dropTarget()}
			</div>
			{clearSelection()}
		</section>
	);
};

createCustomElement('sn-aes-gac-image-uploader', {
	view: GACImageUploader,
	initialState: {
		selectedFile: null,
		selectedImage: null,
		isDragging: false,
		isUploading: false,
		isComplete: false,
		uploadResult: null,
	},
	properties: {
		/**
		 * Label to be displayed above within the drop target
		 * Assumed to be translated when passed in
		 * @type {string}
		 */
		label: { default: 'file', reflect: true },
		/**
		 * disabled - manually override upload button disabled prop
		 * @type {boolean}
		 */
		disabled: { default: false, reflect: true },
		/**
		 * requestUrl - url to POST upload
		 * @type {string}
		 */
		requestUrl: { default: '/api/now/attachment/upload' },
		/**
		 * existingImageUrl - url of image to show on component load
		 * @type {string}
		 */
		existingImageUrl: { default: '' },
	},
	actionHandlers,
	styles,
});
