import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-button';
import '../sn-aes-overflow-tab';
import styles from './sn-aes-tabs-overflow-panel.scss';

createCustomElement('sn-aes-tabs-overflow-panel', {
	view: ({ properties: { items, selectedItemId } }) => {
		return (
			<div className="overflow-panel">
				{items.map(item => {
					return (
						<sn-aes-overflow-tab item={item} is-selected={item.id === selectedItemId} />
					);
				})}
			</div>
		);
	},
	properties: {
		items: { default: [] },
		selectedItemId: { default: null },
	},
	styles,
});
