import { createCustomElement, actionTypes } from '@servicenow/ui-core';
const { COMPONENT_RENDERED } = actionTypes;
import '@servicenow/now-button';

import styles from './sn-aes-guidance-modal.scss';
import { t } from 'sn-translate';
import { GUIDANCE_MODAL__CLOSE_CLICKED } from './constants';

createCustomElement('sn-aes-guidance-modal', {
	view(
		{
			properties: { id, headerText, illustration, description, modalPosition, variant },
		},
		{ dispatch }
	) {
		let arrowPosition = null;
		switch (modalPosition) {
			case 'top':
				arrowPosition = 'down';
				break;
			case 'bottom':
				arrowPosition = 'top';
				break;
			case 'right':
				arrowPosition = 'left';
				break;
			case 'middle':
				arrowPosition = 'down';
				break;
		}

		return (
			<div
				id={`guidance-modal-${id}`}
				key={id}
				class={{
					'guidance-modal': true,
					[modalPosition]: true,
				}}>
				<div id="guidance-modal-anchor" className="guidance-modal-anchor" />
				<div className="header now-align -inline -block-center">
					<div className="header-text truncate">{headerText}</div>
					<now-button-iconic
						bare
						className="close-button"
						icon="close-fill"
						size="md"
						configAria={{ 'aria-label': t('Click to close') }}
						variant="primary"
						on-click={() => dispatch(GUIDANCE_MODAL__CLOSE_CLICKED)}
						on-keydown={event => {
							if (event.key === 'Enter') {
								dispatch(GUIDANCE_MODAL__CLOSE_CLICKED);
							}
						}}
					/>
				</div>
				{illustration ? (
					<div className="image-wrapper now-align -inline-center -block-center">
						<img src={illustration} alt="" />
					</div>
				) : null}
				{description ? (
					<div className="description multiline-truncate">{description}</div>
				) : null}
				<div
					class={{
						arrow: true,
						[`-${arrowPosition}`]: true,
						[`-${variant}`]: true,
					}}
				/>
			</div>
		);
	},
	properties: {
		/**
		 * Guidance modal id
		 * @type {string}
		 */
		id: { default: '' },
		/**
		 * Guidance modal header text
		 * @type {string}
		 */
		headerText: { default: '' },
		/**
		 * Guidance modal illustration
		 * @type {string}
		 */
		illustration: { default: '' },
		/**
		 * Guidance modal description
		 * @type {string}
		 */
		description: { default: '' },
		/**
		 * Position of the guidance modal ("top" | "bottom" | "right")
		 * @type {string}
		 */
		modalPosition: { default: '' },
		/**
		 * Defines color of guidance modal arrow ("primary" | "secondary")
		 * @type {string}
		 */
		variant: { default: 'primary' },
	},
	actionHandlers: {
		[COMPONENT_RENDERED]: ({ host }) => {
			const modalNode = host.shadowRoot.querySelector(`#guidance-modal-anchor`);

			requestAnimationFrame(() => {
				if (modalNode) {
					modalNode.scrollIntoView({
						behavior: 'smooth',
						alignToTop: true,
					});
				}
			});
		},
	},
	dispatches: {
		/**
		 * Dispatched when the close icon on the guidance modal is clicked
		 */
		[GUIDANCE_MODAL__CLOSE_CLICKED]: {},
	},
	styles,
});
