import { createHttpEffect } from '@servicenow/ui-effect-http';

import { APP_PROPERTIES__UPDATE_SUCCEEDED, APP_PROPERTIES__UPDATE_FAILED } from './constants';

export const updateAppPropertiesEffect = createHttpEffect(
	'/api/now/appcreator/app-properties/:sysId',
	{
		method: 'PATCH',
		pathParams: ['sysId'],
		queryParams: ['sysparm_transaction_scope'],
		dataParam: 'appData',
		successActionType: APP_PROPERTIES__UPDATE_SUCCEEDED,
		errorActionType: APP_PROPERTIES__UPDATE_FAILED,
		batch: false,
	}
);
