import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import get from 'lodash/get';

import {
	WIZARD_MANAGER__TEMPLATE_COMPLETE,
	WIZARD_MANAGER__NEXT_OPERATION,
	WIZARD_MANAGER__CANCEL,
	WIZARD_MANAGER__EDIT_RECORD,
	WIZARD_MANAGER__APP_CREATED,
	WIZARD_MANAGER__CRIT_FAIL,
	WIZARD_MANAGER__FETCH_TAXONOMY_FILES,
	WIZARD_MANAGER__FETCH_TAXONOMY_FILES_SUCCEEDED,
	WIZARD_MANAGER__FETCH_TAXONOMY_FILES_FAILED,
	FETCH_APPLICATION_FILE_ROUTE,
	TEMPLATE_DIRTY_SET,
} from './constants';

import { getComponentsBySysIds } from '@devsnc/library-uxf';

import '../sn-aes-builder';
import { PROVIDER_PROPERTIES } from '../sn-aes-provider';
import {
	TEMPLATE_DATA_PROPERTY,
	AOT_MANAGER_KEY,
	CREATE_APPLICATION_TEMPLATE_KEY,
	OBJECT_TEMPLATE_ROLE_DEMO_KEY,
} from '../../behaviors/template-data';
import redispatchAction from '../../helpers/redispatch-action';

import { ALERTS_BEHAVIOR__ADD_ITEM } from '../../behaviors/alerts';

/**
 * getAOTManagerProps
 *
 *
 * @summary Selects props for aot manager based on the operation being run.
 */
function getAOTManagerProps(state) {
	const {
		properties: { templates, operations },
		createAppSysId,
	} = state;

	const currentOperation = operations[0];
	const props = {
		// Operation for creating an application
		CREATE_APPLICATION_OPERATION: {
			// It will use the templateId passed as parameter for app creation if available.
			// If not, then it uses sn-application-creation-wizard for app creation
			templateId:
				(currentOperation.params || {}).templateId ||
				templates[CREATE_APPLICATION_TEMPLATE_KEY],

			// The wizard can use this to know that we are chaining templates together on the frontend.
			// For example we chain the app template and table template together.
			collectedResults: get(currentOperation, 'params.collectedResults'),
		},
		// Operation for object template creation
		CREATE_OBJECT_TEMPLATE_OPERATION: {
			// It will use the templateId passed as parameter for object template if available.
			// If not, then it uses User Role object template for object template creation
			templateId:
				(currentOperation.params || {}).templateId ||
				templates[OBJECT_TEMPLATE_ROLE_DEMO_KEY],
			// It will use the appId passed as parameter for app creation if available.
			// this is generally used when we want to add object template to existing app.
			// If the appId is not available, then it uses the app created
			// in the previous operations.
			// If both are undefined, then AOT Manager will fail
			appId: (currentOperation.params || {}).appId || createAppSysId,
		},
		// Operation for application template creation
		CREATE_APPLICATION_TEMPLATE_OPERATION: {
			// It will use the templateId passed as parameter for app creation.
			templateId: (currentOperation.params || {}).templateId,
		},
	};
	return {
		// since length always reduces by 1, it will guarantee uniqueness
		key: `operation-${currentOperation.operation}-${operations.length}`,
		...props[currentOperation.operation],
	};
}

export const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: async ({ properties, updateState }) => {
		if (properties.operations.length) {
			// Utilize library-uxf to load given template component,
			// see https://code.devsnc.com/dev/uxf-monorepo/tree/project/quebec/library-uxf for more info
			// const [TemplateComponent] = await getComponentsBySysIds([wizard]);

			const [TemplateComponent] = await getComponentsBySysIds([
				properties.templates[AOT_MANAGER_KEY],
			]);

			updateState({
				initialOperations: properties.operations,
			});

			if (TemplateComponent) {
				updateState({ TemplateComponent });
			}
			// else {
			// 	console.warn(`Error loading template, ${wizard} not found.`);
			// }
		}
	},
	'SYS_APP_WIZARD#APP_CREATED': ({ action, dispatch, state: { initialOperations } }) => {
		action.stopPropagation();

		dispatch(WIZARD_MANAGER__APP_CREATED, {
			sysId: action.payload.app.sys_id,
			operations: initialOperations,
		});
	},
	'SN_TEMPLATE_MANAGER#TEMPLATE_COMPLETE': ({
		action,
		properties,
		dispatch,
		updateProperties,
		updateState,
	}) => {
		action.stopPropagation();

		let operations = [...properties.operations];
		const { sys_app } = action.payload;

		// remove the operation which has just completed
		operations.shift();
		updateProperties({ operations });

		if (operations.length) {
			// Case for when new app was created
			if (sys_app) {
				updateState({ createAppSysId: sys_app.sys_id });
			}

			// More operations are present, so go to the next operation
			dispatch(WIZARD_MANAGER__NEXT_OPERATION, { operations });
		} else {
			// All operations have completed
			dispatch(WIZARD_MANAGER__TEMPLATE_COMPLETE, {
				...action.payload,
			});
		}
	},
	'SN_TEMPLATE_MANAGER#TEMPLATE_CANCEL': ({ action, dispatch, state }) => {
		action.stopPropagation();

		redispatchAction(WIZARD_MANAGER__CANCEL)({
			action: {
				stopPropagation: action.stopPropagation,
				payload: { showConfirmModal: state.templateDirty },
			},
			dispatch,
		});
	},

	// Warning case - the template instance has not been created, end user can safely re-attempt
	'SN_TEMPLATE_MANAGER#TEMPLATE_WARNING': ({ action, dispatch }) => {
		action.stopPropagation();
		// Payload maps to the `error` with `message` and `detail` as keys, with `message` being more generic
		const { message, detail } = action.payload;
		dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
			status: 'warning',
			heading: message,
			content: detail,
		});
	},

	// Critical failure case - the template instance has been created, end user cannot re-attempt
	'SN_TEMPLATE_MANAGER#TEMPLATE_FAILURE': ({ action, dispatch }) => {
		action.stopPropagation();
		// Payload is effectively the `error` object with `message` and `detail` as keys, with `message` being more generic
		const { message, detail } = action.payload;
		dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
			status: 'critical',
			heading: message,
			content: detail,
			pagesToPersist: 2,
		});
		// Separating from the WIZARD_MANAGER__CANCEL action due to the requirement of closing the wizard while maintaining the alert
		dispatch(WIZARD_MANAGER__CRIT_FAIL);
	},

	'SN_TEMPLATE_MANAGER#TEMPLATE_EDIT_RECORD': ({ action, updateState, dispatch }) => {
		action.stopPropagation();

		const { sys_app, key: table_name, result } = action.payload;

		let editRecordSysIds = Array.isArray(result) ? result : [result];
		editRecordSysIds = editRecordSysIds.map(r => r.sys_id);

		if (!sys_app.sys_id || !table_name || editRecordSysIds.length === 0) {
			console.error(
				`Unable to open tabs for editing. Insufficient information provided. sys_app.sys_id: ${
					sys_app.sys_id
				} table_name: ${table_name}, results: ${JSON.stringify(result)}`
			);
			return;
		}

		//Working around seismic not allowing us to easily pass state through a request
		updateState({ editRecordAppSysId: sys_app.sys_id });

		dispatch(WIZARD_MANAGER__FETCH_TAXONOMY_FILES, {
			application_sys_id: sys_app.sys_id,
			table_name: table_name,
			object_sys_ids: editRecordSysIds.join(),
		});
	},

	[WIZARD_MANAGER__FETCH_TAXONOMY_FILES]: createHttpEffect(FETCH_APPLICATION_FILE_ROUTE, {
		method: 'GET',
		successActionType: WIZARD_MANAGER__FETCH_TAXONOMY_FILES_SUCCEEDED,
		errorActionType: WIZARD_MANAGER__FETCH_TAXONOMY_FILES_FAILED,
		queryParams: ['record_ids', 'record_type'],
		pathParams: ['application_sys_id', 'table_name', 'object_sys_ids'],
		batch: false,
	}),

	[WIZARD_MANAGER__FETCH_TAXONOMY_FILES_SUCCEEDED]: ({
		action,
		updateState,
		dispatch,
		state: { editRecordAppSysId },
	}) => {
		action.stopPropagation();

		const { result } = action.payload;

		dispatch(WIZARD_MANAGER__EDIT_RECORD, {
			filesToEdit: result,
			appSysId: editRecordAppSysId,
		});

		updateState({ editRecordAppSysId: null });
	},

	[WIZARD_MANAGER__FETCH_TAXONOMY_FILES_FAILED]: ({ action, updateState }) => {
		action.stopPropagation();

		updateState({ editRecordAppSysId: null });

		console.error(
			'Failed to fetch the application files. Cannot open the requested builder tabs.'
		);
	},
	[TEMPLATE_DIRTY_SET]: ({ updateState }) => {
		updateState({ templateDirty: true });
	},
};

/**
 * template page
 *
 * ```jsx
 * <sn-aes-wizard-manager />
 * ```
 *
 * @seismicElement sn-aes-wizard-manager
 * @summary A page for a template's guided wizard to be displayed.
 */
createCustomElement('sn-aes-wizard-manager', {
	view: state => {
		const { TemplateComponent, properties } = state;
		if (!TemplateComponent || !(properties.operations || []).length) {
			return null;
		}

		const aotManagerProps = getAOTManagerProps(state);
		return <TemplateComponent {...aotManagerProps} />;
	},

	initialState: {
		TemplateComponent: null,
		createAppSysId: null,
		editRecordAppSysId: null,
		initialOperations: null,
		templateDirty: false,
	},

	properties: {
		// An array of templates to execute in order.
		operations: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						params: {
							type: 'object',
							properties: {
								templateId: { type: 'string' },
								appId: { type: 'string' },
							},
						},
						operation: { type: 'string' },
					},
				},
			},
		},
		templates: {
			default: PROVIDER_PROPERTIES[TEMPLATE_DATA_PROPERTY],
		},
	},

	actions: {
		'SN_TEMPLATE_MANAGER#TEMPLATE_EDIT_RECORD': {
			schema: {
				type: 'object',
				properties: {
					sys_app: { type: 'object' },
					key: { type: 'string' },
					result: {
						oneOf: [
							{
								type: 'array',
								items: { type: 'string' },
							},
							{ type: 'string' },
						],
					},
				},
				required: ['sys_app', 'key', 'result'],
			},
		},
	},
	actionHandlers,
});
