import { ADMIN_AES_CONTACT } from '../behaviors/aes-configuration';

/**
 * Returns the admin contact if present
 *
 * @param {array} aesConfig
 * @returns {string}
 */
export default function getAdminContact(aesConfig) {
	var contact = aesConfig.find(config => config.name === ADMIN_AES_CONTACT);

	return contact && contact.value;
}
