// Taxonomy data actions - dispatch TAXONOMY_DATA_BEHAVIOR__INITIAL_FETCH from pages to ensure
// that the taxonomy data is availabe. If it had been previously fetched, it won't try to
// fetch it again. To force a refresh, dispatch either of the individual fetch actions
// and re-linking will occur.
export const TAXONOMY_DATA_BEHAVIOR__INITIAL_FETCH = `${BEHAVIOR_NAME}#INITIAL_FETCH`;

