import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-icon';
import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import '@servicenow/now-loader';
import { transitionTo } from '../../behaviors/router';
import { t } from 'sn-translate';
import '../../components/sn-aes-image-card';
import '../../components/sn-aes-search-input';
import '../../components/sn-aes-container';
import '../../components/sn-aes-section-header';
import '../../components/sn-aes-wizard-overlay';

import { createObjectSearchIndex, getFilterFnForInputLength } from '../../helpers/object-search';

import { MY_APPS__CREATE_APP_CLICKED, MY_APPS__SET_SEARCH_INDEX } from './constants';
import { IMAGE_CARD__CARD_CLICKED } from '../../components/sn-aes-image-card/constants';
import { SEARCH_INPUT__CHANGED } from '../../components/sn-aes-search-input/constants';
import {
	WIZARD_OVERLAY__CLOSED,
	WIZARD_OVERLAY__TEMPLATE_COMPLETED,
} from '../../components/sn-aes-wizard-overlay/constants';
import { APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS } from '../../behaviors/apps-data';
import { PROVIDER_PROPERTIES } from '../../components/sn-aes-provider';

import styles from './_sn-aes-page-my-apps.scss';
import { CREATE_APPLICATION_OPERATION } from '../../components/sn-aes-wizard-manager/constants';
import { SEARCH_INPUT_MINIMUM_LENGTH } from '../../helpers/constants';
import { CATEGORY_BEHAVIOR__INITIAL_FETCH } from '../../behaviors/category';
import '../../components/sn-aes-empty-state';
import { sidebarDisplay } from '../../behaviors/sidebar-data';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const SORT_BY_OPTIONS = [
	{ id: 'dateDesc', label: t('Sort (newest)') },
	{ id: 'dateAsc', label: t('Sort (oldest)') },
	{ id: 'nameAsc', label: t('Sort (a to z)') },
	{ id: 'nameDesc', label: t('Sort (z to a)') },
];

const sortQueryParams = sortStrategy => ({
	sort_order: /Asc$/.test(sortStrategy) ? 'asc' : 'desc',
	sort_field: /^name/.test(sortStrategy) ? 'name' : 'updated',
});

const filterApplications = getFilterFnForInputLength(SEARCH_INPUT_MINIMUM_LENGTH);

function renderFilteredCards(filteredApplications, completedRequest) {
	if (!completedRequest) {
		return <now-loader />;
	} else if (completedRequest && filteredApplications.length === 0) {
		const content = (
			<p className="empty-state-message">
				<span>{t('Create an app from scratch or use a ')}</span>
				<now-button-bare
					label="template"
					variant="secondary"
					appendToPayload={{ source: 'myAppsGoToTemplates' }}
				/>
				<span>.</span>
			</p>
		);
		return <sn-aes-empty-state message={content} />;
	} else {
		return (
			<div className="four-up">
				{filteredApplications.map(({ name, sys_id, illustration, updated }) => (
					<sn-aes-image-card
						{...(illustration ? { maxSize: true } : { coverSize: true })}
						id={sys_id}
						key={sys_id}
						cardImage={illustration}
						footerTitle={name.display_value}
						footerDescription={t('Last updated on {0}', updated.display_value)}
					/>
				))}
			</div>
		);
	}
}

function view(state) {
	const {
		filteredApplications,
		appSearchInput,
		sortStrategy,
		properties,
		OverlayComponent,
	} = state;

	return (
		<sn-aes-container className="my-apps-page" expandable>
			{OverlayComponent ? <OverlayComponent /> : null}

			<section
				className="page"
				aria-hidden={OverlayComponent ? 'true' : false}
				class={{ hidden: OverlayComponent }}>
				<header className="header-bar">
					<div className="now-align -inline">
						<sn-aes-section-header icon="grid-four-fill" headerText={t('My Apps')} />
						<div className="now-align -inline-end">
							<now-button
								variant="primary"
								className="now-m-inline-start--lg my-apps__create-app-button"
								label={t('Create app')}
								append-to-payload={{
									source: 'createAppButton',
								}}
							/>
						</div>
					</div>

					<div className="now-align -inline -show-inline">
						<div className="search-container">
							<sn-aes-search-input
								inputValue={appSearchInput}
								name="my-apps-search-input"
								className="search-input"
								placeholder={t('Search all apps...')}
							/>
						</div>

						<now-dropdown
							items={SORT_BY_OPTIONS}
							select="single"
							selected-items={[sortStrategy]}
							variant="tertiary"
							bare
							showPadding
						/>
					</div>
				</header>
				{renderFilteredCards(filteredApplications, properties.completedRequest)}
			</section>
		</sn-aes-container>
	);
}

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ state, properties: { applications }, dispatch }) => {
		// This is required to ensure that sidebar doesn't flicker
		// switching between apps with different sidebar open status
		sidebarDisplay(dispatch, { isDisplayed: false });

		dispatch(CATEGORY_BEHAVIOR__INITIAL_FETCH);

		const requestPayload = sortQueryParams(state.sortStrategy);
		const { sort_order, sort_field } = requestPayload;
		dispatch(APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS, {
			sort_order,
			sort_field,
		});
		dispatch(MY_APPS__SET_SEARCH_INDEX, { applications });
	},

	[COMPONENT_PROPERTY_CHANGED]: ({
		action: {
			payload: { name, value: applications },
		},
		dispatch,
	}) => {
		// This will catch whenever applications have been re-requested, to rebuild the index
		if (name !== 'applications') {
			return;
		}
		dispatch(MY_APPS__SET_SEARCH_INDEX, { applications });
	},

	[MY_APPS__SET_SEARCH_INDEX]: ({
		action: {
			payload: { applications },
		},
		state,
		updateState,
	}) => {
		const { appSearchInput } = state;
		const applicationsIndex = createObjectSearchIndex(
			'sys_id',
			['name.display_value', 'description.display_value'],
			applications
		);

		updateState({
			applicationsIndex,
			filteredApplications: filterApplications(
				{
					list: applications,
					index: applicationsIndex,
				},
				appSearchInput
			),
		});
	},

	[IMAGE_CARD__CARD_CLICKED]: ({ action, dispatch }) => {
		const { id: sysId } = action.payload;
		transitionTo(dispatch, 'my-apps.app', { sysId });
	},

	[MY_APPS__CREATE_APP_CLICKED]: ({ updateState }) => {
		const operations = [{ operation: CREATE_APPLICATION_OPERATION }];
		updateState({
			OverlayComponent: () => (
				<sn-aes-wizard-overlay opened={true} operations={operations} showGuidance={true} />
			),
		});
	},

	[WIZARD_OVERLAY__TEMPLATE_COMPLETED]: ({ action, dispatch }) => {
		const { sysId } = action.payload;
		transitionTo(dispatch, 'my-apps.app', { sysId });
	},

	[WIZARD_OVERLAY__CLOSED]: ({ updateState }) => {
		updateState({
			OverlayComponent: null,
		});
	},

	[SEARCH_INPUT__CHANGED]: ({
		action,
		state: { applicationsIndex },
		properties: { applications },
		updateState,
	}) => {
		action.stopPropagation();
		const { value: appSearchInput } = action.payload;
		updateState({
			appSearchInput,
			filteredApplications: filterApplications(
				{
					list: applications,
					index: applicationsIndex,
				},
				appSearchInput
			),
		});
	},

	['NOW_DROPDOWN#SELECTED_ITEMS_SET']: ({ action, updateState, dispatch }) => {
		action.stopPropagation();
		const [sortStrategy] = action.payload.value;
		updateState({
			sortStrategy,
		});
		const requestQuery = sortQueryParams(sortStrategy);
		const { sort_order, sort_field } = requestQuery;
		dispatch(APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS, {
			sort_order: sort_order,
			sort_field: sort_field,
		});
	},
	['NOW_BUTTON#CLICKED']: ({ action, dispatch }) => {
		action.stopPropagation();
		const { source } = action.payload;

		if (source === 'createAppButton') {
			dispatch(MY_APPS__CREATE_APP_CLICKED);
		}
	},
	['NOW_BUTTON_BARE#CLICKED']: ({ action, dispatch }) => {
		action.stopPropagation();
		const { source } = action.payload;

		if (source === 'myAppsGoToTemplates') {
			transitionTo(dispatch, 'templates');
		}
	},
};

/**
 * My Apps page
 *
 * ```jsx
 * <sn-aes-page-my-apps />
 * ```
 *
 * @seismicElement sn-aes-page-my-apps
 * @summary This page displays all apps that have been created by the user
 */
createCustomElement('sn-aes-page-my-apps', {
	view,
	initialState: {
		appSearchInput: '',
		sortStrategy: 'dateDesc',
		applicationsIndex: {},
		filteredApplications: [],
		OverlayComponent: null,
	},
	properties: {
		applications: {
			default: PROVIDER_PROPERTIES.applicationsDataProperty,
		},
		appsInitialFetch: {
			default: PROVIDER_PROPERTIES.appsInitialFetch,
		},
		completedRequest: {
			default: PROVIDER_PROPERTIES.appsRequestCompleted,
		},
	},
	actionHandlers,
	styles,
});
