import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-aes-section-header.scss';

createCustomElement('sn-aes-section-header', {
	view: ({ properties }) => {
		const { icon, iconSize, headerText, headerTextSize } = properties;

		const renderHeaderText = () => {
			if (headerTextSize === 'md') {
				return <h2 className="header-text md">{headerText}</h2>;
			}

			if (headerTextSize === 'sm') {
				return <h3 className="header-text sm">{headerText}</h3>;
			}

			return <h1 className="header-text lg">{headerText}</h1>;
		};

		const renderIcon = () => {
			if (icon) {
				return (
					<div className="icon-container">
						<now-icon icon={icon} size={iconSize} />
					</div>
				);
			}
			return null;
		};

		return (
			<div className="section-header">
				{renderIcon()}
				{renderHeaderText()}
			</div>
		);
	},
	properties: {
		icon: {},
		iconSize: { default: 'lg' },
		headerText: {},
		headerTextSize: { default: 'lg' },
	},
	styles,
});
