import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

import '@servicenow/now-icon';

import '../sn-aes-with-tooltip';

import styles from './sn-aes-text-card.scss';
import { TEXT_CARD__CLICKED } from './constants';

function TileIcon({ id, cardImage, greyOut, name }) {
	return (
		<div
			id={id}
			key={id}
			class={{
				icon: 'true',
				greyOut,
			}}>
			<img src={cardImage || 'default_spoke_icon.png'} alt={name} />
		</div>
	);
}

function IconCountBadge({ icons }) {
	return (
		<div className="count-badge">
			<span className="badge-count">+{icons.length}</span>
		</div>
	);
}

function renderIcons(iconsList = [], cardId) {
	let renderIcons = [];
	let hiddenIcons = [];
	if (iconsList.length > 6) {
		renderIcons = iconsList.slice(0, 5);
		hiddenIcons = iconsList.slice(5);
	} else {
		renderIcons = iconsList;
	}

	return (
		<div className="icons-wrapper now-align -inline-start">
			{renderIcons.map(icon => (
				<sn-aes-with-tooltip tooltip-content={icon.name} id={icon.id} delay={500}>
					<TileIcon
						id={`${icon.id}_${cardId}`}
						cardImage={icon.illustration}
						greyOut={icon.greyOut}
						name={icon.name}
					/>
				</sn-aes-with-tooltip>
			))}
			{hiddenIcons.length ? <IconCountBadge icons={hiddenIcons} /> : null}
		</div>
	);
}

createCustomElement('sn-aes-text-card', {
	view(
		{
			properties: { id, empty, cardTitle, emptyText, description, iconList },
		},
		{ dispatch }
	) {
		return (
			<div
				className="text-card-wrapper"
				on-click={action => {
					action.stopPropagation();
					dispatch(TEXT_CARD__CLICKED, {
						id,
						empty,
					});
				}}>
				<button
					aria-label={empty ? emptyText : cardTitle}
					className="text-card-button"
					on-click={action => {
						action.stopPropagation();
						dispatch(TEXT_CARD__CLICKED, {
							id,
							empty,
						});
					}}
				/>
				<div className="text-card">
					{empty ? (
						<div className="empty now-align -inline-center -block-center">
							<div className="content-wrapper">
								<div className="add-icon now-align -inline-center">
									<now-icon icon="circle-plus-fill" size="xl" />
								</div>
								<div className="text">{emptyText}</div>
							</div>
						</div>
					) : (
						<Fragment>
							<div className="card-title">{cardTitle}</div>
							{renderIcons(iconList, id)}
							{description ? (
								<div className="card-description">{description}</div>
							) : null}
						</Fragment>
					)}
				</div>
			</div>
		);
	},
	properties: {
		/**
		 * Card id.
		 * @type {id}
		 */
		id: { default: '' },
		/**
		 * Card title/name text.
		 * @type {string}
		 */
		cardTitle: { default: '' },
		/**
		 * Card description text.
		 * @type {string}
		 */
		description: { default: '' },
		/**
		 * Card icon list.
		 * @type {[{ id: string, illustration: string]}]}
		 */
		iconList: { default: [] },
		/**
		 * Flag for showing empty flow card
		 * @type {boolean}
		 */
		empty: { default: false },
		/**
		 * Flow card empty text
		 * @type {string}
		 */
		emptyText: { default: '' },
	},
	styles,
});
