/**
 * Creates a Date object from a string formatted with date/time values. If the Date is not valid - IE a function call
 * to the new Dates getTime() function returns NaN - then we replace dashes ('-' with '/') and try to create the date
 * object again. NOTE: Safari does not like dashes ('-') in the string passed into a Date instantiation.
 * @param dateString - a string with a date/time value used to create a new Date object.
 * @returns {Date} - a valid instance of a Date object
 */
function getValidDate(dateString) {
	let date = new Date(dateString);
	if (isNaN(date.getTime())) {
		// dates formatted with dashes do not work in Safari
		date = new Date(dateString.replace(/-/g, '/'));
	}

	return date;
}

/**
 * The main power behind this entire file. Any time we need to add a new data type to sort,
 * we can just add a new entry on this object with a function that will do so.
 *
 * All functions defined here are defined assuming ascending order. When called by `createSelectSort`
 * the direction of the sort will be evaluated.
 *
 * Adding a new sorting strategy here will also add it to the `selectorSortMap` below automatically...
 * with its ascending and decending variants of course.
 */
const SORTING_STRATEGIES = {
	/**
	 * A function that defines the sort order for a collection of values by ascending date.
	 * @param {string} a - an object containing a date string
	 * @param {string} b - an object containing a date string
	 * @returns {number}
	 */
	date: (a, b) => getValidDate(a) - getValidDate(b),

	/**
	 * A function that defines the sort order for a collection of values by ascending name.
	 * @param {string} a - an object containing a string
	 * @param {string} b - an object containing a string
	 * @returns {number}
	 */
	string: (a, b) => a.localeCompare(b),
};

/**
 * This function creates _another_ function that you can pass into `Array.prototype.sort`.
 *
 * @example
 * const data = [
 * 	{ name: { display_value: 'Animorph' }},
 * 	{ name: { display_value: 'Xenomorph' }}
 * ];
 * const sortNameDesc = createSelectSort('string:desc', item => item.name.display_value);
 * data.sort(sortNameDesc); // =>
 * [
 * 	{ name: { display_value: 'Xenomorph' }}
 * 	{ name: { display_value: 'Animorph' }},
 * ];
 *
 * @public
 * @param {string} sortingStrategy A string denoting `type:strategy` of the sort
 * @param {(item: any) => any} selectCallback A function denoting how to select the desired value from the item
 */
export function createSelectSort(sortingStrategy, selectCallback = value => value) {
	if (typeof sortingStrategy !== 'string') {
		throw new TypeError(`[createSelectSort] sortingStrategy must be a string`);
	}

	const [type, strategy] = sortingStrategy.toLowerCase().split(':');
	const sortCallback = SORTING_STRATEGIES[type];
	const ascending = 'asc' === strategy;

	if (typeof sortCallback !== 'function') {
		throw new TypeError(`[createSelectSort] unsupported data type '${type}'`);
	}

	return (a, b) => {
		const first = selectCallback(a);
		const second = selectCallback(b);

		return ascending ? sortCallback(first, second) : sortCallback(second, first);
	};
}

/**
 * Maps each intersection of sorting type definitions with their ascending or descending variant,
 * and fills them into `createSelectSort` automatically.
 *
 * @example
 * const sortNameDesc = createSelectSort('string:desc', item => item.name.display_value);
 * const selectSortNameDesc = selectSortMap.createStringDescSort(item => item.name.display_value);
 * sortNameDesc == selectSortNameDesc
 *
 */
export const selectSortMap = (() => {
	const map = {};
	const strategies = ['asc', 'desc'];
	const capitalize = string => string.charAt(0).toUpperCase() + string.slice(1);

	for (const type of Object.keys(SORTING_STRATEGIES)) {
		for (const strategy of strategies) {
			const sortingStrategy = `${type}:${strategy}`;
			const mapKey = `create${capitalize(type)}${capitalize(strategy)}Sort`;
			map[mapKey] = createSelectSort.bind(null, sortingStrategy);
		}
	}

	return map;
})();

/**
 * When a sort option is selected from the sort menu, this map is used to determine which sort function to run.
 *
 * @public
 * @deprecated
 * @type {{nameDesc: (function(*, *): number), nameAsc: (function(*, *): number), dateAsc: (function(*, *): number), dateDesc: (function(*, *): number)}}
 */
export const sortMap = {
	dateDesc: selectSortMap.createDateDescSort(item => item.updated.value),
	dateAsc: selectSortMap.createDateAscSort(item => item.updated.value),
	nameDesc: selectSortMap.createStringDescSort(item => item.name),
	nameAsc: selectSortMap.createStringAscSort(item => item.name),
};
