import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;

import {
	BEHAVIOR_NAME,
	LOCAL_STORAGE_PROPERTY,
	SESSION_STORAGE_PROPERTY,
	STORAGE_BEHAVIOR__SET_STORAGE_ITEM,
	STORAGE_BEHAVIOR__UPDATE_STORAGE_ITEM,
	STORAGE_BEHAVIOR__CLEAR_STORAGE_ITEM,
} from './constants';
import { typeOf, loadData, storeData, clearStorageItem } from './helpers';

export default {
	name: BEHAVIOR_NAME,

	properties: {
		[LOCAL_STORAGE_PROPERTY]: {
			default: {},
			selectable: true,
		},

		[SESSION_STORAGE_PROPERTY]: {
			default: {},
			selectable: true,
		},
	},

	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ updateProperties }) => {
			updateProperties({
				[LOCAL_STORAGE_PROPERTY]: loadData(LOCAL_STORAGE_PROPERTY),
				[SESSION_STORAGE_PROPERTY]: loadData(SESSION_STORAGE_PROPERTY),
			});
		},

		[STORAGE_BEHAVIOR__SET_STORAGE_ITEM]: ({ properties, action, updateProperties }) => {
			action.stopPropagation();
			const { key, value } = action.payload;
			const { propName } = action.meta;
			const { [propName]: storageData } = properties;

			storeData(propName, key, value);
			updateProperties({
				[propName]: {
					...storageData,
					[key]: value,
				},
			});
		},

		[STORAGE_BEHAVIOR__UPDATE_STORAGE_ITEM]: ({ properties, action, updateProperties }) => {
			action.stopPropagation();
			const { key, value } = action.payload;
			const { propName } = action.meta;
			const { [propName]: storageData } = properties;
			let { [key]: data } = storageData;
			const dataType = typeOf(data);

			if (dataType === 'object') {
				data = {
					...data,
					...value,
				};
			} else if (dataType === 'array') {
				data = data.concat(value);
			} else {
				data = value;
			}

			storeData(propName, key, data);
			updateProperties({
				[propName]: {
					...storageData,
					[key]: data,
				},
			});
		},

		[STORAGE_BEHAVIOR__CLEAR_STORAGE_ITEM]: ({ properties, action, updateProperties }) => {
			action.stopPropagation();
			const { key } = action.payload;
			const { propName } = action.meta;
			const { [propName]: storageData } = properties;
			delete storageData[key];

			clearStorageItem(propName, key);
			updateProperties({
				[propName]: {
					...storageData,
				},
			});
		},
	},
};
