export class RouteTransaction {
	/** @type {Map<string, Set<function>>} */
	handlers = new Map();

	/**
	 * @param {BrowserRouter} router
	 */
	constructor(router) {
		this.router = router;
	}

	run(routeName, routeState = {}) {
		const { router } = this;

		if (!router.hasRoute(routeName)) {
			this.emit('notfound', { routeName, routeState });
			return;
		}

		let reroutePayload = null;
		const { currentRouteName, currentHistoryState: currentRouteState } = router;
		const { onExit } = router.getRouteOptions(currentRouteName) || {};
		const { onEnter, initialState } = router.getRouteOptions(routeName) || {};

		const transactionHelpers = {
			transitionTo: (routeName, routeState) => {
				if (!reroutePayload) {
					const type = 'transition';
					reroutePayload = { type, routeName, routeState };
				}
			},
			replaceWith: (routeName, routeState) => {
				if (!reroutePayload) {
					const type = 'replacement';
					reroutePayload = { type, routeName, routeState };
				}
			},
			updateRouteState: patch => {
				if (patch && patch.constructor === Object) {
					routeState = {
						...routeState,
						...patch,
					};
				}
			},
		};

		if (initialState && typeof initialState === 'object') {
			routeState = {
				...initialState,
				...routeState,
			};
		}

		// if you're currently in a route and you're not transitioning into itself
		// (which would mean you're not actually "exiting" the route)
		if (currentRouteName && currentRouteName !== routeName && typeof onExit === 'function') {
			onExit({
				currentRouteName,
				currentRouteState,
				targetRouteName: routeName,
				targetRouteState: routeState,
				...transactionHelpers,
			});

			// Rerouting onExit means we're cancelling the current transition early
			// exit here
			if (reroutePayload) {
				this.emit('reroute', reroutePayload);
				return;
			}
		}

		if (typeof onEnter === 'function') {
			onEnter({
				routeName,
				routeState,
				...transactionHelpers,
			});
		}

		this.emit('complete', {
			routeName,
			routeState,
		});

		// Be sure to emit the completion of the current route before emitting the reroute
		if (reroutePayload) {
			this.emit('reroute', reroutePayload);
		}
	}

	emit(name, payload) {
		const handlerSet = this.handlers.get(name);

		if (handlerSet == null) {
			return;
		}

		for (const handler of handlerSet.values()) {
			handler(payload);
		}
	}

	on(name, handler) {
		if (typeof handler !== 'function') {
			throw new TypeError(`[RouteTransaction] Expected handler to be a function`);
		}

		const { handlers } = this;
		/** @type {Set<function>} */
		let handlerSet;

		if (handlers.has(name)) {
			handlerSet = handlers.get(name);
		} else {
			handlerSet = new Set();
			handlers.set(name, handlerSet);
		}

		handlerSet.add(handler);
	}
}

export class RouteTransition extends RouteTransaction {
	constructor(router) {
		super(router);
		this.on('complete', ({ routeName, routeState }) => {
			router.pushRouteState(routeState, routeState, routeName);
		});
	}
}

export class RouteReplacement extends RouteTransaction {
	constructor(router) {
		super(router);
		this.on('complete', ({ routeName, routeState }) => {
			router.replaceRouteState(routeState, routeState, routeName);
		});
	}
}
