import '@servicenow/now-button';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import styles from './sn-aes-confirm.scss';

const { COMPONENT_DISCONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;
import { t } from 'sn-translate';

import { ROOT__HIDE_MODAL } from '../sn-app-engine-studio/constants';

createCustomElement('sn-aes-confirm', {
	view: ({ properties }) => {
		const { primaryText, secondaryText, headerLabel, content, size } = properties;
		return (
			<now-modal
				opened
				manage-opened
				size={size}
				headerLabel={headerLabel}
				footerActions={[
					{
						id: 'primary',
						size: 'md',
						variant: 'primary',
						label: primaryText,
					},
					{
						id: 'secondary',
						size: 'md',
						variant: 'tertiary',
						label: secondaryText,
					},
				]}>
				<p className="custom-content"> {content}</p>
			</now-modal>
		);
	},
	properties: {
		headerLabel: { default: t('Leave without saving your edits?') },
		content: {
			default: t('Changes will be lost if you leave now.'),
		},
		size: { default: 'md' },
		primaryText: { default: t('Yes, leave') },
		secondaryText: { default: t('No, keep working') },
		nextFunction: { default: null },
		cancelFunction: { default: null },
	},
	initialState: {
		operationCompleted: false,
	},
	actionHandlers: {
		['NOW_MODAL#FOOTER_ACTION_CLICKED']: ({
			updateState,
			properties: { nextFunction, cancelFunction },
			action,
			dispatch,
		}) => {
			action.stopPropagation();
			if (action.payload.action.id === 'primary' && nextFunction) {
				// OK has been clicked, so we have to override the save status
				// and continue the run on the router
				nextFunction();
			}

			if (action.payload.action.id === 'secondary' && cancelFunction) {
				// Cancel has been clicked, so we have to reset all the delegates
				cancelFunction();
			}

			// Always hide modal
			dispatch(ROOT__HIDE_MODAL);
			// current modal's operation has been completed
			updateState({ operationCompleted: true });
		},

		// We need these lifecycle methods so that we can check for modal closed
		// using the close icon. Since the close icon bypasses the normal flow,
		// this should be treated as a cancel operation
		[COMPONENT_DISCONNECTED]: ({
			state: { operationCompleted },
			properties: { cancelFunction },
		}) => {
			if (!operationCompleted && cancelFunction) {
				cancelFunction();
			}
		},

		// Modal property changed, so it's a new instance of the modal
		[COMPONENT_PROPERTY_CHANGED]: ({ updateState }) => {
			updateState({ operationCompleted: false });
		},
	},
	styles,
});
