import { BUILDER_UPDATE } from './constants';

import applyDevStudioAPI from './dev-studio-api';

// In Form designer, the elements required to attach listerners are not immediately available and therefore
// retry attaching listeners if it is not already attached. This retry is done for max 20 times.
// Without the listerners, on load of formdesigner, table selector is not hidden and on a change in form designer dirty
// state is not set.
export default async function(iframe, dispatch, appId) {
	const MAX_RETRY_COUNT = 20;
	const applyDMFs = count => {
		setTimeout(() => {
			let areListenersAttached = attachListeners(iframe, dispatch, appId);

			if (!areListenersAttached && count < MAX_RETRY_COUNT) {
				setTimeout(() => applyDMFs(++count), 500);
			} else {
				// Send initial builder state.
				dispatch(BUILDER_UPDATE, {
					isLoading: false,
				});
			}
		}, 500);
	};

	applyDMFs(1);
}

// Attach onLoad and onChange listeners for form designer
function attachListeners(iframe, dispatch, appId) {
	const { contentWindow } = iframe;

	if (getToolbar(contentWindow).length === 0 || getMainContent(contentWindow).length === 0) {
		return false;
	}

	bindLoadListener(contentWindow);
	bindChangeListener(contentWindow, dispatch);
	applyDevStudioAPI(iframe, dispatch, appId);
	return true;
}

// On load of form designer, hide table selector
function bindLoadListener(contentWindow) {
	const toolbar = getToolbar(contentWindow);
	const cancelWatch = toolbar.scope().$watch(
		'views',
		function(views) {
			if (!views || views.length === 0) {
				return;
			}

			hideTableSelector(contentWindow);
			cancelWatch();
		},
		true
	);
}

// Hide the table selector for Form Designer in AES
function hideTableSelector(contentWindow) {
	const tableDropdown = contentWindow.document.querySelector('#sn-fd-table-selector');

	if (tableDropdown) {
		tableDropdown.style.display = 'none';
	}
}

// on change in form designer, dispatch BUILDER_UPDATE event with hasUnsavedChanges = dcFormWatch.changed
function bindChangeListener(contentWindow, dispatch) {
	const toolbar = getToolbar(contentWindow);
	const fields =
		'[dcFormWatch.changed, dcFormWatch._formData.form_select.table, dcFormWatch._formData.form_select.view]';
	if (toolbar.length > 0) {
		toolbar.scope().$watchCollection(fields, function(args) {
			dispatch(BUILDER_UPDATE, {
				hasUnsavedChanges: args[0],
			});
		});
	}
}

function getToolbar(contentWindow) {
	return contentWindow.angular.element('[ng-controller=FDToolbarCtrl]');
}

function getMainContent(contentWindow) {
	return contentWindow.angular.element('[ng-controller=FDMainCtrl]');
}
