import { actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';
import {
	BEHAVIOR_NAME,
	TEMPLATE_DATA_PROPERTY,
	OBJECT_TEMPLATES,
	APPLICATION_TEMPLATES,
	TEMPLATE_DATA_BEHAVIOR__INITIAL_FETCH,
	TEMPLATE_INITIAL_FETCH_PROPERTY,
	FETCH_TEMPLATES_URL,
	TEMPLATE_DATA_BEHAVIOR__FETCH_TEMPLATES,
	TEMPLATE_DATA_BEHAVIOR__TEMPLATES_FETCH_SUCCEEDED,
	TEMPLATE_DATA_BEHAVIOR__TEMPLATES_FETCH_FAILED,
	AOT_MANAGER_KEY,
	AOT_MANAGER_SYS_ID,
	CREATE_APPLICATION_TEMPLATE_KEY,
	CREATE_APPLICATION_TEMPLATE_SYS_ID,
	OBJECT_TEMPLATE_ROLE_DEMO_SYS_ID,
	OBJECT_TEMPLATE_ROLE_DEMO_KEY,
	OBJECT_TEMPLATE_SECURITY_KEY,
	OBJECT_TEMPLATE_SECURITY_SYS_ID,
	OBJECT_TEMPLATE_TABLE_DEMO_SYS_ID,
	OBJECT_TEMPLATE_TABLE_DEMO_KEY,
	TEMPLATE_CATEGORY_DATA_PROPERTY,
	FETCH_TEMPLATE_CATEGORY_URL,
	TEMPLATE_CATEGORY_INITIAL_FETCH_PROPERTY,
	TEMPLATE_DATA_BEHAVIOR__FETCH_TEMPLATE_CATEGORY,
	TEMPLATE_DATA_BEHAVIOR__TEMPLATE_FETCH_CATEGORY_SUCCEEDED,
	TEMPLATE_DATA_BEHAVIOR__TEMPLATE_FETCH_CATEGORY_FAILED,
	CATEGORIZED_TEMPLATES_DATA_PROPERTY,
	TEMPLATE_DATA_BEHAVIOR__UPDATE_OBJECT_TEMPLATES,
	TEMPLATE_REQUEST_COMPLETED_PROPERTY,
	TEMPLATE_CATEGORY_REQUEST_COMPLETED_PROPERTY,
	CATEGORIZED_TEMPLATES_REQUEST_COMPLETED_PROPERTY,
} from './constants';
import { ALERTS_BEHAVIOR__ADD_ITEM } from '../alerts';

const templateDataBehavior = {
	name: BEHAVIOR_NAME,
	properties: {
		[TEMPLATE_DATA_PROPERTY]: {
			default: {
				[AOT_MANAGER_KEY]: AOT_MANAGER_SYS_ID,
				[CREATE_APPLICATION_TEMPLATE_KEY]: CREATE_APPLICATION_TEMPLATE_SYS_ID,
				[OBJECT_TEMPLATE_ROLE_DEMO_KEY]: OBJECT_TEMPLATE_ROLE_DEMO_SYS_ID,
				[OBJECT_TEMPLATE_SECURITY_KEY]: OBJECT_TEMPLATE_SECURITY_SYS_ID,
				[OBJECT_TEMPLATE_TABLE_DEMO_KEY]: OBJECT_TEMPLATE_TABLE_DEMO_SYS_ID,
				[OBJECT_TEMPLATES]: [],
				[APPLICATION_TEMPLATES]: [],
			},
			selectable: true,
		},
		[TEMPLATE_CATEGORY_DATA_PROPERTY]: {
			default: [],
			selectable: true,
		},
		[CATEGORIZED_TEMPLATES_DATA_PROPERTY]: {
			default: {
				[OBJECT_TEMPLATES]: [],
				[APPLICATION_TEMPLATES]: [],
			},
			selectable: true,
		},
		[TEMPLATE_INITIAL_FETCH_PROPERTY]: {
			default: false,
			selectable: true,
		},
		[TEMPLATE_CATEGORY_INITIAL_FETCH_PROPERTY]: {
			default: false,
			selectable: true,
		},
		[TEMPLATE_REQUEST_COMPLETED_PROPERTY]: {
			default: false,
			selectable: true,
		},
		[TEMPLATE_CATEGORY_REQUEST_COMPLETED_PROPERTY]: {
			default: false,
			selectable: true,
		},
		[CATEGORIZED_TEMPLATES_REQUEST_COMPLETED_PROPERTY]: {
			default: false,
			selectable: true,
		},
	},
	actionHandlers: {
		[TEMPLATE_DATA_BEHAVIOR__INITIAL_FETCH]: ({ properties, dispatch }) => {
			if (
				properties[TEMPLATE_INITIAL_FETCH_PROPERTY] &&
				properties[TEMPLATE_CATEGORY_INITIAL_FETCH_PROPERTY]
			) {
				return;
			}
			dispatch(TEMPLATE_DATA_BEHAVIOR__FETCH_TEMPLATES);
			dispatch(TEMPLATE_DATA_BEHAVIOR__FETCH_TEMPLATE_CATEGORY);
		},
		[TEMPLATE_DATA_BEHAVIOR__FETCH_TEMPLATES]: createHttpEffect(FETCH_TEMPLATES_URL, {
			method: 'GET',
			successActionType: TEMPLATE_DATA_BEHAVIOR__TEMPLATES_FETCH_SUCCEEDED,
			errorActionType: TEMPLATE_DATA_BEHAVIOR__TEMPLATES_FETCH_FAILED,
			batch: false,
		}),
		[TEMPLATE_DATA_BEHAVIOR__TEMPLATES_FETCH_SUCCEEDED]: ({ action, updateProperties }) => {
			const { result: templates } = action.payload;

			updateProperties({
				[TEMPLATE_DATA_PROPERTY]: {
					[AOT_MANAGER_KEY]: AOT_MANAGER_SYS_ID,
					[CREATE_APPLICATION_TEMPLATE_KEY]: CREATE_APPLICATION_TEMPLATE_SYS_ID,
					[OBJECT_TEMPLATE_ROLE_DEMO_KEY]: OBJECT_TEMPLATE_ROLE_DEMO_SYS_ID,
					[OBJECT_TEMPLATE_SECURITY_KEY]: OBJECT_TEMPLATE_SECURITY_SYS_ID,
					[OBJECT_TEMPLATE_TABLE_DEMO_KEY]: OBJECT_TEMPLATE_TABLE_DEMO_SYS_ID,
					[OBJECT_TEMPLATES]: templates.filter(
						template => template.application_template === false
					),
					// filter scope app template for citizen developers
					[APPLICATION_TEMPLATES]: templates.filter(
						template =>
							template.application_template === true &&
							template.sys_id !== CREATE_APPLICATION_TEMPLATE_SYS_ID
					),
				},
				[TEMPLATE_INITIAL_FETCH_PROPERTY]: true,
				[TEMPLATE_REQUEST_COMPLETED_PROPERTY]: true,
			});
		},
		[TEMPLATE_DATA_BEHAVIOR__TEMPLATES_FETCH_FAILED]: ({ dispatch, updateProperties }) => {
			updateProperties({
				[TEMPLATE_REQUEST_COMPLETED_PROPERTY]: true,
				[CATEGORIZED_TEMPLATES_REQUEST_COMPLETED_PROPERTY]: true,
			});
			dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
				status: 'critical',
				content: t('We encountered an error while fetching templates.'),
			});
		},
		[TEMPLATE_DATA_BEHAVIOR__FETCH_TEMPLATE_CATEGORY]: createHttpEffect(
			FETCH_TEMPLATE_CATEGORY_URL,
			{
				method: 'GET',
				successActionType: TEMPLATE_DATA_BEHAVIOR__TEMPLATE_FETCH_CATEGORY_SUCCEEDED,
				errorActionType: TEMPLATE_DATA_BEHAVIOR__TEMPLATE_FETCH_CATEGORY_FAILED,
				batch: false,
			}
		),
		[TEMPLATE_DATA_BEHAVIOR__TEMPLATE_FETCH_CATEGORY_SUCCEEDED]: ({
			action,
			updateProperties,
		}) => {
			const { result: objectTemplateCategories } = action.payload;

			updateProperties({
				[TEMPLATE_CATEGORY_DATA_PROPERTY]: objectTemplateCategories,
				[TEMPLATE_CATEGORY_INITIAL_FETCH_PROPERTY]: true,
				[TEMPLATE_CATEGORY_REQUEST_COMPLETED_PROPERTY]: true,
			});
		},
		[TEMPLATE_DATA_BEHAVIOR__TEMPLATE_FETCH_CATEGORY_FAILED]: ({
			dispatch,
			updateProperties,
		}) => {
			updateProperties({
				[TEMPLATE_CATEGORY_REQUEST_COMPLETED_PROPERTY]: true,
				[CATEGORIZED_TEMPLATES_REQUEST_COMPLETED_PROPERTY]: true,
			});

			dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
				status: 'critical',
				content: t('We encountered an error while fetching templates.'),
			});
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action: {
				payload: { name },
			},
			properties,
			updateProperties,
		}) => {
			if (name !== TEMPLATE_DATA_PROPERTY && name !== TEMPLATE_CATEGORY_DATA_PROPERTY) {
				// Early exit if it's neither the templates or templateCategory property
				return;
			}

			if (
				properties[TEMPLATE_INITIAL_FETCH_PROPERTY] &&
				properties[TEMPLATE_CATEGORY_INITIAL_FETCH_PROPERTY]
			) {
				const objectTemplates = properties[TEMPLATE_DATA_PROPERTY][OBJECT_TEMPLATES];
				const objectTemplateCategories = properties[TEMPLATE_CATEGORY_DATA_PROPERTY];
				const objectTemplatesWithCategories = [];
				const objectTemplateCategoryLookup = {};

				objectTemplateCategories.forEach(objectTemplateCategory => {
					const { templateId } = objectTemplateCategory;
					objectTemplateCategoryLookup[templateId] = objectTemplateCategory['categories'];
				});

				objectTemplates.forEach(objectTemplate => {
					const lookupCategory = objectTemplateCategoryLookup[objectTemplate.sys_id];
					objectTemplatesWithCategories.push({
						...objectTemplate,
						categories: lookupCategory || [],
					});
				});

				updateProperties({
					[CATEGORIZED_TEMPLATES_DATA_PROPERTY]: {
						[OBJECT_TEMPLATES]: objectTemplatesWithCategories,
						[APPLICATION_TEMPLATES]: [],
					},
					[CATEGORIZED_TEMPLATES_REQUEST_COMPLETED_PROPERTY]: true,
				});
			}
		},
		[TEMPLATE_DATA_BEHAVIOR__UPDATE_OBJECT_TEMPLATES]: ({
			action: {
				payload: { templates },
			},
			updateProperties,
			properties,
		}) => {
			const objectTemplates =
				properties[CATEGORIZED_TEMPLATES_DATA_PROPERTY][OBJECT_TEMPLATES];

			updateProperties({
				[CATEGORIZED_TEMPLATES_DATA_PROPERTY]: {
					...properties[CATEGORIZED_TEMPLATES_DATA_PROPERTY],
					[OBJECT_TEMPLATES]: objectTemplates.map(template => {
						const updatedTemplate = templates.find(
							newtemplate => newtemplate.sys_id === template.sys_id
						);
						return updatedTemplate || template;
					}),
				},
			});
		},
	},
};

export default templateDataBehavior;
