import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { updateAppPropertiesEffect } from './effects';
import { t } from 'sn-translate';

import { ROOT__HIDE_MODAL, ROOT__SHOW_MODAL } from '../sn-app-engine-studio/constants';
import { ALERTS_BEHAVIOR__ADD_ITEM } from './../../behaviors/alerts';
import {
	APP_PROPERTIES__DATA_LOADED,
	APP_PROPERTIES__SUBMITTED,
	APP_PROPERTIES__UPDATE_SUCCEEDED,
	APP_PROPERTIES__UPDATE_FAILED,
} from './constants';
import {
	FILE_SET,
	FILE_REMOVE,
	EXISTING_IMG_REMOVED,
} from './../sn-aes-gac-image-uploader/constants';

import {
	uploadImageEffect,
	UPLOAD_SUCCESS,
	UPLOAD_FAILURE,
} from './../../helpers/upload-image-effect';
import { DELETE_APPLICATION } from '../../helpers/constants';
import getEffectDetails from '../../helpers/get-effect-details';

import '@servicenow/now-loader';
import '@servicenow/now-input';
import '@servicenow/now-button';
import '@servicenow/now-modal';
import './../sn-aes-copy-text';
import './../sn-aes-gac-image-uploader';
import '../sn-aes-confirm-delete-generic';

import styles from './sn-aes-app-properties.scss';
import { PROVIDER_PROPERTIES } from '../sn-aes-provider';
import getAdminContact from '../../helpers/get-admin-contact';
import { generateStyleToolTipContent } from '../../helpers/sn-naming-helpers';

function getReadOnlyField(label, value) {
	return (
		<Fragment>
			<span>
				<span className="readonly-property-label">{label}</span>
				<now-popover className="now-form-field-popover" interaction-type="none">
					<now-button-iconic
						slot="trigger"
						config-aria={{
							button: { 'aria-label': t('More information') },
						}}
						className={{ 'now-form-field-button': true }}
						icon="circle-info-outline"
						size="sm"
						variant="tertiary"
						bare
						hide-padding
					/>
					<now-form-popover-content slot="content" content={label} role="status" />
				</now-popover>
			</span>
			<p className="readonly-property-value">{value}</p>
		</Fragment>
	);
}

createCustomElement('sn-aes-app-properties', {
	view: state => {
		const {
			isDataLoading,
			isSubmitting,
			localName,
			localShortDescription,
			localIllustration,
		} = state;
		const { scope_name, created, updated, sys_id } = state.properties.app;
		const footerActions = [
			{
				id: 'save',
				size: 'md',
				variant: 'primary',
				label: t('Save'),
				disabled: localName === '' || isSubmitting,
			},
			{
				id: 'cancel',
				size: 'md',
				variant: 'secondary',
				label: t('Cancel'),
			},
			{
				id: 'delete',
				size: 'md',
				variant: 'primary-negative',
				label: t('Delete application'),
			},
		];

		return (
			<now-modal
				footerActions={footerActions}
				opened
				manage-opened
				size="lg"
				headerLabel={t('Edit application properties')}>
				{isDataLoading ? (
					<div className="loader-container">
						<now-loader className="loader" label={t('Loading...')} size="lg" />
					</div>
				) : (
					<div className="app-properties">
						<div className="inputs">
							<div>
								<div className="input-group">
									<now-input
										type="text"
										name="localName"
										label={t('Name')}
										placeholder={t('Enter app name')}
										value={localName}
										helperContent={generateStyleToolTipContent(
											t(
												"This name applies to the container and also shows up in the ServiceNow App store. It's not the name of a Workspace or Mobile application."
											)
										)}
										size="lg"
										autocomplete="off"
										maxlength={100}
										required
									/>
								</div>
								<div className="input-group">
									<now-input
										type="text"
										name="localShortDescription"
										label={t('Description')}
										placeholder={t('Describe your app')}
										value={localShortDescription}
										helperContent={generateStyleToolTipContent(
											t(
												"This description shows up in the ServiceNow App store. It can't be more than 160 characters long."
											)
										)}
										size="lg"
										autocomplete="off"
										maxlength={500}
									/>
								</div>

								<div id="scope" className="readonly-property">
									{getReadOnlyField(t('Scope'), scope_name)}
								</div>
								<div className="readonly-property-columns">
									<div id="date_created" className="readonly-property">
										{getReadOnlyField(t('Date created'), created.display_value)}
									</div>
									<div id="date_modified" className="readonly-property">
										{getReadOnlyField(
											t('Last modified'),
											updated.display_value
										)}
									</div>
								</div>
								<sn-aes-copy-text textToCopy={sys_id}>
									<div id="sys_id" className="readonly-property">
										{getReadOnlyField(t('sys_app ID'), sys_id)}
									</div>
								</sn-aes-copy-text>
							</div>
							<sn-aes-gac-image-uploader
								existingImageUrl={localIllustration}
								label={t('Drag app logo or browse to upload')}
							/>
						</div>
					</div>
				)}
			</now-modal>
		);
	},
	properties: {
		app: {
			default: {},
		},
		onUpdateSuccess: {
			default: null,
		},
		onDeleteSuccess: {
			default: () => {},
		},
		prodInstanceAesConfig: {
			default: PROVIDER_PROPERTIES.prodInstanceAesConfig,
		},
		prodAdminContact: {
			computed({ properties: { prodInstanceAesConfig } }) {
				return getAdminContact(prodInstanceAesConfig);
			},
		},
	},
	initialState: {
		isDataLoading: true,
		isSubmitting: false,
		localName: '',
		localShortDescription: '',
		localIllustration: null,
		fileUpload: {
			hasSelectedFile: false,
			selectedFile: null,
		},
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ state, dispatch, updateState }) => {
			const { name, description, illustration } = state.properties.app;

			updateState({
				localName: name.display_value,
				localShortDescription: description.display_value,
				localIllustration: illustration,
			});
			dispatch(APP_PROPERTIES__DATA_LOADED);
		},
		[APP_PROPERTIES__DATA_LOADED]: ({ updateState }) => {
			updateState({
				isDataLoading: false,
			});
		},
		// Still update state when value is invalid
		// since we're handling validation manually by checking value in state
		['NOW_INPUT#INVALID_SET']: ({ action, updateState }) => {
			action.stopPropagation();

			const { name, fieldValue } = action.payload;

			updateState({
				[name]: fieldValue,
			});
		},
		['NOW_INPUT#VALUE_SET']: ({ action, updateState }) => {
			action.stopPropagation();

			const { name, value } = action.payload;

			updateState({
				[name]: value,
			});
		},
		[EXISTING_IMG_REMOVED]: ({ updateState }) => {
			updateState({
				localIllustration: '',
			});
		},
		[FILE_SET]: ({ state: { fileUpload }, action: { payload }, updateState }) => {
			updateState({
				fileUpload: {
					...fileUpload,
					selectedFile: payload,
					hasSelectedFile: !!payload,
				},
			});
		},
		[FILE_REMOVE]: ({ state: { fileUpload }, updateState }) => {
			updateState({
				fileUpload: {
					...fileUpload,
					selectedFile: null,
					hasSelectedFile: false,
				},
			});
		},
		['NOW_MODAL#FOOTER_ACTION_CLICKED']: ({
			state,
			properties,
			action,
			dispatch,
			updateState,
		}) => {
			action.stopPropagation();

			const { localName, localShortDescription, localIllustration, fileUpload } = state;
			const { onDeleteSuccess, prodAdminContact } = properties;
			const {
				sys_id,
				name: { display_value },
			} = properties.app;
			const appData = {
				name: localName,
				short_description: localShortDescription,
			};

			if (!(localIllustration || fileUpload.hasSelectedFile)) {
				appData.logo = '';
			}

			if (action.payload.action.id === 'save') {
				updateState({
					isSubmitting: true,
				});
				dispatch(APP_PROPERTIES__SUBMITTED, {
					sysId: sys_id,
					sysparm_transaction_scope: sys_id,
					appData,
				});
			} else if (action.payload.action.id === 'delete') {
				const effectDetails = getEffectDetails(DELETE_APPLICATION, ['application_sys_id']);
				const effectPayload = {
					application_sys_id: sys_id,
				};
				const successAlertPayload = {
					content: t(
						`"{0}" has been deleted from App Engine Studio. If this app has already been published to users,
						contact your system administrator to make it unavailable.`,
						display_value
					),
					textLinkProps: prodAdminContact
						? {
								label: t('Contact your admin'),
								href: `mailto:${prodAdminContact}`,
						  }
						: {},
				};
				const errorAlertPayload = {
					content: t('We encountered an error while deleting {0}.', display_value),
				};

				dispatch(ROOT__SHOW_MODAL, {
					renderer: () => (
						<sn-aes-confirm-delete-generic
							translatedHeaderLabel={t('Are you sure you want to delete this app?')}
							effectDetails={effectDetails}
							effectPayload={effectPayload}
							onDeleteSuccess={onDeleteSuccess}
							successAlertPayload={successAlertPayload}
							errorAlertPayload={errorAlertPayload}>
							<div slot="confirm-text">
								{t(
									'Type {0} to confirm that you want to remove this app from App Engine Studio, including anything built for this app.',
									t('DELETE').toLocaleUpperCase()
								)}
							</div>
						</sn-aes-confirm-delete-generic>
					),
				});
			} else {
				dispatch(ROOT__HIDE_MODAL);
			}
		},
		[APP_PROPERTIES__SUBMITTED]: updateAppPropertiesEffect,
		[APP_PROPERTIES__UPDATE_SUCCEEDED]: ({
			state: { fileUpload },
			properties: { onUpdateSuccess, app },
			action: {
				payload: { result },
			},
			dispatch,
			updateState,
		}) => {
			if (fileUpload.hasSelectedFile) {
				dispatch('UPLOAD_LOGO', {
					sys_id: result.id,
					selectedFile: fileUpload.selectedFile,
					appName: result.name,
				});
				return;
			}

			updateState({
				isSubmitting: false,
			});

			onUpdateSuccess(result.id, result.name, app.version);

			dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
				status: 'positive',
				content: t('Application properties updated successfully.'),
			});
			dispatch(ROOT__HIDE_MODAL);
		},
		[APP_PROPERTIES__UPDATE_FAILED]: ({ dispatch, updateState }) => {
			updateState({
				isSubmitting: false,
			});

			dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
				status: 'critical',
				content: t('We encountered an error while updating Application properties'),
			});
		},
		['UPLOAD_LOGO']: uploadImageEffect,
		[UPLOAD_SUCCESS]: ({
			properties: { onUpdateSuccess, app },
			action: { payload },
			dispatch,
			updateState,
		}) => {
			updateState({
				isSubmitting: false,
			});

			onUpdateSuccess(payload.sys_id, payload.appName, app.version);

			dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
				status: 'positive',
				content: t('Application properties updated successfully.'),
			});
			dispatch(ROOT__HIDE_MODAL);
		},
		[UPLOAD_FAILURE]: ({ dispatch, updateState }) => {
			updateState({
				isSubmitting: false,
			});

			dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
				status: 'critical',
				content: t('We encountered an error while updating Application properties'),
			});
		},
	},
	styles,
});
