import { SEARCH_INPUT_MINIMUM_LENGTH } from './constants';

export const INDEX_META_KEY = `__${Date.now()}_meta__`;

/**
 * Polyfill for Array.prototype.flat from MDN
 *
 * @param {any[]} array
 * @param {number} [depth]
 */
function* flatten(array, depth) {
	if (depth === undefined) {
		depth = 1;
	}

	for (const item of array) {
		if (Array.isArray(item) && depth > 0) {
			yield* flatten(item, depth - 1);
		} else {
			yield item;
		}
	}
}

/**
 * Modified implementation of lodash-style get.
 *
 * Dot walks the object tree via the defined path.
 *
 * If an array is encountered, `get` is recursively called on its items
 * with the remaining path
 *
 * @example
 * const source = {
 * 	results: [
 * 		{ name: 'foo' },
 * 		{ name: 'bar' },
 * 		{ name: 'baz' },
 * 	]
 * }
 * get(source, 'results.name')
 * // => ['foo', 'bar', 'baz']
 *
 * @private
 * @param {any} object
 * @param {string} path
 */
export function get(object, path) {
	const paths = path.split('.');
	let index = 0;

	while (typeof object === 'object' && index < paths.length) {
		const prop = paths[index++];
		object = object[prop];

		if (Array.isArray(object)) {
			const path = paths.slice(index).join('.');
			return object.map(obj => get(obj, path));
		}
	}

	return object;
}

/**
 * Get an array of values on an item from its properties defined in indexFields
 *
 * @example
 * const source = {
 * 	results: [
 * 		{ name: 'foo', description: 'a foo' },
 * 		{ name: 'bar', description: 'a bar' },
 * 		{ name: 'baz', description: 'and a baz' },
 * 	]
 * }
 * collect(['results.name', 'results.description'], source)
 * // => ['foo', 'bar', 'baz', 'a foo', 'a bar', 'and a baz']
 *
 * @private
 * @param {string[]} indexFields
 * @param {object} item
 */
export function collect(indexFields, item) {
	let result = [];

	for (const field of indexFields) {
		const value = get(item, field);

		if (value == null) {
			continue;
		}

		if (Array.isArray(value)) {
			const depth = Math.max(field.split('.').length - 1, 0);
			result = result.concat([...flatten(value, depth)].filter(item => item != null));
		} else {
			result.push(value);
		}
	}

	return result;
}

/**
 * Indexes the items in a collection by it's unique identifier and other fields.
 *
 * @example
 * const source = {
 * 	results: [
 * 		{ sys_id: 1, name: 'foo', description: 'a foo' },
 * 		{ sys_id: 2, name: 'bar', description: 'a bar' },
 * 		{ sys_id: 3, name: 'baz', description: 'and a baz' },
 * 	]
 * }
 * createObjectSearchIndex('sys_id', ['name', 'description'], source.results);
 * // =>
 * {
 * 	__<timestamp>_meta__: {
 * 		idField: 'sys_id',
 * 		indexFields: ['name', 'description']
 * 	},
 * 	"1": {
 * 		fullText: 'foo a foo',
 * 		slimText: 'foo a',
 * 		maxWordLength: 3,
 * 	}
 * }
 *
 * @public
 * @param {string} idField The unique identifier property for each object in the collection
 * @param {string[]} indexFields The properties to be indexed for each object in the collection
 * @param {object[]} collection
 */
export function createObjectSearchIndex(idField, indexFields, collection) {
	if (typeof idField !== 'string') {
		throw new TypeError(`Expected 'idField' to be a string`);
	}

	if (
		!Array.isArray(indexFields) ||
		indexFields.length === 0 ||
		indexFields.some(field => typeof field !== 'string')
	) {
		throw new TypeError(`Expected 'indexFields' to be an array of strings`);
	}

	if (!Array.isArray(collection)) {
		throw new TypeError(`Expected 'collection' to be an array`);
	}

	const resultMap = {
		[INDEX_META_KEY]: { idField, indexFields },
	};

	for (const ref of collection) {
		const id = ref[idField];

		if (!['string', 'number'].includes(typeof id)) {
			console.warn(`No valid id ('${idField}') on ${JSON.stringify(ref)}`);
			continue;
		}

		// Collect the values for the given indexFields on the item
		// Since we'll be casting these values to strings, filter out any values
		// who's types are not useful as strings
		const values = collect(indexFields, ref).filter(value => typeof value !== 'object');

		if (values.length === 0) {
			console.warn(
				`No values for indexFields (${JSON.stringify(indexFields)}) on item '${id}'`
			);
			continue;
		}

		// Coerce each value to a lower-case string
		const strings = values.map(value => `${value}`.trim().toLocaleLowerCase());

		// Reduce each string to a Set of unique words
		// This represents words that show-up in the given indexFields only once for the given item
		const words = new Set(strings.reduce((acc, word) => acc.concat(word.split(' ')), []));
		const tags = [...words];

		// Determine which word from the set of words is the longest
		const maxWordLength = tags.reduce((size, word) => Math.max(size, word.length), 0);

		resultMap[id] = {
			maxWordLength,

			// A single string property containing all of the indicated values on the item given the indexFields
			fullText: strings.join(' '),

			// A single string property containing all unique words on the item given the indexFields
			slimText: tags.join(' '),
		};
	}

	return resultMap;
}

/**
 * @example
 * const source = {
 * 	results: [
 * 		{ sys_id: 1, name: 'foo', description: 'a foo' },
 * 		{ sys_id: 2, name: 'bar', description: 'a bar' },
 * 		{ sys_id: 3, name: 'baz', description: 'and a baz' },
 * 	]
 * }
 * const index = createObjectSearchIndex('sys_id', ['name', 'description'], source.results);
 * objectSearch(search.results, index, 'b')
 * // => [2, 3]
 *
 * @public
 * @param {object[]} collection The underlying collection array from the index
 * @param {object} indexMap The index returned from `createObjectSearchIndex`
 * @param {string} query The substring to run the search
 * @param {(ref: object, index: number, collection: object[]) => any} [onMatchMapFn] Optional mapping function for each result
 */
export function objectSearch(collection, indexMap, query, onMatchMapFn) {
	const results = [];
	const { idField } = indexMap[INDEX_META_KEY];

	const queryString = `${query}`.trim().toLocaleLowerCase();
	const queryLength = queryString.length;

	const hasMapFn = typeof onMatchMapFn === 'function';
	const querySpansWord = queryString.includes(' ');

	for (let i = 0; i < collection.length; i++) {
		let ref = collection[i];
		const id = ref[idField];
		const indexItem = indexMap[id];

		if (!indexItem) {
			continue;
		}

		const { slimText, fullText, maxWordLength } = indexItem;
		const isMatch =
			querySpansWord || queryLength > maxWordLength
				? fullText.includes(queryString)
				: slimText.includes(queryString);

		if (!isMatch) {
			continue;
		}

		if (hasMapFn) {
			const result = onMatchMapFn.call(collection, ref, i, collection);
			ref = result != null ? result : ref;
		}

		results.push(ref);
	}

	return results;
}

/**
 * Returns a function that filters indexed list by search input when input length is greater than given length
 * @example
 * const source = {
 * 	results: [
 * 		{ sys_id: 1, name: 'foo', description: 'a foo' },
 * 		{ sys_id: 2, name: 'bar', description: 'a bar' },
 * 		{ sys_id: 3, name: 'baz', description: 'and a baz' },
 * 	]
 * }
 *
 * const index = createObjectSearchIndex('sys_id', ['name', 'description'], source.results);
 * getFilterFnForInputLength(2)
 * // => function({ index, list }, input) => filterList
 *
 * filterList({ index, list: source.results }, 'b')
 * // => [
 * 		{ sys_id: 1, name: 'foo', description: 'a foo' },
 * 		{ sys_id: 2, name: 'bar', description: 'a bar' },
 * 		{ sys_id: 3, name: 'baz', description: 'and a baz' },
 * 	]
 *
 * filterList({ index, list: source.results }, 'bar')
 * // => [ { sys_id: 2, name: 'bar', description: 'a bar' } ]
 *
 * @public
 * @param {number} length The length of search input that triggers the filter
 */
export function getFilterFnForInputLength(length = SEARCH_INPUT_MINIMUM_LENGTH) {
	return function({ index, list }, input) {
		if (index && input.length >= length) {
			return objectSearch(list, index, input);
		}

		return list;
	};
}
