/**
 * Delegates an action to another action specified by the action name provided.
 * The original payload is passed along.
 *
 * @param {string} newActionName - name of the action used with redispatching
 * @returns {function}
 */
const redispatchAction = newActionName => ({ action, dispatch }, stopPropagation = true) => {
	if (stopPropagation) {
		action.stopPropagation();
	}

	dispatch(newActionName, action.payload);
};

export default redispatchAction;
