import { createCustomElement } from '@servicenow/ui-core';

import styles from './sn-aes-container.scss';

createCustomElement('sn-aes-container', {
	view({ properties: { fluid, expandable } }) {
		return (
			<div
				class={{
					container: true,
					'container--expand': expandable,
					'container--fluid': fluid,
				}}>
				<slot />
			</div>
		);
	},
	properties: {
		fluid: {
			default: false,
			schema: { type: 'boolean' },
		},
		expandable: {
			default: false,
			schema: { type: 'boolean' },
		},
	},
	styles,
});
