import { createCustomElement } from '@servicenow/ui-core';
import focusBehavior from '@servicenow/behavior-focus';
import { t } from 'sn-translate';

import '@servicenow/now-icon';
import '@servicenow/now-avatar';
import '@servicenow/now-dropdown';
import '@servicenow/now-tooltip';

import nowLogo from './assets/now-logo.svg';
import styles from './sn-aes-header-chrome.scss';
import {
	TOOLTIP_DELAY,
	HEADER_CHROME__ACTIVE_PAGE_SET,
	HEADER_CHROME__AVATAR_ITEM_CLICKED,
	HEADER_CHROME__SHOW_HELP_SET,
} from './constants';

function buildActionMeta(props) {
	const { activePage, isSessionActive, showHelp, user } = props;

	return {
		csHeaderChrome: {
			activePage,
			isSessionActive,
			showHelp,
			user,
		},
	};
}

export const actionHandlers = {
	/**
	 * @param {object} coeffects
	 * @param {object} coeffects.properties
	 * @param {object} coeffects.action
	 * @param {function} coeffects.action.stopPropagation
	 * @param {{ item: { id: string, label: string }}} coeffects.action.payload
	 * @param {function} coeffects.dispatch
	 */
	['NOW_DROPDOWN_CUSTOM_TARGET#ITEM_CLICKED']: ({ properties, action, dispatch }) => {
		action.stopPropagation();
		const { item } = action.payload;
		const payload = { item };
		const meta = buildActionMeta(properties);

		dispatch(HEADER_CHROME__AVATAR_ITEM_CLICKED, payload, meta);
	},

	/**
	 * @param {object} coeffects
	 * @param {object} coeffects.action
	 * @param {function} coeffects.action.stopPropagation
	 * @param {{ value: boolean }} coeffects.action.payload
	 * @param {function} coeffects.updateState
	 */
	['NOW_DROPDOWN_CUSTOM_TARGET#OPENED_SET']: ({ action, updateState, properties }) => {
		action.stopPropagation();
		if (properties.avatarItems && properties.avatarItems.length) {
			const { value: isAvatarOpened } = action.payload;
			updateState({ isAvatarOpened });
		}
	},
};

/**
 * @param {object} props
 * @param {object} props.page
 * @param {boolean} props.active
 * @param {HTMLAnchorElement} props.navItemRef
 * @param {(page: string, event: MouseEvent) => void} props.onSelect
 * @param {(element: HTMLAnchorElement) => void} props.setRef
 */
function NavItem(props) {
	const { page, onSelect = () => {}, setRef = () => {} } = props;
	const itemId = `${page.name}NavItem`;
	const itemIdTooltip = `${itemId}Tooltip`;

	/** @param {HTMLAnchorElement} link */
	const setNavItemRef = link => {
		if (!link.classList.contains('nav-item')) {
			return undefined;
		}
		const { clientWidth, scrollWidth } = link.querySelector('.nav-item--label');

		// to test if `.nav-item--label` is currently in text overflow
		if (clientWidth < scrollWidth) {
			!props.navItemRef && setRef(page.name, link);
		} else {
			props.navItemRef && setRef(page.name, null);
		}
	};

	return (
		<a
			id={itemId}
			key={page.name}
			className="nav-item now-align -column -block-center"
			aria-current={props.active ? 'page' : 'false'}
			aria-label={page.ariaLabel || page.label}
			aria-describedby={props.navItemRef ? itemIdTooltip : undefined}
			on-mouseenter={({ target }) => setNavItemRef(target)}
			on-mouseleave={({ target }) => setNavItemRef(target)}
			on-click={event => onSelect(page.name, event)}
			href="javascript:void(0)">
			<now-icon className="nav-item--icon" icon={page.icon.type} size={page.icon.size} />
			<div className="nav-item--label truncate now-m-block-start--xs">{page.label}</div>
			<div className="nav-item--selection-bar" />

			{props.navItemRef ? (
				<now-tooltip
					id={itemIdTooltip}
					target-ref={props.navItemRef}
					content={page.label}
					delay={TOOLTIP_DELAY}
					// Opens automatically if the `.nav-item` is currently being hovered
					// Suppresses the tooltip showing if moving over multiple items within the TOOLTIP_DELAY
					opened={props.navItemRef.matches(':hover')}
				/>
			) : null}
		</a>
	);
}

/**
 * @param {object} props
 * @param {boolean} props.active
 * @param {(event: MouseEvent) => void} props.onSelect
 */
function GlobalToolsHelp({ active, onSelect = () => {} }) {
	return (
		<now-icon
			className="global-tools--help now-align -inline-center -block-center"
			icon="circle-question-fill"
			size="md"
			tabindex="0"
			role="button"
			aria-pressed={active ? 'true' : 'false'}
			aria-label={t('Help')}
			on-click={onSelect}
			on-keydown={event => {
				if ([' ', 'Enter'].includes(event.key)) {
					// Prevents page scroll
					event.preventDefault();
					onSelect(event);
				}
			}}
		/>
	);
}

/**
 * @param {object} props
 * @param {object} props.user
 * @param {boolean} props.active
 * @param {{ id: string, label: string }[]} props.avatarItems
 * @param {boolean} props.isSessionActive
 */
function GlobalToolsAvatar(props) {
	const { user, avatarItems } = props;

	return (
		<now-dropdown-custom-target
			class={{
				'global-tools--avatar': true,
				selectable: !!avatarItems.length,
				'now-align': true,
				'-inline-center': true,
				'-block-center': true,
				'-active': props.active,
			}}
			items={props.avatarItems}
			placeholder="This somehow removes default active styling"
			select="none"
			opened={props.active}
			manage-opened>
			<now-avatar
				slot="trigger"
				size="sm"
				presence={props.isSessionActive ? 'available' : 'busy'}
				user-name={user.fullName}
				image-src={`/${user.avatar}`}
			/>
		</now-dropdown-custom-target>
	);
}

/**
 * @param {object} state
 * @param {object} helpers
 * @param {function} helpers.dispatch
 * @param {function} helpers.updateProperties
 * @param {function} helpers.updateState
 */
function CsHeaderChrome(state, { dispatch, updateProperties, updateState }) {
	const { properties: props } = state;
	const [homePage = {}] = props.pages;

	/**
	 * @param {string} page
	 * @param {HTMLAnchorElement} navItemRef
	 */
	const setNavItemRef = (page, navItemRef) => {
		const navItemRefs = { ...state.navItemRefs };

		if (navItemRef) {
			navItemRefs[page] = navItemRef;
		} else {
			delete navItemRefs[page];
		}

		// now-tooltip is set to open automatically inside nav-item when a ref is set,
		// so I'm setting a timeout to simulate the on-hover delay on now-tooltip itself
		// This is not a workaround, but for UX consistency
		window.setTimeout(() => {
			updateState({ navItemRefs });
		}, TOOLTIP_DELAY);
	};

	/** @type {(page: string, event: MouseEvent) => void} */
	const dispatchActivePageSet = (page, event) => {
		// TODO: refactor nav items to not be anchor tags
		event.preventDefault();

		if (props.manageActivePage) {
			const meta = buildActionMeta(props);
			dispatch(HEADER_CHROME__ACTIVE_PAGE_SET, { value: page }, meta);
		} else {
			updateProperties({ activePage: page });
		}
	};

	const dispatchShowHelpSet = () => {
		const value = !props.showHelp;

		if (props.manageShowHelp) {
			const meta = buildActionMeta(props);
			dispatch(HEADER_CHROME__SHOW_HELP_SET, { value }, meta);
		} else {
			updateProperties({ showHelp: value });
		}
	};

	return (
		// https://www.w3.org/TR/wai-aria-1.1/#banner
		<header className="sn-aes-header-chrome">
			<a
				className="brand-logo"
				on-click={event => dispatchActivePageSet(homePage.name, event)}
				href="javascript:void(0)">
				<img
					className="brand-logo--image"
					src={nowLogo}
					alt={t('Now Logo')}
					aria-hidden="true"
				/>
				<div className="brand-logo--pipe">&nbsp;</div>
				<div id="brandLogoLabel" className="brand-logo--label truncate">
					{props.applicationName}&nbsp;
					<span className="now-a11y-label">{homePage.ariaLabel}</span>
				</div>
			</a>

			<nav className="primary-navigation">
				{props.pages.map(page => (
					<NavItem
						page={page}
						active={page.name === props.activePage}
						navItemRef={state.navItemRefs[page.name]}
						setRef={setNavItemRef}
						onSelect={dispatchActivePageSet}
					/>
				))}
			</nav>

			<div className="global-tools">
				<GlobalToolsHelp active={props.showHelp} onSelect={dispatchShowHelpSet} />
				<GlobalToolsAvatar
					user={props.user}
					active={state.isAvatarOpened}
					avatarItems={props.avatarItems}
					isSessionActive={props.isSessionActive}
				/>
			</div>
		</header>
	);
}

/**
 * A page header for App Engine Studio. It contains links to pages defined in `../pages`
 *
 * ```jsx
 * const pages = [{ page: 'home', label: t('Home'), icon: { type: 'home-fill', size: 'lg' }}];
 * const avatarItems = [{ id: 'logout', label: t('Logout') }];
 *
 * // Self-contained header
 * <sn-aes-header-chrome pages={pages} avatar-items={avatarItems} />
 *
 * // Managed hash-change navigation
 * <sn-aes-header-chrome
 * 	pages={pages}
 * 	active-page="@router/currentPage"
 *	manage-active-page
 * 	avatar-items={avatarItems}
 * />
 * ```
 *
 * @seismicElement sn-aes-header-chrome
 * @summary The header chrome for App Engine Studio
 */
createCustomElement('sn-aes-header-chrome', {
	properties: {
		/**
		 * The name of the page currently selected.
		 * Must match the `page` property on `pages`
		 * @see sn-aes-router
		 * @type {string}
		 */
		activePage: {},

		/**
		 * The name of the running application. Will be applied to `.brand-logo`
		 * @type {string}
		 */
		applicationName: {
			// TODO: [bfischer] This may very well be removed and set static in the template
			// depending on whether we want it to be translated or not
			default: t('App Engine Studio'),
		},

		/**
		 * Array of items to display in `.global-tools--avatar`
		 * When one of these items are selected will dispatch `HEADER_CHROME#AVATAR_ITEM_CLICKED`
		 * @type {{ id: string, label: string }[]}
		 */
		avatarItems: {
			default: [],
		},

		/**
		 * Sets the presence indicator on the avatar
		 * @see now-avatar
		 * @type {boolean}
		 */
		isSessionActive: { default: true },

		/**
		 * Enable this property to override automatic updates to the `activePage` property.
		 * An action with type `HEADER_CHROME#ACTIVE_PAGE_SET` will be dispatched to request updates,
		 * which should be handled by the parent or application.
		 * @type {boolean}
		 */
		manageActivePage: { default: false },

		/**
		 * Enable this property to override automatic updates to the `showHelp` property.
		 * An action with type `HEADER_CHROME#SHOW_HELP_SET` will be dispatched to request updates,
		 * which should be handled by the parent or application.
		 * @type {boolean}
		 */
		manageShowHelp: { default: false },

		/**
		 * Array of pages to display in the primary navigation.
		 * The first item in this array will be used to describe the `.brand-logo` link
		 * @see sn-aes-router
		 * @type {{ page: string, label: string, ariaLabel?: string, icon: { type: string, size: string}}[]}
		 */
		pages: { default: [] },

		/**
		 * Sets the `active` state on `.global-tools--help`.
		 * @type {boolean}
		 */
		showHelp: { default: false },

		/**
		 * Properties from the user object needed to show the avatar
		 * @see sn-aes-user-data-provider
		 * @type {{ fullName: string, avatar: string }}
		 */
		user: { default: {} },
	},

	dispatches: {
		/**
		 * Dispatched when `.brand-logo` or a `.nav-item` is clicked
		 * Payload contains a boolean of true when the help icon is active or false when the help icon is inactive.
		 * Set the `manage-active-page` property to override the default behavior and handle this action manually.
		 * @type {{ value: string }}
		 */
		['HEADER_CHROME#ACTIVE_PAGE_SET']: {},

		/**
		 * Dispatched when an item in `.global-tools--avatar` is clicked
		 * @type {{ item: { id: string, label: string }}}
		 */
		['HEADER_CHROME#AVATAR_ITEM_CLICKED']: {},

		/**
		 * Dispatched when `.global-tools--help` is clicked
		 * Payload contains a boolean of true when the help icon is active or false when the help icon is inactive.
		 * Set the `manage-show-help` property to override the default behavior and handle this action manually.
		 * @type {{ value: boolean }}
		 */
		['HEADER_CHROME#SHOW_HELP_SET']: {},
	},

	initialState: {
		isAvatarOpened: false,
		navItemRefs: {},
	},

	behaviors: [focusBehavior],
	view: CsHeaderChrome,
	actionHandlers,
	styles,
});
