/**
 * Returns the effect details object based on path, pathParams and queryParams
 *
 * @param {string} path
 * @param {string} pathParams
 * @param {string} queryParams
 * @returns {object}
 */
export default function getEffectDetails(path, pathParams, queryParams) {
	if (!path) {
		throw new Error('Path is required as a the first argument');
	}

	return {
		path,
		pathParams: pathParams || [],
		queryParams: queryParams || [],
	};
}
