import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import get from 'lodash/get';

import '@servicenow/now-button';
import '@servicenow/now-tabs';
import 'sn-component-carousel';
import '@servicenow/now-rich-text';

import { t } from 'sn-translate';
import styles from './sn-aes-template-overlay.scss';

import dispatchActionViaPayload from '../../helpers/dispatch-action-via-payload';
import { transitionTo } from '../../behaviors/router';
import { CREATE_APPLICATION_TEMPLATE_OPERATION } from '../sn-aes-wizard-manager/constants';

import { createHttpEffect } from '@servicenow/ui-effect-http';
import { PROVIDER_PROPERTIES } from '../sn-aes-provider';
import { TEMPLATE_CATEGORY_DATA_PROPERTY } from '../../behaviors/template-data';
import getPlaceholderImage from '../../helpers/get-placeholder-image';
import {
	renderTabContent,
	renderSingleTab,
	getCarouselElements,
	getTabElements,
} from './sn-aes-template-overlay-utils';
import {
	TEMPLATE_MANIFEST_ROUTE,
	TEMPLATE_OVERLAY__FETCH_TEMPLATE_MANIFEST,
	TEMPLATE_OVERLAY__FETCH_TEMPLATE_MANIFEST_SUCCEEDED,
	TEMPLATE_OVERLAY__FETCH_TEMPLATE_MANIFEST_FAILED,
	TEMPLATE_OVERLAY__USE_TEMPLATE,
	TEMPLATE_OVERLAY__SHOW_ALL_BLOCKS,
} from './constants';
import defaultManifestIcon from './assets/default-manifest-icon.svg';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const view = state => {
	const {
		properties: { template, templateBlocksData, blocksDrawerIsOpen },
		selectedTabId,
	} = state;

	const slidesRichText = getCarouselElements(template);
	const tabs = getTabElements(template);
	const headerImage =
		get(template, 'details.header_image', '') ||
		get(template, 'details.icon', '') ||
		getPlaceholderImage(template.sys_id);
	const author = get(template, 'details.author');
	const shortDescription = get(template, 'details.short_description.display_value', '');
	const detailedDescription = get(template, 'details.detailed_description.display_value', '');
	const otherUseCases = get(template, 'details.other_use_cases', '');
	const headerBackgroundColor = get(template, 'details.header_background_color');
	const renderEmptyState = !(
		shortDescription ||
		detailedDescription ||
		slidesRichText.length ||
		otherUseCases ||
		templateBlocksData.totalBlocks ||
		tabs.length
	);

	const hasHeaderImage =
		!!get(template, 'details.header_image', '') || !!get(template, 'details.icon', '');

	return (
		<div className="template-overlay">
			<section
				className="header-section"
				style={headerBackgroundColor ? { backgroundColor: headerBackgroundColor } : {}}>
				<div className="section-container">
					<div class={{ header: true, 'has-placeholder': !hasHeaderImage }}>
						{headerImage ? (
							<div className="header-image">
								<img src={headerImage} alt="" />
							</div>
						) : null}
						<div className="header-content">
							<div>
								<h1 className="title">{get(template, 'name.display_value', '')}</h1>
							</div>
							{author ? (
								<div className="subtitle">
									{t('by')} {author}
								</div>
							) : null}

							<now-button
								className="use-template-btn"
								id="use-template-btn"
								variant="primary"
								label={t('Use Template')}
								append-to-payload={{
									actionName: TEMPLATE_OVERLAY__USE_TEMPLATE,
								}}
							/>
						</div>
					</div>
				</div>
			</section>
			<div className="main">
				{shortDescription || detailedDescription || slidesRichText.length ? (
					<section id="description-image-carousel">
						<div className="section-container">
							<div className="section-content">
								{slidesRichText.length ? (
									<div className="carousel-outer-container">
										<div className="carousel-inner-container">
											<sn-component-carousel
												slides={slidesRichText}
												height="460"
												rounded-corners={true}
											/>
										</div>
									</div>
								) : null}
								<div className="section-name">{shortDescription}</div>
								<div className="section-body">{detailedDescription}</div>
							</div>
						</div>
					</section>
				) : null}
				{otherUseCases ? (
					<section id="other-use-cases">
						<div className="section-container">
							<div className="section-content">
								<div className="section-name">
									{t('Other ways to use this app')}
								</div>
								<div className="section-body">{otherUseCases}</div>
							</div>
						</div>
					</section>
				) : null}
				{templateBlocksData.totalBlocks ? (
					<section id="building-blocks">
						<div className="section-container">
							<div className="section-content">
								<div className="section-name">{t('What this app contains')}</div>
								<div className="blocks section-body">
									{templateBlocksData.visibleBlocks.map((block, index) => {
										return (
											<div className="block" id={index}>
												{block.taxonomyIcon ? (
													<img src={block.taxonomyIcon} alt="" />
												) : null}
												<div>
													{block.header ? (
														<div className="header">
															<strong>{block.header}</strong>
														</div>
													) : null}
													<div className="description">
														{block.description.display_value}
													</div>
												</div>
											</div>
										);
									})}
								</div>
								{!blocksDrawerIsOpen &&
								templateBlocksData.visibleBlocks.length !==
									templateBlocksData.totalBlocks ? (
									<now-button
										className="see-all-blocks"
										id="see-all-blocks"
										class="see-all-blocks"
										size="md"
										variant="secondary"
										label={t('See everything this app contains')}
										append-to-payload={{
											actionName: TEMPLATE_OVERLAY__SHOW_ALL_BLOCKS,
										}}
									/>
								) : null}
							</div>
						</div>
					</section>
				) : null}

				{tabs.length ? (
					<section id="documentation">
						<div className="section-container">
							<div className="section-content">
								<div className="section-name">{t('Documentation')}</div>
								<div className="section-body">
									{tabs.length > 1 ? (
										<div>
											<now-tabs
												id="documentation-tabs"
												items={tabs}
												selectedItem={selectedTabId}
												maxWidth={240}
											/>
											{renderTabContent(state)}
										</div>
									) : (
										renderSingleTab(tabs, template)
									)}
								</div>
							</div>
						</div>
					</section>
				) : null}
				{renderEmptyState ? <section /> : null}
			</div>
		</div>
	);
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ state, dispatch }) => {
		const {
			properties: { template },
		} = state;

		dispatch(TEMPLATE_OVERLAY__FETCH_TEMPLATE_MANIFEST, {
			template: template.sys_id,
		});
	},
	[TEMPLATE_OVERLAY__FETCH_TEMPLATE_MANIFEST]: createHttpEffect(TEMPLATE_MANIFEST_ROUTE, {
		queryParams: ['template'],
		method: 'GET',
		batch: false,
		successActionType: TEMPLATE_OVERLAY__FETCH_TEMPLATE_MANIFEST_SUCCEEDED,
		errorActionType: TEMPLATE_OVERLAY__FETCH_TEMPLATE_MANIFEST_FAILED,
	}),
	[TEMPLATE_OVERLAY__FETCH_TEMPLATE_MANIFEST_SUCCEEDED]: ({
		action: {
			payload: { result },
		},
		updateProperties,
	}) => {
		updateProperties({ templateManifest: result });
	},
	[TEMPLATE_OVERLAY__FETCH_TEMPLATE_MANIFEST_FAILED]: () => {},
	['NOW_BUTTON#CLICKED']: dispatchActionViaPayload,
	[TEMPLATE_OVERLAY__USE_TEMPLATE]: ({ dispatch, properties: { template } }) => {
		transitionTo(dispatch, 'template', {
			operations: [
				{
					operation: CREATE_APPLICATION_TEMPLATE_OPERATION,
					params: { templateId: template.sys_id },
				},
			],
		});
	},
	['NOW_TABS#SELECTED_ITEM_SET']: ({
		action: {
			payload: { value },
		},
		updateState,
	}) => {
		updateState({ selectedTabId: value });
	},
	[TEMPLATE_OVERLAY__SHOW_ALL_BLOCKS]: ({ updateProperties }) => {
		updateProperties({ blocksDrawerIsOpen: true });
	},
};

createCustomElement('sn-aes-template-overlay', {
	initialState: {
		selectedTabId: 'instructions',
	},
	properties: {
		blocksDrawerIsOpen: false,
		template: {
			default: {},
		},
		templateManifest: {
			default: [],
		},
		templateManifestTaxonomyMapping: {
			default: PROVIDER_PROPERTIES[TEMPLATE_CATEGORY_DATA_PROPERTY],
		},
		templateBlocksData: {
			computed({
				properties: {
					templateManifestTaxonomyMapping,
					templateManifest,
					template,
					blocksDrawerIsOpen,
				},
			}) {
				const relevantMapping = templateManifestTaxonomyMapping.find(
					mapping => mapping.templateId === template.sys_id
				);

				let computedManifest = templateManifest.filter(manifest => {
					return get(manifest, 'description.display_value');
				});

				computedManifest = computedManifest.map(manifest => {
					const relevantTaxonomyManifestCategory =
						relevantMapping &&
						relevantMapping.categories.find(category => {
							return category.tableSysId === get(manifest, 'table_name.value');
						});
					const taxonomyIcon = get(
						relevantTaxonomyManifestCategory,
						'taxonomyIcon',
						defaultManifestIcon
					);
					const header =
						get(relevantTaxonomyManifestCategory, 'taxonomyType') ||
						get(manifest, 'table_name.display_value');

					return {
						...manifest,
						taxonomyIcon,
						header,
					};
				});

				const sorted = computedManifest.sort((x, y) => {
					return x.order.value - y.order.value;
				});
				const totalBlocks = sorted.length;

				return {
					visibleBlocks: blocksDrawerIsOpen ? sorted : sorted.splice(0, 6),
					totalBlocks,
				};
			},
		},
	},
	view,
	actionHandlers,
	styles,
});
