import {
	BEHAVIOR_NAME,
	IMPORTING_DATA_PROPERTY,
	IMPORTING_DATA_BEHAVIOR__FETCH_AND_DELETE_TABLE_DATA,
	IMPORTING_DATA_BEHAVIOR__FETCH_AND_DELETE_TABLE_DATA_FAILURE,
	IMPORTING_DATA_BEHAVIOR__FETCH_AND_DELETE_TABLE_DATA_SUCCESS,
	SYS_DB_OBJECT_TABLE,
	IMPORTING_DATA__DELETE_STARTED,
	IMPORTING_DATA__REMOVE_PENDING_RECORDS,
	SN_GUIDED_WIZARD__IMPORTSET_PROCESSING,
	SN_GUIDED_WIZARD__IMPORTSET_DONE,
} from './constants';
import uniq from 'lodash/uniq';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { TABLE_API_GENERIC, TABLE_API_RECORD } from '../../helpers/constants';
import getEffectDetails from '../../helpers/get-effect-details';

const importingDataBehavior = {
	name: BEHAVIOR_NAME,
	properties: {
		[IMPORTING_DATA_PROPERTY]: {
			default: [],
			selectable: true,
		},
	},
	actionHandlers: {
		[IMPORTING_DATA__REMOVE_PENDING_RECORDS]: ({ updateProperties, properties, dispatch }) => {
			let importSetTableNames = properties[IMPORTING_DATA_PROPERTY];

			// fetch information around the table name so we can grab the sys_id
			// which is needed in order to delete
			if (importSetTableNames.length > 0) {
				importSetTableNames.forEach(tableName => {
					dispatch(IMPORTING_DATA_BEHAVIOR__FETCH_AND_DELETE_TABLE_DATA, {
						tableName: SYS_DB_OBJECT_TABLE,
						sysparm_query: `name=${tableName}`,
					});
				});
				updateProperties({ [IMPORTING_DATA_PROPERTY]: [] });
			}
		},
		[SN_GUIDED_WIZARD__IMPORTSET_PROCESSING]: ({ properties, action, updateProperties }) => {
			const { importSetTableName } = action.payload;
			let importSetTableNames = properties[IMPORTING_DATA_PROPERTY];
			importSetTableNames.push(importSetTableName);
			importSetTableNames = uniq(importSetTableNames);
			updateProperties({ [IMPORTING_DATA_PROPERTY]: importSetTableNames });
		},
		[SN_GUIDED_WIZARD__IMPORTSET_DONE]: ({ updateProperties, action, properties }) => {
			const { importSetTableName } = action.payload;
			let importSetTableNames = properties[IMPORTING_DATA_PROPERTY];

			importSetTableNames = importSetTableNames.filter(name => name !== importSetTableName);
			updateProperties({ [IMPORTING_DATA_PROPERTY]: importSetTableNames });
		},

		[IMPORTING_DATA_BEHAVIOR__FETCH_AND_DELETE_TABLE_DATA]: createHttpEffect(
			TABLE_API_GENERIC,
			{
				pathParams: ['tableName'],
				queryParams: ['sysparm_query'],
				method: 'GET',
				batch: false,
				successActionType: IMPORTING_DATA_BEHAVIOR__FETCH_AND_DELETE_TABLE_DATA_SUCCESS,
				errorActionType: IMPORTING_DATA_BEHAVIOR__FETCH_AND_DELETE_TABLE_DATA_FAILURE,
			}
		),
		[IMPORTING_DATA_BEHAVIOR__FETCH_AND_DELETE_TABLE_DATA_SUCCESS]: ({ action, dispatch }) => {
			const { result } = action.payload;
			const tableData = result[0];

			const {
				sys_id,
				sys_scope: { value },
			} = tableData;
			const effectDetails = getEffectDetails(
				TABLE_API_RECORD,
				['tableName', 'sysId'],
				['sysparm_transaction_scope']
			);

			const payload = {
				sysId: sys_id,
				tableName: 'sys_db_object',
				sysparm_transaction_scope: value,
				effectDetails,
			};

			dispatch(IMPORTING_DATA__DELETE_STARTED, payload);
		},
		[IMPORTING_DATA_BEHAVIOR__FETCH_AND_DELETE_TABLE_DATA_FAILURE]: () => {},
		[IMPORTING_DATA__DELETE_STARTED]: {
			effect: () => null,
			interceptors: [
				context => {
					const {
						coeffects: {
							action: { payload },
						},
					} = context;
					const {
						effectDetails: { path, queryParams, pathParams },
					} = payload;
					const options = {
						method: 'DELETE',
						batch: false,
						queryParams,
						pathParams,
					};

					return {
						...context,
						effects: [
							{
								...createHttpEffect(path, options),
								args: [path, options, context.coeffects],
							},
						],
					};
				},
			],
		},
	},
};

export default importingDataBehavior;
