import { createCustomElement } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';

import { ROOT__HIDE_MODAL } from '../sn-app-engine-studio/constants';
import { ALERTS_BEHAVIOR__ADD_ITEM } from './../../behaviors/alerts';
import {
	CONFIRM_DELETE__SUBMITTED,
	CONFIRM_DELETE__DELETE_SUCCEEDED,
	CONFIRM_DELETE__DELETE_FAILED,
} from './constants';

import '@servicenow/now-modal';
import '@servicenow/now-input';

import styles from './sn-aes-confirm-delete-generic.scss';

createCustomElement('sn-aes-confirm-delete-generic', {
	view: state => {
		const {
			isDeleting,
			userInput,
			properties: { translatedHeaderLabel },
		} = state;
		const userInputValid = userInput.toLocaleUpperCase() === t('DELETE').toLocaleUpperCase();
		const footerActions = [
			{
				id: 'primary',
				size: 'md',
				variant: 'primary-negative',
				label: t('Delete'),
				disabled: !userInputValid || isDeleting,
			},
			{
				id: 'secondary',
				size: 'md',
				variant: 'tertiary',
				label: t('Cancel'),
			},
		];

		return (
			<now-modal
				opened
				manage-opened
				size="md"
				headerLabel={translatedHeaderLabel}
				footerActions={footerActions}>
				<div className="modal-confirm-text">
					<slot name="confirm-text" />
				</div>
				<slot />
				<div className="user-input">
					<now-input
						type="text"
						name="userInput"
						placeholder={t('DELETE')}
						value={userInput}
						autofocus
					/>
				</div>
			</now-modal>
		);
	},
	properties: {
		onDeleteSuccess: {
			default: () => {},
		},
		translatedHeaderLabel: {
			default: t('Are you sure you want to proceed with the delete?'),
			schema: {
				type: 'string',
			},
		},
		effectDetails: {
			schema: {
				type: 'object',
				properties: {
					path: {
						type: 'string',
					},
					pathParams: {
						default: [],
						type: 'array',
						items: {
							type: 'string',
						},
					},
					queryParams: {
						default: [],
						type: 'array',
						items: {
							type: 'string',
						},
					},
				},
				required: ['path'],
			},
		},
		effectPayload: {
			schema: {
				type: 'object',
			},
		},
		successAlertPayload: {
			default: {
				content: t('Delete has been successful.'),
			},
			schema: {
				type: 'object',
			},
		},
		errorAlertPayload: {
			default: {
				content: t('We encountered an error while performing the delete.'),
			},
			schema: {
				type: 'object',
			},
		},
	},
	initialState: {
		isDeleting: false,
		userInput: '',
	},
	actionHandlers: {
		['NOW_INPUT#VALUE_SET']: ({ action, updateState }) => {
			action.stopPropagation();

			const { value } = action.payload;

			updateState({
				userInput: value,
			});
		},
		['NOW_MODAL#FOOTER_ACTION_CLICKED']: ({
			properties: { effectPayload },
			action,
			dispatch,
			updateState,
		}) => {
			action.stopPropagation();

			if (action.payload.action.id === 'primary') {
				updateState({
					isDeleting: true,
				});
				dispatch(CONFIRM_DELETE__SUBMITTED, effectPayload);
			} else {
				dispatch(ROOT__HIDE_MODAL);
			}
		},
		[CONFIRM_DELETE__SUBMITTED]: {
			effect: () => null,
			interceptors: [
				context => {
					const {
						coeffects: { properties },
					} = context;
					const {
						effectDetails: { path, queryParams, pathParams },
					} = properties;
					const options = {
						method: 'DELETE',
						batch: false,
						queryParams,
						pathParams,
						successActionType: CONFIRM_DELETE__DELETE_SUCCEEDED,
						errorActionType: CONFIRM_DELETE__DELETE_FAILED,
					};

					return {
						...context,
						effects: [
							{
								...createHttpEffect(path, options),
								args: [path, options, context.coeffects],
							},
						],
					};
				},
			],
		},
		[CONFIRM_DELETE__DELETE_SUCCEEDED]: ({
			properties: { onDeleteSuccess, successAlertPayload },
			dispatch,
			updateState,
		}) => {
			updateState({
				isDeleting: false,
			});

			dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
				status: 'positive',
				pagesToPersist: 1,
				...successAlertPayload,
			});
			dispatch(ROOT__HIDE_MODAL);
			onDeleteSuccess();
		},
		[CONFIRM_DELETE__DELETE_FAILED]: ({
			properties: { errorAlertPayload },
			dispatch,
			updateState,
		}) => {
			updateState({
				isDeleting: false,
			});
			dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
				status: 'critical',
				...errorAlertPayload,
			});
		},
	},
	styles,
});
