import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-button';
import { t } from 'sn-translate';
import styles from './sn-aes-search-input.scss';

import { SEARCH_INPUT__BLUR, SEARCH_INPUT__CHANGED, SEARCH_INPUT__FOCUS } from './constants';

/**
 * Input
 *
 * ```jsx
 * <sn-aes-search-input name="field-name" />
 * ```
 *
 * @seismicElement sn-aes-search-input
 * @summary Input
 */
createCustomElement('sn-aes-search-input', {
	properties: {
		/**
		 * The value for this input.
		 * @type {string}
		 */
		inputValue: { reflect: true },
		placeholder: { default: '' },
		/**
		 * A ServiceNow icon value to be used as a button in the input
		 * @type {string}
		 * @example 'magnifying-glass-outline'
		 */
		icon: { default: 'magnifying-glass-fill' },
		isActive: { default: false },
		disabled: { default: false },
		setAutoFocus: { default: false },
	},
	view: (state, { dispatch }) => {
		const {
			label,
			inputValue,
			name,
			icon,
			isActive,
			disabled,
			placeholder,
			setAutoFocus,
		} = state.properties;

		const { componentId } = state;

		return (
			<div>
				{label && (
					<div className="now-align">
						<label
							className="sn-aes-search-input-label now-m-inline-end--xs"
							htmlFor={name}>
							{label}
						</label>
					</div>
				)}

				<div
					class={{
						'sn-aes-search-input-container': true,
						'sn-aes-search-input-active': isActive,
					}}>
					<input
						className="sn-aes-search-input"
						id={componentId}
						name={name}
						on-blur={e => {
							dispatch(SEARCH_INPUT__BLUR, {
								value: e.target.value,
								input: name,
							});
						}}
						on-focus={e => {
							dispatch(SEARCH_INPUT__FOCUS, {
								value: e.target.value,
								input: name,
							});
						}}
						on-input={e => {
							dispatch(SEARCH_INPUT__CHANGED, {
								value: e.target.value,
								input: name,
							});
						}}
						placeholder={placeholder}
						type={t('text')}
						value={inputValue}
						disabled={disabled}
						autofocus={setAutoFocus}
					/>
					<span className="button-icon-container">
						<now-button-iconic
							bare
							icon={icon}
							tertiary
							size="md"
							config-aria={{
								'aria-label':
									!inputValue || inputValue.length === 0
										? t('Enter a search term')
										: t('Clear the search term'),
							}}
						/>
					</span>
				</div>
			</div>
		);
	},
	actionHandlers: {
		'NOW_BUTTON_ICONIC#CLICKED': ({
			action,
			properties: { inputValue, name },
			updateProperties,
			dispatch,
		}) => {
			action.stopPropagation();

			if (inputValue && inputValue.length > 0) {
				updateProperties({
					inputValue: '',
					isActive: false,
					icon: 'magnifying-glass-fill',
				});
				dispatch(SEARCH_INPUT__CHANGED, { name, value: '' });
			}
		},
		[SEARCH_INPUT__FOCUS]: ({ updateState }) => {
			updateState({
				focused: true,
			});
		},
		[SEARCH_INPUT__BLUR]: ({ updateState }) => {
			updateState({
				focused: false,
			});
		},
		[SEARCH_INPUT__CHANGED]: ({ action, updateProperties }) => {
			if (action.payload.value.length > 0) {
				updateProperties({
					inputValue: action.payload.value,
					isActive: true,
					icon: 'close-outline',
				});
			} else {
				updateProperties({
					inputValue: action.payload.value,
					isActive: false,
					icon: 'magnifying-glass-fill',
				});
			}
		},
	},
	styles,
});
