import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-button';

import styles from './sn-aes-builder-header.scss';

createCustomElement('sn-aes-builder-header', {
	view({ properties: { builderTitle, icon, buttonList } }) {
		return (
			<div className="builder-header now-align -inline -block-center">
				<div className="builder-title now-align -inline-start -block-center">
					<img className="icon" alt="" aria-hidden="true" src={icon} />
					<div className="title">{builderTitle}</div>
				</div>
				<div className="builder-buttons now-align -inline-end -block-center">
					{buttonList.map(
						({ id, buttonLabel, variant, actionName, disabled = false }) => (
							<now-button
								id={id}
								size="md"
								label={buttonLabel}
								variant={variant}
								disabled={disabled}
								append-to-payload={{ actionName }}
							/>
						)
					)}
				</div>
			</div>
		);
	},
	properties: {
		/**
		 * Title for the builder header
		 * @type {string}
		 */
		builderTitle: { default: '' },
		/**
		 * Icon for the builder header
		 * @type {string}
		 */
		icon: { default: '' },
		/**
		 * List of buttons to be displayed on the right side
		 * with corresponding click actions.
		 * @type {[
		 * 	{
		 * 		id: string,
		 * 		buttonLabel: string,
		 * 		variant: string,
		 * 		actionName: string,
		 * 		disabled: boolean
		 * 		}
		 *]}
		 */
		buttonList: { default: [] },
	},
	actionHandlers: {
		['NOW_BUTTON#CLICKED']: ({ action, dispatch }, stopPropagation = true) => {
			if (stopPropagation) {
				action.stopPropagation();
			}

			dispatch(action.payload.actionName, action.payload);
		},
	},
	styles,
});
