import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-aes-builder.scss';
import legacyBuilderHandler from './legacy-builder-handler';
import formDesignerBuilderHandler from './form-designer-builder-handler';
import { getBuilderType } from '../../helpers/generateBuilderUrl';
import { LEGACY_BUILDER, FORM_DESIGNER_BUILDER, GENERIC_BUILDER } from '../../helpers/constants';
import {
	default as genericBuilderHandler,
	AES_BUILDER_ACTION_MAP,
} from './generic-builder-handler';

import {
	BUILDER_UPDATE,
	BUILDER_UPDATE_SESSION,
	ENABLE_NOW_UI_EVENT,
	NOW_UI_EVENT,
} from './constants';
import get from 'lodash/get';

createCustomElement('sn-aes-builder', {
	properties: {
		/**
		 * The URL for the builder
		 * @type {string}
		 */
		url: { default: '' },
		/**
		 * The application scope
		 */
		appScope: { default: '' },
		/**
		 * The application sys_id
		 */
		appId: { default: '' },
	},
	view: ({ properties: { url, appScope, appId } }, { dispatch }) => {
		/*
		 Determine the correct handler depending on what builder is being loaded.

		 Handlers hook into a builder to listen for BUILDER_UPDATE and BUILDER_EXIT
		 events.  Older builders
			 - (legacy platform form) need to be retrofitted
			 to send BUILDER_UPDATE and BUILDER_EXIT events (see legacy-builder-handler).
			 - form_designer_builder needs to be retrofitted to send BUILDER_UPDATE event in addition to hiding
			 table selector
		 Determing the right handler for a builder by it's URL, we currently support
		 3 types: legacy_builder, generic_builder and form_designer_builder.

		 TODO (eguo): We need to figure out a long term way to determine what
		 builder handler should be used here. Right now, assuming anything with
		 `.do` in the URL is rendered by a `ui.jforms` file and hence legacy
		 forms/lists.
		 (nhariprasad): The exception to the `.do` pattern mentioned by (eguo) is flow designer and form designer
		*/

		const builderType = getBuilderType(url);

		return (
			<div className="builder-container">
				<iframe
					frameBorder="0"
					className="builder-iframe"
					title="builder"
					width="100%"
					src={url}
					appEngineStudioBuilderAPI={genericBuilderHandler(dispatch, appScope, appId)}
					on-load={async ({ target: iframe }) => {
						if (builderType === FORM_DESIGNER_BUILDER) {
							formDesignerBuilderHandler(iframe, dispatch, appId);
						} else if (builderType === LEGACY_BUILDER) {
							legacyBuilderHandler(iframe, dispatch, url, appId);
						} else {
							dispatch(BUILDER_UPDATE, { isLoading: false });
							dispatch(BUILDER_UPDATE_SESSION, {
								url: get(iframe, 'contentWindow.location.href'),
							});
						}

						iframe.style = 'visibility:visible;opacity:1';

						// Uxf applications don't have access to the window object. This listens
						// for all the events inside the builder and proxies the aes supported events to our app.
						if (get(iframe, 'contentDocument') && builderType === GENERIC_BUILDER) {
							iframe.contentWindow[ENABLE_NOW_UI_EVENT] = true;

							iframe.contentDocument.addEventListener(NOW_UI_EVENT, ({ detail }) => {
								if (detail.type === 'ACTION_DISPATCHED') {
									const { type, payload } = detail.action;

									if (AES_BUILDER_ACTION_MAP.hasOwnProperty(type)) {
										// This line proxies the action received to aes
										AES_BUILDER_ACTION_MAP[type](dispatch, {
											...payload,
											appId,
										});
									}
								}
							});
						}
					}}
					// TODO: Loading any builder/template wizard using UXF will unfortunately
					// try to take over the top window. Setting the `id` as "testFrame", is
					// workaround suggested by the UXF team.
					id="testFrame"
				/>
			</div>
		);
	},
	styles,
});
