import { createCustomElement } from '@servicenow/ui-core';

import '../../components/sn-aes-search-input';
import '../../components/sn-aes-container';
import './sn-aes-resources-content/sn-aes-resources-content';

import {
	getResourcesContentFailed,
	getResourcesContentSuccess,
	getResourcesPageContent,
	handleSearchInputChanged,
	setContentSearchIndex,
} from './actions';

import {
	RESOURCES__CONTENT_FETCH_REQUESTED,
	RESOURCES__CONTENT_FETCH_FAILED,
	RESOURCES__CONTENT_FETCH_SUCCEEDED,
	RESOURCES__CONTENT_SET_SEARCH_INDEX,
	RESOURCES,
	RESOURCES_DESCRIPTION,
	SEARCH_PLACEHOLDER,
	NO_RESULTS_MESSAGE,
} from './constants';

import { SEARCH_INPUT__CHANGED } from '../../components/sn-aes-search-input/constants';

import styles from './sn-aes-page-resources.scss';

createCustomElement('sn-aes-page-resources', {
	view: state => {
		const {
			filteredContent,
			searchInputValue,
			properties: { topics, loading },
		} = state;

		const noSearchResults = searchInputValue && !filteredContent.length;

		const renderContent = () => {
			if (noSearchResults) {
				return (
					<div className="empty-state-container">
						<sn-aes-empty-state message={NO_RESULTS_MESSAGE} hideActions={true} />
					</div>
				);
			}

			return (
				<div className="content-container">
					<sn-aes-resources-content
						content={filteredContent}
						topics={topics}
						showLoader={loading}
					/>
				</div>
			);
		};

		return (
			<sn-aes-container>
				<sn-aes-section-header icon="news-fill" headerText={RESOURCES} />
				<p className="resources-description">{RESOURCES_DESCRIPTION}</p>
				<div className="search-container">
					<sn-aes-search-input
						name="sn-aes-resources-search-input"
						className="search-input"
						placeholder={SEARCH_PLACEHOLDER}
						inputValue={searchInputValue}
						setAutoFocus={true}
					/>
				</div>
				{renderContent()}
			</sn-aes-container>
		);
	},
	actionHandlers: {
		[RESOURCES__CONTENT_FETCH_REQUESTED]: getResourcesPageContent,
		[RESOURCES__CONTENT_FETCH_SUCCEEDED]: getResourcesContentSuccess,
		[RESOURCES__CONTENT_FETCH_FAILED]: getResourcesContentFailed,
		[RESOURCES__CONTENT_SET_SEARCH_INDEX]: setContentSearchIndex,
		[SEARCH_INPUT__CHANGED]: handleSearchInputChanged,
	},
	properties: {
		content: {
			default: [],
		},
		topics: {
			default: [],
		},
		loading: {
			default: true,
		},
	},
	initialState: {
		filteredContent: [],
		contentIndex: [],
		searchInputValue: '',
	},
	styles,
});
