import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment, createRef } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';

import { COPY_TEXT__COPY_SUCCEEDED, COPY_TEXT__COPY_FAILED } from './constants';

import styles from './sn-aes-copy-text.scss';

createCustomElement('sn-aes-copy-text', {
	view: state => {
		const { inputRef } = state;
		const { textToCopy } = state.properties;

		return (
			<Fragment>
				<input
					type="hidden"
					ref={inputRef}
					value={textToCopy}
					readonly
					aria-hidden="true"
				/>
				<div className="width-limiter">
					<div className="width-expander" aria-hidden="true">
						{textToCopy}
					</div>
					<slot />
				</div>
				<now-button-bare variant="secondary" size="sm" label={t('Copy')} />
			</Fragment>
		);
	},
	initialState: {
		copied: false,
		inputRef: null,
	},
	properties: {
		textToCopy: {
			default: '',
		},
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ updateState }) => {
			updateState({
				inputRef: createRef(),
			});
		},
		['NOW_BUTTON_BARE#CLICKED']: async ({ action, state: { inputRef }, dispatch }) => {
			action.stopPropagation();

			if (!navigator.clipboard) {
				// only IE-11 doesnt supports Clipboard API
				dispatch(COPY_TEXT__COPY_FAILED);
				return;
			}

			try {
				await navigator.clipboard.writeText(inputRef.current.value);
				dispatch(COPY_TEXT__COPY_SUCCEEDED);
			} catch (e) {
				dispatch(COPY_TEXT__COPY_FAILED);
			}
		},
		[COPY_TEXT__COPY_SUCCEEDED]: ({ updateState }) => {
			updateState({
				copied: true,
			});
		},
		[COPY_TEXT__COPY_FAILED]: ({ updateState }) => {
			updateState({
				copied: false,
			});
		},
	},
	styles,
});
