import uuid from 'uuid';

import {
	BEHAVIOR_NAME,
	ALERTS_BEHAVIOR__ADD_ITEM,
	ALERTS_BEHAVIOR__REMOVE_ITEM,
	ALERTS_BEHAVIOR__CLEANUP,
	ALERTS_BEHAVIOR__CLEAR,
} from './constants';

export default {
	name: BEHAVIOR_NAME,
	initialState: {
		alerts: [],
		initialPageLoad: true,
	},
	actionHandlers: {
		/**
		 * Using manageItems prop on now-alert-list
		 * so we must handle these actions to
		 * manage removal and clearing of alert items
		 * when action or dismiss all buttons are clicked.
		 */
		['NOW_ALERT#ACTION_CLICKED']: ({ action, dispatch }) => {
			action.stopPropagation();

			const {
				payload: { alertId },
			} = action;
			dispatch(ALERTS_BEHAVIOR__REMOVE_ITEM, { id: alertId });
		},

		['NOW_ALERT_LIST#ITEMS_SET']: ({ action, dispatch }) => {
			action.stopPropagation();

			/**
			 * If there are no alerts left in the value array,
			 * it means either this action was dispatched as the result of
			 * clicking the action of the last present alert, or the
			 * dismiss all button was clicked while multiple alerts were present.
			 */
			const {
				payload: { value },
			} = action;
			if (value && value.length) {
				return;
			}

			dispatch(ALERTS_BEHAVIOR__CLEAR);
		},

		// See <now-alert> and <now-alert-list> documentation for list of available props
		[ALERTS_BEHAVIOR__ADD_ITEM]: ({ action: { payload }, updateState }) => {
			const defaults = {
				id: uuid.v4(),
				status: 'info',
				icon: 'bell-outline',
				action: { type: 'dismiss' },
				/**
				 * By default alerts are cleared during a route transition.
				 *
				 * Set this to a number higher than zero if you want
				 * the alert to persist across a route transition.
				 *
				 * Use case example: During form submit, add a success alert,
				 * and transition to a different page. Alert will exist on new page.
				 */

				pagesToPersist: 0,
			};

			updateState({
				path: `behaviors.${BEHAVIOR_NAME}.alerts`,
				operation: 'push',
				value: { ...defaults, ...payload },
			});
		},

		[ALERTS_BEHAVIOR__REMOVE_ITEM]: ({
			action: {
				payload: { id },
			},
			updateState,
		}) => {
			updateState(({ state: { behaviors: { ALERTS_BEHAVIOR: { alerts } } } }) => {
				const newAlerts = alerts.filter(alert => alert.id !== id);
				const shouldUpdate = newAlerts.length !== alerts.length;

				return {
					path: `behaviors.${BEHAVIOR_NAME}.alerts`,
					operation: 'set',
					value: newAlerts,
					shouldUpdate: shouldUpdate,
				};
			});
		},

		[ALERTS_BEHAVIOR__CLEANUP]: ({ updateState }) => {
			updateState(
				({
					state: {
						behaviors: {
							ALERTS_BEHAVIOR: { alerts, initialPageLoad },
						},
					},
				}) => {
					// An initial route entered action is dispatched when the app loads and the first page is loaded.
					// We want to skip alert cleanup on the initial dispatch.
					if (initialPageLoad) {
						return {
							path: `behaviors.${BEHAVIOR_NAME}.initialPageLoad`,
							operation: 'set',
							value: false,
							shouldUpdate: true,
						};
					}

					if (alerts.length === 0) {
						return {
							path: `behaviors.${BEHAVIOR_NAME}.alerts`,
							shouldUpdate: false,
						};
					}

					const persistedAlerts = alerts.filter(alert => alert.pagesToPersist > 0);

					const decrementedAlerts = persistedAlerts.map(alert => {
						if (alert.pagesToPersist > 0) {
							return { ...alert, pagesToPersist: alert.pagesToPersist - 1 };
						}

						return alert;
					});

					return {
						path: `behaviors.${BEHAVIOR_NAME}.alerts`,
						operation: 'set',
						value: decrementedAlerts,
						shouldUpdate: true,
					};
				}
			);
		},

		[ALERTS_BEHAVIOR__CLEAR]: ({ updateState }) => {
			updateState({
				path: `behaviors.${BEHAVIOR_NAME}.alerts`,
				operation: 'set',
				value: [],
			});
		},
	},
};
