import routerBehavior from '../../behaviors/router';

import '../../pages/sn-aes-page-app';
import '../../pages/sn-aes-page-home';
import '../../pages/sn-aes-page-resources';
import '../../pages/sn-aes-page-my-apps';
import '../../pages/sn-aes-page-template';
import '../../pages/sn-aes-page-templates';
import '../../pages/sn-aes-page-404';
import '../../../../../../../sn-aes-role-builder';

export const ROUTE_DEFINITIONS = {
	behavior: routerBehavior,
	options: {
		/**
		 * Ideally, the app's namespace would be provided by uxf somehow,
		 * but until then, we rely on a hard-coded build-time solution
		 *
		 * This will receive `state` and `properties` of the host component
		 * ...for when it's a possibility
		 */
		namespace: () =>
			// eslint-disable-next-line
			process.env.NODE_ENV === 'development' ? '' : '/now/appenginestudio',

		/**
		 * Define routes by path length.
		 * Longer paths go higher up while shorter ones lower
		 *
		 * `initialState` denotes properties and values that will always be part of that route's state.
		 * Here, nearly every route has a `PageComponent` property to define which component will act
		 * as that route's "outlet". This binds each route to that component via the `getPageForRoute` function above
		 *
		 * `initialState` serves as a way for a transition's origin context to not need provide all values the desination expects.
		 *
		 * @example
		 * transitionTo(dispatch, 'my-apps.app', { sysId: 'sys_id' })
		 * sn-aes-page-app#routeState === {
		 * 	PageComponent: 'sn-aes-page-app',
		 * 	tabs: [],
		 * 	sysId: 'sys_id',
		 * 	tabId: 'sys_id'
		 * }
		 *
		 * transitionTo(dispatch, 'my-apps.app', { sysId: 'sys_id', tabId: 'tab_id' })
		 * sn-aes-page-app#routeState === {
		 * 	PageComponent: 'sn-aes-page-app',
		 * 	tabs: [],
		 * 	sysId: 'sys_id',
		 * 	tabId: 'tab_id'
		 * }
		 */
		routes: {
			'my-apps.app': {
				path: '/my-apps/:sysId',
				initialState: {
					PageComponent: 'sn-aes-page-app',
					tabs: [],
				},
				onEnter({ routeState, updateRouteState }) {
					// default the tabId on route state to the sysId if the `transitionTo` or `replaceWith`
					// didn't provide it already
					if (typeof routeState.tabId !== 'string') {
						updateRouteState({ tabId: routeState.sysId });
					}
				},
			},
			home: {
				path: '/home',
				initialState: {
					PageComponent: 'sn-aes-page-home',
				},
			},
			'my-apps': {
				path: '/my-apps',
				initialState: {
					PageComponent: 'sn-aes-page-my-apps',
				},
			},
			template: {
				path: '/template',
				initialState: {
					PageComponent: 'sn-aes-page-template',
					operations: [],
				},
				onExit({ targetRouteName, currentRouteState }) {
					switch (targetRouteName) {
						case 'my-apps.app':
							// eslint-disable-next-line no-console
							console.warn(
								`The app was created using the initParams: \n ${JSON.stringify(
									currentRouteState.initParams,
									null,
									2
								)}`
							);
							break;
					}
				},
			},
			templates: {
				path: '/templates',
				initialState: {
					PageComponent: 'sn-aes-page-templates',
				},
			},
			resources: {
				path: '/resources',
				initialState: {
					PageComponent: 'sn-aes-page-resources',
				},
			},
			'aes-role-builder': {
				path: '/aes-role-builder/application/:appId/role/:roleId',
				initialState: {
					PageComponent: 'sn-aes-role-builder',
				},
			},
			root: {
				path: '/',
				// `replaceWith` in this context is different than its counterpart that is exported from the router behavior.
				// While their end-result is the same, using this context better syncs with the seismic lifecycle
				onEnter({ replaceWith }) {
					// We're loading to the root of the app
					// Immediately re-route to the 'home' page without adding
					// the transaction to browser history
					replaceWith('home');
				},
			},
		},
	},
};
