import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import get from 'lodash/get';
import map from 'lodash/map';
import intersection from 'lodash/intersection';
import flatten from 'lodash/flatten';
import '@servicenow/now-modal';
import '@servicenow/now-alert';

import styles from './sn-aes-object-template-details-modal.scss';

import { ROOT__HIDE_MODAL } from '../sn-app-engine-studio/constants';

import { PROVIDER_PROPERTIES } from '../sn-aes-provider';
import getAdminContact from '../../helpers/get-admin-contact';

import {
	DEPENDENCIES_DATA_PROPERTY,
	DEPENDENCY_STATUS_RESULT_TYPES_MAP,
} from '../../behaviors/dependencies-data';
import { getDependencyCheckDetails } from '../../behaviors/dependencies-data/helper';

const dependencyCheckTypes = flatten([
	DEPENDENCY_STATUS_RESULT_TYPES_MAP.SPOKE_INSTALLED,
	DEPENDENCY_STATUS_RESULT_TYPES_MAP.SPOKE_CONFIGURED,
]);

const getTemplateDependenciesAlerts = (template, dependencies = {}, adminContact) =>
	map(template.dependencies || {}, (checkTypes, scopeName) => {
		const dependency = dependencies[scopeName];
		const details = getDependencyCheckDetails(
			dependency,
			intersection(dependencyCheckTypes, checkTypes),
			adminContact
		);

		if (details && !details.hasPassedChecks) {
			return (
				<now-alert
					id={details.id}
					status={details.severity}
					icon={details.messageIcon}
					header={details.message}
					content={{
						type: 'jsx',
						value: details.messageDetails,
					}}
				/>
			);
		}
		return null;
	});

createCustomElement('sn-aes-object-template-details-modal', {
	view: ({ properties: { template, dependencies, prodAdminContact } }) => {
		const { hero1_image, icon } = template.details;

		return (
			<now-modal
				opened
				manage-opened
				size="lg"
				contentFullWidth
				headerLabel={<span aria-label={template.name.display_value} />}
				footerActions={[
					{
						label: t('Begin'),
						variant: 'primary',
					},
				]}>
				{hero1_image ? (
					<div className="image-container">
						<img className="hero1_image" src={hero1_image} alt="" />
					</div>
				) : null}
				<div
					class={{
						'content-container': true,
						'-with-image': !!hero1_image,
					}}>
					<div className="title">
						{icon ? <img className="title-icon" src={icon} alt="icon" /> : null}
						<h2 className="title-text">{template.name.display_value}</h2>
					</div>
					<div className="content">
						{getTemplateDependenciesAlerts(template, dependencies, prodAdminContact)}
						{get(template, 'details.detailed_description.display_value', '') ||
							get(template, 'details.short_description.display_value', '')}
					</div>
				</div>
			</now-modal>
		);
	},
	properties: {
		template: {
			default: {},
		},
		onConfirm: {
			default: () => {},
		},
		prodInstanceAesConfig: {
			default: PROVIDER_PROPERTIES.prodInstanceAesConfig,
		},
		dependencies: {
			default: PROVIDER_PROPERTIES[DEPENDENCIES_DATA_PROPERTY],
		},
		prodAdminContact: {
			computed({ properties: { prodInstanceAesConfig } }) {
				return getAdminContact(prodInstanceAesConfig);
			},
		},
	},
	actionHandlers: {
		'NOW_MODAL#FOOTER_ACTION_CLICKED': ({
			action: { stopPropagation },
			properties: { onConfirm },
			dispatch,
		}) => {
			stopPropagation();
			dispatch(ROOT__HIDE_MODAL);
			onConfirm();
		},
	},
	styles,
});
