import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-button';
import { PAGINATE__PAGE_SET, PAGINATE__PAGE_UPDATED } from './constants';

// FIXME: This component needs to be built properly, this is added for base functionality only

createCustomElement('sn-aes-simple-paginate', {
	view: (state, dispatch) => {
		const { pageSize, itemLength, currentPage } = state.properties;
		const handlePageDown = () => {
			if (currentPage > 1) {
				dispatch(PAGINATE__PAGE_SET, currentPage - 1);
			}
		};
		const handlePageUp = () => {
			if (currentPage < itemLength / pageSize) {
				dispatch(PAGINATE__PAGE_SET, currentPage + 1);
			}
		};
		return (
			<div>
				<now-button bare iconName="minus-fill" on-click={handlePageDown} />
				<span>{`${currentPage} of ${Math.ceil(itemLength / pageSize)}`}</span>
				<now-button bare iconName="plus-fill" on-click={handlePageUp} />
			</div>
		);
	},
	properties: {
		name: { default: null, reflect: true },
		itemLength: { default: 100 },
		pageSize: { default: 10 },
		currentPage: { default: 1, reflect: true },
		managed: { default: false },
	},
	actionHandlers: {
		[PAGINATE__PAGE_SET]: ({ state, action: { payload }, dispatch, updateProperties }) => {
			const { managed, pageSize, itemLength, name } = state.properties;
			const pageData = {
				currentPage: payload,
				pageSize,
				itemLength,
				name,
			};
			if (!managed) {
				updateProperties({ ...pageData });
			}
			dispatch(PAGINATE__PAGE_UPDATED, { ...pageData });
		},
	},
});
