import {
	DEPENDENCY_RESULT_TYPE_DETAILS_MAP,
	PRIORITIZED_DEPENDENCY_RESULT_TYPES,
} from './constants';

export const getDependencyCheckDetails = (dependency, resultTypes, adminContact) => {
	let dependencyDetails = { ...dependency };
	let dependencyHasResult = false;
	let resultTypesToCheck = PRIORITIZED_DEPENDENCY_RESULT_TYPES;

	if (resultTypes) {
		resultTypesToCheck = resultTypes;
	}

	if (resultTypesToCheck.length === 0) {
		return;
	}

	PRIORITIZED_DEPENDENCY_RESULT_TYPES.some(type => {
		if (resultTypesToCheck.includes(type) && dependency.hasOwnProperty(type)) {
			dependencyHasResult = true;
			const statusDetails = DEPENDENCY_RESULT_TYPE_DETAILS_MAP[type];
			dependencyDetails.hasPassedChecks = dependency[type];
			dependencyDetails.message = statusDetails.getMsg(dependency.name);
			dependencyDetails.messageDetails = statusDetails.getDetails(adminContact);
			dependencyDetails.messageIcon = statusDetails.icon;
			dependencyDetails.severity = statusDetails.severity;
			return !dependency[type]; // Found the highest priority failure, can exit now
		}
		return false;
	});

	if (dependencyHasResult) {
		return dependencyDetails;
	}
};
