import { t } from 'sn-translate';
import get from 'lodash/get';

import '@servicenow/now-text-link';

export const BEHAVIOR_NAME = 'DEPENDENCIES_DATA_BEHAVIOR';

export const DEPENDENCIES_DATA_BEHAVIOR__GET_TEMPLATE_DEPENDENCIES = `${BEHAVIOR_NAME}#GET_TEMPLATE_DEPENDENCIES`;
export const DEPENDENCIES_DATA_BEHAVIOR__SET_DEPENDENCIES = `${BEHAVIOR_NAME}#SET_DEPENDENCIES `;
export const DEPENDENCIES_DATA_BEHAVIOR__FETCH_DEPENDENCIES = `${BEHAVIOR_NAME}#FETCH_DEPENDENCIES`;
export const DEPENDENCIES_DATA_BEHAVIOR__FETCH_DEPENDENCIES_SUCCEEDED = `${BEHAVIOR_NAME}#FETCH_DEPENDENCIES_SUCCEEDED`;
export const DEPENDENCIES_DATA_BEHAVIOR__FETCH_DEPENDENCIES_FAILED = `${BEHAVIOR_NAME}#FETCH_DEPENDENCIES_FAILED`;

export const DEPENDENCIES_DATA_PROPERTY = 'dependenciesDataProperty';

export const GET_SPOKE_URL = '/api/now/template_spokes_details';

// Dependency definitions
export const DEPENDENCY_RESULT_TYPES = {
	TEMPLATE_SPOKE_INSTALLED: 'TEMPLATE_SPOKE_INSTALLED',
	SPOKE_INSTALLED: 'SPOKE_INSTALLED',
	CONNECTION_CONFIGURED: 'CONNECTION_CONFIGURED',
};

export const PRIORITIZED_DEPENDENCY_RESULT_TYPES = [
	DEPENDENCY_RESULT_TYPES.SPOKE_INSTALLED,
	DEPENDENCY_RESULT_TYPES.TEMPLATE_SPOKE_INSTALLED,
	DEPENDENCY_RESULT_TYPES.CONNECTION_CONFIGURED,
];

export const DEPENDENCY_STATUS_RESULT_TYPES_MAP = {
	SPOKE_INSTALLED: [
		DEPENDENCY_RESULT_TYPES.TEMPLATE_SPOKE_INSTALLED,
		DEPENDENCY_RESULT_TYPES.SPOKE_INSTALLED,
	],
	SPOKE_CONFIGURED: [DEPENDENCY_RESULT_TYPES.CONNECTION_CONFIGURED],
};

const uninstalledDetails = {
	icon: 'lock-outline',
	severity: 'critical',
	getId: res => get(res, 'dependency.inputs.scope_name'),
	getName: res =>
		get(res, 'dependency.inputs.spoke_name') || get(res, 'dependency.inputs.scope_name'),
	getMsg: name => t('{0} is not installed.', name) + ' ',
	getDetails: adminContact => (
		<span>
			<now-text-link
				href="/api/now/v1/context_doc_url/CSHelp:IntegrationHub-Licensing"
				label={t('Request an IntegrationHub subscription.') + ' '}
				opensWindow
			/>
			{t('Then install this spoke from ServiceNow store.')}
			{t('If you need help setting up this spoke, contact your system administrator')}
			{adminContact ? (
				<now-text-link
					href={`mailto:${adminContact}`}
					label={t('Contact administrator')}
					opensWindow
				/>
			) : null}
		</span>
	),
};

export const DEPENDENCY_RESULT_TYPE_DETAILS_MAP = {
	[DEPENDENCY_RESULT_TYPES.TEMPLATE_SPOKE_INSTALLED]: uninstalledDetails,
	[DEPENDENCY_RESULT_TYPES.SPOKE_INSTALLED]: uninstalledDetails,
	[DEPENDENCY_RESULT_TYPES.CONNECTION_CONFIGURED]: {
		icon: 'triangle-exclamation-outline',
		severity: 'warning',
		getId: res => get(res, 'dependency.inputs.scope_name', 'dependency.inputs.alias_id'),
		getName: res =>
			get(res, 'dependency.inputs.spoke_name') || get(res, 'dependency.inputs.scope_name'),
		getMsg: name => t('{0} does not have a connection set up.', name) + ' ',
		getDetails: adminContact => (
			<span>
				{t(
					'You can proceed with your flow, but you may need to contact your system administrator to set up the connection.'
				)}
				{adminContact ? (
					<now-text-link
						href={`mailto:${adminContact}`}
						label={t('Contact administrator')}
						opensWindow
					/>
				) : null}
			</span>
		),
	},
};
