import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import { PROVIDER_PROPERTIES } from '../../components/sn-aes-provider';
import {
	TEMPLATE_DATA_BEHAVIOR__INITIAL_FETCH,
	TEMPLATE_DATA_PROPERTY,
	APPLICATION_TEMPLATES,
} from '../../behaviors/template-data';
import { SEARCH_INPUT__CHANGED } from '../../components/sn-aes-search-input/constants';
import { ROOT__SHOW_OVERLAY } from '../../components/sn-app-engine-studio/constants';
import {
	PAGE_TEMPLATES__SET_SEARCH_INDEX,
	PAGE_TEMPLATES__TEMPLATE_CARD_CLICKED,
} from './constants';

import { createObjectSearchIndex, getFilterFnForInputLength } from '../../helpers/object-search';

import '@servicenow/now-icon';
import '../../components/sn-aes-image-card';
import '../../components/sn-aes-container';
import '../../components/sn-aes-app-template-card';
import '../../components/sn-aes-section-header';

import styles from './sn-aes-page-templates.scss';
import '../../components/sn-aes-empty-state';
import '../../components/sn-aes-template-overlay';

import { SEARCH_INPUT_MINIMUM_LENGTH } from '../../helpers/constants';

const filterAppTemplates = getFilterFnForInputLength(SEARCH_INPUT_MINIMUM_LENGTH);

export const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
		dispatch(TEMPLATE_DATA_BEHAVIOR__INITIAL_FETCH);
		dispatch(PAGE_TEMPLATES__SET_SEARCH_INDEX);
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
		dispatch,
		action: {
			payload: { name },
		},
	}) => {
		if (name === 'templates') {
			dispatch(PAGE_TEMPLATES__SET_SEARCH_INDEX);
		}
	},
	[PAGE_TEMPLATES__SET_SEARCH_INDEX]: ({ properties: { appTemplates }, state, updateState }) => {
		if (!appTemplates.length) {
			return;
		}

		const { searchInput } = state;
		const appTemplatesIndex = createObjectSearchIndex(
			'sys_id',
			['name.display_value', 'details.short_description.display_value'],
			appTemplates
		);

		updateState({
			appTemplatesIndex,
			filteredAppTemplates: filterAppTemplates(
				{
					list: appTemplates,
					index: appTemplatesIndex,
				},
				searchInput
			),
		});
	},
	[SEARCH_INPUT__CHANGED]: ({
		action,
		state: { appTemplatesIndex },
		properties: { appTemplates },
		updateState,
	}) => {
		action.stopPropagation();
		const { value: searchInput } = action.payload;

		updateState({
			searchInput,
			filteredAppTemplates: filterAppTemplates(
				{
					list: appTemplates,
					index: appTemplatesIndex,
				},
				searchInput
			),
		});
	},
	[PAGE_TEMPLATES__TEMPLATE_CARD_CLICKED]: ({ action, dispatch }) => {
		action.stopPropagation();
		dispatch(ROOT__SHOW_OVERLAY, {
			renderer: () => <sn-aes-template-overlay template={action.payload.template} />,
		});
	},
};

/**
 * Templates page
 *
 * ```jsx
 * <sn-aes-page-templates />
 * ```
 *
 * @seismicElement sn-aes-page-templates
 * @summary This page lists all the available templates to choose from.
 */
createCustomElement('sn-aes-page-templates', {
	view: state => {
		const { searchInput, filteredAppTemplates } = state;
		const isAppTemplateEmpty = filteredAppTemplates.length === 0;
		const renderFilteredAppTemplates = () => {
			if (searchInput && isAppTemplateEmpty) {
				return (
					<sn-aes-empty-state
						message={t(
							"This template doesn't exist. Try using another template or create an app from scratch, instead."
						)}
					/>
				);
			} else if (isAppTemplateEmpty) {
				return (
					<sn-aes-empty-state
						message={t(
							'No templates found. Try creating an app from scratch, instead.'
						)}
					/>
				);
			} else {
				return (
					<div className="four-up">
						{filteredAppTemplates.map(template => (
							<sn-aes-app-template-card
								template={template}
								id={template.sys_id}
								key={template.sys_id}
								onClickAction={PAGE_TEMPLATES__TEMPLATE_CARD_CLICKED}
							/>
						))}
					</div>
				);
			}
		};

		return (
			<sn-aes-container>
				<section className="templates-page-container">
					<sn-aes-section-header icon="documents-fill" headerText={t('Templates')} />
					<div className="search-container">
						<sn-aes-search-input
							inputValue={searchInput}
							name="sn-aes-page-templates-search-input"
							className="search-input"
							placeholder={t('Search all templates...')}
						/>
					</div>
					{renderFilteredAppTemplates()}
				</section>
			</sn-aes-container>
		);
	},
	initialState: {
		searchInput: '',
		filteredAppTemplates: [],
		appTemplatesIndex: [],
	},
	properties: {
		templates: {
			default: PROVIDER_PROPERTIES[TEMPLATE_DATA_PROPERTY],
		},
		appTemplates: {
			computed({ properties: { templates } }) {
				return templates[APPLICATION_TEMPLATES];
			},
		},
	},
	actionHandlers,
	styles,
});
