import get from 'lodash/get';

import { CREATE_APPLICATION_TEMPLATE_OPERATION } from '../components/sn-aes-wizard-manager/constants';

export default function isCreateFromScratchApp(operations) {
	// Blocking stepper for apps created from application templates
	if (get(operations, '[0].operation') === CREATE_APPLICATION_TEMPLATE_OPERATION) {
		return false;
	}

	// Allowing stepper for apps created from scratch and object templates
	return true;
}
