import { createCustomElement } from '@servicenow/ui-core';

import styles from './sn-aes-skeleton-card.scss';

createCustomElement('sn-aes-skeleton-card', {
	view({ properties: { type } }) {
		if (type === 'minimal') {
			return (
				<div className="skeleton skeleton--minimal">
					<div className="skeleton__image-wrap">
						<div className="skeleton__icon" />
					</div>
					<div className="skeleton__body">
						<div className="skeleton__text" />
					</div>
				</div>
			);
		} else {
			return (
				<div className="skeleton">
					<div className="skeleton__image-wrap" />
					<div className="skeleton__body">
						<div className="skeleton__heading" />
						<div className="skeleton__text" />
						<div className="skeleton__text" />
					</div>
				</div>
			);
		}
	},
	properties: {
		type: {
			default: '',
			schema: { type: 'string' },
		},
	},
	styles,
});
