import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import { ROOT__HIDE_MODAL } from './../sn-app-engine-studio/constants';

import '@servicenow/now-button';
import '@servicenow/now-modal';

import successSvg from './assets/illustration_submit.svg';
import styles from './sn-aes-submit-success.scss';

createCustomElement('sn-aes-submit-success', {
	view: () => {
		return (
			<now-modal
				opened
				manage-opened
				headerLabel={t('Success')}
				size="lg"
				footerActions={[
					{
						id: 'close',
						size: 'md',
						variant: 'primary',
						label: t('Close'),
					},
				]}>
				<div className="submission-container">
					<div className="content">
						<h2>{t('Nice Job! Your app is on the way for review')}</h2>
						<div>
							{t(
								"You'll get a notification when it's ready. You can track your app's status from your app page."
							)}
						</div>
						<div className="now-align -inline-center image-container">
							<img src={successSvg} alt="" />
						</div>
					</div>
				</div>
			</now-modal>
		);
	},
	actionHandlers: {
		['NOW_MODAL#FOOTER_ACTION_CLICKED']: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(ROOT__HIDE_MODAL);
		},
	},
	styles,
});
