import { actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	BEHAVIOR_NAME,
	SET_SCOPE_URL,
	SESSION_SCOPE_BEHAVIOR__SET_APPLICATION_ID,
	SESSION_SCOPE_BEHAVIOR__SET_SCOPE,
	SESSION_SCOPE_BEHAVIOR__SET_SCOPE_SUCCEEDED,
	SESSION_SCOPE_BEHAVIOR__SET_SCOPE_FAILED,
	SESSION_SCOPE_BEHAVIOR__VISIBILTY_CHANGED,
} from './constants';

import { addVisibilityListener } from './visibility-util';

const sessionScopeBehavior = {
	name: BEHAVIOR_NAME,
	initialState: {
		appId: '',
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			dispatch,
			action: {
				payload: { options },
			},
		}) => {
			if (options.onVisibleOnly) {
				// add event listeners for visibility API
				addVisibilityListener(isHidden =>
					dispatch(SESSION_SCOPE_BEHAVIOR__VISIBILTY_CHANGED, { isHidden })
				);
			}
		},
		[SESSION_SCOPE_BEHAVIOR__SET_APPLICATION_ID]: ({
			dispatch,
			action: { payload: appId },
			updateState,
		}) => {
			updateState({
				appId,
			});
			dispatch(SESSION_SCOPE_BEHAVIOR__SET_SCOPE, {
				data: {
					app_id: appId,
				},
			});
		},
		[SESSION_SCOPE_BEHAVIOR__SET_SCOPE]: createHttpEffect(SET_SCOPE_URL, {
			method: 'PUT',
			successActionType: SESSION_SCOPE_BEHAVIOR__SET_SCOPE_SUCCEEDED,
			errorActionType: SESSION_SCOPE_BEHAVIOR__SET_SCOPE_FAILED,
			dataParam: ['data'],
			batch: false,
		}),
		[SESSION_SCOPE_BEHAVIOR__VISIBILTY_CHANGED]: ({
			dispatch,
			state: { appId },
			action: {
				payload: { isHidden },
			},
		}) => {
			if (!isHidden) {
				dispatch(SESSION_SCOPE_BEHAVIOR__SET_SCOPE, {
					data: {
						app_id: appId,
					},
				});
			}
		},
	},
};

export default sessionScopeBehavior;
