/**
 * This meant to be used with search results for template pages where the search results aren't returned for a query.
 *
 * @param message
 * @returns {JSX.Element}
 */

import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-button';
import { t } from 'sn-translate';
import styles from './sn-aes-empty-state.scss';
import { transitionTo } from '../../behaviors/router';
import { CREATE_APPLICATION_OPERATION } from '../sn-aes-wizard-manager/constants';
import dispatchActionViaPayload from '../../helpers/dispatch-action-via-payload';
import { EMPTY_STATE__CREATE_APP_CLICKED } from './constants';

const actionHandlers = {
	['NOW_BUTTON#CLICKED']: dispatchActionViaPayload,
	[EMPTY_STATE__CREATE_APP_CLICKED]: ({ action, dispatch }) => {
		action.stopPropagation();
		transitionTo(dispatch, 'template', {
			operations: [{ operation: CREATE_APPLICATION_OPERATION }],
		});
	},
};

const view = state => {
	const { message, hideActions } = state.properties;

	return (
		<div className="sn-aes-empty-state-container">
			<div className="sn-aes-empty-state-icon">
				<img src="empty-page.svg" alt={t('no results found')} height="200" width="200" />
			</div>

			<div className="sn-aes-empty-state-title">{t('No results found')}</div>

			<div className="sn-aes-empty-state-body">{message}</div>

			{!hideActions ? (
				<div className="sn-aes-empty-state-actions">
					<now-button
						size="md"
						variant="primary"
						label={t('Create app')}
						append-to-payload={{
							actionName: EMPTY_STATE__CREATE_APP_CLICKED,
						}}
					/>
				</div>
			) : null}
		</div>
	);
};

createCustomElement('sn-aes-empty-state', {
	view,
	properties: {
		message: { default: '' },
		hideActions: {
			default: false,
		},
	},
	actionHandlers,
	styles,
});
