// Generic variables
export const BEHAVIOR_NAME = 'APPLICATIONS_DATA_BEHAVIOR';

// Applications data variables
export const APPLICATIONS_DATA_PROPERTY = 'applicationsDataProperty';
export const APPLICATIONS_INITIAL_FETCH_PROPERTY = 'appsInitialFetch';

// Allows us to know when the request is complete, regardless if it succeeded or failed
export const APPLICATIONS_REQUEST_COMPLETED_PROPERTY = 'appsRequestCompleted';

// Applications data actions - dispatch APPLICATIONS_DATA_BEHAVIOR__INITIAL_FETCH from pages to ensure
// that the applications data is availabe. If it had been previously fetched, it won't try to fetch
// it again. To force it to refresh, dispatch the fetch action directly.
export const APPLICATIONS_DATA_BEHAVIOR__INITIAL_FETCH = `${BEHAVIOR_NAME}#INITIAL_FETCH`;

export const FETCH_APPLICATIONS_URL = '/api/sn_app_eng_studio/applications';
export const APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS = `${BEHAVIOR_NAME}#FETCH_APPLICATIONS`;
export const APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_STARTED = `${BEHAVIOR_NAME}#APPLICATIONS_FETCH_STARTED`;
export const APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_SUCCEEDED = `${BEHAVIOR_NAME}#APPLICATIONS_FETCH_SUCCEEDED`;
export const APPLICATIONS_DATA_BEHAVIOR__APPLICATIONS_FETCH_FAILED = `${BEHAVIOR_NAME}#APPLICATIONS_FETCH_FAILED`;
