import uuid from 'uuid';
import { createCustomElement } from '@servicenow/ui-core';
import {
	BUILDER_UPDATE,
	BUILDER_UPDATE_SESSION,
	BUILDER_EXIT,
	BUILDER_NEW_TAB,
} from '../sn-aes-builder/constants';

import {
	TABS__UPDATE_TAB,
	TABS__UPDATE_SESSION,
	TABS__CLOSE_TAB,
	TABS__OPEN_TABS,
} from '../sn-aes-tabs/constants';

export const actionHandlers = {
	[BUILDER_UPDATE]: ({ properties, dispatch, action: { payload } }) => {
		const { tabId } = properties;
		const { title, subtitle, hasUnsavedChanges, isLoading } = payload;

		dispatch(TABS__UPDATE_TAB, {
			tabId,
			title,
			subtitle,
			isUnsaved: hasUnsavedChanges,
			isLoading,
		});
	},
	[BUILDER_UPDATE_SESSION]: ({ properties, dispatch, action: { payload } }) => {
		const { tabId } = properties;
		const { title, subtitle, url } = payload;

		dispatch(TABS__UPDATE_SESSION, {
			tabId,
			title,
			subtitle,
			url,
		});
	},
	[BUILDER_EXIT]: ({
		dispatch,
		properties: { tabId },
		action: {
			payload: { nextSelectedTabId },
		},
	}) => {
		dispatch(TABS__CLOSE_TAB, { tabId, nextSelectedTabId });
	},
	[BUILDER_NEW_TAB]: ({
		action: {
			payload: { url, title, subtitle = '', id },
		},
		dispatch,
	}) => {
		if (!url || !title) {
			throw new Error('Please provide both a url and title in payload');
		}

		// To prevent consumers from opening app engine studio recursively
		if (url.includes('/now/appenginestudio')) {
			throw new Error('URL provided is not allowed');
		}

		const newTab = {
			id: id ? id : uuid.v4(), // TODO: Maybe find a better way to generate an ID
			title,
			subtitle,
			url,
			isLoading: true,
		};
		dispatch(TABS__OPEN_TABS, { tabs: [newTab] });
	},
};

createCustomElement('sn-aes-builder-proxy', {
	properties: {
		tabId: { default: null },
	},
	view: () => <slot />,
	actionHandlers,
});
