import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import get from 'lodash/get';

import '@servicenow/now-icon';
import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import '@servicenow/now-loader';

import '../../components/sn-aes-image-card';
import { OBJECT_DETAILS_MODAL__CONFIRM_CLICKED } from '../../components/sn-aes-object-template-details-modal';

import { IMAGE_CARD__CARD_CLICKED } from '../../components/sn-aes-image-card/constants';
import { PROVIDER_PROPERTIES } from '../../components/sn-aes-provider';

import styles from './sn-aes-app-add-object.scss';

import { ADD_OBJECT__CONTINUE } from './constants';
import {
	OBJECT_TEMPLATES,
	TEMPLATE_DATA_BEHAVIOR__INITIAL_FETCH,
	CATEGORIZED_TEMPLATES_DATA_PROPERTY,
	CATEGORIZED_TEMPLATES_REQUEST_COMPLETED_PROPERTY,
} from '../../behaviors/template-data';
import '../sn-aes-wizard-manager';
import { ROOT__SHOW_MODAL } from '../sn-app-engine-studio/constants';
import { EXPERIENCE_CATEGORY_SYS_ID } from '../sn-aes-app-tab/constants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

function view(state, { dispatch }) {
	const {
		properties: {
			filteredTemplates,
			category,
			categorizedTemplatesRequestCompleted,
			inAppHome,
		},
	} = state;

	if (!categorizedTemplatesRequestCompleted) {
		// Categorized templates is not ready, so wait for it
		return <now-loader />;
	}

	if (filteredTemplates && filteredTemplates.length === 1) {
		// We only have one object template for this category,
		// so immediately transition to the only option
		dispatch(ADD_OBJECT__CONTINUE, {
			templateSysId: filteredTemplates[0].sys_id,
			inAppHome,
		});
		return null;
	}

	return (
		<div className={`sn-aes-app-add-object category-${category.name}`}>
			<div className="text-wrapper">
				<div>
					<h1 className="title">
						{t('ADD {0}', (category.name || '').toLocaleUpperCase())}
					</h1>
				</div>
				<div className="action">
					{t(
						'What type of {0} do you want to add to your app?',
						(category.name || '').toLocaleLowerCase()
					)}
				</div>
				<div className="action-info">
					{category.sys_id === EXPERIENCE_CATEGORY_SYS_ID
						? 'Select an experience to learn more about the interface and what it can do for your users.'
						: 'Select one to learn more.'}
				</div>
			</div>
			{filteredTemplates.map(ot => (
				<sn-aes-image-card
					id={ot.sys_id}
					key={ot.sys_id}
					footerImage={get(ot, 'details.icon', '')}
					cardImage={get(ot, 'details.hero1_image', '')}
					footerTitle={get(ot, 'name.display_value', '')}
					footerDescription={get(ot, 'details.short_description.display_value', '')}
					append-to-payload={{
						template: ot,
					}}
					// Use cover size if card image is not available
					coverSize={!get(ot, 'details.hero1_image', '')}
				/>
			))}
		</div>
	);
}

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
		dispatch(TEMPLATE_DATA_BEHAVIOR__INITIAL_FETCH);
	},

	[IMAGE_CARD__CARD_CLICKED]: ({
		action: {
			stopPropagation,
			payload: { template },
		},
		dispatch,
	}) => {
		stopPropagation();

		dispatch(ROOT__SHOW_MODAL, {
			renderer: () => (
				<sn-aes-object-template-details-modal
					template={template}
					onConfirm={() => dispatch(OBJECT_DETAILS_MODAL__CONFIRM_CLICKED, { template })}
				/>
			),
		});
	},

	[OBJECT_DETAILS_MODAL__CONFIRM_CLICKED]: ({
		// FIXME: Remove once hoisting bug is fixed
		properties: { inAppHome = false },
		action: {
			stopPropagation,
			payload: { template },
		},
		dispatch,
	}) => {
		stopPropagation();
		dispatch(ADD_OBJECT__CONTINUE, { templateSysId: template.sys_id, inAppHome });
	},
};

/**
 * App Experience Objects page
 *
 * ```jsx
 * <sn-aes-app-add-object />
 * ```
 *
 * @seismicElement sn-aes-app-add-object
 * @summary This page displays all object templates with category `category` which could be added to an app
 */
createCustomElement('sn-aes-app-add-object', {
	view,
	initialState: {},
	properties: {
		appSysId: { default: '' },
		category: { default: null },
		categorizedTemplatesRequestCompleted: {
			default: PROVIDER_PROPERTIES[CATEGORIZED_TEMPLATES_REQUEST_COMPLETED_PROPERTY],
		},
		categorizedTemplates: {
			default: PROVIDER_PROPERTIES[CATEGORIZED_TEMPLATES_DATA_PROPERTY],
		},
		filteredTemplates: {
			computed({ properties: { categorizedTemplates, category } }) {
				return categorizedTemplates[OBJECT_TEMPLATES].filter(ot => {
					return ot.categories.some(cat => {
						return cat.taxonomyCategorySysId === category.sys_id;
					});
				});
			},
		},
		// FIXME: Delete once hoisting bug is fixed
		inAppHome: {
			default: false,
		},
	},
	actionHandlers,
	styles,
});
