import { actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';

import { normalizeById, groupBy } from '../../helpers/normalize';
import { generateLegacyFormUrl } from '../../helpers/generateBuilderUrl';
import { PROVIDER_PROPERTIES } from '../sn-aes-provider';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

import { createObjectSearchIndex, objectSearch } from '../../helpers/object-search';
import getEffectDetails from '../../helpers/get-effect-details';

import {
	POST_DEPLOYMENT_REQUEST_ROUTE,
	FETCH_SYS_APP_TABLE_API_ROUTE,
	FETCH_APPLICATION_STATUS_ROUTE,
	FETCH_DEPLOYMENT_REQUEST_ROUTE,
	TABLE_API_RECORD,
	CREATE_APP_DETAILS_ROUTE,
} from '../../helpers/constants';

import {
	APP_TAB__OPEN_EDITOR_TAB,
	APP_TAB__CATEGORY_CREATE_PRESSED,
	APP_TAB__SORT_TYPE_SET,
	APP_TAB__SET_SEARCH_INDEX,
	APP_TAB_DELETE_OBJECT_CLICKED,
	APP_TAB__MOBILE_CARD_CLICKED,
	APP_TAB__SUBMIT_APP_FOR_DEPLOYMENT,
	APP_TAB__SUBMIT_APP_FOR_DEPLOYMENT_SUCCEEDED,
	APP_TAB__SUBMIT_APP_FOR_DEPLOYMENT_FAILED,
	APP_TAB__GET_APP_VERSION,
	APP_TAB__GET_APP_VERSION_SUCCEEDED,
	APP_TAB__GET_APP_VERSION_FAILED,
	APP_TAB__PATCH_APP_VERSION,
	APP_TAB__PATCH_APP_VERSION_SUCCEEDED,
	APP_TAB__PATCH_APP_VERSION_FAILED,
	APP_TAB__UPDATE_APP_DETAILS,
	APP_TAB__UPDATE_APP_DETAILS_SUCCEEDED,
	APP_TAB__UPDATE_APP_DETAILS_FAILED,
	APP_TAB__APP_SUBMIT_CLICKED,
	APP_TAB__APP_SUBMIT_CONFIRMED,
	APP_TAB__FETCH_APP_STATUS,
	APP_TAB__FETCH_APP_STATUS_SUCCEEDED,
	APP_TAB__FETCH_APP_STATUS_FAILED,
	APP_TAB__FETCH_DEPLOYMENT_REQUEST_STATUS,
	APP_TAB__FETCH_DEPLOYMENT_REQUEST_STATUS_SUCCEEDED,
	APP_TAB__FETCH_DEPLOYMENT_REQUEST_STATUS_FAILED,
	APP_TAB__OPEN_DEPLOYMENT_REQUEST_STATUS,
	IN_DEVELOPMENT,
	AUTOMATION_CATEGORY_SYS_ID,
	ALL_CATEGORY,
	APP_TAB__SHOW_OVERLAY,
	APP_TAB__HIDE_OVERLAY,
	APP_TAB__SHOW_BLANK_OVERLAY,
	APP_TAB__HIDE_OVERLAY_CONFIRM,
	APP_TAB__OVERLAY_CONTINUE_CLICKED,
} from './constants';

import {
	UPDATE_APP_VERSION,
	APP_HOME__DELETE_FILE,
	FETCH_APP_THROTTLED,
} from '../../pages/sn-aes-page-app/constants';

import { MODAL_OVERLAY__OPEN_SET } from '../sn-aes-modal-overlay/constants';

import { SEARCH_INPUT__CHANGED } from '../sn-aes-search-input/constants';

import { TABS__CLOSE_TAB, TABS__OPEN_TABS } from '../sn-aes-tabs/constants';

import {
	APPLICATION_OBJECT_CARD__CARD_CLICKED,
	APPLICATION_OBJECT_CARD__ACTION_ITEM_CLICKED,
} from '../sn-aes-application-object-card/constants';

import { BUILDER_EXIT, BUILDER_EXIT_TYPE_SUCCESS } from '../sn-aes-builder/constants';

import { ROOT__SHOW_MODAL } from '../sn-app-engine-studio/constants';

import { APP_PROPERTIES__UPDATE_COMPLETED } from '../sn-aes-app-properties/constants';
import { APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS } from '../../behaviors/apps-data';
import { ALERTS_BEHAVIOR__ADD_ITEM } from '../../behaviors/alerts';

import {
	WIZARD_MANAGER__TEMPLATE_COMPLETE,
	WIZARD_MANAGER__CANCEL,
	WIZARD_MANAGER__CRIT_FAIL,
	WIZARD_MANAGER__EDIT_RECORD,
	CREATE_OBJECT_TEMPLATE_OPERATION,
} from '../sn-aes-wizard-manager/constants';
import {
	SIDEBAR_STEP_TYPE_CATEGORY,
	SIDEBAR_STEP_TYPE_COMPLETION,
	sidebarDisplay,
	sidebarSetAccessibleSteps,
	sidebarSetStep,
} from '../../behaviors/sidebar-data';
import {
	getSidebarStepFromCategory,
	setDelegate,
	getSidebarStartStep,
	showModal,
	unsetDelegate,
} from '../../pages/sn-aes-page-app/helpers';
import { SESSION_SCOPE_BEHAVIOR__SET_APPLICATION_ID } from '../../behaviors/session-scope';
import { ADD_OBJECT__CONTINUE } from '../sn-aes-app-add-object';
import { ADD_AUTOMATION__CONTINUE } from '../sn-aes-app-add-automation';

import { GUIDANCE_MODAL__CLOSE_CLICKED } from '../sn-aes-guidance-modal/constants';
import { sidebarLockSteps } from '../../behaviors/sidebar-data/helpers';
import {
	getSubmissionErrorAlert,
	appendAdminContactToAlert,
	getTabDetails,
	addTaxonomyDefinitionToFile,
	isMobileObject,
	getSubmitTooltipContent,
} from './sn-aes-app-tab-utils';

import { transitionTo } from '../../behaviors/router';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import semver from 'semver';
import '../../components/sn-aes-modal-overlay';
import { DATA_CATEGORY_SYS_ID } from '../../../../../../../sn-aes-role-builder/constants';
import { IMPORTING_DATA__REMOVE_PENDING_RECORDS } from '../../behaviors/importing-data/constants';
import { PAGINATE__PAGE_UPDATED } from '../sn-aes-simple-paginate/constants';

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch, properties: { app } }) => {
		dispatch(APP_TAB__SET_SEARCH_INDEX);
		dispatch(APP_TAB__FETCH_APP_STATUS, {
			appSysId: app.sys_id,
			appVersion: app.version,
		});
		dispatch(APP_TAB__FETCH_DEPLOYMENT_REQUEST_STATUS, {
			appSysId: app.sys_id,
			appVersion: app.version,
		});
		dispatch(SESSION_SCOPE_BEHAVIOR__SET_APPLICATION_ID, app.sys_id);
	},
	[APP_TAB__FETCH_DEPLOYMENT_REQUEST_STATUS]: createHttpEffect(FETCH_DEPLOYMENT_REQUEST_ROUTE, {
		method: 'GET',
		batch: false,
		successActionType: APP_TAB__FETCH_DEPLOYMENT_REQUEST_STATUS_SUCCEEDED,
		errorActionType: APP_TAB__FETCH_DEPLOYMENT_REQUEST_STATUS_FAILED,
		pathParams: ['appSysId', 'appVersion'],
	}),

	[APP_TAB__FETCH_DEPLOYMENT_REQUEST_STATUS_SUCCEEDED]: ({
		properties: { prodAdminContact },
		action: {
			payload: { result },
		},
		updateState,
	}) => {
		if (isEmpty(result)) {
			updateState({ isDeploymentRequestOpen: false });
		} else {
			const { status } = result;
			const value = status && status.value;
			const isDeploymentRequestOpen = APP_TAB__OPEN_DEPLOYMENT_REQUEST_STATUS.includes(value);

			updateState({
				isDeploymentRequestOpen,
				submitTooltipContent: isDeploymentRequestOpen
					? getSubmitTooltipContent(prodAdminContact)
					: null,
			});
		}
	},

	[APP_TAB__FETCH_DEPLOYMENT_REQUEST_STATUS_FAILED]: ({
		properties: { prodAdminContact },
		updateState,
	}) => {
		updateState({
			isDeploymentRequestOpen: true,
			submitTooltipContent: getSubmitTooltipContent(prodAdminContact, true),
		});
	},

	[APP_TAB__FETCH_APP_STATUS]: createHttpEffect(FETCH_APPLICATION_STATUS_ROUTE, {
		method: 'GET',
		batch: false,
		successActionType: APP_TAB__FETCH_APP_STATUS_SUCCEEDED,
		errorActionType: APP_TAB__FETCH_APP_STATUS_FAILED,
		pathParams: ['appSysId', 'appVersion'],
	}),

	[APP_TAB__FETCH_APP_STATUS_SUCCEEDED]: ({
		properties,
		dispatch,
		action: {
			payload: { result },
		},
		updateState,
	}) => {
		const {
			app,
			sidebarAllSteps,
			appTabFocused,
			appCreatedFromScratch,
			sidebarOpen,
		} = properties;
		if (result.status.value === IN_DEVELOPMENT && appTabFocused && appCreatedFromScratch) {
			sidebarDisplay(dispatch, { isDisplayed: true });
			sidebarSetStep(dispatch, {
				step: getSidebarStartStep(app, sidebarAllSteps),
			});
			sidebarSetAccessibleSteps(dispatch, {
				types: [SIDEBAR_STEP_TYPE_CATEGORY, SIDEBAR_STEP_TYPE_COMPLETION],
			});
			updateState({ isGuidanceModalOpen: sidebarOpen });
		} else {
			sidebarDisplay(dispatch, { isDisplayed: false });
		}

		updateState({
			appStatus: result.status,
			publishDate: result.last_published_date,
		});
	},

	[APP_TAB__FETCH_APP_STATUS_FAILED]: ({ properties, updateState, dispatch }) => {
		const {
			app,
			sidebarAllSteps,
			appTabFocused,
			appCreatedFromScratch,
			sidebarOpen,
		} = properties;
		updateState({ appStatus: null, publishDate: null });

		// Show sidebar even if status api call fails
		if (appTabFocused && appCreatedFromScratch) {
			sidebarDisplay(dispatch, { isDisplayed: true });
			sidebarSetStep(dispatch, {
				step: getSidebarStartStep(app, sidebarAllSteps),
			});
			sidebarSetAccessibleSteps(dispatch, {
				types: [SIDEBAR_STEP_TYPE_CATEGORY, SIDEBAR_STEP_TYPE_COMPLETION],
			});
			updateState({ isGuidanceModalOpen: sidebarOpen });
		}
		dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
			status: 'critical',
			content: t('We encountered an error while retrieving application status.'),
		});
	},

	[COMPONENT_PROPERTY_CHANGED]: ({
		updateState,
		state: { appStatus },
		properties: {
			app,
			appCreatedFromScratch,
			sidebarAllSteps,
			sidebarOpen,
			appTabFocused,
			sidebarCurrentStep,
		},
		action: {
			payload: { name },
		},
		dispatch,
	}) => {
		if (name === 'app') {
			dispatch(APP_TAB__SET_SEARCH_INDEX);
		}

		// sidebar step has changed and sidebar is open, so display guidance modal
		if (name === 'sidebarCurrentStep' && sidebarCurrentStep && sidebarOpen) {
			updateState({ isGuidanceModalOpen: true });
		}

		// sidebar has been toggled
		if (name === 'sidebarOpen') {
			// sidebar toggled open
			if (sidebarOpen) {
				sidebarDisplay(dispatch, { isDisplayed: true });
				sidebarSetStep(dispatch, {
					step: getSidebarStartStep(app, sidebarAllSteps),
				});
				updateState({ isGuidanceModalOpen: true });
			}

			// sidebar toggled close
			if (!sidebarOpen) {
				updateState({ isGuidanceModalOpen: false });
			}
		}

		if (name === 'appTabFocused') {
			// main tab for the app is focused

			if (
				// we show sidebar if appStatus is in development
				// or when appStatus is null (i.e. the API has failed)
				(appStatus === null || get(appStatus, 'value') === IN_DEVELOPMENT) &&
				appTabFocused &&
				appCreatedFromScratch
			) {
				sidebarDisplay(dispatch, { isDisplayed: true });
			} else {
				// builder tab is focused
				sidebarDisplay(dispatch, { isDisplayed: false });
			}
		}
	},
	[APP_TAB__SET_SEARCH_INDEX]: ({ properties: { app }, updateState }) => {
		// FIXME: Update these to use the computed property versions
		// Normalize records by ID
		const filesById = normalizeById(app.files);
		const categoriesById = normalizeById(app.taxonomy_categories);
		const objectTypesById = normalizeById(app.taxonomy_definitions);

		// Group file sys_id's by taxonomy_category id
		const filesByCategoryId = groupBy(app.files, 'taxonomy_category', 'sys_id');

		const objectSearchIndex = createObjectSearchIndex(
			'sys_id',
			['name', 'description.display_value'],
			app.files
		);

		updateState({
			filesById,
			categoriesById,
			filesByCategoryId,
			objectTypesById,
			objectSearchIndex,
		});
	},
	[SEARCH_INPUT__CHANGED]: ({
		state,
		properties,
		action: {
			payload: { value },
		},
		updateState,
	}) => {
		const { objectSearchIndex } = state;
		const { app } = properties;
		const searchResultsById = {};
		const files = objectSearch(app.files, objectSearchIndex, value, file => {
			searchResultsById[file.sys_id] = file;
		});

		const searchResultsByCategoryId = groupBy(files, 'taxonomy_category', 'sys_id');

		updateState({
			searchResultsById,
			searchResultsByCategoryId,
			searchTerm: value,
			pagination: {},
		});
	},
	[APP_TAB__SORT_TYPE_SET]: ({ action: { payload }, updateState }) =>
		updateState({ sortType: payload }),
	['NOW_TABS#SELECTED_ITEM_SET']: ({
		state,
		action: {
			payload: { value },
		},
		updateState,
	}) => {
		const { categoriesById } = state;
		const category =
			value === ALL_CATEGORY
				? { name: ALL_CATEGORY, sys_id: ALL_CATEGORY }
				: categoriesById[value];

		updateState({
			category,
		});
	},
	[APPLICATION_OBJECT_CARD__CARD_CLICKED]: ({
		state: { filesById, objectTypesById },
		action: { payload },
		dispatch,
	}) => {
		const file = filesById[payload.id];

		if (payload.empty) {
			dispatch(APP_TAB__CATEGORY_CREATE_PRESSED, {
				category: { sys_id: payload.id, name: payload.categoryName },
			});
		} else if (file) {
			addTaxonomyDefinitionToFile(file, objectTypesById);
			dispatch(APP_TAB__OPEN_EDITOR_TAB, { file });
		}
	},
	[APPLICATION_OBJECT_CARD__ACTION_ITEM_CLICKED]: ({
		state: { filesById, categoriesById, objectTypesById },
		action: { payload },
		dispatch,
	}) => {
		const { id, item } = payload;
		const file = filesById[id];

		if (!file) {
			console.warn(`HANDLER FOR CREATE ${categoriesById[id].name} NOT YET IMPLEMENTED`);
			return;
		}

		addTaxonomyDefinitionToFile(file, objectTypesById);

		if (isMobileObject(file)) {
			dispatch(APP_TAB__MOBILE_CARD_CLICKED, { file });
			return;
		}

		// As dropdown items are added to the object cards add what they do to the below object.
		// The properties below correspond to the dropdown items' `id` property
		const triggers = {
			edit() {
				dispatch(APP_TAB__OPEN_EDITOR_TAB, { file });
			},

			launch() {
				const { href } = new URL(file.launch_url, window.location.origin);
				window.open(href, '_blank');
			},

			delete() {
				dispatch(APP_TAB_DELETE_OBJECT_CLICKED, file);
			},
		};

		if (item.id in triggers) {
			triggers[item.id]();
		} else {
			console.warn(`No trigger defined for dropdown item ${item.id}`);
		}
	},
	[APP_TAB__OPEN_EDITOR_TAB]: ({
		properties: { app },
		state: { objectTypesById },
		action,
		dispatch,
	}) => {
		const { file } = action.payload;

		addTaxonomyDefinitionToFile(file, objectTypesById);

		if (isMobileObject(file)) {
			dispatch(APP_TAB__MOBILE_CARD_CLICKED, { file });
			return;
		}

		dispatch(TABS__OPEN_TABS, {
			tabs: [getTabDetails(app.sys_id, file)],
		});
	},
	[BUILDER_EXIT]: ({ action: { payload }, updateState }) => {
		// TODO: Capture a payload of the created record so only that record
		// needs to be loaded. Previously, we were loading whole project but
		// removed it as it should probably be handled up higher
		if (payload.type === BUILDER_EXIT_TYPE_SUCCESS) {
			updateState({ selectedApplication: null, selectedCategory: null });
		}
	},
	['NOW_BUTTON#CLICKED']: ({ action, dispatch }) => {
		const {
			payload: { source },
		} = action;

		action.stopPropagation();

		if (source === 'submit') {
			dispatch(APP_TAB__APP_SUBMIT_CLICKED);
		}
	},
	[APP_TAB__APP_SUBMIT_CLICKED]: ({ dispatch }) => {
		const confirmCallback = () => {
			dispatch(APP_TAB__APP_SUBMIT_CONFIRMED);
		};
		dispatch(ROOT__SHOW_MODAL, {
			renderer: () => <sn-aes-confirm-submit confirm-callback={confirmCallback} />,
		});
	},
	[APP_TAB__APP_SUBMIT_CONFIRMED]: ({ state, updateState, dispatch }) => {
		const { app } = state.properties;

		updateState({ isSubmittingApp: true });

		// Steps for deployment request submission
		// 1. Get the latest app version to ensure we always have the latest
		// 2. Increment the app version
		// 3. Submit the deployment request
		// 4. Update application details w/ submitted date/time

		dispatch(APP_TAB__GET_APP_VERSION, {
			appSysId: app.sys_id,
		});
	},
	// ************ Update app version ****************
	[APP_TAB__GET_APP_VERSION]: createHttpEffect(FETCH_SYS_APP_TABLE_API_ROUTE, {
		method: 'GET',
		batch: false,
		successActionType: APP_TAB__GET_APP_VERSION_SUCCEEDED,
		errorActionType: APP_TAB__GET_APP_VERSION_FAILED,
		pathParams: ['appSysId'],
	}),
	[APP_TAB__GET_APP_VERSION_SUCCEEDED]: ({
		action: {
			payload: {
				result: { version },
			},
		},
		dispatch,
		updateState,
		properties: { app, prodAdminContact },
	}) => {
		let coercedAppVersion = version;

		if (!semver.valid(version)) {
			coercedAppVersion = semver.coerce(version);

			if (!coercedAppVersion) {
				dispatch(
					ALERTS_BEHAVIOR__ADD_ITEM,
					appendAdminContactToAlert(
						{
							status: 'critical',
							content: t(
								'Your app was not submitted. The app version is expected to be in semantic version format.'
							),
						},
						prodAdminContact
					)
				);
				updateState({ isSubmittingApp: false });

				return;
			}
		}

		const incrementedAppVersion = semver.inc(coercedAppVersion, 'patch');

		dispatch(APP_TAB__PATCH_APP_VERSION, {
			sysId: app.sys_id,
			appData: { version: incrementedAppVersion },
		});
	},
	[APP_TAB__GET_APP_VERSION_FAILED]: ({
		updateState,
		dispatch,
		properties: { prodAdminContact },
	}) => {
		updateState({ isSubmittingApp: false });

		dispatch(ALERTS_BEHAVIOR__ADD_ITEM, getSubmissionErrorAlert(prodAdminContact));
	},

	// ************ Increase the app version ****************
	[APP_TAB__PATCH_APP_VERSION]: createHttpEffect('/api/now/appcreator/app-properties/:sysId', {
		method: 'PATCH',
		pathParams: ['sysId'],
		queryParams: ['sysparm_transaction_scope'],
		dataParam: 'appData',
		successActionType: APP_TAB__PATCH_APP_VERSION_SUCCEEDED,
		errorActionType: APP_TAB__PATCH_APP_VERSION_FAILED,
		batch: false,
	}),
	[APP_TAB__PATCH_APP_VERSION_SUCCEEDED]: ({
		dispatch,
		action: { payload, meta },
		properties: { app },
	}) => {
		const {
			result: { version },
		} = payload;
		const requestedVersion = get(meta, 'request.data.version');

		// if the app version that comes back from the PATCH App Properties API call is
		// NOT the same as the version returned from the API call,
		// then something went wrong and the app version was never increased or updated.
		// we should fail the deployment request / app submission process
		if (requestedVersion !== version) {
			dispatch(APP_TAB__PATCH_APP_VERSION_FAILED);
		} else {
			dispatch(UPDATE_APP_VERSION, { version });

			dispatch(APP_TAB__SUBMIT_APP_FOR_DEPLOYMENT, {
				appSysId: app.sys_id,
				appDeploymentRequestBody: {
					app_name: app.name && app.name.display_value,
					app_version: version,
				},
			});
		}
	},
	[APP_TAB__PATCH_APP_VERSION_FAILED]: ({
		updateState,
		dispatch,
		properties: { prodAdminContact },
	}) => {
		updateState({ isSubmittingApp: false });
		dispatch(ALERTS_BEHAVIOR__ADD_ITEM, getSubmissionErrorAlert(prodAdminContact));
	},

	// ************ Submit the deployment request ****************
	[APP_TAB__SUBMIT_APP_FOR_DEPLOYMENT]: createHttpEffect(POST_DEPLOYMENT_REQUEST_ROUTE, {
		method: 'POST',
		batch: false,
		successActionType: APP_TAB__SUBMIT_APP_FOR_DEPLOYMENT_SUCCEEDED,
		errorActionType: APP_TAB__SUBMIT_APP_FOR_DEPLOYMENT_FAILED,
		pathParams: ['appSysId'],
		dataParam: 'appDeploymentRequestBody',
	}),
	[APP_TAB__SUBMIT_APP_FOR_DEPLOYMENT_SUCCEEDED]: ({
		updateState,
		dispatch,
		action: { payload },
		properties: { app },
	}) => {
		const {
			result: { status },
		} = payload;

		// app has been deployed, so we no longer need to show
		// the guidance sidebar and the guidance modal
		sidebarDisplay(dispatch, { isDisplayed: false });

		updateState({
			isSubmittingApp: false,
			appStatus: status,
			isDeploymentRequestOpen: true,
			isGuidanceModalOpen: false,
		});

		dispatch(APP_TAB__UPDATE_APP_DETAILS, {
			sysId: app.sys_id,
			appDetailsRequestBody: {
				deployment_request_submitted: true,
			},
		});
	},
	[APP_TAB__SUBMIT_APP_FOR_DEPLOYMENT_FAILED]: ({
		updateState,
		dispatch,
		properties: { prodAdminContact },
	}) => {
		updateState({ isSubmittingApp: false });
		dispatch(ALERTS_BEHAVIOR__ADD_ITEM, getSubmissionErrorAlert(prodAdminContact));
	},

	// ************ Update Application Details ****************
	[APP_TAB__UPDATE_APP_DETAILS]: createHttpEffect(CREATE_APP_DETAILS_ROUTE, {
		method: 'PATCH',
		batch: false,
		successActionType: APP_TAB__UPDATE_APP_DETAILS_SUCCEEDED,
		errorActionType: APP_TAB__UPDATE_APP_DETAILS_FAILED,
		pathParams: ['sysId'],
		dataParam: 'appDetailsRequestBody',
	}),
	[APP_TAB__UPDATE_APP_DETAILS_SUCCEEDED]: ({
		dispatch,
		updateState,
		action: {
			payload: { result },
		},
	}) => {
		updateState({
			publishDate: result.submitted_on,
		});

		dispatch(ROOT__SHOW_MODAL, {
			renderer: () => <sn-aes-submit-success />,
		});
	},
	[APP_TAB__UPDATE_APP_DETAILS_FAILED]: ({
		updateState,
		dispatch,
		properties: { prodAdminContact },
	}) => {
		updateState({ isSubmittingApp: false });
		dispatch(ALERTS_BEHAVIOR__ADD_ITEM, getSubmissionErrorAlert(prodAdminContact));
	},

	[PAGINATE__PAGE_UPDATED]: ({ state, action: { payload }, updateState }) => {
		updateState({
			pagination: {
				...state.pagination,
				[payload.name]: { ...payload },
			},
		});
	},
	[APP_TAB__CATEGORY_CREATE_PRESSED]: ({
		properties: { app, sidebarAllSteps },
		action: {
			payload: { category },
		},
		dispatch,
	}) => {
		// Setting step to whatever overlay was opened
		sidebarSetStep(dispatch, {
			step: getSidebarStepFromCategory(category, sidebarAllSteps),
		});
		sidebarLockSteps(dispatch, { isLocked: true });

		if (category.sys_id === AUTOMATION_CATEGORY_SYS_ID) {
			dispatch(APP_TAB__SHOW_OVERLAY, {
				renderer: () => (
					<sn-aes-app-add-automation appSysId={app.sys_id} inAppHome={true} />
				),
			});
		} else {
			dispatch(APP_TAB__SHOW_OVERLAY, {
				renderer: props => (
					<sn-aes-app-add-object
						appSysId={app.sys_id}
						category={category}
						{...props}
						inAppHome={true}
					/>
				),
			});
		}
	},
	['NOW_BUTTON_ICONIC#CLICKED']: ({
		properties: { app },
		dispatch,
		action: {
			payload: { source },
		},
	}) => {
		if (source === 'update-properties') {
			const onUpdateSuccess = (sysId, appName, version) => {
				dispatch(APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS);
				dispatch(APP_PROPERTIES__UPDATE_COMPLETED, { sysId, appName });
				dispatch(APP_TAB__FETCH_APP_STATUS, {
					appSysId: sysId,
					appVersion: version,
				});
			};
			const onDeleteSuccess = () => {
				dispatch(APPLICATIONS_DATA_BEHAVIOR__FETCH_APPLICATIONS);
				transitionTo(dispatch, 'home');
			};

			dispatch(ROOT__SHOW_MODAL, {
				renderer: () => (
					<sn-aes-app-properties
						onUpdateSuccess={onUpdateSuccess}
						app={app}
						onDeleteSuccess={onDeleteSuccess}
					/>
				),
			});
		}
	},
	[APP_TAB_DELETE_OBJECT_CLICKED]: ({
		properties: { app },
		state: { objectTypesById },
		action: { payload: file },
		dispatch,
	}) => {
		addTaxonomyDefinitionToFile(file, objectTypesById);

		const type = file.taxonomy_definition.type.toLowerCase();
		const effectDetails = getEffectDetails(
			TABLE_API_RECORD,
			['tableName', 'sysId'],
			['sysparm_transaction_scope']
		);
		const effectPayload = {
			sysId: file.sys_id,
			tableName: file.taxonomy_definition.table_name,
			sysparm_transaction_scope: app.sys_id,
		};
		const translatedHeaderLabel = t('Are you sure you want to delete this {0}?', type);
		const confirmationText = t(
			"The {0} '{1}'  will be permanently deleted and could affect this and other applications in unforeseen ways. Type {2} to confirm.",
			type,
			file.name,
			t('DELETE').toLocaleUpperCase()
		);
		const successAlertPayload = {
			content: t("Your app no longer contains the {0}, '{1}'.", type, file.name),
		};
		const errorAlertPayload = {
			content: t('We encountered an error while deleting {0}', file.name),
		};
		const onDeleteSuccess = () => {
			// Re-fetch the app to remove the deleted object
			dispatch(FETCH_APP_THROTTLED, { sysId: app.sys_id });

			// Close the tab which was just deleted
			dispatch(TABS__CLOSE_TAB, { tabId: file.sys_id, overrideConfirmationModal: true });
		};

		dispatch(ROOT__SHOW_MODAL, {
			renderer: () => (
				<sn-aes-confirm-delete-generic
					translatedHeaderLabel={translatedHeaderLabel}
					effectDetails={effectDetails}
					effectPayload={effectPayload}
					onDeleteSuccess={onDeleteSuccess}
					successAlertPayload={successAlertPayload}
					errorAlertPayload={errorAlertPayload}>
					<div slot="confirm-text">{confirmationText}</div>
				</sn-aes-confirm-delete-generic>
			),
		});
	},
	[APP_TAB__MOBILE_CARD_CLICKED]: ({ properties, action, dispatch }) => {
		const { app } = properties;
		const { file = {} } = action.payload;
		const editorUrl = generateLegacyFormUrl(file.editor_url, {
			applicationId: app.sys_id,
		});

		dispatch(ROOT__SHOW_MODAL, {
			renderer: () => (
				<sn-aes-edit-mobile-dialog
					currentInstanceAesConfig={PROVIDER_PROPERTIES.currentInstanceAesConfig}
					editorUrl={editorUrl}
				/>
			),
		});
	},
	[GUIDANCE_MODAL__CLOSE_CLICKED]: ({ updateState }) => {
		updateState({ isGuidanceModalOpen: false });
	},

	[APP_TAB__SHOW_OVERLAY]: ({
		properties: { appTabOverlayTargetRef },
		action: { payload },
		updateState,
		dispatch,
	}) => {
		const ModalContent = payload.renderer;
		setDelegate({ dispatch, delegateId: 'sn-aes-app-tab' });
		updateState({
			OverlayComponent: () => (
				<sn-aes-modal-overlay
					key="app-tab-overlay"
					nowHoist
					nowHoistAppendTo={appTabOverlayTargetRef}
					opened={true}
					decorated={false}>
					{ModalContent ? <ModalContent /> : null}
				</sn-aes-modal-overlay>
			),
		});
	},

	// FIXME (devin): Handle overlay close. Bad name
	[MODAL_OVERLAY__OPEN_SET]: ({ dispatch }) => {
		dispatch(APP_TAB__HIDE_OVERLAY, { showConfirmModal: false });
	},

	[WIZARD_MANAGER__TEMPLATE_COMPLETE]: ({ dispatch }) => {
		dispatch(APP_TAB__HIDE_OVERLAY);
	},

	[WIZARD_MANAGER__CANCEL]: ({ dispatch, action }) => {
		const { showConfirmModal } = action.payload;
		dispatch(APP_TAB__HIDE_OVERLAY, { showConfirmModal });
	},

	[WIZARD_MANAGER__CRIT_FAIL]: ({ dispatch }) => {
		dispatch(APP_TAB__HIDE_OVERLAY, { showConfirmModal: false });
	},

	[APP_TAB__HIDE_OVERLAY]: ({ dispatch, action: { payload } }) => {
		if (payload && payload.showConfirmModal) {
			showModal({
				dispatch,
				id: 'sn-aes-page-app-confirm',
				nextFunction: () => {
					dispatch(APP_TAB__HIDE_OVERLAY_CONFIRM);
				},
			});
		} else {
			dispatch(APP_TAB__HIDE_OVERLAY_CONFIRM);
		}
	},
	[APP_TAB__HIDE_OVERLAY_CONFIRM]: ({
		updateState,
		dispatch,
		properties: { app, sidebarAllSteps, sidebarOpen, importSetTableNames },
		state,
	}) => {
		// Remove the delegate if it exists
		unsetDelegate({ dispatch, delegateId: 'sn-aes-app-tab' });
		sidebarSetStep(dispatch, {
			step: getSidebarStartStep(app, sidebarAllSteps),
		});
		sidebarLockSteps(dispatch, { isLocked: false });
		updateState({ OverlayComponent: null, isGuidanceModalOpen: sidebarOpen });

		let { filesByCategoryId, filesById } = state;
		const dataFiles = filesByCategoryId[DATA_CATEGORY_SYS_ID];

		// remove imported records that were not imported
		if (importSetTableNames.length > 0 && dataFiles) {
			// these are files that we do not want to show on the UI as they are getting deleted
			// since the deletion takes time.
			importSetTableNames.forEach(tableName => {
				Object.keys(filesById).forEach(id => {
					const file = filesById[id];
					if (file.name_value === tableName) {
						dispatch(APP_HOME__DELETE_FILE, { deletedSysId: file.sys_id });
					}
				});
			});

			dispatch(IMPORTING_DATA__REMOVE_PENDING_RECORDS);
		}
	},

	// A blank version of an overlay is needed for the wizards. In this case
	// the wizard renders the close and back buttons, not the overlay component.
	[APP_TAB__SHOW_BLANK_OVERLAY]: ({
		properties: { appTabOverlayTargetRef },
		action: { payload },
		updateState,
		dispatch,
	}) => {
		const ModalContent = payload.renderer;
		setDelegate({ dispatch, delegateId: 'sn-aes-app-tab' });
		updateState({
			OverlayComponent: () => (
				<sn-aes-modal-overlay
					key="blank-app-tab-overlay"
					nowHoist
					nowHoistAppendTo={appTabOverlayTargetRef}
					opened={true}
					decorated={false}
					blank={true}>
					{ModalContent ? <ModalContent /> : null}
				</sn-aes-modal-overlay>
			),
		});
	},

	[WIZARD_MANAGER__EDIT_RECORD]: ({ properties, action, dispatch }) => {
		const {
			app: { sys_id },
		} = properties;
		const { filesToEdit } = action.payload;

		const newEditTabs = filesToEdit.map(file => getTabDetails(sys_id, file));

		dispatch(TABS__OPEN_TABS, { tabs: newEditTabs });

		dispatch(APP_TAB__HIDE_OVERLAY);
	},

	[ADD_OBJECT__CONTINUE]: ({ action: { stopPropagation, payload }, dispatch }) => {
		stopPropagation();
		dispatch(APP_TAB__OVERLAY_CONTINUE_CLICKED, payload);
	},
	[ADD_AUTOMATION__CONTINUE]: ({ action: { stopPropagation, payload }, dispatch }) => {
		stopPropagation();
		dispatch(APP_TAB__OVERLAY_CONTINUE_CLICKED, payload);
	},
	[APP_TAB__OVERLAY_CONTINUE_CLICKED]: ({
		properties: { app },
		action: {
			payload: { templateSysId },
		},
		dispatch,
	}) => {
		dispatch(APP_TAB__SHOW_BLANK_OVERLAY, {
			renderer: () => (
				<sn-aes-wizard-manager
					operations={[
						{
							operation: CREATE_OBJECT_TEMPLATE_OPERATION,
							params: {
								templateId: templateSysId,
								appId: app.sys_id,
							},
						},
					]}
				/>
			),
		});
	},
};
