import { t } from 'sn-translate';
import { generateLegacyFormUrl, getBuilderType } from '../../helpers/generateBuilderUrl';
import { LEGACY_BUILDER } from '../../helpers/constants';
import get from 'lodash/get';
import { ALL_CATEGORY, ALL_CATEGORY_LABEL, MOBILE_CARD_PREFIX } from './constants';

export const getSubmissionErrorAlert = prodAdminContact => {
	let alert = {
		status: 'critical',
	};

	if (prodAdminContact) {
		alert = {
			...alert,
			content: t('Connection is temporarily unavailable. Try again, later.'),
			textLinkProps: {
				label: t('Contact your admin if this persists.'),
				href: `mailto:${prodAdminContact}`,
			},
		};
	} else {
		alert = {
			...alert,
			content: t(
				'Connection is temporarily unavailable. Try again, later. If this persists, contact your admin.'
			),
		};
	}

	return alert;
};

export const appendAdminContactToAlert = (alert, prodAdminContact) => {
	var alertWithContactInfo = { ...alert };

	if (prodAdminContact) {
		alertWithContactInfo.textLinkProps = {
			textLinkProps: {
				label: t('Contact your admin if this persists.'),
				href: `mailto:${prodAdminContact}`,
			},
		};
	} else {
		alertWithContactInfo.content =
			alertWithContactInfo.content + ' ' + t('If this persists, contact your admin.');
	}

	return alertWithContactInfo;
};

export const getTabDetails = (appSysId, file) => {
	let editorUrl;

	if (get(file, 'editor_url')) {
		editorUrl = generateLegacyFormUrl(file.editor_url, {
			applicationId: appSysId,
		});
	}

	// (eguo): You may ask yourself, 'why?'. Well for legacy forms/lists, we rely
	// on a specific `.getDisplayValue()` method off of `window.g_form` to provide
	// the readable name. This method is called after the iframe has initially
	// rendered and so we have to display some label in the tab in the meantime.
	//
	// For other builders (i.e. UIB) that are NOT legacy lists/forms, there won't be
	// a `window.g_form` and the application file name should be treated as the
	// readable name.

	const builderType = getBuilderType(get(file, 'editor_url'));
	const title = builderType === LEGACY_BUILDER ? t('Loading...') : file.name;

	return {
		id: get(file, 'sys_id'),
		title,
		subtitle: get(file, 'taxonomy_definition.type', ''),
		url: editorUrl,
		isLoading: true,
		tableName: get(file, 'taxonomy_definition.table_name'),
	};
};

export function addTaxonomyDefinitionToFile(file, objectTypesById) {
	if (file && typeof file.taxonomy_definition === 'string') {
		file.taxonomy_definition = {
			...objectTypesById[file.taxonomy_definition],
		};
	}
}

export const isMobileObject = file => {
	return get(file, 'taxonomy_definition.table_name', '').startsWith(MOBILE_CARD_PREFIX);
};

export const getTabs = (
	hasSearchResults,
	categoriesById,
	filesByCategoryId,
	searchResultsByCategoryId
) => {
	let allCount = 0;
	let tabs = [];

	Object.keys(categoriesById).forEach(categoryId => {
		const count = hasSearchResults
			? get(searchResultsByCategoryId, `[${categoryId}].length`, 0)
			: get(filesByCategoryId, `[${categoryId}].length`, 0);
		allCount += count;
		tabs.push({ id: categoryId, label: categoriesById[categoryId].name, count });
	});

	tabs.unshift({ id: ALL_CATEGORY, label: ALL_CATEGORY_LABEL, count: allCount });
	return tabs;
};

export const getSubmitTooltipContent = (aesAdminContact, isServerError) => {
	if (isServerError) {
		if (aesAdminContact) {
			return t(
				'There was an error while retrieving your application deployment information. Contact your admin {0} in order to submit your app.',
				aesAdminContact
			);
		}

		return t(
			'There was an error while retrieving your application deployment information. Contact your admin in order to submit your app.'
		);
	}

	if (aesAdminContact) {
		return t(
			'Your previously submitted app is still being tested. Contact your admin {0} in order to submit a new version.',
			aesAdminContact
		);
	}

	return t(
		'Your previously submitted app is still being tested. Contact your admin in order to submit a new version.'
	);
};
