import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

// Now Components
import '@servicenow/now-modal';

import { ROOT__HIDE_MODAL } from '../sn-app-engine-studio/constants';

//Styles
import styles from './sn-aes-confirm-submit.scss';

// SVG
import connectorSvg from './assets/progress_icon.svg';

import { steps } from './constants';

createCustomElement('sn-aes-confirm-submit', {
	view: () => {
		const connectorElement = (
			<div className="connector">
				<img src={connectorSvg} alt="" />
			</div>
		);

		return (
			<now-modal
				size="lg"
				opened
				manage-opened
				headerLabel={<span aria-label={'Submit'} />}
				footerActions={[
					{
						id: 'submit',
						size: 'md',
						variant: 'primary',
						label: t('Submit'),
					},
				]}>
				<div className="submission-container">
					<h1 className="header-label">{t('Submit')}</h1>
					<div className="content">
						<h2>{t('Ready to submit your app for IT review?')}</h2>
						<div className="modal-info">
							{t(
								"Here's what to expect from your IT administrator during the review process."
							)}
						</div>
						{steps.map((step, index) => {
							const stepElement = (
								<div className="step" key={step.name}>
									<div>
										<img src={step.svg} alt="" />
										{index < steps.length - 1 ? connectorElement : null}
									</div>
									<div>
										<strong>{step.name}</strong>  {step.description}
									</div>
								</div>
							);

							return <div key={step.name}>{stepElement}</div>;
						})}
					</div>
				</div>
			</now-modal>
		);
	},
	properties: {
		confirmCallback: {
			required: true,
		},
	},
	actionHandlers: {
		['NOW_MODAL#FOOTER_ACTION_CLICKED']: ({ action, state, dispatch }) => {
			const {
				properties: { confirmCallback },
			} = state;

			action.stopPropagation();
			confirmCallback();

			dispatch(ROOT__HIDE_MODAL);
		},
	},
	styles,
});
