/**
 * @callback PopStateCallback
 * @param {PopStateEvent} event
 * @param {BrowserRouter} router
 */

export default class PopStateEventListener {
	/** @type {Set<PopStateCallback>} */
	handlers = new Set();

	/**
	 * @param {BrowserRouter} router
	 */
	constructor(router) {
		this.router = router;
		window.addEventListener('popstate', this, false);
	}

	destroy() {
		window.removeEventListener('popstate', this);
		this.handlers.clear();
	}

	/**
	 * @param {PopStateCallback} handler
	 */
	addHandler(handler) {
		if (typeof handler === 'function') {
			this.handlers.add(handler);
		}
	}

	/**
	 * @param {PopStateEvent} event
	 */
	handleEvent(event) {
		const { router, handlers } = this;

		for (const handler of handlers.values()) {
			handler(event, router);
		}
	}
}
