/**
 * Unions multiple arrays by the supplied key.
 * Result values are chosen from the first array in which the value occurs.
 *
 * Arguments
 * [arrays] The arrays to inspect.
 * [property] The property which should be unique
 *
 * Returns
 *	(Array): Returns the new array of combined values.
 *
 * Example:
 * unionBy([{ 'x': 1 }], [{ 'x': 2 }, { 'x': 1 }], 'x');
 * result => [{ 'x': 1 }, { 'x': 2 }]
 */
export default function(...arrays) {
	let by = arrays.pop();

	const flatArrays = arrays.reduce((acc, val) => acc.concat(val), []);

	const result = [];
	const map = new Map();
	for (const item of flatArrays) {
		if (typeof item[by] === 'undefined') {
			return;
		}
		if (!map.has(item[by])) {
			map.set(item[by], true);
			result.push(item);
		}
	}

	return result;
}
