import { createHttpEffect } from '@servicenow/ui-effect-http';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	appMetadataDeleteSubscription,
	appMetadataSubscription,
	appRecordSubscription,
} from './app-subscribe';
import {
	FETCH_APP_THROTTLED,
	FETCH_APP_SUCCEEDED,
	FETCH_APP_FAILED,
	SYS_METADATA_SUBSCRIBE,
	SYS_METADATA_DATASET_CHANGED,
	SYS_METADATA_SUBSCRIBE_FAILED,
	SYS_APP_RECORD_SUBSCRIBE,
	SYS_APP_RECORD_CHANGED,
	SYS_APP_SUBSCRIBE_FAILED,
	APP_SUBSCRIPTIONS_INITIATED,
	APP_SUBSCRIPTIONS_TERMINATED,
	APP_HOME__DELETE_FILE,
	SYS_METADATA_DELETE_SUBSCRIBE,
	SYS_METADATA_DELETE_DATASET_CHANGED,
	SYS_METADATA_DELETE_SUBSCRIBE_FAILED,
} from './constants';

import get from 'lodash/get';

import { t } from 'sn-translate';

export const requestFetchApp = createHttpEffect(`/api/sn_app_eng_studio/applications/:sysId`, {
	method: 'GET',
	successActionType: FETCH_APP_SUCCEEDED,
	errorActionType: FETCH_APP_FAILED,
	batch: false,
	pathParams: ['sysId'],
});

export const appMetadataSubscriptionEffect = createGraphQLEffect(appMetadataSubscription, {
	templateVarList: ['sysId'],
	itemChangedActionType: SYS_METADATA_DATASET_CHANGED,
	itemEnteredActionType: SYS_METADATA_DATASET_CHANGED,
	itemExitedActionType: SYS_METADATA_DATASET_CHANGED,
	subscriptionFailedActionType: SYS_METADATA_SUBSCRIBE_FAILED,
});

export const appRecordSubscriptionEffect = createGraphQLEffect(appRecordSubscription, {
	templateVarList: ['sysId'],
	itemChangedActionType: SYS_APP_RECORD_CHANGED,
	itemEnteredActionType: SYS_APP_RECORD_CHANGED,
	itemExitedActionType: SYS_APP_RECORD_CHANGED,
	subscriptionFailedActionType: SYS_APP_SUBSCRIBE_FAILED,
});

export const appMetadataDeleteSubscriptionEffect = createGraphQLEffect(
	appMetadataDeleteSubscription,
	{
		templateVarList: ['sysId'],
		itemChangedActionType: SYS_METADATA_DELETE_DATASET_CHANGED,
		itemEnteredActionType: SYS_METADATA_DELETE_DATASET_CHANGED,
		itemExitedActionType: SYS_METADATA_DELETE_DATASET_CHANGED,
		subscriptionFailedActionType: SYS_METADATA_DELETE_SUBSCRIBE_FAILED,
	}
);

export const appSubscriptionDataChanged = ({ action, state, dispatch }) => {
	const operation = get(
		action,
		'payload.GlideRecord_Subscription.sys_metadata._subscription.subscription_operation'
	);
	const sysClassName = get(
		action,
		'payload.GlideRecord_Subscription.sys_metadata._results[0].sys_class_name.value'
	);

	if (
		(operation === 'insert' && sysClassName === 'sys_metadata_delete') ||
		operation === 'delete'
	) {
		// This will be handled by sys_metadata_delete subscription, so do nothing here
		return;
	}

	// If there were any changes at all to data we are subscribed to, we are re-fetching the app
	// This is seamless to the user. We should be more efficient in the future and only update the UI
	// with objects that were changed.
	const sysId = get(state, 'app.sys_id', '');
	dispatch(FETCH_APP_THROTTLED, { sysId });
};

export const appMetadataDeleteChanged = ({ action, state, dispatch }) => {
	const tableName = get(
		action,
		'payload.GlideRecord_Subscription.sys_metadata_delete._results[0].sys_db_object.value'
	);

	const deletedSysId = get(
		action,
		'payload.GlideRecord_Subscription.sys_metadata_delete._results[0].sys_metadata.value'
	);

	if (state.taxonomyTableNames.includes(tableName) && deletedSysId) {
		// This is a delete operation on one of AES taxonomies, so send it through the delete flow
		dispatch(APP_HOME__DELETE_FILE, { deletedSysId });
	}
};

export const appSubscriptionErrorOccurred = ({ action }) => {
	// Intentionally fail silently
	console.error(
		t(`An error occurred during subscription attempt. Source action: {0}`, action.type)
	);
};

export const handleAppSubscription = ({ action, dispatch }) => {
	const sysId = get(action, 'payload.sysId', '');

	if (action.type === APP_SUBSCRIPTIONS_INITIATED) {
		dispatch(SYS_METADATA_SUBSCRIBE, { sysId, subscribe: true });
		dispatch(SYS_METADATA_DELETE_SUBSCRIBE, { sysId, subscribe: true });
		dispatch(SYS_APP_RECORD_SUBSCRIBE, { sysId, subscribe: true });
	}

	if (action.type === APP_SUBSCRIPTIONS_TERMINATED) {
		dispatch(SYS_METADATA_SUBSCRIBE, { sysId, subscribe: false });
		dispatch(SYS_METADATA_DELETE_SUBSCRIBE, { sysId, subscribe: false });
		dispatch(SYS_APP_RECORD_SUBSCRIBE, { sysId, subscribe: false });
	}
};
