import { createCustomElement } from '@servicenow/ui-core';
import { csPageInvalidLicense, csPageSetup } from './sn-aes-page-error-messages';
import { VALIDATE_LICENSE, REDIRECT } from '../../behaviors/aes-access-check/constants';
import getAdminContact from '../../helpers/get-admin-contact';

import aesConfigurationBehavior from '../../behaviors/aes-configuration';

// Styles
import styles from './sn-aes-page-aes-access-check.scss';

function view(state) {
	const {
		properties: { pageView, prodAdminContact },
	} = state;

	if (pageView === VALIDATE_LICENSE) {
		return csPageInvalidLicense(prodAdminContact);
	} else if (pageView === REDIRECT) {
		return csPageSetup(prodAdminContact);
	}
}

/**
 * Base or entry point for landing pages
 *
 * ```jsx
 * <sn-aes-page-aes-access-check />
 * ```
 *
 * @seismicElement sn-aes-page-aes-access-check
 * @summary This page displays a message alerting the user of an error
 */
createCustomElement('sn-aes-page-aes-access-check', {
	view,
	properties: {
		pageView: {
			default: null,
		},
		prodAdminContact: {
			computed({ properties: { prodInstanceAesConfig } }) {
				return getAdminContact(prodInstanceAesConfig);
			},
		},
	},
	behaviors: [aesConfigurationBehavior],
	styles,
});
