import PopStateEventListener from './PopStateEventListener';

/** @typedef {{path: string}} RoutePathDefinition */
/** @typedef {Object.<string, RoutePathDefinition>} RoutePathDefinitions */

function createPatternMatchRegExp(patternString) {
	const wildcardRegExp = /\*$/;
	let regexpString = patternString.replace(/:\w+/g, '\\w+');

	if (wildcardRegExp.test(patternString)) {
		regexpString = regexpString.replace(wildcardRegExp, '.*');
	}

	return new RegExp(`${regexpString}$`);
}

/**
 * @param {String} urlString
 */
export function matchURL(urlString) {
	if (typeof urlString !== 'string') {
		throw new TypeError(`[matchURL] expected url to be a string`);
	}

	const { pathname } = new URL(urlString, window.location.origin);
	const normalizedPath = pathname.replace(/(\w+)\/$/, '$1');

	/**
	 * @param {RegExp} patternRegExp
	 * @returns {boolean}
	 */
	function withPatternRegExp(patternRegExp) {
		if (!(patternRegExp instanceof RegExp)) {
			return false;
		}

		return patternRegExp.test(normalizedPath);
	}

	/**
	 * @param {String} patternString
	 * @returns {boolean}
	 */
	function withPatternString(patternString) {
		if (typeof patternString !== 'string') {
			return false;
		}

		const patternRegExp = createPatternMatchRegExp(patternString);
		return withPatternRegExp(patternRegExp);
	}

	return { withPatternString, withPatternRegExp };
}

export function matchParams(params = {}) {
	return {
		withObject(matchParams = {}) {
			for (const [key, value] of Object.entries(params)) {
				if (matchParams[key] !== value) {
					return false;
				}
			}
			return true;
		},
	};
}

/**
 * @param {string} pattern
 * @param {string} params
 * @returns {string}
 */
export function serializeURLPattern(pattern, params = {}) {
	if (typeof pattern !== 'string' || typeof params !== 'object' || Array.isArray(params)) {
		return '';
	}

	return pattern
		.replace(/\/:(\w+)/g, (match, capture) => {
			return capture in params ? `/${params[capture]}` : match;
		})
		.replace(/\/\*?$/, '');
}

/**
 * @param {string} pattern
 * @param {string} url
 * @returns {Object.<string, string>}
 */
export function extractURLParams(url, pattern) {
	if (typeof url !== 'string' || typeof pattern !== 'string') {
		return {};
	}

	const patternPaths = pattern.split('/');
	const urlPaths = url.split('/');

	return patternPaths.reduce((params, patternSegment, index) => {
		if (patternSegment.charAt(0) !== ':') {
			return params;
		}

		const propName = patternSegment.substr(1);
		return {
			...params,
			[propName]: urlPaths[index],
		};
	}, {});
}

/**
 * The pattern passed in is expected to be the `path` correlated to a matching route.
 * This implies that it adheres to the path normalization from `normalizeRoutePaths`
 *
 * @param {string} pattern
 * @returns {string}
 */
export function deriveNamespace(pattern) {
	if (typeof pattern !== 'string' || pattern === '' || pattern.charAt(0) !== '/') {
		return '';
	}

	const { pathname } = window.location;
	const pathParts = pathname.split('/');
	const patternParts = pattern.split('/');

	if (pathParts.length <= patternParts.length) {
		return '';
	}

	const [, firstPart] = patternParts;
	if (firstPart === '*' || firstPart === '') {
		return pathname;
	}

	const regexp = new RegExp(`${pattern.replace(/\/\*$/, '').replace(/:\w+/g, '\\w+')}.*$`);
	return pathname.replace(regexp, '');
}

/**
 * @param {RoutePathDefinitions} routes Route definitions
 * @returns {RoutePathDefinitions}
 */
function normalizeRoutePaths(routes) {
	const normalizedRoutes = {};

	if (typeof routes === 'object') {
		for (const [routeName, options] of Object.entries(routes)) {
			const { path } = options;

			if (typeof path !== 'string') {
				continue;
			}

			const { pathname } = new URL(path, window.location.origin);
			normalizedRoutes[routeName] = {
				...options,
				path: pathname.length === 1 ? pathname : pathname.replace(/\/$/, ''),
			};
		}
	}

	return normalizedRoutes;
}

export const UNDEFINED = Symbol('UNDEFINED_VALUE');
export const CURRENT_ROUTE_NAME = Symbol('__current_route_name__');
export const CURRENT_ROUTE_PARAMS = Symbol('__current_route_params__');

export default class BrowserRouter {
	[CURRENT_ROUTE_NAME] = UNDEFINED;
	[CURRENT_ROUTE_PARAMS] = UNDEFINED;

	/**
	 * @param {{namespace?: string, routes: RoutePathDefinitions}} options
	 */
	constructor(options = {}) {
		Object.defineProperties(this, {
			routes: {
				configurable: false,
				writable: false,
				// TODO: add matching order logic to routes
				value: Object.seal(normalizeRoutePaths(options.routes)),
			},
			popStateEventListener: {
				configurable: false,
				writable: false,
				value: new PopStateEventListener(this),
			},
		});

		this.onPopState(() => {
			this.clearPropertyCache();
		});

		let namespace = options.namespace;
		if (typeof namespace !== 'string') {
			const { path } = this.currentRouteOptions;
			namespace = deriveNamespace(path);
		}

		Object.defineProperty(this, 'namespace', {
			configurable: false,
			writable: false,
			value: namespace,
		});
	}

	get currentHistoryState() {
		return window.history.state;
	}

	get currentRouteName() {
		let { [CURRENT_ROUTE_NAME]: currentRoute } = this;

		if (currentRoute === UNDEFINED) {
			currentRoute = this.getRouteNameByUrl(window.location.href);
			this[CURRENT_ROUTE_NAME] = currentRoute;
		}

		return currentRoute;
	}

	get currentRouteParams() {
		let { [CURRENT_ROUTE_PARAMS]: currentParams } = this;

		if (currentParams === UNDEFINED) {
			currentParams = this.extractRouteParams(this.currentRouteName, window.location.href);
			this[CURRENT_ROUTE_PARAMS] = currentParams;
		}

		return currentParams;
	}

	get currentRouteOptions() {
		return this.getRouteOptions(this.currentRouteName) || {};
	}

	getRouteOptions(routeName) {
		return this.routes[routeName];
	}

	hasRoute(routeName) {
		return routeName in this.routes;
	}

	getRouteNameByUrl(urlString) {
		const { namespace, routes = {} } = this;
		const entries = Object.entries(routes);
		const match = matchURL(urlString.replace(namespace, ''));

		for (const [routeName, { path }] of entries) {
			if (match.withPatternString(path)) {
				return routeName;
			}
		}

		return undefined;
	}

	serializeRouteURL(routeName, params = {}) {
		if (typeof params !== 'object') {
			return undefined;
		}

		const { namespace } = this;
		const { path } = this.getRouteOptions(routeName);
		const serializedPattern = serializeURLPattern(path, params);
		return `${namespace}${serializedPattern}`;
	}

	extractRouteParams(routeName, urlString) {
		const url = new URL(urlString, window.location.origin);
		const urlPath = url.pathname.replace(this.namespace, '');
		const { path } = this.getRouteOptions(routeName);
		return extractURLParams(urlPath, path);
	}

	pushRouteState(state, params, routeName) {
		if (this.hasRoute(routeName) && !this.isMatchWithCurrentRoute(routeName, params)) {
			const url = this.serializeRouteURL(routeName, params);
			window.history.pushState(state, null, url);
			this.clearPropertyCache();
		} else {
			window.history.pushState(state, null);
		}
	}

	replaceRouteState(state, params, routeName) {
		if (this.hasRoute(routeName) && !this.isMatchWithCurrentRoute(routeName, params)) {
			const url = this.serializeRouteURL(routeName, params);
			window.history.replaceState(state, null, url);
			this.clearPropertyCache();
		} else {
			window.history.replaceState(state, null);
		}
	}

	/**
	 * @param {PopStateCallback} handler
	 */
	onPopState(handler) {
		this.popStateEventListener.addHandler(handler);
	}

	isMatchWithCurrentRoute(routeName, params) {
		return (
			routeName === this.currentRouteName &&
			matchParams(this.currentRouteParams).withObject(params)
		);
	}

	clearPropertyCache() {
		this[CURRENT_ROUTE_NAME] = UNDEFINED;
		this[CURRENT_ROUTE_PARAMS] = UNDEFINED;
	}

	destroy() {
		this.popStateEventListener.destroy();
	}
}
