import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-tooltip';

createCustomElement('sn-aes-with-tooltip', {
	view: (state, { updateState }) => (
		<Fragment>
			<div
				hook-insert={({ elm }) => updateState({ elementRef: elm })}
				hook-destroy={() => updateState({ elementRef: null })}>
				<slot />
			</div>
			<now-tooltip
				content={state.properties.tooltipContent}
				delay={state.properties.delay}
				id={`${state.properties.id}-tooltip`}
				target-ref={state.elementRef}
			/>
		</Fragment>
	),
	properties: {
		tooltipContent: {
			default: null,
		},
		id: {
			default: '',
		},
		delay: {
			default: 500,
		},
	},
	initialState: {
		elementRef: null,
	},
});
