import { createHttpEffect } from '@servicenow/ui-effect-http';

import { ALERTS_BEHAVIOR__ADD_ITEM } from '../../behaviors/alerts';

import {
	RESOURCES__CONTENT_FETCH_SUCCEEDED,
	RESOURCES__CONTENT_FETCH_FAILED,
	RESOURCES__CONTENT_SET_SEARCH_INDEX,
	FETCH_RESOURCES_ENDPOINT,
	ERROR_CONTENT,
} from './constants';

import { getContentIndex, filterContent } from './helpers';

import get from 'lodash/get';

export const getResourcesPageContent = createHttpEffect(FETCH_RESOURCES_ENDPOINT, {
	method: 'GET',
	successActionType: RESOURCES__CONTENT_FETCH_SUCCEEDED,
	errorActionType: RESOURCES__CONTENT_FETCH_FAILED,
	batch: false,
});

export const getResourcesContentSuccess = ({ action, updateProperties, dispatch }) => {
	const content = get(action, 'payload.result.content', []);
	const topics = get(action, 'payload.result.topics', []);
	dispatch(RESOURCES__CONTENT_SET_SEARCH_INDEX, { content });
	updateProperties({ content, topics, loading: false });
};

export const getResourcesContentFailed = ({ dispatch, updateProperties }) => {
	dispatch(ALERTS_BEHAVIOR__ADD_ITEM, {
		status: 'critical',
		content: ERROR_CONTENT,
	});

	updateProperties({ loading: false });
};

export const handleSearchInputChanged = ({ action, state, properties, updateState }) => {
	action.stopPropagation();
	const content = get(properties, 'content', []);
	const contentIndex = get(state, 'contentIndex', {});
	const { value: searchInputValue } = action.payload;

	updateState({
		searchInputValue,
		filteredContent: filterContent(
			{
				list: content,
				index: contentIndex,
			},
			searchInputValue
		),
	});
};

export const setContentSearchIndex = ({ action: { payload }, state, updateState }) => {
	const content = get(payload, 'content', []);
	const { searchInputValue } = state;
	const contentIndex = getContentIndex(content);

	updateState({
		contentIndex,
		filteredContent: filterContent(
			{
				list: content,
				index: contentIndex,
			},
			searchInputValue
		),
	});
};
