import { t } from 'sn-translate';
import { ADMIN_ROLE, SECURITY_ADMIN_ROLE, MAINT_ROLE } from './constants';

export function checkboxState(files, numACLPerFile, onlyBoolean = false) {
	const maxACLs = files.length * numACLPerFile;
	let checkedStatus = false;
	let checkedCount = 0;

	for (const file of files) {
		checkedCount += Object.keys(file.acls).length;
	}

	if (checkedCount === maxACLs) {
		// All checkboxes are checked
		checkedStatus = true;
	}

	if (!onlyBoolean && checkedCount > 0 && checkedCount < maxACLs) {
		// Only some checkboxes are checked
		checkedStatus = 'indeterminate';
	}

	return checkedStatus;
}

export function getRoleName(roleName) {
	if (!roleName) {
		// Role name is null/undefined/empty string, so return (empty) string
		return t('(empty)');
	}

	const data = roleName.split('.');

	if (data.length === 2) {
		// Role name in the format app_scope.role_name
		return data[1];
	}

	// Does not match anything, so don't do anything
	return roleName;
}

export function cannotEditRoles(user = {}) {
	const { roles = [] } = user;

	// if user is not maint and has admin role but doesn't have security_admin role
	return (
		!roles.includes(MAINT_ROLE) &&
		(roles.includes(ADMIN_ROLE) && !roles.includes(SECURITY_ADMIN_ROLE))
	);
}
