import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import 'sn-polyfills/dist/polyfills/ce-sd.min';
import { snHelpActionTypes, snHelpBehavior } from '@devsnc/sn-help/src/sn-help';
import '@servicenow/now-alert';
import '@servicenow/now-modal';
import '../sn-aes-user-data-provider';

import '../sn-aes-modal-overlay';
import { USER_DATA_BEHAVIOR__UPDATE_PREF } from '../../behaviors/user-data';
import { HELP_GUIDANCE_ID } from './constants';
import { USER_PREF_HAS_VIEWED_WELCOME } from '../../behaviors/user-data/constants';

createCustomElement('sn-aes-help-manager', {
	view: () => {
		return <sn-help id={HELP_GUIDANCE_ID} />;
	},

	behaviors: [snHelpBehavior],

	initialState: {},

	properties: {
		guidanceId: {
			default: '',
		},
		enableGuidanceModal: {
			default: false,
		},
	},
	actionHandlers: {
		[snHelpActionTypes.GUIDANCE_STARTED]: ({ host }) => {
			// attempt to update styles as the elements are not rendered directly when
			// guidance is started
			const setStyles = attempts => {
				// override sn-help modal styles in order to get closer to design
				// sn-help modal does not have additional configurations for these yet
				const stylesUpdated = updateStyles(host);

				if (!stylesUpdated && attempts > 0) {
					setTimeout(() => setStyles(--attempts), 100);
				}
			};
			setTimeout(() => setStyles(50), 100);
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({ dispatch, properties, action }) => {
			const {
				payload: { name, value: enableGuidanceModal },
			} = action;

			// start guidance modal when it has been updated to true
			if (name === 'enableGuidanceModal' && !!enableGuidanceModal) {
				const { guidanceId } = properties;
				dispatch(snHelpActionTypes.GUIDANCE_START_REQUESTED, { guidanceId });
			}
		},
		[snHelpActionTypes.GUIDANCE_ENDED]: ({ dispatch }) => {
			dispatch(USER_DATA_BEHAVIOR__UPDATE_PREF, {
				name: USER_PREF_HAS_VIEWED_WELCOME,
				value: true,
			});
		},
		['SN_HELP_MODAL#MODAL_CLOSED']: ({ dispatch }) => {
			dispatch(USER_DATA_BEHAVIOR__UPDATE_PREF, {
				name: USER_PREF_HAS_VIEWED_WELCOME,
				value: true,
			});
		},
	},
});

const updateStyles = hostNode => {
	const { headerNode, subHeaderNode, wrapperNode } = getModalNodes(hostNode);

	if (headerNode && subHeaderNode && wrapperNode) {
		const overrideStyles = {
			newHeaderColor: '#01778E',
			newFontWeight: 'bold',
			newFontSize: '20px',
		};

		// update header to match the color scheme of aes
		headerNode.style.color = overrideStyles.newHeaderColor;

		// update font size and weight to match aes welcome sub header
		subHeaderNode.style.fontWeight = overrideStyles.newFontWeight;
		subHeaderNode.style.fontSize = overrideStyles.newFontSize;

		// set wrapper node height to auto instead of fixed
		wrapperNode.style.height = 'auto';
		return true;
	}
	return false;
};

const getModalNodes = host => {
	const nowModalSelectors = [
		`#${HELP_GUIDANCE_ID}`,
		'sn-help-step-orchestrator',
		'sn-help-modal',
		'now-modal',
	];
	const nowModalNode = selectNestedShadowNode(host, nowModalSelectors);
	// these selectors are within in the now modal and not nested in the shadow dom
	const subHeaderNode = nowModalNode && nowModalNode.querySelector('now-rich-text');
	const wrapperNode = nowModalNode && nowModalNode.querySelector('.sn-help-modal-wrapper');

	// these nodes are found nested within the shadow dom.
	const headerNode = selectNestedShadowNode(nowModalNode, ['.now-modal-heading']);

	return {
		wrapperNode,
		headerNode,
		subHeaderNode,
	};
};

const selectNestedShadowNode = (node, selectors) => {
	let selectedNode = node;
	for (let i = 0; i < selectors.length; i++) {
		const selector = selectors[i];
		selectedNode =
			selectedNode &&
			selectedNode.shadowRoot &&
			selectedNode.shadowRoot.querySelector(selector);
		if (!selectedNode) {
			return selectedNode;
		}
	}
	return selectedNode;
};
