import { actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	FETCH_LICENSE_VALIDATION_STATUS_ROUTE,
	FETCH_REDIRECT_DETAILS_ROUTE,
} from '../../helpers/constants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

import {
	AES_ACCESS,
	VALIDATE_LICENSE,
	VALIDATE_LICENSE__FETCH_VALIDATE_LICENSE_DETAILS,
	VALIDATE_LICENSE__FETCH_VALIDATE_LICENSE_DETAILS_SUCCEEDED,
	VALIDATE_LICENSE__FETCH_VALIDATE_LICENSE_DETAILS_FAILED,
	REDIRECT,
	REDIRECT__FETCH_REDIRECT_DETAILS,
	REDIRECT__FETCH_REDIRECT_DETAILS_SUCCEEDED,
	REDIRECT__FETCH_REDIRECT_DETAILS_FAILED,
} from './constants';

const aesAccessCheckBehavior = {
	name: AES_ACCESS,

	initialState: {
		areChecksComplete: false,
		accessPageView: '',
	},

	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch(VALIDATE_LICENSE__FETCH_VALIDATE_LICENSE_DETAILS);
		},
		[VALIDATE_LICENSE__FETCH_VALIDATE_LICENSE_DETAILS]: createHttpEffect(
			FETCH_LICENSE_VALIDATION_STATUS_ROUTE,
			{
				method: 'GET',
				batch: false,
				successActionType: VALIDATE_LICENSE__FETCH_VALIDATE_LICENSE_DETAILS_SUCCEEDED,
				errorActionType: VALIDATE_LICENSE__FETCH_VALIDATE_LICENSE_DETAILS_FAILED,
			}
		),
		[VALIDATE_LICENSE__FETCH_VALIDATE_LICENSE_DETAILS_SUCCEEDED]: ({
			action: {
				payload: { result },
			},
			dispatch,
			updateState,
		}) => {
			const { hasLicenseForApp } = result;

			if (!hasLicenseForApp) {
				updateState({
					path: `behaviors.${AES_ACCESS}`,
					operation: 'set',
					value: {
						areChecksComplete: true,
						accessPageView: VALIDATE_LICENSE,
					},
				});
			} else {
				dispatch(REDIRECT__FETCH_REDIRECT_DETAILS);
			}
		},
		[VALIDATE_LICENSE__FETCH_VALIDATE_LICENSE_DETAILS_FAILED]: ({ updateState }) => {
			updateState({
				path: `behaviors.${AES_ACCESS}`,
				operation: 'set',
				value: {
					areChecksComplete: true,
					accessPageView: VALIDATE_LICENSE,
				},
			});
		},

		[REDIRECT__FETCH_REDIRECT_DETAILS]: createHttpEffect(FETCH_REDIRECT_DETAILS_ROUTE, {
			method: 'GET',
			batch: false,
			successActionType: REDIRECT__FETCH_REDIRECT_DETAILS_SUCCEEDED,
			errorActionType: REDIRECT__FETCH_REDIRECT_DETAILS_FAILED,
		}),
		[REDIRECT__FETCH_REDIRECT_DETAILS_SUCCEEDED]: ({
			action: {
				payload: { result },
			},
			updateState,
		}) => {
			const { isProdInstance, devUrl } = result;

			if (!isProdInstance) {
				updateState({
					path: `behaviors.${AES_ACCESS}.areChecksComplete`,
					operation: 'set',
					value: true,
				});
				return;
			}

			const currentPathName = new URL(window.location.href).pathname;
			let redirectToUrl = new URL(currentPathName, devUrl);

			window.location.href = redirectToUrl.href;
		},
		[REDIRECT__FETCH_REDIRECT_DETAILS_FAILED]: ({ updateState }) => {
			updateState({
				path: `behaviors.${AES_ACCESS}`,
				operation: 'set',
				value: {
					areChecksComplete: true,
					accessPageView: REDIRECT,
				},
			});
		},
	},
};

export default aesAccessCheckBehavior;
