import {
	BUILDER_EXIT,
	BUILDER_EXIT_TYPE_DELETE,
	BUILDER_UPDATE,
	BUILDER_UPDATE_SESSION,
} from './constants';
import applyDevStudioAPI from './dev-studio-api';

import get from 'lodash/get';

/*

This builder handler is specifically for the legacy platform record form
(ex. sys_db_object.do?sysId=).  Adds the necessary hooks into the form to
dispatch:
	- BUILDER_UPDATE: when the title and/or field has been modified (unsaved changes)
	- BUILDER_EXIT: when the record is deleted

 Visually, customizes the toolbar by hiding the backbutton.

 */

// Get the current title of the form. If the title has been modified but not yet
// saved, return this.
function getTitle({ contentWindow: { g_scratchpad, g_form } }) {
	if (!g_form) {
		return;
	}
	// Check the scratchpad (unsaved changes) for the current title
	const create_now_studio = g_scratchpad && g_scratchpad.create_now_studio;
	if (create_now_studio) {
		return g_form.getValue(`${g_form.getTableName()}.${create_now_studio.display_name}`);
	}

	// If we don't have something on the scratchpad, fallback to our best-bet, the display value.
	return g_form.getDisplayValue();
}

// Determine whether the form has any unsaved changes by checking if there are
// any modified fields.
function getHasUnsavedChanges({ contentWindow }) {
	if (!contentWindow.g_form) {
		return;
	}
	const { modifiedFields } = contentWindow.g_form;
	for (const field of Object.keys(modifiedFields)) {
		if (modifiedFields[field]) {
			return true;
		}
	}
	return false;
}

const getBuilderState = iframe => {
	return {
		hasUnsavedChanges: getHasUnsavedChanges(iframe),
		title: getTitle(iframe),
	};
};

/**
 * Compare the pathname (i.e. `sys_db_object.do?`) and the `sys_id` param between the contentWindow
 * inside the iframe and the outer creator studio tabUrl. If either don't match, then return `true`
 * to hide that back button!
 */
const shouldHideBackButton = (iframePath, tabPath) => {
	const iframeUrl = new URL(iframePath, window.location.href);
	const tabUrl = new URL(tabPath, window.location.href);

	return (
		iframeUrl.pathname === tabUrl.pathname &&
		iframeUrl.searchParams.get('sys_id') === tabUrl.searchParams.get('sys_id')
	);
};

// Punch the back button on the legacy list out of existence when outer and inner urlpaths match!
function hideLegacyBackButton({ contentWindow }, csTabPath) {
	const iframePath = contentWindow.location.pathname + contentWindow.location.search;
	const hideButton = shouldHideBackButton(iframePath, csTabPath);

	if (hideButton) {
		const backButton = contentWindow.document.querySelector(
			'.btn.btn-default.icon-chevron-left.navbar-btn'
		);

		if (backButton) {
			backButton.style.display = 'none';
		}
	}
}

// Hook into the form's field changed callback and dispatch a BUIDLER_UPDATE
// action with the latest state of the builder on field change.
function listenForBuilderStateChanges(iframe, dispatch) {
	const {
		contentWindow: { g_form },
	} = iframe;
	if (!g_form) {
		return;
	}

	g_form.fieldChanged = g_form.fieldChanged.wrap((callOriginal, attrName, changed) => {
		const result = callOriginal(attrName, changed);
		dispatch(BUILDER_UPDATE, getBuilderState(iframe));
		return result;
	});
}

// Hook into the record deleted callback and dispatch a BUILDER_EXIT action on
// deletion.
function listenForDeletion({ contentWindow }, dispatch) {
	// TODO(pwong): Some legacy form pages don't have this callback.  A fallback
	// approach using AMB (same as Dev Studio) will need to be added.
	const callOriginal = contentWindow.deleteCompleted;
	contentWindow.deleteCompleted = (...args) => {
		dispatch(BUILDER_EXIT, { type: BUILDER_EXIT_TYPE_DELETE });
		if (typeof callOriginal === 'function') {
			return callOriginal(...args);
		}
	};

	/**
	 * TODO(eguo): Might be able to delete the code above, but a bigger gap here
	 * is that if the object/record isn't currently open in a sn-aes-tab then its
	 * deletion would not be caught
	 */
	if (contentWindow.amb) {
		// We're grabbing the record-watcher channel that the iframe is subscribed to...
		const channelNames = Object.keys(contentWindow.amb.getClient().getChannels());

		const rwChannel = channelNames.find(channel => channel.includes('/rw/default/'));

		// ...and having creator studio register a callback to that same channel
		rwChannel &&
			contentWindow.amb
				.getClient()
				.getChannel(rwChannel)
				.subscribe(evt => {
					const {
						data: { operation },
					} = evt;
					if (operation === 'delete') {
						dispatch(BUILDER_EXIT, { type: BUILDER_EXIT_TYPE_DELETE });
					}
				});
	}
}

/**
 * Inspired by https://code.devsnc.com/dev/dev-studio/blob/master/dev-studio/src/main/plugins/com.glide.dev-studio/ui.html/scripts/dev-studio/editing/dmf/factory.gFormDmf.js#L39
 *
 * Overrides submit and update button functionality to mimic
 * insert and stay/update and stay functionality in order to
 * ensure the user is redirected to the newly created record.
 *
 * @param {object} iframe
 */
function handleSubmitUpdateButtons({ contentWindow }) {
	if (!(contentWindow && contentWindow.gsftSubmit)) {
		return;
	}

	contentWindow.gsftSubmit = contentWindow.gsftSubmit.wrap(
		(callOriginal, control, form, action_name) => {
			let actionName = control && control.id;

			if (
				[
					'sysverb_insert',
					'sysverb_insert_bottom',
					'sysverb_update',
					'sysverb_update_bottom',
				].includes(actionName)
			) {
				return callOriginal(contentWindow.gel('sysverb_update_and_stay'));
			}

			return callOriginal(control, form, action_name);
		}
	);
}

// Makes the navbar look nicer with App Engine Studio tabs.
function applyStylesToNavbar({ contentWindow }) {
	const navbar = contentWindow.document.querySelector('nav.navbar.navbar-default');

	if (navbar) {
		navbar.style.backgroundColor = '#f6f6f6';
		navbar.style.borderTop = 0;
	}
}

/**
 * TODO(eguo): This is the minimum amount of DMFs we think we have to implement
 * for the `sys_db_object` case but there are a number of them that we have omitted,
 * including notably the `contentWindow.gsftSubmit` DMF - https://code.devsnc.com/dev/dev-studio/blob/master/dev-studio/src/main/plugins/com.glide.dev-studio/ui.html/scripts/dev-studio/editing/dmf/factory.gFormDmf.js#L39
 */

export default async function(iframe, dispatch, urlPath, appId) {
	const applyDMFs = () => {
		hideLegacyBackButton(iframe, urlPath);
		applyStylesToNavbar(iframe);
		listenForBuilderStateChanges(iframe, dispatch);
		listenForDeletion(iframe, dispatch);
		handleSubmitUpdateButtons(iframe);
		applyDevStudioAPI(iframe, dispatch, appId);
	};

	await applyDMFs();

	// Send initial builder state.
	dispatch(BUILDER_UPDATE, {
		...getBuilderState(iframe),
		isLoading: false,
	});

	dispatch(BUILDER_UPDATE_SESSION, {
		title: getTitle(iframe),
		url: get(iframe, 'contentWindow.location.href'),
	});
}
